const getters = {
  getNews: (state) => (id) => {
    for (var i in state.news) {
      if (state.news[i]["id"] == id) {
        return state.news[i];
      }
    }
    return {}
  },

  hasNews: (state) => {
    return state.hasNews
  }
}

const actions = {
  readnews({
    commit
  }) {
    console.log("xxxxxxx read")
    Storage.set(ONES_NOTIFICATION, "ones_6")
    commit("read")
  },
}

const mutations = {
  read(state) {
    state.hasNews = false
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
}


