import Http from './http'
import account from '../store/modules/account'

export default {
    detail() {
        return Http.post({
            path: "/invite/detail",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {}
        })
    },
    rebates() {
        return Http.post({
            path: "/invite/rebates",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {}
        })
    },
    claim(order_id) {
        return Http.post({
            path: "/invite/claim",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {
                order_id: order_id
            }
        })
    },
    qrcode() {
        return Http.post({
            path: "/invite/qrcode",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {}
        })
    }
}


