<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import { setup } from './locales/index'
import Storage from './utils/storage'

export default {
  name: 'App',
  data() {
    return {}
  },
  methods: {},
  beforeCreate() {
    var lang = this.$route.query.lang
    if (lang == 'en-us' || lang == 'en') {
      setup('en')
    } else if (lang == 'zh-hans' || lang == 'zh-hant' || lang == 'zh') {
      setup('zh')
    } else {
      setup()
    }
  },
  created() {
    if (this.$store.state.account.isLogin) {
      this.$store.dispatch('account/check').then(res => {
        if (res.code != 0) {
          var route_name = this.$route.name
          if (
            !(
              route_name == 'gameScene' ||
              route_name == 'loginScene' ||
              route_name == 'registerScene'
            )
          ) {
            this.$message({
              message: '登录信息过期，请重新登录',
              type: 'error',
              center: true
            })

            this.$router.push({
              name: 'loginScene'
            })
          }
        }
        console.log(res)
        // });
      })
    }

    var source = this.$route.query.source
    if (source) {
      this.$store.dispatch('ASET_SOURCE', source)
    }

    var ref = this.$route.query.ref
    if (ref) {
      this.$store.dispatch('ASET_REF', ref)
    }

    var channel = this.$route.query.channel
    if (channel) {
      this.$store.dispatch('ASET_CHANNEL', channel)
    }

    var os = this.$route.query.os
    if (os) {
      Storage.set('ones_os', os)
    }
  },
  mounted() {
    try {
      window.JSInterface && window.JSInterface.showCloseBtn(true)
    } catch (err) {
      console.log(err)
    }

    try {
      window.webkit &&
        window.webkit.messageHandlers &&
        window.webkit.messageHandlers.showCloseBtn.postMessage({
          body: '',
          callback: ''
        })
    } catch (err) {
      console.log(err)
    }

    var source = this.$route.query.source
    if (source == 'zt') {
      this.$notify.success({
        title: '欢迎ZT用户来到OnesPlay',
        message:
          'ONES与ZT强强联手,为用户打造游戏生态,目前有怼怼三国、勇闯女巫塔，驯龙高手、龙堡争霸,可以通过手机号注册登录，直接充值ZTB和USDT畅玩游戏获得ONES',
        position: 'bottom-right'
      })
    } else if (source == 'shandw') {
      setTimeout(
        function() {
          this.$notify.info({
            title: '小提示',
            message: '从左向右滑动可退出游戏',
            position: 'bottom-left'
          })
        }.bind(this),
        3000
      )
    }

    if (this.$route.query.domain) {
      var domain = this.$route.query.domain
      if (
        domain == 'dragonex.io' ||
        domain == 'dragonex.co' ||
        domain == 'dragonex.in' ||
        domain == 'dragonex.kr'
      ) {
        Storage.set('dragonex_domain', domain)
      }
    }

    if (this.$route.query.ref) {
      var invite_code = this.$route.query.ref
      Storage.set('ones_invite_code', invite_code)
    }

    if (source) {
      if (source == 'justalk') {
        var justalk = {
          justalk_uid: this.$route.query.justalk_uid,
          mobile: this.$route.query.mobile,
          nickname: this.$route.query.nickname
        }
        Storage.set('ones_justalk', justalk)
      }
    } else {
      var ones_source = Storage.get('ones_source')
      if (ones_source == 'zt') {
        Storage.remove('ones_source')
      }
    }
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
html {
  -webkit-tap-highlight-color: transparent;
}
body {
  margin: 0;
}
a {
  text-decoration: none;
}

a:focus,
button:focus,
input:focus,
textarea:focus {
  outline: 0;
}
ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
button,
input,
textarea {
  color: inherit;
  font: inherit;
}

html {
  background-color: rgb(74, 73, 73);
  margin: 0;
  padding: 0;
}
body {
  background: #f5f5f5;
  overflow-x: hidden !important;
  margin: 0 auto;
  width: 100%;
  height: 100%;

  /* -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%); 
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
  filter: progid:DXImageTransform.Microsoft.BasicImage(grayscale=1);
  filter: gray;  */
}

@media screen and (min-width: 320px) {
  html {
    font-size: 14px;
    height: 100%;
  }
}
@media screen and (min-width: 411px) {
  html {
    font-size: 15px;
  }
}

@media screen and (min-width: 520px) {
  html {
    font-size: 15.5px;
  }
}

@media screen and (min-width: 760px) {
  html {
    font-size: 25px;
  }
}

@media (min-width: 768px) {
  body {
    width: 442px;
    max-height: 812px;
    overflow: hidden;
  }
  html {
    font-size: 13px;
  }

  #app {
    width: 100%;
    height: 100%;
  }
}

.el-dialog__wrapper {
  /* width: 100%; */
  margin: 15px !important;
  border-radius: 0.375rem;
}

.el-dialog {
  width: 100%;
}

.el-dialog__body {
  padding: 10px 20px 20px;
}

@media (min-width: 768px) {
  .el-dialog__wrapper {
    max-width: 414px;
    margin: 0 auto !important;
  }
}
</style>



