<template>
  <div class="walletContainer" ref="walletContainer">
    <div class="account">
      <div class="nav" v-if="zone == 'pandora'">
        <div class="back">
          <i class="el-icon-arrow-left" name="arrow-left" @click="goBack"></i>
          <!-- 充值 -->
        </div>
      </div>
      <div class="ecr20-img" v-if="zone != 'eos' && zone != 'pandora'">
        <img src="../../assets/banner/erc20.png" />
      </div>
      <ul>
        <li
          v-for="(item,index) in balanceList"
          class="currency-item"
          v-if="item.isShow"
          :key="index"
        >
          <div class="logoBox" @click="openCurrentBox(index)">
            <img :src="item.img" alt class="logoImg" />
            <div class="type">
              <span>{{item.balances.total}}</span>
              {{ item.type | toUp_filter}}
              <i
                v-if="item.type == 'bean'"
                class="el-icon-info"
                @click="happyBeansDialog = true"
              ></i>
            </div>
          </div>

          <ul class="currencyBox" v-if="item.showCenter && zone != 'eos'">
            <li v-for="(single,key,index) in item.balances" v-if=" key != 'total' " :key="index">
              <span class="name" :data-key="key">{{ $t("assetScene."+ key) }}</span>
              <!-- <span class="name" :data-key="key">{{$t("assetScene."+ key)}}</span> -->
              <span class="num">{{ single }}</span>
              <div
                class="transfer-btn"
                v-if="(key == 'game' || item.type == 'ones') && key!='share' && item.balances.length>1"
              >
                <img
                  src="../../assets/out.png"
                  alt
                  v-if="item.type !== 'ones'"
                  @click="transferBalance('out',key,item,single)"
                />
                <img
                  src="../../assets/out.png"
                  alt
                  v-if="item.type == 'ones' "
                  @click="transferBalance('transform',key,item,single)"
                />
              </div>
            </li>
          </ul>

          <div class="currencybtn">
            <el-button
              style="padding:10px 12px;min-width:6.7rem;"
              round
              size="normal"
              v-if="item.withdraws.length>0"
              @click="OpenPopUp('withdraw',item,index)"
            >{{$t("assetScene.withdraw")}}</el-button>

            <el-button
              type="primary"
              round
              size="normal"
              v-if="item.deposits.length>0"
              style="margin-top:0.3rem;margin-left:0;min-width:6.7rem;padding:10px 12px ;"
              @click="OpenPopUp('deposit',item,index)"
            >{{$t("assetScene.deposit")}}</el-button>
          </div>
        </li>
      </ul>
    </div>

    <el-dialog :title="$t('assetScene.transfer')" :visible.sync="transferDialogVisible" width="95%">
      <div class="transfer-box">
        <el-form :inline="true" :model="transferForm" class="demo-form-inline">
          <el-form-item :label="$t('assetScene.from')">
            <el-input
              :value="$t('assetScene.'+transferForm.from)"
              style="width:100%"
              :disabled="true"
            ></el-input>
          </el-form-item>
          <el-form-item :label="$t('assetScene.transferto')">
            <el-select v-model="transferForm.to">
              <el-option
                :label="$t('assetScene.'+item)"
                :value="item"
                v-for="item in transferForm.toList"
                :key="item"
              ></el-option>
              <!-- <el-option label="Erc20" value="erc20"></el-option> -->
            </el-select>
          </el-form-item>
        </el-form>
        <el-form :model="transferForm" class="demo-form-inline">
          <el-form-item :label="$t('assetScene.transferamount')">
            <el-input v-model="transferForm.num" style="width:70%"></el-input>
          </el-form-item>
        </el-form>
        <p class="most-text">
          {{$t("assetScene.transfermost")}}
          <span>{{transferForm.amount}}</span>
        </p>
        <el-button
          type="primary"
          class="confirm-btn"
          @click="submitTransferForm"
        >{{$t("assetScene.confirm")}}</el-button>
      </div>
    </el-dialog>

    <el-dialog title="欢乐豆" :visible.sync="happyBeansDialog" width="95%" class="happyBeansDialog">
      <h6>欢乐豆是什么？</h6>
      <p>欢乐豆是OnesPlay的游戏积分</p>
      <h6>欢乐豆有什么用？</h6>
      <p>欢乐豆可以用于购买游戏道具抵扣，700个欢乐豆可以抵扣1U</p>
      <h6>欢乐豆如何获取？</h6>
      <p>用户在除驯龙、龙堡游戏中消费即可获得欢乐豆（1U会获得70个欢乐豆），也可在签到等活动都可获得</p>
    </el-dialog>

    <!-- 内容弹出框 -->
    <!-- 下弹出框 -->
    <el-drawer
      class="PopUpContainer"
      :visible.sync="drawer"
      :with-header="false"
      direction="btt"
      :style="PopUpContainerObj"
      size="auto"
      modal-append-to-body="false"
    >
      <!-- 充值框 -->
      <div class="deposit-content" v-if="this.deposit">
        <ul>
          <li v-for="item in depositPop" @click="runDeposit(item)">{{$t("assetScene."+item)}}</li>
          <li @click="ClosePopUp">{{this.$t("assetScene.cancel")}}</li>
        </ul>
      </div>

      <!-- 提币框 -->
      <div class="withdraw-content" v-if="this.withdraw">
        <ul>
          <li v-for="item in withdrawPop" @click="runWithdraw(item)">{{$t("assetScene."+item)}}</li>
          <li @click="ClosePopUp">{{this.$t("assetScene.cancel")}}</li>
        </ul>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import i18n from '../../locales/index'

import AssetService from '../../api/asset'
import Storage from '../../utils/storage'

export default {
  id: 'walletContainer',
  data() {
    return {
      zone: this.$store.getters.zone,

      happyBeansDialog: false,

      transferForm: {
        from: 'game',
        toList: [],
        amount: 111,
        to: 'erc20',
        num: 0,
        coin: ''
      },
      transferDialogVisible: false,
      // coins:{},
      balanceList: [
        {
          type: 'bean',
          img: require('../../assets/token/bean.png'),
          balances: {},
          balance: 0,
          withdraws: [],
          deposits: [],
          showCenter: true,
          isShow: true
        },
        // ones
        {
          type: 'ones',
          img: require('../../assets/token/ones.png'),
          balances: {},
          balance: 0,
          withdraws: [],
          deposits: [],
          showCenter: true,
          isShow: true
        },
        // USDT
        {
          type: 'usdt',
          img: require('../../assets/token/usdt.png'),
          balances: {},
          balance: 0,
          totalBalance: 0,
          withdraws: [],
          deposits: [],
          showCenter: false,
          isShow: false
        },
        // DT
        {
          type: 'dt',
          img: require('../../assets/token/dt.png'),
          balances: {},
          balance: 0,
          totalBalance: 0,
          withdraws: [],
          deposits: [],
          showCenter: false,
          isShow: false
        },
        {
          type: 'czz',
          img: require('../../assets/token/czz.png'),
          balances: {},
          balance: 0,
          totalBalance: 0,
          withdraws: [],
          deposits: [],
          showCenter: false,
          isShow: false
        },
        // ZTB
        {
          type: 'ztb',
          img: require('../../assets/token/ztb.png'),
          balances: {},
          balance: 0,
          totalBalance: 0,
          withdraws: [],
          deposits: [],
          showCenter: false,
          isShow: false
        },
        // JUST
        {
          type: 'just',
          img: require('../../assets/token/just.png'),
          balances: {},
          balance: 0,
          totalBalance: 0,
          withdraws: [],
          deposits: [],
          showCenter: false,
          isShow: false
        },
        // gxb
        {
          type: 'gxc',
          img: require('../../assets/token/gxc.png'),
          balances: {},
          balance: 0,
          withdraws: ['gxb'],
          deposits: ['gxb'],
          showCenter: false,
          isShow: false
        },
        //jus
        {
          type: 'jus',
          img: require('../../assets/token/jus.png'),
          balances: {},
          balance: 0,
          totalBalance: 0,
          withdraws: [],
          deposits: [],
          showCenter: false,
          isShow: false
        },
        // eos
        {
          type: 'eos',
          img: require('../../assets/token/eos.png'),
          balances: {},
          balance: 0,
          totalBalance: 0,
          withdraws: [],
          deposits: [],
          showCenter: false,
          isShow: false
        },
        {
          type: 'bqb',
          img: require('../../assets/token/bqb.png'),
          balances: {},
          balance: 0,
          totalBalance: 0,
          withdraws: [],
          deposits: [],
          showCenter: false,
          isShow: false
        }
      ],
      depositPop: [],
      withdrawPop: [],
      nowPopType: '',
      currencyBoxShow: false,
      drawer: false,

      PopUpContainerObj: {
        width: ''
      },
      balances: {
        usdt: '',
        ones: '',
        dt: '',
        ztb: ''
      },
      dragonex_balances: {
        usdt: '',
        ones: '',
        dt: '',
        ztb: ''
      },
      erc20_balances: {
        usdt: '',
        ones: '',
        dt: '',
        ztb: ''
      },
      justalk_balances: {
        just: '',
        jus: ''
      },
      game_balances: {},
      eth_address: '',
      walletContainer: true,
      cur_coin_code: '',
      deposit: false,
      withdraw: false,
      activeName: 'first',

      isDragonExdeposit: true,
      isErc20deposit: true
    }
  },
  computed: {},
  created() {
    this.getBalance()
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    submitTransferForm() {
      var params = {
        coin_code: this.transferForm.coin,
        amount: this.transferForm.num,
        from: this.transferForm.from,
        to: this.transferForm.to
      }

      AssetService.transfer(params).then(res => {
        if (res.code == 0) {
          this.transferDialogVisible = false
          this.$message({
            message: this.$t('assetScene.transfersuccess'),
            type: 'success',
            center: true
          })
          this.getBalance()
        } else {
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'error',
            center: true
          })
        }
      })
    },
    transferBalance(type, key, item, num) {
      this.transferForm.to = ''
      this.transferForm.toList = []
      this.transferForm.amount = num
      this.transferForm.num = num

      this.transferDialogVisible = true
      this.transferForm.from = key

      this.transferForm.coin = item.type

      for (var i in item.balances) {
        this.transferForm.toList.push(i)
      }
      Array.prototype.indexOf = function(val) {
        for (var i = 0; i < this.length; i++) {
          if (this[i] == val) return i
        }
        return -1
      }
      Array.prototype.remove = function(val) {
        var index = this.indexOf(val)
        if (index > -1) {
          this.splice(index, 1)
        }
      }

      this.transferForm.toList.remove('total')
      this.transferForm.toList.remove(key)
      this.transferForm.toList.remove('share')

      var dragonexIndex = 0
      for (var i in this.transferForm.toList) {
        if (this.transferForm.toList[i] == 'dragonex') {
          dragonexIndex = i
        }
      }
      this.transferForm.to = this.transferForm.toList[dragonexIndex]
    },

    handleClick(tab, event) {
      console.log(tab, event)
    },

    getBalance() {
      var ones_source = Storage.get('ones_source')

      AssetService.balances().then(res => {
        if (res.code == 0) {
          console.log(res)
          if (this.zone == 'pandora') {
            this.balanceList = [
              {
                type: 'usdt',
                img: require('../../assets/token/usdt.png'),
                balances: {},
                balance: 0,
                totalBalance: 0,
                withdraws: ['erc20'],
                deposits: ['erc20'],
                showCenter: false,
                isShow: false
              }
            ]
          }
          for (var i in res.data.coins) {
            for (var j in this.balanceList) {
              if (this.balanceList[j].type == i) {
                this.balanceList[j].balances = res.data.coins[i].balances

                if (this.zone != 'pandora') {
                  this.balanceList[j].deposits = res.data.coins[i].deposits
                  this.balanceList[j].withdraws = res.data.coins[i].withdraws
                } else {
                  // console.log(this.balanceList[j].balances)
                  var balances = this.balanceList[j].balances
                  this.balanceList[j].balances = {
                    erc20: balances['erc20'],
                    total: balances['total']
                  }
                }
                this.balanceList[j].isShow = true

                if (ones_source == 'justalk' && i == 'usdt') {
                  var deposits = this.balanceList[j].deposits
                  var _deposits = []
                  for (var i in deposits) {
                    if (deposits[i] != 'erc20') {
                      _deposits.push(deposits[i])
                    }
                  }
                  this.balanceList[j].deposits = _deposits
                }
              }
            }
          }
        }
      })
    },

    openCurrentBox(index, isShow) {
      var len = 0
      for (var i in this.balanceList[index].balances) {
        len++
      }
      if (len == 1) {
        return
      }
      if (isShow) {
        this.balanceList[index].showCenter = true
      } else {
        this.balanceList[index].showCenter = !this.balanceList[index].showCenter
      }
    },

    OpenPopUp(_type, item, index) {
      if (this.zone == 'eos') {
        this.nowPopType = item.type
        if (_type == 'deposit') {
          this.runDeposit('eos')
        } else {
          this.runWithdraw('eos')
        }
        return
      }
      this.openCurrentBox(index, true)
      var coin_code = item.type

      this.drawer = true
      this.cur_coin_code = coin_code

      if (_type == 'deposit') {
        this.withdraw = false
        this.deposit = true
        // if ( == 0) {
        // }
        this.depositPop = item.deposits
        this.nowPopType = item.type
      }
      if (_type == 'withdraw') {
        this.withdraw = true
        this.deposit = false

        this.withdrawPop = item.withdraws
        this.nowPopType = item.type
      }
    },

    ClosePopUp() {
      this.drawer = false
    },

    runWithdraw(item) {
      console.log(item)
      console.log(this.nowPopType + '____' + item)
      if (item == 'dragonex') {
        this.$router.push({
          name: 'dragonexWithdrawScene',
          params: {
            coin_code: this.nowPopType
            // coin_amount: this.balances[this.cur_coin_code]
          }
        })
      } else if (item == 'erc20') {
        this.$router.push({
          name: 'erc20WithdrawScene',
          params: {
            coin_code: this.nowPopType
            // coin_amount: this.balances[this.cur_coin_code]
          }
        })
      } else if (item == 'hicoin') {
        this.$router.push({
          name: 'hicoinWithdrawScene',
          params: {
            coin_code: this.nowPopType
          }
        })
      } else if (item == 'gxb') {
        this.$router.push({
          name: 'gxbWithdrawScene',
          params: {
            coin_code: this.nowPopType
          }
        })
      } else if (item == 'eos') {
        this.$router.push({
          name: 'eosWithdrawScene',
          params: {
            coin_code: this.nowPopType
          }
        })
      }
    },
    runDeposit(item) {
      console.log(this.nowPopType + '____' + item)
      if (item == 'dragonex') {
        this.$router.push({
          name: 'dragonexDepositScene',
          params: {
            coin_code: this.nowPopType,
            // coin_amount: this.balances[this.cur_coin_code],
            eth_address: this.eth_address
          }
        })
      } else if (item == 'gxb') {
        this.$router.push({
          name: 'gxbDepositScene',
          params: {
            coin_code: this.nowPopType,
            // coin_amount: this.balances[this.cur_coin_code],
            eth_address: this.eth_address
          }
        })
      } else if (item == 'erc20') {
        this.$router.push({
          name: 'erc20DepositScene',
          params: {
            coin_code: this.nowPopType,
            // coin_amount: this.balances[this.cur_coin_code],
            eth_address: this.eth_address
          }
        })
      } else if (item == 'justalk') {
        this.$router.push({
          name: 'justalkDepositScene',
          params: {
            coin_code: this.nowPopType
          }
        })
      } else if (item == 'hicoin') {
        this.$router.push({
          name: 'hicoinDepositScene',
          params: {
            coin_code: this.nowPopType
          }
        })
      } else if (item == 'bqex') {
        this.$router.push({
          name: 'bqexDepositScene',
          params: {
            coin_code: this.nowPopType
          }
        })
      } else if (item == 'eos') {
        this.$router.push({
          name: 'eosDepositScene',
          params: {
            coin_code: this.nowPopType
          }
        })
      }
    },

    erc20deposit() {
      this.$router.push({
        name: 'erc20DepositScene',
        params: {
          coin_code: this.cur_coin_code,
          coin_amount: this.balances[this.cur_coin_code],
          eth_address: this.eth_address
        }
      })
    },
    dragonExdeposit() {
      this.$router.push({
        name: 'dragonexdepositScene',
        params: {
          coin_code: this.cur_coin_code,
          coin_amount: this.balances[this.cur_coin_code]
        }
      })
    },
    Erc20Withdraw() {
      this.$router.push({
        name: 'erc20WithdrawScene',
        params: {
          coin_code: this.cur_coin_code,
          coin_amount: this.balances[this.cur_coin_code]
        }
      })
    },
    DragonExWithdraw() {
      this.$router.push({
        name: 'dragonexWithdrawScene',
        params: {
          coin_code: this.cur_coin_code,
          coin_amount: this.balances[this.cur_coin_code]
        }
      })
    }
  },
  mounted() {
    // this.ContainerStyleObj.height =
    //   this.$refs.walletContainer.offsetParent.clientHeight - 50 + 'px'
    this.PopUpContainerObj.width =
      this.$refs.walletContainer.offsetParent.clientWidth + 'px'
    // todo
  },
  filters: {
    toUp_filter(value) {
      if (value == 'bean') {
        // return '欢乐豆'
        return i18n.t('assetScene.bean')
      }
      return value.toUpperCase()
    }
  }
}
</script>

