/* 零钱包 */
.account > ul .game-current-item {
  background-color: rgba(49, 122, 190, 0.589);
  color: #fff;
}
.account > ul .game-current-title {
  font-size: 1.2rem;
  line-height: 2.5rem;
  position: relative;
}
.account > ul .game-current-icon {
  position: absolute;
  right: 10px;
  top: 10px;
}

.transfer-box .most-text span {
  color: rgb(97, 165, 204);
}
.transfer-box .confirm-btn {
  width: 100%;
  margin: 2rem 0;
}
.transfer-box /deep/ .el-form--inline {
  display: flex;
}
.transfer-box /deep/ .el-form--inline .el-form-item {
  display: flex;
}
.transfer-box /deep/ .el-form--inline .el-form-item .el-form-item__label {
  white-space: nowrap;
}

.happyBeansDialog .el-dialog__body {
  padding: 20px 14px;
}
.happyBeansDialog h6 {
  line-height: 30px;
}
.happyBeansDialog p {
  line-height: 20px;
  font-size: 12px;
  color: #888;
}

.nav {
  height: 50px;
  position: relative;
  color: rgb(164, 159, 154);
}

.nav .back i {
  font-size: 1.7rem;
  margin-left: 1rem;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}
</style>


