<template>
  <div class="container">
    <div class="hicoindeposit">
      <el-form :model="depositForm" ref="depositForm" label-width="60px" :rules="depositRules">
        <el-form-item :label="this.$t('assetScene.amount')" prop="amount">
          <div>
            <el-input
              :placeholder="this.$t('assetScene.depositamount')"
              v-model="depositForm.amount"
              class="input-with-select"
            >
              <el-select
                v-model="depositForm.coin_code"
                slot="append"
                placeholder="请选择"
                style="width:100px"
              >
                <el-option label="DT" value="DT"></el-option>
                <el-option label="USDT" value="USDT"></el-option>
                <el-option label="ONES" value="ONES"></el-option>
              </el-select>
            </el-input>
          </div>
        </el-form-item>
        <el-form-item label-width="0px" align="center">
          <el-button
            type="primary"
            :loading="loading"
            @click="predeposit"
            style="width:50%;"
          >{{this.$t("assetScene.deposit")}}</el-button>
        </el-form-item>
      </el-form>

      <div class="current">
        {{this.$t("assetScene.AccountBalance")}}
        <span>{{cur_coin_amount}} {{cur_coin_code}}</span>
      </div>

      <div class="erc20deposit-tip">
        <p
          v-if="cur_coin_code != 'ONES'"
        >{{this.$t("assetScene.depositTips4",{coin_code:cur_coin_code,reward_amount:reward_amount,deposit_amount:deposit_amount})}}</p>
        <p>{{this.$t("assetScene.depositTips5")}}</p>
        <p>{{this.$t("assetScene.depositTips6")}}</p>
        <p>{{this.$t("assetScene.depositTips66")}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import AssetService from '../../../api/asset'
import Util from '../../../utils/util'

export default {
  data() {
    var validateAmount = (rule, value, callback) => {
      if (value == '')
        return callback(new Error(this.$t('assetScene.depositPlaceholder')))
      return callback()
    }
    return {
      deposit_amount: 100,
      reward_amount: '3',
      loading: false,
      cur_coin_code: 'DT',
      cur_coin_amount: '0.00',

      depositForm: {
        amount: '',
        coin_code: 'DT'
      },
      balances: {
        usdt: 0,
        dt: 0,
        ones: 0
      },
      depositRules: {
        amount: [{ validator: validateAmount, trigger: 'blur' }]
      }
    }
  },
  watch: {
    'depositForm.coin_code': function(val) {
      console.log(val)
      var key = val.toLowerCase()
      this.cur_coin_code = val
      this.cur_coin_amount = this.balances[key]

      if (this.cur_coin_code == 'DT') {
        this.reward_amount = '1.0'
        this.deposit_amount = '100.00'
      } else if (this.cur_coin_code == 'USDT') {
        this.reward_amount = '3.0'
        this.deposit_amount = '100.00'
      } else if (this.cur_coin_code == 'ZTB') {
        this.reward_amount = '5.0'
        this.deposit_amount = '100.00'
      } else {
        this.reward_amount = '10.0'
        this.deposit_amount = '100.00'
      }
    }
  },

  mounted: function() {
    //action=deposit
    var coin_code = this.$route.params.coin_code
    var coin_amount = this.$route.params.coin_amount
    if (coin_code) {
      this.depositForm.coin_code = coin_code.toUpperCase()
      this.cur_coin_code = coin_code
      this.cur_coin_amount = Number(coin_amount).toFixed(4)

      if (this.cur_coin_code.toUpperCase() == 'DT') {
        this.reward_amount = '1.0'
        this.deposit_amount = '100.00'
      } else if (this.cur_coin_code.toUpperCase() == 'USDT') {
        this.reward_amount = '3.0'
        this.deposit_amount = '100.00'
      } else if (this.cur_coin_code.toUpperCase() == 'ZTB') {
        this.reward_amount = '5.0'
        this.deposit_amount = '100.00'
      } else if (this.cur_coin_code.toUpperCase() == 'ONES') {
        this.reward_amount = '5.0'
        this.deposit_amount = '100.00'
      } else {
        this.reward_amount = '10.0'
        this.deposit_amount = '100.00'
      }
    }
    const action = this.$route.query.action
    if (action == 'dodeposit') {
      this.dodeposit()
    } else {
      this.getBalances()
    }
  },

  methods: {
    predeposit() {
      this.$refs.depositForm.validate(valid => {
        if (valid) {
          this.loading = true
          var amount = this.depositForm.amount
          var coin_code = this.depositForm.coin_code.toLowerCase()
          var params = {
            type: 'hicoin',
            amount: amount,
            coin_code: coin_code
          }
          AssetService.predeposit(params).then(res => {
            if (res.code == 0) {
              this.loading = false
              const loading = this.$loading({
                lock: true,
                text: '跳转中',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
              })
              window.location.href = res.data.pay_url
            } else {
              this.$message({
                message: this.$t('message.' + res.msg),
                type: 'error',
                center: true
              })
              this.loading = false
            }
          })
        } else {
          return false
        }
      })
    },

    dodeposit() {
      const loading = this.$loading({
        lock: true,
        text: '充值确认中',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })

      const trade_no = this.$route.query.trade_no
      var params = {
        type: 'hicoin',
        trade_no: trade_no
      }
      AssetService.deposit(params).then(res => {
        loading.close()
        if (res.code == 0) {
          this.$message({
            message: this.$t('message.deposit_success'),
            type: 'success',
            center: true
          })
        } else {
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'error',
            center: true
          })
        }
        this.getBalances()
      })
    },

    getBalances() {
      AssetService.balance().then(res => {
        if (res.code == 0) {
          // this.balances = res.data.dragonex_balances
          for (var i in res.data.coins) {
            this.balances[i] = res.data.coins[i]['balances']['hicoin']
              ? res.data.coins[i]['balances']['hicoin']
              : '0.00'
          }
          console.log(this.cur_coin_code)
          var key = this.cur_coin_code.toLowerCase()
          console.log(this.balances, key)
          this.cur_coin_amount = Number(this.balances[key]).toFixed(4)
        }
      })
    }
  }
}
</script>

<style scoped>
.hicoindeposit {
  width: 96%;
  margin: 0 auto;
}

.container {
  width: 100%;
  /* height: 100vh; */
  margin-top: 45px;
  box-sizing: border-box;
  overflow: hidden;
}

.current {
  width: 100%;
  line-height: 7vh;
  margin-top: 0vh;
  font-size: 0.9rem;
  color: #888;
  text-align: right;
}
.current span {
  color: #ffc02d;
  font-size: 0.9rem;
  font-weight: 800;
}

.erc20deposit-tip {
  padding: 10px 15px;
  line-height: 22px;
  font-size: 12px;
  color: #e64c65;
}
</style>


