<template>
  <div class="luckywheelContainer" ref="luckywheelContainer">
    <div class="content-wrap">
      <div class="activity-widget">
        <div class="top-banner">
          <div class="luckyplayer-list">
            <div class="prefix" v-if="lucky_players.length>0">获奖名单：</div>
            <ul
              class="luckyplayer-list-inner"
              :style="{transform: 'translateY('+ -1.8 * currentIndex+'rem)'}"
            >
              <li v-for="item in lucky_players" :key="item" class="prize-item ellipsis">
                恭喜{{item.player}}获得
                <span>{{item.amount}} ONES</span>
              </li>
            </ul>
          </div>
        </div>
        <!---->
        <div class="rotate-pool">当前奖池 {{prize_pool}} ONES</div>

        <div class="common-act-rotate-widget">
          <div class="rotate-inner">
            <div class="rotate-target" ref="rotateTarget">
              <ul class="product-list">
                <li
                  v-for="item in prize_items"
                  :key="item"
                  class="product-item"
                  :style="{transform: 'rotate('+item.deg+'deg) translateX(-50%)'}"
                >
