                ></li>
                <li
                  class="product-sector"
                  style="transform: rotate(136deg) skew(-44deg); background-color: rgb(103, 76, 249);"
                ></li>
                <li
                  class="product-sector"
                  style="transform: rotate(181deg) skew(-44deg); background-color: rgb(154, 133, 252);"
                ></li>
                <li
                  class="product-sector"
                  style="transform: rotate(226deg) skew(-44deg); background-color: rgb(103, 76, 249);"
                ></li>
                <li
                  class="product-sector"
                  style="transform: rotate(271deg) skew(-44deg); background-color: rgb(154, 133, 252);"
                ></li>
              </ul>
            </div>
            <a href="javascript:;" class="rotate-start" @click="luckdraw()">GO!</a>
          </div>
          <div class="product-lamp"></div>
          <div class="rotate-footer small">剩余次数：{{remainCounter}}次</div>
          <div class="luckydraw-setting">
            <el-button
              icon="el-icon-setting"
