              round
              @click="settingDialogVisible = true"
            >设置金额</el-button>
          </div>
        </div>
        <!---->
        <div class="section-item">
          <div class="section-title">活动规则</div>
          <div class="section-content">
            <p>
              1、玩家每日可获得3次免费抽奖机会；
              <br />2、OnesGamePlay合约每一秒内，会有0.01个ONES产生，持续100天。用于空投奖励；
              <br />3、玩家可以将任意金额的 EOS 转账至 onesgameplay 这个账户，智能合约会将等量的 EOS 返回至玩家账户，并会将 onesgameplay 这个账户中存有的 ONES 随机(1%~0.01%)发送到用户账户中。；
              <br />4、玩家转账金额越大，比例越大 如100EOS至少会获得0.1%的比例奖励
            </p>
          </div>
        </div>

        <div class="luckydraw-result" v-if="resultDialogVisible">
          <div class="container">
            <div class="dialog-title"></div>
            <div class="dialog-content">
              <div class="content-title">恭喜获得{{resultOnes}} ONES</div>
              <div class="result-brief">每天都有3次免费抽奖机会,还剩余{{remainCounter}}次</div>
              <div class="content-button">
                <el-button icon="el-icon-close" circle @click="resultDialogVisible=false"></el-button>
              </div>
            </div>
          </div>
        </div>
        <!-- 弹出框 -->
        <el-dialog
          class="setting-dialog"
          title="设置抽奖转账金额"
          :visible.sync="settingDialogVisible"
          width="95%"
        >
          <!-- 认购 -->
          <div class="setting-body">
            <div>
              <!-- <div>
                <el-radio v-model="lucky_radio" label="min">最小值</el-radio>
              </div>-->
              <div>
                <el-radio v-model="lucky_radio" label="max">当前余额</el-radio>
              </div>
              <div>
                <el-radio v-model="lucky_radio" label="const">固定值</el-radio>
                <el-input v-model="lucky_input" placeholder="请输入金额"></el-input>
              </div>
            </div>

            <div class="setting-tips">
              <p>1)智能合约会将等量的EOS返回至玩家账户</p>
              <p>2)转账金额越大抽奖概率越大</p>
            </div>
            <div class="setting-btns">
              <el-button
                type="warning"
                style="width:200px;"
                @click="confirmSetting()"
              >{{$t("node.confirm")}}</el-button>
            </div>
          </div>
        </el-dialog>

        <!---->
      </div>
    </div>
  </div>
</template>

<script>
var r = function(t, i, e, a, s) {
    return -a * ((i = i / s - 1) * i * i * i - 1) + e
  },
  c = function() {}

var l = function() {
  ;(this.timer = -1),
    (this.animateStartTime = 0),
    (this.animateStartAngle = this.angle)
}
;(l.prototype.rotate = function(t) {
  this._setupParams(t), this._handleRotation()
}),
  (l.prototype.stopRotate = function() {
    this.status = 1
    clearTimeout(this.timer), (this.timer = -1)
  }),
  (l.prototype._setupParams = function(t) {
    ;(this.target = t.target),
      (this.angle = t.angle),
      (this.animateTo =
        'number' === typeof t.animateTo ? t.animateTo : this.angle),
      (this.step = t.step || null),
      (this.easing = t.easing || r),
      (this.duration = t.duration || 1e3),
      (this.callback = t.callback || c),
      (this.status = 0)
  }),
  (l.prototype._handleRotation = function() {
    this.angle === this.animateTo
      ? this._rotate(this.angle)
      : this._animateStart()
  }),
  (l.prototype._rotate = function(t) {
    ;(this.angle = t),
      (this.target.style['transform'] = 'rotate('.concat(t % 360, 'deg)'))
  }),
  (l.prototype._animateStart = function() {
    clearTimeout(this.timer),
      (this.animateStartTime = +new Date()),
      (this.animateStartAngle = 0),
      this._animate()
  }),
  (l.prototype._animate = function() {
    var t = this,
      i = +new Date(),
      e = i - this.animateStartTime > this.duration
    // if (e) clearTimeout(this.timer)
    // else
    {
      var a = this.easing(
        0,
        i - this.animateStartTime,
        this.animateStartAngle,
        this.animateTo - this.animateStartAngle,
        this.duration
      )
      // console.log(a)
      this._rotate(~~(10 * a) / 10),
        this.step && this.step(this.angle),
        (this.timer = setTimeout(function() {
          t._animate()
        }, 10))
    }
    'function' === typeof this.callback &&
      this.status &&
      ((this.angle = this.animateTo), this._rotate(this.angle), this.callback())
  })

import EosService from '../../utils/eosservice'
import Storage from '../../utils/storage'

export default {
  data() {
    return {
      eosio: null,
      eos_balance: 0,
      ones_balance: 0,

      prize_items: [
        {
          deg: '22.5',
          value: 0.001,
          color: 'rgb(255, 255, 255)',
          percent: 0.008
        },
        {
          deg: '67.5',
          value: 0.01,
          color: 'rgb(45, 30, 174)',
          percent: 0.0001
        },
        {
          deg: '112.5',
          value: 0.002,
          color: 'rgb(255, 255, 255)',
          percent: 0.0004
        },
        {
          deg: '157.5',
          value: 0.02,
          color: 'rgb(45, 30, 174)',
          percent: 0.0008
        },
        {
          deg: '202.5',
          value: 0.1,
          color: 'rgb(255, 255, 255)',
          percent: 0.001
        },
        {
          deg: '247.5',
          value: 0.05,
          color: 'rgb(45, 30, 174)',
          percent: 0.002
        },
        {
          deg: '292.5',
          value: 0.005,
          color: 'rgb(255, 255, 255)',
          percent: 0.004
        },
        {
          deg: '337.5',
          value: 0.0001,
          color: 'rgb(45, 30, 174)',
          percent: 0.01
        }
      ],
      remainCounter: 3,
      prize_pool: 0,
      currentIndex: 0,
      lucky_players: [],
      timerId: -1,

      luckydraw_amount: 0,

      settingDialogVisible: false,
      resultDialogVisible: false,
      resultOnes: '',

      lucky_radio: 'max',
      lucky_input: '',

      isConnected: false,
      disabled: false
    }
  },

  computed: {
    EosAccount() {
      return (
        this.$store.state.account.user &&
        this.$store.state.account.user.eos_address
      )
    }
  },

  mounted() {
    var r = document.body.clientWidth
    var e = Math.min(Math.max(Math.min(r, 442) / 20, 12), 22)
    document
      .querySelector('html')
      .setAttribute('style', 'font-size :' + e + 'px')

    window.onresize = function() {
      setTimeout(function() {
        var r = document.body.clientWidth
        var e = Math.min(Math.max(Math.min(r, 442) / 20, 12), 22)
        document
          .querySelector('html')
          .setAttribute('style', 'font-size :' + e + 'px')
      }, 30)
    }
    // n.style.fontSize = ''.concat(e, 'px')

    this.eosio = this.$store.getters.eosio
    if (!this.eosio) {
      console.log('eosio is null')
      this.eosio = new EosService(process.env.EOS_CONTRACT_NAME)
    }

    var luckydraw_amount = Storage.get('ones_luckydraw_amount')
    if (luckydraw_amount == 'min') {
      this.luckydraw_amount = '0.0001'
    } else if (luckydraw_amount == 'max') {
      this.luckydraw_amount = 'max'
    } else if (luckydraw_amount && parseFloat(luckydraw_amount) > 0) {
      this.luckydraw_amount = Number(luckydraw_amount).toFixed(4)
    }

    this.getPrizePool()
    this.getLuckyPlayer()

    this.getEosBalance()
    this.getUserInfo()
    // this.getOnesBlance()
  },

  beforeDestroy() {
    document.querySelector('html').removeAttribute('style')
  },
  methods: {
    luckdraw() {
      if (this.disabled) {
        return
      }
      this.disabled = true
      if (!this.luckydraw_amount) {
        this.$message({
          message: '请先设置转账金额',
          type: 'error',
          center: true
        })
        this.disabled = false
        return
      }
      if (this.remainCounter <= 0) {
        this.$message({
          message: '抽奖已用完,明天再来吧',
          type: 'error',
          center: true
        })
        this.disabled = false
        return
      }
      console.log('this.eosio.account', this.eosio.account)
      if (this.eosio.account) {
        if (this.luckydraw_amount == 'max') {
          this.eosio.getEosBalance(
            function(balance) {
              console.log('eos balances', balance)
              this.eos_balance = balance
              this._luckdraw(balance)
            }.bind(this)
          )
        } else {
          var amount = this.luckydraw_amount

          this._luckdraw(amount)
        }
      } else {
        this.eosio.connect(
          function(code, msg, data) {
            console.log(code, msg, data)
            if (code != 0) {
              this.disabled = false

              this.$message({
                message: this.$t('message.' + msg), //todo
                type: 'error',
                center: true
              })
            } else {
              this.$store.dispatch('ASET_EOSIO', this.eosio)
              if (this.luckydraw_amount == 'max') {
                this.eosio.getEosBalance(
                  function(balance) {
                    console.log('eos balances', balance)
                    this.eos_balance = balance
                    this._luckdraw(balance)
                  }.bind(this)
                )
              } else {
                var amount = this.luckydraw_amount

                this._luckdraw(amount)
              }
            }
          }.bind(this)
        )
      }
    },

    _luckdraw(amount) {
      // return
      var that = this
      this.eosio.getOnesBalance(this.eosio.account.name, function(balance) {
        var begin_ones_balance = balance
        console.log(balance)
        that.playWheel()

        that.eosio.transfer('EOS', amount, 'luckydraw', function(
          code,
          msg,
          data
        ) {
          that.disabled = false
          that.stopWheel()
          console.log(code, msg, data)
          if (code == 0) {
            that.eosio.getOnesBalance(this.eosio.account.name, function(
              balance
            ) {
              var cur_ones_balance = balance
              if (parseFloat(balance) > 0) {
                var reward =
                  parseFloat(cur_ones_balance) - parseFloat(begin_ones_balance)
                reward = Number(reward).toFixed(4)
                that.resultDialogVisible = true
                that.resultOnes = reward

                that.getPrizePool()
                that.getLuckyPlayer()
                that.getEosBalance()
                that.getUserInfo()
              } else {
                setTimeout(function() {
                  that.eosio.getOnesBalance(this.eosio.account.name, function(
                    balance
                  ) {
                    var cur_ones_balance = balance
                    var reward =
                      parseFloat(cur_ones_balance) -
                      parseFloat(begin_ones_balance)
                    reward = Number(reward).toFixed(4)
                    that.resultDialogVisible = true
                    that.resultOnes = reward

                    that.getPrizePool()
                    that.getLuckyPlayer()
                    that.getEosBalance()
                    that.getUserInfo()
                  })
                }, 200)
              }
            })
          } else {
            that.$message({
              message: msg,
              type: 'error',
              center: true
            })
          }
        })
      })
    },

    playWheel: function() {
      var target = this.$refs.rotateTarget

      var i = 1000

      if (!this.wheel) {
        this.wheel = new l()
      }
      console.log('playWheel')

      this.wheel.stopRotate()
      this.wheel.rotate({
        target: target,
        angle: 0,
        animateTo: 1800 + 1800,
        duration: 3e3,
        callback: function() {
          console.log('stop')
          return
        }
      })
    },

    stopWheel() {
      if (this.wheel) {
        this.wheel.stopRotate()
      }
    },

    getLuckyPlayer() {
      this.eosio.getTableRows(
        {
          code: 'onesgameplay',
          scope: 'onesgameplay',
          table: 'luckyrecords',
          limit: 20,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          console.log(data)

          if (code == 0) {
            var rows = []
            for (var i in data.rows) {
              var row = data.rows[i]
              rows.push({
                player: row['player'],
                amount: Number(row['amount'] / 10000).toFixed(4)
              })
            }
            this.lucky_players = rows
            this.startPollLuckyPlayer()
          }
        }.bind(this)
      )
    },

    getPrizePool() {
      this.eosio.getTableRows(
        {
          code: 'onesgameplay',
          scope: 'onesgameplay',
          table: 'luckydraw',
          limit: 1,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          console.log('getPrizePool', data)
          if (code == 0 && data.rows.length > 0) {
            this.prize_pool = data.rows[0]['balance'] / 10000

            for (var i in this.prize_items) {
              this.prize_items[i]['value'] = Number(
                this.prize_pool * this.prize_items[i]['percent']
              ).toFixed(4)
            }
          }
        }.bind(this)
      )
    },

    getUserInfo() {
      if (!this.EosAccount) {
        return
      }
      this.eosio.getTableRows(
        {
          code: 'onesgameplay',
          scope: 'onesgameplay',
          table: 'luckyusers',
          limit: 1,
          lower_bound: this.EosAccount,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          console.log(data)
          if (
            code == 0 &&
            data.rows.length > 0 &&
            data.rows[0]['player'] == this.EosAccount
          ) {
            var counter = data.rows[0]['counter']
            var daytime = parseInt(new Date().setHours(0, 0, 0, 0) / 1000)

            if (data.rows[0]['draw_time'] > daytime) {
              this.remainCounter = 3 - counter
            }
            // console.log()
          }
        }.bind(this)
      )
    },

    startPollLuckyPlayer: function() {
      var t = this
      clearInterval(this.timerId),
        this.lucky_players.length <= 0 ||
          (this.timerId = setInterval(function() {
            t.currentIndex >= t.lucky_players.length - 1 &&
              (t.currentIndex = -1),
              (t.currentIndex += 1)
          }, 2e3))
    },

    confirmSetting: function() {
      var val =
        this.lucky_radio == 'const' ? this.lucky_input : this.lucky_radio
      Storage.set('ones_luckydraw_amount', val)

      this.settingDialogVisible = false

      if (val == 'min') {
        this.luckydraw_amount = '0.0001'
      } else if (val == 'max') {
        this.luckydraw_amount = 'max'
      } else if (val && parseFloat(val) > 0) {
        this.luckydraw_amount = Number(val).toFixed(4)
      }
    },

    getEosBalance: function() {
      this.eosio.getEosBalance(
        function(balance) {
          console.log('eos balances', balance)
          this.eos_balance = balance
        }.bind(this)
      )
    }
  }
}
</script>

