  margin-bottom: 0.15rem;
}

.common-act-rotate-widget {
  position: relative;
  z-index: 1;
  overflow: hidden;
  margin-top: 0.2rem;
  height: 20.267rem;
  background-image: url(../../assets/activity/luckywheel.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: 50%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.common-act-rotate-widget ul {
  top: 50%;
  left: 50%;
  position: absolute;
}

.common-act-rotate-widget .product-list {
  z-index: 2;
}

.common-act-rotate-widget .product-sector-container {
  z-index: -1;
}

.common-act-rotate-widget .rotate-inner {
  position: absolute;
  z-index: 3;
  width: 14.3rem;
  height: 14.3rem;
  border-radius: 20rem;
  overflow: hidden;
  left: 2.85rem;
  top: 4.4rem;
}

.common-act-rotate-widget .rotate-inner .rotate-target {
  background-size: contain;
  background-position: 50%;
  position: absolute;
  width: 14.3rem;
  height: 14.3rem;
  left: 50%;
  margin-left: -7.2rem;
  overflow: hidden;
  border-radius: 20rem;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transform: rotate(0);
  transform: rotate(0);
}

.common-act-rotate-widget .rotate-inner .rotate-start {
  width: 5.4rem;
  height: 5.4rem;
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  margin-top: -3.28rem;
  margin-left: -2.5rem;
  text-indent: -999em;
  color: #fff;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
  background-image: url(../../assets/activity/luckywheel-draw.png);
  -webkit-transform: rotate(-2deg);
  transform: rotate(-2deg);
}

.common-act-rotate-widget .rotate-inner .product-item {
  width: 3.4rem;
  position: absolute;
  bottom: 0;
  height: 7.6rem;
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
}

.common-act-rotate-widget .rotate-inner .product-sector {
  width: 10rem;
  position: absolute;
  bottom: 0;
  height: 10rem;
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
}

.common-act-rotate-widget .rotate-inner .product-item p {
  display: block;
  margin: 0 auto;
  font-size: 0.7rem;
  word-wrap: break-word;
  letter-spacing: 0.1rem;
  overflow: hidden;
  text-align: center;
  color: #fff;
  font-weight: 700;
  width: 130%;
  margin-top: 1rem;
  margin-left: -15%;
}

.common-act-rotate-widget .rotate-inner .product-item img {
  position: absolute;
  margin: 0 auto;
  height: 1.6rem;
  width: 1.6rem;
  top: 50%;
  left: 50%;
  margin-top: -0.4rem;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.common-act-rotate-widget .product-lamp {
  background-size: contain;
  width: 14.3rem;
  height: 14.3rem;
  left: 50%;
  margin-left: -7.2rem;
  overflow: hidden;
  z-index: 1;
  position: absolute;
  background-image: url(../../assets/activity/luckywheel-lamp.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: 50%;
  -webkit-animation: animation-steps-bring 1.2s linear infinite;
  animation: animation-steps-bring 1.2s linear infinite;
  -webkit-animation-timing-function: steps(2);
  animation-timing-function: steps(2);
  pointer-events: none;
  margin-left: -7.9rem;
  width: 15.8rem;
  height: 15.8rem;
  top: 3.7rem;
}

.common-act-rotate-widget .rotate-footer {
  position: absolute;
  top: 0.4rem;
  width: 100%;
  font-size: 0.82rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
  height: 2rem;
  line-height: 2.2rem;
}
.rotate-pool {
  /* position: absolute; */
  /* top: 0.4rem; */
  width: 100%;
  font-size: 0.82rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
  margin-top: 10px;
  /* height: 2rem; */
  /* line-height: 2.2rem; */
}

.common-act-rotate-widget .rotate-footer.small {
  font-size: 0.76rem;
}

.common-act-rotate-widget .button-prize {
  position: absolute;
  right: 1rem;
  top: 1rem;
  color: #fff;
  font-weight: 700;
  font-size: 0.9rem;
  line-height: rem;
}

@-webkit-keyframes animation-steps-bring {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(30deg);
    transform: rotate(30deg);
  }
}

@keyframes animation-steps-bring {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(30deg);
    transform: rotate(30deg);
  }
}

.luckydraw-result {
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 12;
  position: fixed;
  /* background: transparent; */
  background: rgba(0, 0, 0, 0.7);
}

.luckydraw-result .container {
  position: absolute;
  top: 50%;
  left: 50%;
  background: #fff;
  border-radius: 0.427rem;
  padding: 0.8rem;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #333;
  width: 16.533rem;
  -webkit-transform: translateX(-50%) translateY(-80%);
  transform: translateX(-50%) translateY(-80%);
  opacity: 1;

  transform: translateX(-50%) translateY(-60%);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  padding: 0px;
  background-color: transparent;
  width: 17rem;
  height: 14.564rem;
  background-image: url(../../assets/activity/luckywheel-reward.png);
}

.luckydraw-result .content-title {
  font-size: 1.2rem;
  font-weight: 700;
  color: #fed93c;
  text-align: center;
  margin-top: 6.6rem;
}

.luckydraw-result .content-button {
  text-align: center;
  margin-top: 6.6rem;
}

.luckydraw-result .result-brief {
  text-align: center;
  font-size: 0.7rem;
  color: #fff;
  width: 86%;
  margin: 0.1rem auto 0;
  word-wrap: break-word;
  word-break: break-all;
  margin-top: 1rem;
}
</style>



