<template>
  <!-- // 系统通知 -->
  <div class="container">
    <ul class="list">
      <li v-for="item in news">
        <div class="header">
          <i class="el-icon-message-solid titleicon"></i>
          <span class="title">{{item.title}}</span>
          <span class="time">{{item.time}}</span>
        </div>
        <div class="body">{{item.introduction}}</div>
        <div class="fotter" @click="enterMessage(item.id)">
          <span>查看详情</span>
          <i class="el-icon-arrow-right enter"></i>
        </div>
      </li>
      <li></li>
      <li></li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {}
  },
  methods: {
    enterMessage(id) {
      this.$router.push({
        name: 'noticeInfoScene',
        params: { id }
      })
    }
  },
  beforeMount() {
    console.log(this.$store.getters['notification/hasNews'])
    if (this.$store.getters['notification/hasNews']) {
      this.$store.dispatch('notification/readnews')
    }
  },
  computed: {
    news() {
      var zone = this.$store.getters.zone
      var docs = this.$store.state.notification.news
      var news = []
      for (var i in docs) {
        if (docs[i]['zone'] == 'all' || docs[i]['zone'] == zone) {
          news.push(docs[i])
        }
      }
      return news
    }
  }
}
</script>

<style scoped>
.container {
  background-color: #f5f5f5;
  overflow-y: auto;
}
.container .list {
  margin-top: 1rem;
}
.container .list > li {
  width: 93%;
  margin: 0 auto;
  margin-bottom: 1rem;
  background-color: #fff;
  box-shadow: 1px 1px 3px rgb(172, 172, 172);
  border-radius: 8px;
}
.container .list > li .header {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  height: 3.2rem;
  line-height: 3.2rem;
  background-color: #f7f7f7;
  color: #7d7d7d;
  font-weight: 800;
  box-sizing: border-box;
  padding-left: 5%;
  padding-right: 5%;
}
.container .list > li .header .titleicon {
  font-size: 1.24rem;
  color: rgb(247, 178, 89);
}
.container .list > li .header .title {
  font-size: 1.1rem;
}
.container .list > li .header .time {
  font-weight: 400;
  float: right;
}
.container .list > li .body {
  margin-top: 1rem;
  line-height: 1.5rem;
  font-size: 0.9rem;
  color: rgb(159, 156, 156);
  box-sizing: border-box;
  padding-left: 6%;
  padding-right: 6%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.container .list > li .fotter {
  margin-top: 1rem;
  height: 3rem;
  box-sizing: border-box;
  padding-left: 6%;
  padding-right: 6%;
  line-height: 3rem;
  border-top: 1px solid #ccc;
}
.container .list > li .fotter span {
  font-size: 0.85rem;
  color: rgb(160, 160, 160);
  font-weight: 800;
}
.container .list > li .fotter .enter {
  font-size: 0.8rem;
  font-weight: 800;
  color: rgb(160, 160, 160);
}
</style>


