            所有参与的玩家使用ONES投票
          </li>
          <li>第一天：1个ONES抵3票 （04-27 20:00 ~ 04-28 20:00）</li>
          <li>第二天：1个ONES抵2.5票（04-28 20:00 ~ 04-29 20:00）</li>
          <li>第三天：1个ONES抵2票（04-29 20:00 ~ 04-30 20:00）</li>
          <li>第四天：1个ONES抵1.5票（04-30 20:00 ~ 05-01 20:00）</li>
          <li>第五天：1个ONES抵1票（05-01 20:00 ~ 05-02 20:00）</li>

          <li>3、投票活动持续5天，投票期间使用的ONES会被锁定直到投票结束，且没有分红。</li>
