            5、玩家收益：300人*1元*10级=3000元
          </p>
          <p>
            二、如果邀请的好友完成了新人任务，邀请人和被邀请人各得75个ones，32级满级重生后，再送25ONES
            1、玩家邀请了300个好友
            2、假如有60个好友完成了新人任务，玩家获得奖励=100 ONES*60人=6000 ONES（ones/usdt=0.15u），约6300元
          </p>
          <p>
            三、邀请好友入金返佣5%，最高返佣15%
            返佣收益，玩家邀请300个好友，其中30个好友平均入金150u，
            玩家获得奖励=150u*30人*5%=75u，约1575元
          </p>

          <p>
            邀请人本月的收益：
            3000+6300+1575=10875元
          </p>
        </div>-->
      </div>
    </el-dialog>
