  border-top: 2px solid #f5f5f5;
}
.nav-bar .title {
  white-space: nowrap;
  height: 50px;
  line-height: 50px;
  font-size: 1.5rem;
  color: #fff;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
}
.router-link-right {
  height: 50px;
  float: right;
  line-height: 50px;
  font-size: 1.5rem;
  color: rgb(236, 236, 236);
  margin-right: 18px;
}
.router-link-left {
  height: 50px;
  float: left;
  line-height: 50px;
  font-size: 1.5rem;
  color: rgb(236, 236, 236);
  margin-left: 18px;
}
.nav-bar {
  /* background-color: rgb(65, 153, 247); */
  background-color: #fff;
}
.nav-bar ul {
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -ms-flex-align: center;
  align-items: center;
  height: 52px;
}

.nav-bar a {
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 100%;
  font-size: 10px;
  font-family: PingFangSC-Regular;
  color: #b2b2b2;
  text-align: center;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  padding-top: 24px;
  background-position: top;
  background-repeat: no-repeat;
  background-size: 20px 20px;
}

.nav-bar .active {
  color: rgb(65, 153, 247);
}

.nav-bar .route-index {
  background-image: url(../assets/game-icon.png);
}

.nav-bar .route-index.active {
  background-image: url(../assets/game-active-icon.png);
}

.nav-bar .route-wallet {
  background-image: url(../assets/wallet-icon.png);
}

.nav-bar .route-wallet.active {
  background-image: url(../assets/wallet-active-icon.png);
}

.nav-bar .route-my {
  background-image: url(../assets/my-icon.png);
}

.nav-bar .route-my.active {
  background-image: url(../assets/my-active-icon.png);
}

.nav-bar .route-defi {
  background-image: url(../assets/defi-icon.png);
}

.nav-bar .route-defi.active {
  background-image: url(../assets/defi-active-icon.png);
}
.nav-bar .title {
  color: #fff;
}

@media screen and (min-width: 760px) {
  .nav-bar {
    bottom: auto;
  }
}

.theme-default .router-link-right span a {
  font-size: 1.5rem;
  color: #fff;
}

.theme-default .router-link-left span a {
  font-size: 1.5rem;
  color: #fff;
}

.theme-white .nav-bar {
  background-color: rgb(255, 255, 255);
}
.theme-white .nav-bar .title {
  color: rgb(65, 153, 247);
}

.theme-white .router-link-right span a {
  font-size: 1.5rem;
  color: rgb(65, 153, 247);
}

.theme-white .router-link-left span a {
  font-size: 1.5rem;
  color: rgb(65, 153, 247);
}

#programme {
  width: 100%;
  text-align: left;
}
#programme .top-nav {
  font-size: 1.1rem;
  text-align: center;
  margin-bottom: 5px;
}
#programme .sub-nav {
  font-size: 1rem;
  /* line-height: 2rem; */
  text-align: left;
  line-height: 1.7rem;
}
#programme ul li {
  text-align: left;
  margin-bottom: 0.3rem;
  line-height: 1.3rem;
}
#programme .example {
  width: 97%;
  margin: 20px auto;
  box-sizing: border-box;
  background-color: rgb(202, 202, 202);
  padding: 10px 15px;
  border-radius: 20px;
  text-align: left;
  font-size: 0.7rem;
  line-height: 1.4rem;
}
/* /deep/ .el-dialog__body {
  padding: 10px 15px;
} */
</style>



