<template>
  <div class="container" ref="settingContainer">
    <ul class="item">
      <li>
        <span>{{this.$t("settingScene.choseI18n")}}</span>
        <span>
          <el-select
            v-model="langValue"
            placeholder="请选择"
            size="mini"
            style="width:93px;"
            @change="changeI18nType"
          >
            <el-option
              v-for="item in langOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </span>
      </li>
      <li v-if="lang=='en'">
        <span>{{this.$t("settingScene.hideNonEnglisthGame")}}</span>
        <span>
          <el-switch v-model="hiddenNonEnGame" active-color="#13ce66"></el-switch>
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
import i18n, { setup } from '../../locales/index'
import Storage from '../../utils/storage'

export default {
  data() {
    return {
      hiddenNonEnGame: Storage.get('ones_nonengame') == 1 ? true : false,

      langValue: i18n.locale,
      langOptions: [
        {
          value: 'zh',
          label: '中文'
        },
        {
          value: 'en',
          label: 'English'
        }
      ],
      i18nType: true,
      itemTitle: '切换中文',
      language: i18n.t('message.chinese'),
      lang:
        typeof Storage && localStorage.localeLanguage
          ? localStorage.localeLanguage
          : 'zh'
    }
  },
  watch: {
    hiddenNonEnGame(val) {
      if (val == false) {
        Storage.remove('ones_nonengame')
      } else {
        Storage.set('ones_nonengame', '1')
      }
    }
  },
  methods: {
    changeI18nType(language) {
      if (language == 'zh') {
        this.lang = 'zh'
        setup(this.lang)
        this.language = i18n.t('message.chinese')
      } else {
        this.lang = 'en'
        setup(this.lang)
        this.language = i18n.t('message.english')
      }
    }
  }
}
</script>

<style scoped>
.container {
  background-color: #f5f5f5;
  height: 100%;
  position: relative;
}

.item {
  width: 100%;
  box-sizing: border-box;
}

.item li {
  padding: 0 6%;
  font-size: 1.2rem;
  color: rgb(96, 96, 96);
  /* font-weight: 800; */
  line-height: 3.4rem;
  height: 3.4rem;
  display: flex;
  margin-bottom: 5px;
  justify-content: space-between;
  background-color: #fff;
}

.item li span:last-child {
  font-size: 1.5rem;
}
</style>


