<template>
  <div class="downloadContainer" ref="downloadContainer" :style="downloadContainerObj">
    <div class="return">
      <i class="el-icon-arrow-left" @click="back"></i>
    </div>
    <div class="top-text">
      <img src="../../assets/downloadtips.png" alt />
    </div>
    <div class="center">
      <img src="../../assets/downloadcode.png" alt />
    </div>
    <div class="download-btns">
      <el-button type="warning" @click="download()">Android版下载</el-button>
      <el-button type="warning" plain>IOS版本(上架申请中...)</el-button>
    </div>
    <!-- <div class="bottom">
      <img src="../../assets/wechat-qrcode.jpg" alt />
    </div>-->
  </div>
</template>

<script>
import Storage from '../../utils/storage'
import Util from '../../utils/util'

export default {
  data() {
    return {
      downloadContainerObj: {
        height: ''
      },
      isMobile:Util.isMobile(),
      isAndroid: navigator.userAgent.match(/(Android);?[\s\/]+([\d.]+)?/)
    }
  },
  methods: {
    download() {
      var os = Storage.get('ones_os')
      // if (this.isAndroid || os == 'android') {
      //   window.location.href =
      //     'intent://cdn.jsdelivr.net/gh/onesgame/onesgame.github.io/android/onesplay-v0.1.apk#Intent;scheme=http;action=android.intent.action.VIEW;end'
      // } else {
      window.open(
        'https://cdn.jsdelivr.net/gh/onesgame/onesgame.github.io/android/onesplay-v0.1.apk'
      )
      // window.location.href =
      // 'https://cdn.jsdelivr.net/gh/onesgame/onesgame.github.io/android/onesplay-v0.1.apk'
      // }
    },
    back() {
      this.$router.push({
        name: 'gameScene'
      })
    }
  },
  mounted() {
    // this.downloadContainerObj.height = this.$refs.downloadContainer.offsetParent.clientHeight  + 'px'
  }
}
</script>

<style scoped>
.downloadContainer {
  width: 100%;
  height: 100vh;
  background-image: url(../../assets/downloadbg.png);
  background-size: 100% 100%;
  overflow-y: scroll;
  text-align: center;
}
.return {
  text-align: left;
  overflow: hidden;
}
.return i {
  font-size: 26px;
  color: #888;
  margin-top: 5px;
}
.top-text {
  margin: 0 auto;
  margin-top: 6vh;
  width: 80%;
  height: 170px;
}
.top-text img {
  width: 100%;
  height: 100%;
}
.center {
  width: 100%;
  margin-top: 40px;
  font-size: 20px;
  font-weight: 800;
  color: rgb(104, 104, 104);
  /* line-height: 50px; */
}
.center img {
  width: 120px;
  height: 120px;
  margin: 15px auto;
}
.download-btns {
  box-sizing: border-box;
  width: 100%;
  padding: 0 10%;
  margin-top: 10px;
}
.download-btns .el-button {
  width: 100%;
  height: 60px;
}
.download-btns .el-button:nth-child(2) {
  margin-left: 0;
  margin-top: 20px;
}
.bottom {
  width: 90px;
  height: 90px;
  margin: 15px auto;
}
.bottom img {
  width: 100%;
  height: 100%;
}
</style>



