import Http from './http'
import account from '../store/modules/account'

export default {
    sign(tx) {
        return Http.post({
            path: "/eos/sign",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {
                tx: tx //用途
            }
        })
    },
    getSwapLog(address) {
        return Http.post({
            path: "/eos/swaplog",
            headers: {
                // "access-token": account.state.user.access_token
            },
            data: {
                address: address
            }
        })
    },
    getMarketLog(address) {
        return Http.post({
            path: "/eos/marketlog",
            headers: {
                // "access-token": account.state.user.access_token
            },
            data: {
                address: address
            }
        })
    },
    getSwapRank() {
        return Http.post({
            path: "/eos/swaprank",
            headers: {
                // "access-token": account.state.user.access_token
            },
            data: {}
        })
    },

    log(data) {
        return Http.post({
            path: "/eos/log",
            headers: {
                // "access-token": account.state.user.access_token
            },
            data: data
        })
    }

}


