var router = new Router({
  routes: routes,
  mode: 'history',
  srcollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash
      }
    }
  }
})

router.beforeEach((to, from, next) => {

  if (store.state.zone == 'defi') {
    if (to.meta.title) {
      document.title = to.meta.title
    } else {
      document.title = 'OnesDeFi'
    }
    return next()
  }
  if (to.meta.title) {
    document.title = to.meta.title
  } else {
    document.title = 'OnesPlay'
  }
  const publicPages = [
    '/',
    '',
    '/game',
    '/game/',
    '/contact',
    '/setting',
    '/account/login',
    '/account/register',
    '/download',
    '/node',
    '/presale',
    '/defi',
  ]

  const loginPages = ['/account/login', '/account/register']
  var authRequired = !publicPages.includes(to.path)
  if (authRequired && to.path.indexOf("/defi/") >= 0) {
    authRequired = false
  }
  if (authRequired && to.path.indexOf('/game/') >= 0) {
    authRequired = false
  }
  const loginRequired = !loginPages.includes(to.path)

  const loggedIn = store.state.account.isLogin

  if (to.path == "/oauth/login" && window.location.host.indexOf("play.ones.game") >= 0) {
    //todo 
    if (navigator.userAgent.indexOf("TokenPocket") >= 0 || navigator.userAgent.indexOf("dappbirds") >= 0) {
      window.location.href = window.location.href.replace("play.ones.game", "eos.ones.game")
      return
    }
  }
  console.log('log', loggedIn, to, to.path, authRequired)
  if (authRequired && '/oauth/login' != to.path && !loggedIn) {
    if (from.name == 'loginScene') {
      return next()
    }
    if (from.name == 'registerScene') {
      return next()
    }
    // if (from.name == 'gameScene') {
    //   return next()
    // }
    // console.log("xxxxx")
    return next('/account/login?url=' + to.fullPath)
  } else if (!loginRequired && loggedIn) {
    if (window.location.host == "pandora.ones.game") {
      window.history.replaceState(null, null, "https://pandora.ones.game")
      window.location.href = "/"
    } else {
      return next('/')
    }
  } else {
    next()
  }
})
export default router


