// 授权支付
<template>
  <div class="oauthPaymentContainer" :style="oauthPaymentContainerObj" ref="oauthPaymentContainer">
    <div class="top">
      <div class="square"></div>
      <span>支付</span>
    </div>
    <ul class="content">
      <li class="gametype">
        <div class="gameImg">
          <el-image style="width:100%; height: 100%; " :src="app_logo"></el-image>
        </div>
        <div class="gameName">{{app_name}}</div>
      </li>
      <li class="orderPrice">
        <span class="title">价格：</span>
        <span class="context">
          <span class="type">{{order.amount}} {{order.coin_code}}</span>
        </span>
      </li>
      <li class="exchangeRate">
        <span class="title">汇率：</span>
        <span class="context">
          <span class="type">1：{{order.ones_exchange}}</span>
        </span>
      </li>
      <li class="onesPrice">
        <span class="title">合计：</span>
        <span class="context">
          <span class="type">{{order.ones_amount}} {{order.ones_coin}}</span>
        </span>
      </li>

      <li class="form">
        <!-- 密码 -->
        <div>
          <div class="confirmPrice">
            需支付
            <span class="confirmPriceNum">{{order.ones_amount}}</span>
            <!-- {{order.ones_coin}} -->
            <el-select v-model="order.ones_coin" placeholder="请选择">
              <el-option v-for="item in order.ones_coins" :label="item" :value="item" :key="item"></el-option>
            </el-select>
          </div>

          <li class="onesReward" v-if="order.reward_amount>0">
            <span class="title">奖励</span>
            <span class="context">
              <span class="amount">{{order.reward_amount}} 欢乐豆</span>
            </span>
          </li>

          <el-button type="primary" class="confirmBtn" :loading="loading" @click="pay">{{payText}}</el-button>
          <el-button plain type="primary" class="cancelBtn" @click="cancel">取消</el-button>
        </div>
        <!-- 密码 -->
      </li>

      <li class="balance">
        <span>当前账户余额：</span>
        <span class="num">
          {{coin_amount}} {{order.ones_coin}}
          <el-button
            v-if="zone != 'eos'"
            size="small"
            type="warning"
            style="margin-left:5px"
            @click="toRecharge"
          >去充值</el-button>
        </span>
      </li>
    </ul>

    <el-dialog title="确认支付" :visible.sync="confrimDialogVisible" width="100%" center>
      <div>由于区块链上链有延迟,请点击同步支付信息,如果多次点击失败并确认已支付，请联系小七(微信号:onesgame_777)</div>
      <div class>
        <el-button type="primary" class="confirmBtn" :loading="loading" @click="pay">{{payText}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import OauthService from '../../api/oauth'
import AssetService from '../../api/asset'
import Storage from '../../utils/storage'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      zone: this.$store.getters.zone,
      eosio: null,
      confrimDialogVisible: false,

      balances: {},
      oauthPaymentContainerObj: {
        height: ''
      },
      app_id: '',
      order_id: '',

      pay_password: '123456',
      app_name: '',
      app_logo: '',
      order: {
        amount: '0.0',
        ones_amount: '0.0',
        ones_exchange: '1:7',
        ones_coin: 'USDT',
        coin_code: 'CNY'
      },
      paymentMethod: '1',
      coin_amount: '0.00',
      loading: false,
      payText: '确认支付',
      payStatus: 0
    }
  },

  beforeMount: function() {
    console.log('...............')

    this.app_id = this.$route.query.app_id
    this.order_id = this.$route.query.order_id

    var source = Storage.get('ones_source')
    OauthService.getOrder(this.app_id, this.order_id, source).then(res => {
      console.log(res)

      if (res.code == 0) {
        this.app_name = res.data.name
        this.app_logo = res.data.logo

        this.order = res.data.order
        this.order.ones_coin = this.order.ones_coin.toUpperCase()
        this.order.amount = Number(this.order.amount).toFixed(2)

        if (this.order.status == 1) {
          this.isredirecting = false
          this.redirect(this.order)
        } else {
          this.getBalances()
        }
      } else {
        this.$alert(this.$t('message.' + res.msg), '温馨提示', {
          confirmButtonText: '确定',
          center: true,
          type: 'warning',
          callback: action => {
            // window.location.href = "/";
            var redirect_url = this.$route.query.redirect_url

            if (redirect_url.indexOf('?') > 0) {
              redirect_url += '&'
            } else {
              redirect_url += '?'
            }
            var params = this.$route.query

            redirect_url +=
              'status=-1' +
              '&coin_code=' +
              params.coin_code +
              '&amount=' +
              params.amount +
              '&order_id=' +
              params.order_id +
              '&state=' +
              params.state

            window.location.href = redirect_url
          }
        })
      }
    })

    if (this.zone == 'eos') {
      this.eosio = this.$store.getters.eosio
      if (!this.eosio) {
        this.eosio = new EosService(process.env.EOS_CONTRACT_NAME)
        this.$store.dispatch('ASET_EOSIO', this.eosio)
        const loading = this.$loading({
          lock: true,
          text: '加载中',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })

        this.eosio.connect(
          function(code, msg, data) {
            console.log(code, msg, data)
            if (code == 0) {
              loading.close()
              this.getEosBalances()
            } else {
              this.$message({
                message: '请使用EOS钱包打开',
                type: 'warning'
              })
            }
          }.bind(this)
        )
      }
    }
  },

  watch: {
    'order.ones_coin': function(val) {
      console.log(val)
      val = val.toUpperCase()
      // this.order.ones_coin =
      for (var i in this.order.ones_coins) {
        console.log(this.order.ones_coins[i])
        if (val == this.order.ones_coins[i]) {
          this.order.ones_exchange = this.order.ones_exchanges[i]
          this.order.ones_amount = this.order.ones_amounts[i]
          this.order.reward_amount =
            this.order.reward_amounts && this.order.reward_amounts[i]
              ? this.order.reward_amounts[i]
              : 0

          var coin_code = val.toLowerCase()
          console.log(coin_code, this.balances)
          this.coin_amount = this.balances[coin_code]
            ? Number(this.balances[coin_code]).toFixed(4)
            : '0.0000'
          break
        }
      }
    }
  },
  methods: {
    getBalances() {
      if (this.zone == 'eos') return
      AssetService.balance().then(res => {
        if (res.code == 0) {
          var balances = res.data.balances
          this.balances = res.data.balances
          var coin_code = 'usdt'
          if (this.order.ones_coin != 'USDT') {
            coin_code = this.order.ones_coin.toLowerCase()
          }
          this.coin_amount = Number(balances[coin_code]).toFixed(4)
        }
      })
    },
    getEosBalances() {
      this.eosio.getBalance(
        'eosio.token',
        'EOS',
        function(balance) {
          this.balances['eos'] = balance
        }.bind(this)
      )

      this.eosio.getBalance(
        'eosonestoken',
        'ONES',
        function(balance) {
          this.balances['ones'] = balance
          this.order.ones_coin = this.order.ones_coin
          var coin_code = this.order.ones_coin
          this.coin_amount = this.balances[coin_code]
            ? Number(this.balances[coin_code]).toFixed(4)
            : this.coin_amount
        }.bind(this)
      )

      this.eosio.getBalance(
        'tethertether',
        'USDT',
        function(balance) {
          this.balances['usdt'] = balance
        }.bind(this)
      )
    },

    pay() {
      if (this.zone == 'eos') {
        return this.payByEos()
      } else {
        return this.payByWallet()
      }
    },
    payByWallet() {
      this.loading = true
      this.payText = '支付中'
      var params = this.$route.query

      var source = Storage.get('ones_source')

      var data = {
        app_id: params.app_id,
        scopes: params.scopes,
        state: params.state,
        device: params.device,
        coin_code: this.order.coin_code,
        ones_coin: this.order.ones_coin,
        amount: this.order.amount,
        ones_amount: this.order.ones_amount,
        pay_password: this.pay_password,
        order_id: params.order_id,
        source: source
      }

      OauthService.pay(data).then(res => {
        console.log(res)

        if (res.code == 0) {
          OauthService.callback(this.app_id, this.order_id).then(res1 => {
            var order = res.data.order
            this.$message({
              message: this.$t('message.pay_success'),
              type: 'success',
              center: true
            })
            this.loading = false
            this.payText = '支付成功,跳转中'

            this.redirect(order)
          })
        } else {
          this.loading = false
          this.payText = '确认支付'

          this.$message.error({
            message: this.$t('message.' + res.msg),
            center: true
          })
        }
      })
    },

    payByEos() {
      this.loading = true
      this.payText = this.payStatus ? '同步数据中' : '支付中'
      var params = this.$route.query

      var source = Storage.get('ones_source')
      var third_orderid = this.order.third_orderid
      var _data = {
        app_id: params.app_id,
        scopes: params.scopes,
        state: params.state,
        device: params.device,
        coin_code: this.order.coin_code,
        ones_coin: this.order.ones_coin,
        amount: this.order.amount,
        ones_amount: this.order.ones_amount,
        pay_password: this.pay_password,
        order_id: params.order_id,
        source: source
      }

      var memo = 'game#' + third_orderid

      if (!this.payStatus) {
        this.eosio.transfer(
          this.order.ones_coin,
          this.order.ones_amount,
          memo,
          function(code, msg, data) {
            console.log(code, msg, data)
            if (code == 0) {
              _data.trx_id = data.txid
              this.trx_id = data.txid
              this.dopayByEos(_data)
            } else {
              this.loading = false
              this.payText = '确认支付'
              this.$message({
                message: msg,
                type: 'error',
                center: true
              })
            }
          }.bind(this)
        )
      } else {
        _data.trx_id = this.trx_id
        this.dopayByEos(_data)
      }
    },

    dopayByEos(data) {
      OauthService.eospay(data).then(res => {
        console.log(res)

        if (res.code == 0) {
          OauthService.callback(this.app_id, this.order_id).then(res1 => {
            var order = res.data.order
            this.$message({
              message: this.$t('message.pay_success'),
              type: 'success',
              center: true
            })
            this.loading = false
            this.payText = '支付成功,跳转中'

            this.redirect(order)
          })
        } else {
          this.loading = false
          this.payText = '请点击同步支付信息'
          this.payStatus = 1
          this.confrimDialogVisible = true

          this.$message.error({
            message: this.$t('message.' + res.msg),
            center: true
          })
        }
      })
    },

    redirect(order) {
      var redirect_url = this.$route.query.redirect_url

      if (redirect_url.indexOf('?') > 0) {
        redirect_url += '&'
      } else {
        redirect_url += '?'
      }
      redirect_url +=
        'status=' +
        1 +
        '&coin_code=' +
        order.coin_code +
        '&ones_coin=' +
        order.ones_coin +
        '&amount=' +
        order.amount +
        '&ones_amount=' +
        order.ones_amount +
        '&order_id=' +
        order.order_id +
        '&state=' +
        order.state +
        '&domain=' +
        window.location.host

      console.log(redirect_url)

      this.$loading({
        lock: true,
        text: '已支付,正在跳转中',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })

      window.location.href = redirect_url
    },

    cancel() {
      var redirect_url = this.$route.query.redirect_url

      if (redirect_url.indexOf('?') > 0) {
        redirect_url += '&'
      } else {
        redirect_url += '?'
      }
      var params = this.$route.query

      redirect_url +=
        'status=-1' +
        '&coin_code=' +
        params.coin_code +
        '&amount=' +
        params.amount +
        '&order_id=' +
        params.order_id +
        '&state=' +
        params.state +
        '&domain=' +
        window.location.host

      window.location.href = redirect_url
    },
    toRecharge() {
      this.$router.push({
        name: 'walletScene'
      })
    }
  },

  mounted() {
    this.oauthPaymentContainerObj.height =
      this.$refs.oauthPaymentContainer.offsetParent.clientHeight + 'px'
  }
}
</script>

<style scoped>
.oauthPaymentContainer {
  background-color: #f5f5f5;
}
.top {
  height: 2.25rem;
  line-height: 2.25rem;
}
.top .square {
  width: 0.5rem;
  height: 0.9rem;
  background-color: rgb(243, 215, 56);
  margin-left: 5%;
  border-radius: 30%;
  margin-top: 0.7rem;
  float: left;
}
.top span {
  margin-left: 0.3rem;
  font-size: 0.8rem;
}
.content li {
  width: 100%;
  background-color: #fff;
  padding-left: 5%;
  padding-right: 3%;
  box-sizing: border-box;
}
.gametype {
  overflow: hidden;
  height: 6rem;
  border-bottom: 1px dashed #ccc;
}

.gametype .gameImg {
  width: 4rem;
  height: 4rem;
  margin-top: 1rem;
  border-radius: 25%;
  overflow: hidden;
  float: left;
}

.gametype .gameName {
  float: left;
  height: 100%;
  line-height: 5rem;
  margin-left: 1rem;
  font-size: 1.2rem;
}
.exchangeRate,
.orderPrice {
  height: 2rem;
  line-height: 2rem;
  text-align: right;
  font-size: 0.85rem;
}

.onesPrice {
  height: 2rem;
  line-height: 2rem;
  margin-bottom: 0.8rem;
  text-align: right;
  font-size: 0.85rem;
}

.onesPrice .context .type,
.exchangeRate .context .type,
.orderPrice .context .type {
  color: rgb(255, 166, 0);
  font-weight: 800;
}

.onesReward {
  margin-top: 0.8rem;
  text-align: center;
  font-size: 1rem;
}

.onesReward .amount {
  color: #f56c6c;
}

.account {
  height: 2.5rem;
  line-height: 2.5rem;
  margin-bottom: 0.5rem;
}

.account .accountName {
  color: rgb(17, 183, 220);
  font-weight: 800;
}

.content .paymentMethod {
  display: block;
  height: 5.6rem;
}
.paymentMethod .title {
  height: 1.6rem;
  width: 100%;
  line-height: 1.6rem;
  font-size: 0.75rem;
  border-bottom: 1px dotted #ccc;
}

.confirmPrice {
  width: 100%;
  /* height: 4rem; */
  /* line-height: 5rem; */
  font-size: 1.2rem;
  text-align: center;
  padding-top: 15px;
  /* background-color: red; */
}
.confirmPrice .confirmPriceNum {
  font-size: 1.8rem;
  color: rgb(255, 153, 0);
}
.confirmBtn {
  width: 90%;
  margin-left: 5%;
  margin-top: 2rem;
  margin-bottom: 0.8rem;
  background-color: rgb(255, 153, 0);
  border-color: rgb(255, 153, 0);
}
.cancelBtn {
  margin-top: 0.5rem;
  margin-bottom: 10px;
  width: 90%;
  margin-left: 5%;
  border: 1px solid #c4c9c7;
  color: #a1a4a3;
}

.form {
  /* height: 8.5rem; */
  margin-top: 0.7rem;
  padding-left: 2% !important;
  padding-right: 2% !important;
}

.balance {
  width: 100%;
  /* height: 5rem; */
  margin-top: 1rem;
  line-height: 3.5rem;
  background-color: red;
}
.balance .num {
  float: right;
  margin-right: 2rem;
  color: rgb(255, 153, 0);
}

.el-select /deep/ .el-input {
  width: 100px;
}
</style>


