<template>
  <div class="onesContainer" ref="onesContainer">
    <!-- 分红部分 -->
    <div id="content">
      <div id="tab_bar">
        <ul>
          <li v-bind:class="{ active: tabName=='tab1' }" @click="showTab('tab1')">ONES</li>
          <li
            v-bind:class="{ active: tabName=='tab2' }"
            @click="showTab('tab2')"
          >{{this.$t("onesScene.mine")}}</li>
        </ul>
      </div>

      <div class="tab_css" id="tab1_content" v-if="tabName=='tab1'">
        <ul>
          <li>
            ONES{{this.$t("onesScene.Total")}}
            <div class="num">
              {{zone=='eos'?'10,000,000' : '63,000,000'}}
              <span>枚</span>
            </div>
          </li>
          <li v-if="zone != 'eos'">
            {{this.$t("onesScene.MiningPhase")}}
            <div class="num">
              {{onesList.ones.stage}}
              <el-button type="text" style="font-size:1.2rem" @click="alertPopContainer">
                <i class="el-icon-info"></i>
              </el-button>
            </div>
          </li>
          <li v-if="zone != 'eos'">
            {{this.$t("onesScene.difficulty")}}
            <div class="num">{{onesList.ones.factor}}</div>
          </li>
          <!-- <li v-if="zone == 'eos'">
            {{this.$t("onesScene.rewardEos")}}
            <div class="num">{{onesList.ones.factor}}{{this.$t("onesScene.Piece")}}</div>
          </li> -->
          <li v-if="zone != 'eos'">
            {{this.$t("onesScene.allpledged")}}
            <div class="num">
              {{onesList.stake.all_amount/10000 | numFilter_4}}
              <!-- {{onesList.ones.all/10000 | numFilter_2}} -->
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li v-if="zone == 'eos'">
            {{this.$t("onesScene.allmined")}}(OnesPlay)
            <div class="num">
              {{onesList.ones.all | numFilter_4}}
              <!-- {{onesList.ones.all/10000 | numFilter_2}} -->
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li v-if="zone == '1eos'">
            {{this.$t("onesScene.allbooked")}}
            <div class="num">
              {{onesList.ones.booked | numFilter_4}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li v-if="zone == '1eos'">
            {{this.$t("onesScene.circulation")}}
            <div class="num">
              {{onesList.ones.circulation | numFilter_4}}
              <!-- {{onesList.ones.all/10000 | numFilter_2}} -->
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li v-if="zone != 'eos'">
            {{this.$t("onesScene.frozenQuantity")}}
            <div class="num">
              {{onesList.ones.freezed}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li >
            {{this.$t("onesScene.allnode")}}
            <div class="num">
              {{onesList.ones.node}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li>
            {{this.$t("onesScene.alldestroyed")}}
            <div class="num">
              {{onesList.ones.retire}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
        </ul>
        <div id="loader">
          <progress id="p" :value="onesList.ones.all/(zone=='eos'?100000:630000)" max="100"></progress>
          <span class="percent">{{onesList.ones.all/(zone=='eos'?100000:630000) | numFilter_4}}%</span>
          <!-- todo -->
          <p>
            {{this.$t("onesScene.allmined")}}
            <span>{{onesList.ones.all | numFilter_2}}</span>
            {{this.$t("onesScene.Piece")}}
          </p>
        </div>

        <el-dialog
          :visible.sync="popUp"
          width="100%"
          :fullscreen="true"
          :before-close="popUpClose"
          id="diamondHelp"
        >
          <el-table v-if="zone!='eos'" :data="onesMineData" width="100%" size="mini">
            <el-table-column prop="stage" label="阶段" align="center"></el-table-column>
            <el-table-column prop="total" label="总量" align="center"></el-table-column>
            <el-table-column prop="difficulty" label="难度系数" align="center"></el-table-column>
            <el-table-column prop="lockCabin" label="锁仓比例" align="center"></el-table-column>
            <el-table-column prop="lockDays" label="锁仓天数" align="center"></el-table-column>
          </el-table>

          <el-table v-if="zone=='eos'" :data="eosMineData" width="100%" size="mini">
            <el-table-column prop="stage" label="阶段" align="center"></el-table-column>
            <el-table-column prop="total" label="总量" align="center"></el-table-column>
            <el-table-column prop="difficulty" label="赠送个数" align="center"></el-table-column>
          </el-table>
        </el-dialog>
      </div>
      <div class="tab_css" id="tab2_content" v-if="tabName=='tab2'">
        <!-- {{onesList.user}} -->
        <ul>
          <li>
            {{this.$t("onesScene.balance")}}
            <div class="num">
              {{onesList.user.balance/10000 | numFilter_2}}
              <span>枚</span>
            </div>
          </li>
          <li>
            {{this.$t("onesScene.mined")}}
            <div class="num">
              {{onesList.user.released/10000 | numFilter_2}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li>
            {{this.$t("onesScene.released")}}
            <div class="num">
              {{onesList.user.released/10000 | numFilter_2}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li>
            {{this.$t("onesScene.unmined")}}
            <div class="num">
              {{onesList.user.unreleased/10000 | numFilter_2}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li>
            {{this.$t("onesScene.booked")}}
            <div class="num">
              {{onesList.user.booked/10000 | numFilter_2}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li v-if="zone=='eos'">
            {{this.$t("onesScene.node")}}
            <div class="num">
              <!-- {{onesList.user.reward/10000}} -->
              {{ onesList.user.node/10000 | numFilter_2}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
          <li v-if="zone!='eos'">
            {{this.$t("onesScene.reward")}}ONES
            <div class="num">
              <!-- {{onesList.user.reward/10000}} -->
              {{ onesList.user.reward/10000 | numFilter_2}}
              <span>{{this.$t("onesScene.Piece")}}</span>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import AccountService from '../../api/account'
import Util from '../../utils/util'
export default {
  data() {
    return {
      tabName: 'tab1',
      zone: this.$store.getters.zone,
      onesMineData: [
        {
          stage: '1-1',
          total: '300000',
          difficulty: '1.00',
          lockCabin: '50%',
          lockDays: '100'
        }
      ],
      inline: true,
      popUp: false,
      bonus: {
        ones_amount: '',
        dragon_amount: '',
        per_ones_income: '',
        user_income: '', //您的预估收益DT
        rewarded: '',
        bonus_user_reward: ''
      },

      usdt_bonus: {
        ones_amount: '',
        dragon_amount: '',
        per_ones_income: '',
        user_income: '', //{{this.$t("onesScene.all")}}
        rewarded: '', //全网已分红
        bonus_user_reward: '' //你已获得分红Usdt
      },
     
      ones: {
        all: '', //开采难度的百分比
        released: '',
        reward: '',
        freezed: '', //冻结数量
        retire: '', //全网已销毁
        stage: '', //开采难度
        factor: '' //难度系数
      },
      user: {
        all: '', //您的钻石 2
        reward: '', //奖励钻石
        released: '', //已开采
        unreleased: '', //未开采
        ones_balance: ''
      },
      stake: {
        all_amount: '',
        unstake_remain_time: 0,
        user_amount: 0,
        unstake_amount: 0,
        stake_amount: 0
      },
      num: 56
    }
  },
  mounted: function() {
    // 获取页面数据
    this.bonus = this.$store.getters.bonus
    this.usdt_bonus = this.$store.getters.usdt_bonus
    this.ones = this.$store.getters.ones
    this.user = this.$store.getters.user
    this.stake = this.$store.getters.stake
    this.stake.unstake_amount = this.$store.getters.stake.unstake_amount
  },
  methods: {
    popUpClose() {
      this.popUp = false
    },
    alertPopContainer() {
      this.popUp = true
      this.onesMineData = []
      this.eosMineData = []

      var factor = 1
      var _factor = 0.05
      for (var i = 1; i <= 210; i++) {
        var stage1 = 1 + parseInt((parseInt(i) - 1) / 10)
        var stage2 = (i - (stage1 - 1) * 10) % 10
        stage2 = stage2 == 0 ? '10' : stage2
        var percent = '90%'
        var day = 80
        if (i <= 4) {
          percent = '50%'
          day = 100
        } else if (i == 5) {
          percent = '60%'
          day = 100
        } else if (i == 6) {
          percent = '80%'
          day = 90
        }

        if (i >= 4) {
          factor += _factor
        }
        if (i % 30 == 0) {
          _factor += 0.05
        }
        if (i == 180) {
          _factor = 0.4
        } else if (i == 190) {
          _factor = 0.5
        } else if (i == 200) {
          _factor = 0.6
        }

        // var onesMineData = [];
        var _Data = {
          stage: stage1 + '-' + stage2,
          total: parseInt(i * 300000),
          difficulty: Number(factor).toFixed(2),
          lockCabin: percent,
          lockDays: day
        }
        this.onesMineData.push(_Data)
        // console.log(_Data);
      }

      var eos_factors = [
        0.1,
        0.2,
        0.15,
        0.15,
        0.1,
        0.1,
        0.05,
        0.05,
        0.025,
        0.025
      ]
      var t = 10 * 100
      var stage1 = (stage2 = 0)
      for (var i = 1; i <= 100; i++) {
        var stage1 = 1 + parseInt((parseInt(i) - 1) / 10)
        var stage2 = (i - (stage1 - 1) * 10) % 10
        var factor = parseFloat(Number(t / 100).toFixed(2))

        var _i = parseInt(i / 10)
        t -= parseInt(100 * eos_factors[_i])
        this.eosMineData.push({
          stage: stage1 + '-' + stage2,
          total: parseInt(i * 100000),
          difficulty: Number(factor).toFixed(2),
          lockCabin: '100%',
          lockDays: 60
        })
      }
    },

    type(row, column, cellValue, index) {
      if (cellValue == 'decoration') return '装饰'
      if (cellValue == 'coin') return '金币'
    },

    createTime(row, column, cellValue, index) {
      return Util.formatDate(cellValue, 'MM-dd hh:mm')
    },
    amount(row, column, cellValue, index) {
      var _value =
        Number(parseFloat(cellValue / 10000)).toFixed(2) +
        this.BillTableData[index].coin_code.toUpperCase()
      return _value
    },
    reward_ones(row, column, cellValue, index) {
      return Number(parseFloat(cellValue / 10000)).toFixed(2)
    },
    showBill() {
      this.showBillBox = true
    },

    showTab(tabName) {
      this.tabName = tabName
    }
  },
  computed: {
    onesList() {
      return this.$store.state.onesbonus
    }
  },
  created: function() {
    this.$store.dispatch('getOnesBonus')
  },
  filters: {
    timeFilter(value) {
      var time = parseInt(value) + '秒'
      if (parseInt(value) > 60) {
        var second = parseInt(value) % 60
        var min = parseInt(value / 60)
        time = min + '分' + second + '秒'

        if (min > 60) {
          min = parseInt(value / 60) % 60
          var hour = parseInt(parseInt(value / 60) / 60)
          time = hour + '小时' + min + '分' + second + '秒'

          if (hour > 24) {
            hour = parseInt(parseInt(value / 60) / 60) % 24
            var day = parseInt(parseInt(parseInt(value / 60) / 60) / 24)
            time = day + '天' + hour + '小时' + min + '分' + second + '秒'
          }
        }
      }

      return time
    },
    time_Filter_DDmm(value) {
      return Util.formatDate(value, 'MM-dd hh:mm')
    },
    numFilter_4(value) {
      // 截取当前数据到小数点后4位
      let tempVal = parseFloat(value).toFixed(5)
      let realVal = tempVal.substring(0, tempVal.length - 1)
      return realVal
    },
    numFilter_2(value) {
      // 截取当前数据到小数点后2位
      let tempVal = parseFloat(value).toFixed(3)
      let realVal = tempVal.substring(0, tempVal.length - 1)
      return realVal
    },
    toUp_filter(value) {
      return value.toUpperCase()
    }
  }
}
</script>

<style scoped>
.onesContainer {
  position: relative;
}

#content {
  width: 100%;
}
#tab_bar {
  width: 100%;
  height: 3rem;
  float: left;
}
#tab_bar ul {
  display: flex;
  height: 3rem;
  text-align: center;
}
#tab_bar ul .active {
  border-bottom: 2px solid rgb(93, 112, 148);
  color: rgb(88, 88, 88);
  font-weight: 800;
  background-color: #fff;
}
#tab_bar li {
  flex: 1;
  list-style-type: none;
  float: left;
  width: 133.3px;
  height: 2.5rem;
  line-height: 2.5rem;
  /* background-color: gray; */
}
.tab_css {
  width: 100%;
  float: left;
}

#tab1_content ul li {
  margin-bottom: 0.5rem;
  background-color: #fff;
  padding-left: 4%;
  padding-right: 4%;
  box-sizing: border-box;
}
#tab1_content ul li {
  height: 3rem;
  line-height: 3rem;
  font-weight: 800;
  color: rgb(57, 57, 57);
}
#tab1_content ul li .num {
  float: right;
  color: rgb(255, 166, 0);
}

#tab2_content ul li {
  margin-bottom: 0.5rem;
  background-color: #fff;
  padding-left: 4%;
  padding-right: 4%;
  box-sizing: border-box;
}
#tab2_content ul li {
  height: 3rem;
  line-height: 3rem;
  font-weight: 800;
  color: rgb(57, 57, 57);
}
#tab2_content ul li .num {
  float: right;
  color: rgb(255, 166, 0);
}
/* 进度条 */
#loader {
  flex-direction: column-reverse;
  color: #8aaaff;
  font: 2.5em sans-serif;
  text-align: center;
}

#loader p {
  font-size: 1rem;
}
#loader .percent {
  font-size: 1.6rem;
}

#loader p > span {
  font-size: 1.4rem;
  font-weight: 800;
  color: #ff792c;
}
progress[value] {
  width: 95%;
  height: 1rem;
  border: none;
  border-radius: 0.125em;
  background: #fff;
  box-shadow: 3px 3px 8px rgb(91, 91, 91);
  /* background: #ffffff; */
}
progress[value]::-webkit-progress-bar {
  border: none;
  border-radius: 0.125em;
  background: #fff;
}
progress[value]::-webkit-progress-value {
  border: none;
  border-radius: inherit;
  background: linear-gradient(90deg, #8aaaff, #fa8cff);
}
progress[value]::-moz-progress-bar {
  border: none;
  border-radius: inherit;
  background: linear-gradient(90deg, #8aaaff, #fa8cff);
}
progress[value]::-ms-fill {
  border: none;
  border-radius: inherit;
  background: linear-gradient(90deg, #8aaaff, #fa8cff);
}
output:not(:empty) {
  padding-bottom: 1em;
}
output:not(:empty):after {
  content: '%';
}

#diamondHelp /deep/ .el-dialog__body {
  padding: 20px 0 !important;
}
#diamondHelp /deep/ .el-dialog__body::-webkit-scrollbar {
  width: 0 !important;
}
</style>


