import Http from './http'
import account from '../store/modules/account'

export default {
    send(usage) {
        return Http.post({
            path: "/sms/send",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {
                usage: usage //用途
            }
        })
    },
    bind(area, mobile) {
        return Http.post({
            path: "/sms/bind",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {
                area: area,
                mobile: mobile,
                usage: "bind" //用途
            }
        })
    },
    login(area, mobile) {
        return Http.post({
            path: "/sms/login",
            headers: {},
            data: {
                area: area,
                mobile: mobile
            }
        })
    },
    register(area, mobile) {
        return Http.post({
            path: "/sms/register",
            headers: {},
            data: {
                area: area,
                mobile: mobile
            }
        })
    },
}


