<template>
  <div class="container">
    <div class="erc20Withdraw">
      <!-- erc20Withdraw -->
      <el-form ref="withdrawForm" :model="withdrawForm" :rules="withdrawRules" label-width="80px">
        <el-form-item label="提币地址" prop="address">
          <el-input
            v-model="withdrawForm.address"
            :placeholder="this.$t('assetScene.withdrawErc20AddressPlaceholder')"
          ></el-input>
        </el-form-item>
        <el-form-item label="提币数量" prop="amount">
          <el-input
            v-model="withdrawForm.amount"
            :placeholder="this.$t('assetScene.withdrawamount')"
          ></el-input>
        </el-form-item>

        <el-button type="primary" :loading="loading" @click="withdraw" style="width:100%">提币</el-button>
      </el-form>
      <div class="current">
        当前账户余额
        <span>{{coin_amount}} {{coin_code}}</span>
      </div>
      <div class="erc20withdraw-tip">
        <p>{{this.$t("assetScene.erc20WithdrawTips1")}}</p>
        <p>{{this.$t("assetScene.erc20WithdrawTips2",{coin_code:coin_code})}}</p>
        <p>{{this.$t("assetScene.erc20WithdrawTips3",{coin_code:coin_code,amount:fee})}}</p>
      </div>
    </div>
    <div class="withdraw-items">
      <div class="title">{{this.$t("assetScene.withdrawRecord")}}</div>

      <div
        class="title"
        v-if="withdrawRecords && withdrawRecords.length==0"
      >{{this.$t("assetScene.noWithdrawRecord")}}</div>

      <ul class="infinite-list" style="overflow:auto">
        <li v-for="(item,index) in withdrawRecords">
          <div class="item-text">
            <span>{{$t("assetScene.amount")}}</span>
            <span>{{Number(item.amount/10000).toFixed(2)}}{{coin_code}}</span>
          </div>
          <div class="item-text">
            <span>{{$t("assetScene.address")}}</span>
            <span>{{item.to}}</span>
          </div>
          <div class="item-text">
            <span>{{$t("assetScene.status")}}</span>
            <span>{{item.status==1?$t("assetScene.status_success"):$t("assetScene.status_waiting")}}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import AssetService from '../../../api/asset'

export default {
  data() {
    var validateAmount = (rule, value, callback) => {
      if (value == '')
        return callback(new Error(this.$t('assetScene.withdrawPlaceholder')))
      if (parseFloat(value) < 1) {
        return callback(new Error(this.$t('assetScene.withdrawErc20MinLimit')))
      }
      return callback()
    }
    var validateAddress = (rule, value, callback) => {
      if (value == '')
        return callback(
          new Error(this.$t('assetScene.withdrawErc20AddressPlaceholder'))
        )
      return callback()
    }
    return {
      fee: '1.00',
      loading: false,

      withdrawForm: {
        address: '',
        amount: ''
      },
      withdrawRules: {
        amount: [{ validator: validateAmount, trigger: 'blur' }],
        address: [{ validator: validateAddress, trigger: 'blur' }]
      },
      coin_code: 'USDT',
      coin_amount: 0,
      withdrawRecords: []
    }
  },
  mounted: function() {
    //action=deposit
    var coin_code = this.$route.params.coin_code
    var coin_amount = this.$route.params.coin_amount
    if (coin_code) {
      this.coin_code = coin_code.toUpperCase()
      this.coin_amount = Number(coin_amount).toFixed(4)

      if (this.coin_code == 'DT') {
        this.reward_amount = '1.0'
        this.deposit_amount = '100.00'
      } else if (this.coin_code == 'USDT') {
        this.reward_amount = '3.0'
        this.deposit_amount = '100.00'
      } else if (this.coin_code == 'ZTB') {
        this.reward_amount = '5.0'
        this.deposit_amount = '100.00'
      } else {
        this.reward_amount = '1.0'
        this.deposit_amount = '100.00'
      }
    }
    this.getBalances()
    this.getTransactions()
  },
  methods: {
    getBalances() {
      AssetService.balance().then(res => {
        if (res.code == 0) {
          this.balances = res.data.balances

          this.coin_address = res.data.eth_address

          console.log(this.coin_code)
          var key = this.coin_code.toLowerCase()
          this.coin_amount = Number(this.balances[key]).toFixed(4)
        }
      })
    },
    getTransactions() {
      AssetService.transactions(this.coin_code, 'withdraw').then(res => {
        if (res.code == 0) {
          this.withdrawRecords = res.data.txs
        }
      })
    },
    withdraw() {
      this.$refs.withdrawForm.validate(valid => {
        console.log(valid, this.withdrawForm)
        if (valid) {
          this.loading = true
          var amount = this.withdrawForm.amount
          var address = this.withdrawForm.address

          var coin_code = this.coin_code.toLowerCase()
          var params = {
            type: 'erc20',
            amount: amount,
            address: address,
            coin_code: coin_code
          }
          AssetService.withdraw(params).then(res => {
            this.loading = false

            if (res.code == 0) {
              this.$message({
                message: this.$t('message.withdraw_success'),
                type: 'success',
                center: true
              })
              this.getBalances()
            } else {
              this.$message({
                message: this.$t('message.' + res.msg),
                type: 'error',
                center: true
              })
            }
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style scoped>
.erc20Withdraw {
  width: 95%;
  margin: 0 auto;
  margin-top: 2vh;
}

.container {
  width: 100%;
  /* height: 100vh; */
  margin-top: 45px;
  box-sizing: border-box;
  overflow: hidden;
}

.erc20withdraw-tip {
  padding: 10px 15px;
  line-height: 22px;
  font-size: 12px;
}

.erc20withdraw-tip .erc20-tips {
  color: #e64c65;
}

.erc20Withdraw .current {
  line-height: 3vh;
  margin-top: 0vh;
  font-size: 0.9rem;
  color: #888;
  text-align: right;
  padding: 5px 20px;
}
.erc20Withdraw .current span {
  color: #ffc02d;
  font-size: 0.9rem;
  font-weight: 800;
}
.withdraw-items {
  border-top: 1px solid #ccc;
  margin-top: 10px;
  background: #f5f5f5;
}
.withdraw-items .title {
  text-align: center;
  margin-bottom: 10px;
  margin-top: 10px;
}
.withdraw-items li {
  margin-bottom: 0.7rem;
  box-sizing: border-box;
  color: #606266;
  padding: 2% 3%;
  background-color: #fff;
}
.withdraw-items li .top {
  font-size: 1.2rem;
}
.withdraw-items li .top span {
  float: right;
  font-size: 1rem;
}
.withdraw-items .item-text {
  display: flex;
  justify-content: space-between;
  margin-top: 0.1rem;
  font-size: 1rem;
}

.withdraw-items .item-text span:nth-child(1) {
  color: #303133;
}
.withdraw-items .item-text span:nth-child(2) {
  font-size: 1.1rem;
  font-weight: 500;
}
</style>


