import Http from './http'
import account from '../store/modules/account'

export default {
    getApp(app_id) {
        return Http.post({
            path: "/oauth/app",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {
                app_id: app_id
            }
        })
    },

    getOrder(app_id, order_id, source) {
        return Http.post({
            path: "/oauth/order",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {
                app_id: app_id,
                order_id: order_id,
                source: source
            }
        })
    },
    login(params) {
        return Http.post({
            path: "/oauth/login",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: params
        })
    },

    pay(params) {
        return Http.post({
            path: "/oauth/pay",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: params
        })
    },

    eospay(params) {
        return Http.post({
            path: "/oauth/eospay",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: params
        })
    },

    callback(app_id, order_id) {
        return Http.post({
            path: "/oauth/callback",
            headers: {
                "access-token": account.state.user.access_token
            },
            data: {
                app_id: app_id,
                order_id: order_id
            }
        })
    }
}


