<script>
import Storage from '../../utils/storage'
import i18n from '../../locales/index'
import Util from '../../utils/util'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      category: 'market',
      mineContract: 'onesgamemine',
      defiContract: 'onesgamedefi',
      defaultImg: '/static/img/tokens/eos.png',

      tokens: ['ONES', 'BOX'],

      cur_time: parseInt(new Date() / 1000),
      marketAll: 1000000 * 10000,
      marketReleased: 0,
      marketReward: 0,

      marketUnreleased: 0,
      marketMined: 0,
      boxReward: 0,
      boxDoubleReward: 0,

      swapAll: 2000000 * 10000,
      swapReleased: 0,
      swapUnreleased: 0,
      swapMined: 0,

      quantityDialogVisible: false,
      mineInterval: null,

      mineTableData: [],
      claimTableData: [],
      claimDialogVisible: false,
      mineDialogVisible: false,
      claimLoading: false,
      max_liquidity_id: 0,
      liquiditys: {},
      quantity: '0.00',
      box_quantity: '0.00',
      all_quantity: '0.00',
      all_box_quantity: '0.00',

      tableData: [
        {
          name: 'Swap交易用户',
          percent: '20%'
        },
        {
          name: 'Swap做市用户',
          percent: '10%'
        },
        {
          name: '游戏用户',
          percent: '20%'
        },
        {
          name: '用于其他DeFi挖矿暂不分配',
          percent: '50%'
        }
      ]
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },

    account: function() {
      return this.$store.state.eos_account
    }
  },
  beforeMount() {
    this.category = this.$route.params.category
      ? this.$route.params.category
      : ''
    this.category =
      this.category == 'market' || this.category == 'swap'
        ? this.category
        : 'market'
  },
  mounted() {
    this.getMineConfig()

    this.getBoxReward()

    clearInterval(this.mineInterval)
    this.mineInterval = setInterval(
      function() {
        this.getMineConfig()
      }.bind(this),
      5000
    )
    this.getMyMine()

    this.getLiquidities()
  },
  watch: {},
  beforeDestroy() {
    clearInterval(this.mineInterval)
  },
  methods: {
    getLiquidities(liquidity_id, cb) {
      this.eosio.getTableRows(
        {
          code: this.defiContract,
          scope: this.defiContract,
          table: 'liquidity',
          limit: 100,
          lower_bound: liquidity_id,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          console.log(code, msg)
          if (code == 0) {
            for (var i in data.rows) {
              var row = data.rows[i]
              var liquidity_id = row.liquidity_id

              this.max_liquidity_id =
                this.max_liquidity_id > liquidity_id
                  ? this.max_liquidity_id
                  : liquidity_id

              var symbol1 = row.token1.symbol.split(',')
              var address1 = row.token1.address
              var key1 = address1 + '-' + symbol1[1].toLowerCase()
              var token1 = {
                address: address1,
                symbol: symbol1[1],
                precision: parseInt(symbol1[0]),
                key: key1
              }

              var symbol2 = row.token2.symbol.split(',')
              var address2 = row.token2.address
              var key2 = address2 + '-' + symbol2[1].toLowerCase()
              var token2 = {
                address: address2,
                symbol: symbol2[1],
                precision: parseInt(symbol2[0]),
                key: key2
              }

              var liquidity = {
                key1: key1,
                key2: key2,
                token1: token1,
                token2: token2,
                quantity1: row.quantity1,
                quantity2: row.quantity2,
                liquidity_token: row.liquidity_token,
                price1: row.price1,
                price2: row.price2,
                swap_weight: row.swap_weight,
                liquidity_weight: row.liquidity_weight
              }
              Vue.set(this.liquiditys, liquidity_id, liquidity)
            }
            if (data.more) {
              setTimeout(
                function() {
                  this.getLiquidities(this.max_liquidity_id, cb)
                }.bind(this),
                500
              )
            } else {
              if (cb && typeof cb == 'function') {
                cb()
              }
            }
          }
        }.bind(this)
      )
    },
    claim() {
      this.claimLoading = true
      var actions = [
        {
          account: this.mineContract,
          name: 'claim',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            account: this.account.name
          }
        }
      ]
      console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.claimLoading = false

          if (code == 0) {
            this.$message({
              message: '领取成功',
              type: 'success',
              center: true
            })
            this.getMyMine()
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },

    getMineConfig() {
      this.eosio.getTableRows(
        {
          code: this.mineContract,
          scope: this.mineContract,
          table: 'config',
          limit: 1,
          json: true
        },
        function(code, msg, data) {
          if (code == 0) {
            // console.log(data)
            var row = data.rows[0]
            var cur_time = parseInt(new Date() / 1000)

            this.marketMined = row.market_suply[0]
            this.marketUnreleased =
              this.marketAll - this.marketMined - this.marketReleased

            this.swapMined = row.swap_suply
            this.swapReleased =
              parseInt((cur_time - row.swap_time) * 0.01 * 10000) +
              row.swap_quantity
            this.marketReleased = this.swapReleased *2

            this.marketReward = row.market_quantity

            this.swapUnreleased =
              this.swapAll - this.swapMined - this.swapReleased
          }
        }.bind(this)
      )
    },
    getBoxReward() {
      this.eosio.getTableRows(
        {
          code: 'lptoken.defi',
          scope: 'lptoken.defi',
          table: 'rewards',
          limit: 1,
          lower_bound: 'onesgamedefi',
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (
            code == 0 &&
            data.rows &&
            data.rows.length == 1 &&
            data.rows[0]['owner'] == 'onesgamedefi'
          ) {
            var row = data.rows[0]
            this.boxReward = Number(row.cumulative / 1000000).toFixed(6)
            this.boxDoubleReward = Number((2 * row.cumulative) / 1000000).toFixed(6)
          }
        }.bind(this)
      )
    },
    getMyMine() {
      this.eosio.getTableRows(
        {
          code: this.mineContract,
          scope: this.mineContract,
          table: 'account',
          limit: 1,
          lower_bound: this.account.name,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (
            code == 0 &&
            data.rows &&
            data.rows.length == 1 &&
            data.rows[0]['account'] == this.account.name
          ) {
            var row = data.rows[0]

            this.quantity = Number(row.quantity[0] / 10000).toFixed(4) + ' ONES'
            this.box_quantity =
              Number(row.quantity[1] / 1000000).toFixed(6) + ' BOX'

            this.market_quantity = row.market_quantity
            this.swap_quantity = row.swap_quantity
            this.all_quantity =
              Number(
                parseFloat(row.market_quantity[0] / 10000) +
                  parseFloat(row.swap_quantity)
              ).toFixed(4) + ' ONES'

            this.all_box_quantity =
              Number(parseFloat(row.market_quantity[1] / 1000000)).toFixed(6) +
              ' BOX'

            this.mineTableData = [
              {
                type: this.$t('defiScene.swapmining'),
                amount: this.swap_quantity
              },
              {
                type: this.$t('defiScene.marketmining'),
                amount:
                  Number(this.market_quantity[0] / 10000).toFixed(4) + ' ONES'
              },
              {
                type: this.$t('defiScene.marketmining'),
                amount:
                  Number(this.market_quantity[1] / 1000000).toFixed(6) + ' BOX'
              }
            ]
            this.claimTableData = [
              {
                type: this.$t('defiScene.marketmining'),
                amount: this.quantity
              },
              {
                type: this.$t('defiScene.marketmining'),
                amount: this.box_quantity
              }
            ]
            // this.claimTableData.push()
          }
        }.bind(this)
      )
    }
  },
  filters: {
    numberFilter(value) {
      return Number(value / 10000).toFixed(4)
    },

    numbersFilter(value) {
      var arr = []
      var tokens = ['ONES']

      for (var i in value) {
        if (tokens[i]) {
          if (tokens[i] == 'BOX') {
            arr.push(Number(value[i] / 1000000).toFixed(6) + ' ' + tokens[i])
          } else {
            arr.push(Number(value[i] / 10000).toFixed(4) + ' ' + tokens[i])
          }
        }
      }
      return arr.join(' | ')
    },
    weightFilter(value) {
      return Number(value).toFixed(1)
    }
  }
}
</script>

