.coinList .coinItem {
  box-sizing: border-box;
  padding: 16px 5px 12px;
  background: #040718;
  border-radius: 10px;
  width: 49%;
  margin-bottom: 7px;
  border: 1px solid rgb(99, 130, 158);
}

.coinList .coinItem:nth-child(odd) {
  margin-right: 2%;
}
.coinList .coinContent {
  display: flex;
  justify-content: space-between;
}

.coinList .coinItem .coinContent .coinInfo {
  text-align: center;
  width: 45%;
}
.coinList .coinItem .coinImg {
  display: flex;
  justify-content: center;
}

.coinList .coinItem img {
  display: block;
  width: 26px;
  max-height: 26px;
}

.coinList .coinItem .coinContent .addIcon {
  -webkit-align-self: flex-start;
  align-self: flex-start;
  color: #999;
  font-size: 0.45333rem;
}
.coinList .coinItem .coinContent .coinInfo div:nth-child(3) {
  font-size: 0.28rem;
  color: #999;
}
.coinList .coinItem .itemBottom {
  text-align: center;
  font-size: 0.34667rem;
  color: #fff;
  margin-top: 0.26667rem;
}

/* /deep/ .el-table thead th {
  background:#040718;
} */

.claim-btn {
  height: 55px;
  width: 100%;
  margin-top: 20px;
  font-size: 18px;
}

</style>


