<template>
  <div class="container">
    <!-- 消息详情 -->
    <div class="title_part">
      <div class="title">{{this.news.title}}</div>
    </div>
    <div class="body_part">
      <div class="notice-user">{{this.news.user}}</div>
      <div class="notice-content">
        <div v-html="this.news.content">&nbsp;&nbsp;&nbsp;</div>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{this.news.tips}}</p>
        <p style="text-align:right;">{{this.news.time}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      news: {}
    }
  },
  beforeMount() {
    var id = this.$route.params.id
    this.news = this.$store.getters['notification/getNews'](id)
  }
}
</script>

<style scoped>
.container {
  background-color: #f5f5f5;
  overflow-y: auto;
}
.title_part {
  width: 100%;
  height: 4rem;
  padding-left: 5%;
  padding-right: 5%;
  line-height: 4rem;
  box-sizing: border-box;
  /* background-color: rgba(255, 255, 255, 0.551); */
  border-bottom: 1px solid rgb(148, 148, 148);
}
.title_part .title {
  font-size: 1.4rem;
  font-weight: 800;
  color: rgb(140, 140, 140);
}
.body_part {
  margin-top: 1rem;
  width: 100%;
  box-sizing: border-box;
  padding-left: 5%;
  padding-right: 5%;
  line-height: 3rem;
  color: rgb(93, 93, 93);
  margin-bottom: 3rem;
}
.body_part .notice-user {
  font-size: 1.1rem;
  font-weight: 800;
  color: #888;
}
.body_part .notice-content {
  font-size: 0.95rem;
  font-weight: 400;
  color: #888;
  line-height: 2rem;
}
</style>


