import Storage from '../utils/storage'

var getZone = function () {
   var url = window.location.host
   if (url.indexOf("eos.ones.game") >= 0 || url.indexOf("nft.ones.game") >= 0) {
      return "eos"
   }
   if (url.indexOf("github") >= 0) {
      return "eos"
   }
   if (url.indexOf("defi.ones.game") >= 0) {
      return "defi"
   }
   if (url.indexOf('pandora.ones.game') >= 0) {
      return 'pandora'
   }

   if (url.indexOf("localhost") >= 0 || url.indexOf("127.0.0.1") >= 0) {
      return 'eos'
   }
   return "ones"
}

var getEosAccount = function () {
   var account = Storage.get("ones_eos_account")
   if (account && account.name) {
      account.isonline = false;
      return account
   }
   return null;
}

let state = {
   token: '',
   isLogin: '',
   channel: Storage.get("ones_channel"),
   source: Storage.get("ones_source"),
   ref: Storage.get("ones_ref"),
   zone: getZone(),
   eosio: null,
   eos_account: getEosAccount()
}

export default state;


