<template>
  <div class="onesContainer" ref="onesContainer">
    <!-- 分红部分 -->
    <div id="content">
      <div id="tab_bar">
        <ul>
          <li
            v-bind:class="{ active: tabName=='tab1' }"
            @click="showTab('tab1')"
          >{{this.$t("onesScene.CurrentPledge")}}</li>
          <li
            v-bind:class="{ active: tabName=='tab2' }"
            @click="showTab('tab2')"
          >{{this.$t("onesScene.Dividend")}}</li>
        </ul>
      </div>


      <div class="tab_css" id="tab1_content" v-if="tabName=='tab1'">
        <div>
          <ul>
            <li>
              <div class="pledge-title">
                {{this.$t("onesScene.Pledgeable")}}
                <span
                  style="color: #ff792c;"
                >{{onesList.user.ones_balance/10000 | numFilter_2}}ONES</span>
              </div>
              <!-- v-model="sms" -->
              <div class="pledge-form">
                <el-form
                  ref="amountPledgeForm"
                  :model="amountPledgeForm"
                  label-width="80px"
                  label-position="left"
                  :inline="inline"
                >
                  <el-form-item label="质押金额">
                    <el-input v-model="amountPledgeForm.num" placeholder="请输入质押金额"></el-input>
                  </el-form-item>
                  <el-button
                    type="primary"
                    @click="stakeones(amountPledgeForm.num)"
                    size="small"
                    class="pledge-btn"
                  >质押</el-button>
                </el-form>
              </div>
            </li>

            <li>
              <div class="pledge-title">
                {{this.$t("onesScene.Pledged")}}
                <span style="color: rgb(58, 168, 38)">
                  {{onesList.stake.stake_amount/10000 | numFilter_2}}
                  ONES
                </span>
              </div>
              <!-- v-model="sms" -->
              <div class="pledge-form">
                <el-form
                  ref="amountRansomForm"
                  :model="amountRansomForm"
                  label-width="80px"
                  label-position="left"
                  :inline="inline"
                >
                  <el-form-item label="赎回金额">
                    <el-input v-model="amountRansomForm.num" placeholder="请输入赎回金额"></el-input>
                  </el-form-item>
                  <el-button
                    type="warning"
                    @click="unstake(amountRansomForm.num)"
                    size="small"
                    class="pledge-btn"
                  >赎回</el-button>
                </el-form>
              </div>
            </li>
          </ul>
        </div>
        <p
          style="width:100%;text-align:center;margin-bottom:5px;font-size:0.9rem;color:#777;margin-bottom:10px"
        >{{this.$t("onesScene.tips")}}</p>
        <!-- 质押后 -->
        <div class="redeemBox" v-if="onesList.stake.unstake_amount !== 0 && redeemBoxShow">
          <p>
            <span>{{this.$t("onesScene.in")}}</span>
            {{onesList.stake.unstake_amount/10000 | numFilter_2}}ONES
          </p>
          <p>
            <span>{{this.$t("onesScene.djs")}}</span>
            {{onesList.stake.unstake_remain_time | timeFilter}}
          </p>
          <p v-if="onesList.stake.unstake_remain_time" style="text-align:center;height:4rem">
            <el-button type="warning" plain @click="restake()">{{this.$t("onesScene.jxzy")}}</el-button>
          </p>
          <p v-if="!onesList.stake.unstake_remain_time" style="text-align:center;height:4rem">
            <el-button type="warning" plain @click="claim()">{{this.$t("onesScene.lq")}}</el-button>
          </p>
        </div>
      </div>
      <div class="tab_css" id="tab2_content" v-if="tabName=='tab2'">
        <div>
          <ul class="list">
            <li >
              <!-- {{onesList.bonus}} -->
              <div class="title">DT {{this.$t("onesScene.Dividend")}}</div>
              <ul class="itemlist">
                <li>
                  {{this.$t("onesScene.PrizePoolAmount")}}
                  <span>
                    {{onesList.bonus.ones_amount | numFilter_4}}
                    <span>DT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.Dividends")}}
                  <span>
                    {{onesList.bonus.rewarded | numFilter_4}}
                    <span>DT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.paid")}}
                  <span>
                    {{onesList.bonus.bonus_user_reward | numFilter_4}}
                    <span>DT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.allpledged")}}
                  <span>
                    {{onesList.stake.all_amount/10000 | numFilter_2}}
                    <span>ONES</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.youpaid")}}
                  <span>
                    {{onesList.stake.user_amount/10000 | numFilter_2}}
                    <span>ONES</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.perones")}}
                  <span>
                    {{onesList.bonus.per_ones_income/100 | numFilter_4}}
                    <span>DT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.all")}}
                  <span>
                    {{onesList.bonus.user_income | numFilter_4}}
                    <span>DT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.next")}}
                  <span>
                    {{onesList.bonus.user_income*0.05 | numFilter_4}}
                    <span>DT</span>
                  </span>
                </li>
              </ul>
            </li>
            <li >
              <div class="title">USDT {{this.$t("onesScene.Dividend")}}</div>
              <ul class="itemlist">
                <li>
                  {{this.$t("onesScene.PrizePoolAmount")}}
                  <span>
                    {{onesList.usdt_bonus.ones_amount | numFilter_4}}
                    <span>USDT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.Dividends")}}
                  <span>
                    {{onesList.usdt_bonus.rewarded | numFilter_4}}
                    <span>USDT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.paid")}}
                  <span>
                    {{onesList.usdt_bonus.bonus_user_reward | numFilter_4}}
                    <span>USDT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.allpledged")}}
                  <span>
                    {{onesList.stake.all_amount/10000 | numFilter_2}}
                    <span>ONES</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.youpaid")}}
                  <span>
                    {{onesList.stake.user_amount/10000 | numFilter_4}}
                    <span>ONES</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.perones")}}
                  <span>
                    {{onesList.usdt_bonus.per_ones_income/100 | numFilter_4}}
                    <span>USDT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.all")}}
                  <span>
                    {{onesList.usdt_bonus.user_income | numFilter_4}}
                    <span>USDT</span>
                  </span>
                </li>
                <li>
                  {{this.$t("onesScene.next")}}
                  <span>
                    {{onesList.usdt_bonus.user_income*0.05 | numFilter_4}}
                    <span>USDT</span>
                  </span>
                </li>
              </ul>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AccountService from '../../api/account'
import Util from '../../utils/util'
export default {
  data() {
    return {
      tabName: 'tab1',
      zone: this.$store.getters.zone,

      amountPledgeForm: {
        num: ''
      },
      amountRansomForm: {
        num: ''
      },
      inline: true,
      pledgeNum: null,
      redeemNum: null,
      redeemBoxShow: true,
      bonus: {
        ones_amount: '',
        dragon_amount: '',
        per_ones_income: '',
        user_income: '', //您的预估收益DT
        rewarded: '',
        bonus_user_reward: ''
      },
      usdt_bonus: {
        ones_amount: '',
        dragon_amount: '',
        per_ones_income: '',
        user_income: '', //{{this.$t("onesScene.all")}}
        rewarded: '', //全网已分红
        bonus_user_reward: '' //你已获得分红Usdt
      },
      
      ones: {
        all: '', //开采难度的百分比
        released: '',
        reward: '',
        freezed: '', //冻结数量
        retire: '', //全网已销毁
        stage: '', //开采难度
        factor: '' //难度系数
      },
      user: {
        all: '', //您的钻石 2
        reward: '', //奖励钻石
        released: '', //已开采
        unreleased: '', //未开采
        ones_balance: ''
      },
      stake: {
        all_amount: '',
        unstake_remain_time: 0,
        user_amount: 0,
        unstake_amount: 0,
        stake_amount: 0
      },
      num: 56
    }
  },
  mounted: function() {
    // 获取页面数据
    this.bonus = this.$store.getters.bonus
    this.usdt_bonus = this.$store.getters.usdt_bonus
    this.ones = this.$store.getters.ones
    this.user = this.$store.getters.user
    this.stake = this.$store.getters.stake
    this.stake.unstake_amount = this.$store.getters.stake.unstake_amount

    this.getShopOrders()
  },
  methods: {

    amount(row, column, cellValue, index) {
      var _value =
        Number(parseFloat(cellValue / 10000)).toFixed(2) +
        this.BillTableData[index].coin_code.toUpperCase()
      return _value
    },
    // 质押
    stakeones(num, ct) {
      if (ct) {
        this.redeemBoxShow = false
      }
      AccountService.stake(num).then(res => {
        if (res.code == 0) {
          this.$message({
            message: this.$t('message.stake_success'),
            type: 'success',
            center: true
          })
          this.$store.dispatch('getOnesBonus')
          this.amountPledgeForm.num = ''
        } else {
          this.$message({
            message: this.$t('message.' + (res.msg ? res.msg : 'stake_error')),
            type: 'error',
            center: true
          })
        }
      })
    },

    // 赎回
    unstake(num) {
      this.redeemBoxShow = true
      AccountService.unstake(num).then(res => {
        if (res.code == 0) {
          console.log(res)
          console.log(res)
          console.log(res)
          this.$message({
            message: this.$t('message.unstake_success'),
            type: 'success',
            center: true
          })
          this.$store.dispatch('getOnesBonus')
          this.amountRansomForm.num = ''
        } else {
          this.$message({
            message: this.$t('message.unstake_error'),
            type: 'error',
            center: true
          })
          this.amountRansomForm.num = ''
        }
        console.log(res)
      })
    },

    restake() {
      this.redeemBoxShow = false
      AccountService.restake().then(res => {
        if (res.code == 0) {
          this.$message({
            message: this.$t('message.restake_success'),
            type: 'success',
            center: true
          })
          this.$store.dispatch('getOnesBonus')
        } else {
          this.$message({
            message: this.$t('message.restake_error'),
            type: 'error',
            center: true
          })
        }
        console.log(res)
      })
    },

    claim() {
      this.redeemBoxShow = false
      AccountService.claim().then(res => {
        if (res.code == 0) {
          this.$message({
            message: this.$t('message.claim_success'),
            type: 'success',
            center: true
          })
          this.$store.dispatch('getOnesBonus')
        } else {
          this.$message({
            message: this.$t('message.claim_error'),
            type: 'error',
            center: true
          })
        }
        console.log(res)
      })
    },

    showTab(tabName) {
      this.tabName = tabName
    },

    getShopOrders() {
      AccountService.getShopOrders().then(res => {
        if (res.code == 0) {
          // console.log(res.data.orders.length)
          this.BillTableData = res.data.orders
        } else {
          // console.log(this.tableData)
        }
        console.log(res)
        console.log(res)
        console.log(res)
      })
    }
  },
  computed: {
    onesList() {
      return this.$store.state.onesbonus
    }
  },
  created: function() {
    this.$store.dispatch('getOnesBonus')
  },
  filters: {
    timeFilter(value) {
      var time = parseInt(value) + '秒'
      if (parseInt(value) > 60) {
        var second = parseInt(value) % 60
        var min = parseInt(value / 60)
        time = min + '分' + second + '秒'

        if (min > 60) {
          min = parseInt(value / 60) % 60
          var hour = parseInt(parseInt(value / 60) / 60)
          time = hour + '小时' + min + '分' + second + '秒'

          if (hour > 24) {
            hour = parseInt(parseInt(value / 60) / 60) % 24
            var day = parseInt(parseInt(parseInt(value / 60) / 60) / 24)
            time = day + '天' + hour + '小时' + min + '分' + second + '秒'
          }
        }
      }

      return time
    },
    
    numFilter_4(value) {
      // 截取当前数据到小数点后4位
      let tempVal = parseFloat(value).toFixed(5)
      let realVal = tempVal.substring(0, tempVal.length - 1)
      return realVal
    },
    numFilter_2(value) {
      // 截取当前数据到小数点后2位
      let tempVal = parseFloat(value).toFixed(3)
      let realVal = tempVal.substring(0, tempVal.length - 1)
      return realVal
    },
    toUp_filter(value) {
      return value.toUpperCase()
    }
  }
}
</script>

<style scoped>
.onesContainer {
  position: relative;
}

#content {
  width: 100%;
}
#tab_bar {
  width: 100%;
  height: 3rem;
  float: left;
}
#tab_bar ul {
  display: flex;
  height: 3rem;
  text-align: center;
}
#tab_bar ul .active {
  border-bottom: 2px solid rgb(93, 112, 148);
  color: rgb(88, 88, 88);
  font-weight: 800;
  background-color: #fff;
}
#tab_bar li {
  flex: 1;
  list-style-type: none;
  float: left;
  width: 133.3px;
  height: 2.5rem;
  line-height: 2.5rem;
  /* background-color: gray; */
}
.tab_css {
  width: 100%;
  float: left;
}

#tab1_content ul > li {
  width: 100%;
  background-color: #fff;
  padding-left: 5%;
  padding-right: 5%;
  box-sizing: border-box;
  margin-bottom: 2rem;
}
#tab1_content ul > li .pledge-title {
  height: 3rem;
  line-height: 3rem;
  font-size: 1.2rem;
  border-bottom: 1px solid #ccc;
}
#tab1_content ul > li .pledge-form {
  box-sizing: border-box;
  padding: 5px 0;
  /* background: rgba(0, 0, 0, 0.233); */
}
#tab1_content ul > li .pledge-form /deep/ .el-form-item {
  margin-bottom: 0;
}
#tab1_content ul > li .pledge-form /deep/ .el-input__inner {
  border: 0px;
}
#tab1_content ul > li .pledge-form /deep/ .pledge-btn {
  float: right;
}
#tab1_content ul > li .pledge-title span {
  float: right;
}

#tab2_content .list > li {
  width: 100%;
  margin-bottom: 1rem;
}
#tab2_content .title {
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  font-size: 1.5rem;
  font-weight: 800;
  color: #8aaaff;
  text-align: center;
  background-color: #f5f5f5;
}
#tab2_content .itemlist li {
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  box-sizing: border-box;
  padding: 0 5%;
  font-weight: 800;
  margin-bottom: 0.5rem;
  background-color: #fff;
  color: rgb(57, 57, 57);
}
#tab2_content .itemlist li span {
  float: right;
  color: #ff8e4d;
}
#tab2_content .itemlist li span span {
  font-weight: 400;
  color: #4b5c91;
}
.redeemBox {
  width: 90%;
  margin: 0 auto;
  /* height: 10rem; */
  border-top: 1px solid rgb(248, 232, 0);
  background-color: #fff;
}
.redeemBox > p {
  height: 3rem;
  line-height: 3rem;
  width: 100%;
  box-sizing: border-box;
  padding: 0 10%;
  text-align: right;
  font-size: 1rem;
  font-weight: 800;
  color: #ff8e4d;
}
.redeemBox > p span {
  float: left;
  color: #4b5c91;
}
</style>


