import Storage from '../utils/storage'

const SET_SOURCE = 'SET_SOURCE'
const SET_REF = 'SET_REF'
const SET_CHANNEL = 'SET_CHANNEL'
const SET_ZONE = 'SET_ZONE'
const SET_EOSIO = 'SET_EOSIO'
const SET_EOS_ACCOUNT = 'SET_EOS_ACCOUNT'

const mutation = {
  // 回调函数，就处理 state 的方法啦
  [SET_SOURCE](state, source) {
    state.source = source;
    Storage.set("ones_source", source)
  },

  [SET_REF](state, ref) {
    state.ref = ref;
    Storage.set("ones_ref", ref)
  },

  [SET_CHANNEL](state, channel) {
    state.channel = channel;
    Storage.set("ones_channel", channel)
  },

  // 回调函数，就处理 state 的方法啦
  [SET_ZONE](state, zone) {
    state.source = zone;
    Storage.set("ones_zone", zone)
  },

  [SET_EOS_ACCOUNT](state, account) {
    state.eos_account = account;
    Storage.set("ones_eos_account", account)
  },

  [SET_EOSIO](state, eosio) {
    if (state.eosio == null) {
      state.eosio = {}
    }
    state.eosio = eosio;
  }
}

export default mutation;


