<template>
  <div class="container" ref="safeContainer">
    <ul class="safe">
      <li class="safeitem">
        <div class="icon">
          <i class="el-icon-money"></i>
        </div>
        <div class="itemname">{{this.$t("loginPart.LoginPassword")}}</div>
        <div class="setting">
          <el-button
            type="text"
            style="color:rgb(145, 191, 255);"
            @click="OpenPopUp('loginPassWordShow')"
          >{{this.$t("safetyScene.setup")}}</el-button>
        </div>
      </li>
      <li class="safeitem" style="display:none;">
        <div class="icon">
          <i class="el-icon-s-finance"></i>
        </div>
        <div class="itemname">支付密码</div>
        <div class="setting">
          <el-button
            type="text"
            style="color:rgb(145, 191, 255);"
            @click="OpenPopUp('capitalPassWordShow')"
          >设置</el-button>
        </div>
      </li>
    </ul>

    <!-- 弹出层 -->
    <el-drawer
      class="PopUpContainer"
      :visible.sync="PopUpContainer"
      :with-header="false"
      :direction="decoration"
      :style="PopUpContainerObj"
      size="auto"
      modal-append-to-body="false"
    >
      <div class="popup-wrape">
        <!-- 登录密码 -->
        <div class="loginPassWord" v-if="loginPassWordShow">
        <el-form
          :model="loginPassWordForm"
          status-icon
          :rules="loginPassWordFormRules"
          ref="loginPassWordForm"
          label-width="20%"
          label-position="left"
          class="demo-ruleForm"
        >
          <el-form-item :label='$t("loginPart.LoginPassword")' prop="newpass">
            <el-input
              type="password"
              v-model="loginPassWordForm.newpass"
              :placeholder='$t("loginPart.PasswordPlaceHolder")'
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item :label='$t("loginPart.LoginPassword1")' prop="checkPass">
            <el-input
              type="password"
              v-model="loginPassWordForm.checkPass"
              :placeholder='$t("registerPart.PasswordAgain")'
              autocomplete="off"
            ></el-input>
          </el-form-item>

          <el-form-item :label='$t("loginPart.VerificationCode")' prop="code">
            <div style="display:flex;">
              <el-input v-model="loginPassWordForm.code" placeholder="请输入验证码" style="flex:4"></el-input>
              <el-button
                type="primary"
                style="flex:1"
                @click="sendCode('setloginpassword')"
                :disabled="loginCode"
              >{{loginCodeValue}}</el-button>
            </div>
          </el-form-item>

          <el-button
            type="primary"
            @click="submitForm(loginPassWordForm,'loginPassWordForm')"
            style="width:100%"
          >{{this.$t("safetyScene.submit")}}</el-button>
        </el-form>
        </div>
        <!-- 付款密码 -->
        <div class="capitalPassWord" v-if="capitalPassWordShow">
        <el-form
          :model="PayPassWordForm"
          status-icon
          :rules="PayPassWordFormRules"
          ref="PayPassWordForm"
          label-width="20%"
          label-position="left"
          class="demo-ruleForm"
        >
          <el-form-item label="支付密码" prop="checkPass">
            <el-input
              type="password"
              v-model="PayPassWordForm.pass"
              placeholder="请输入支付密码"
              autocomplete="off"
            ></el-input>
          </el-form-item>

          <el-form-item label="验证码" prop="code">
            <div style="display:flex;">
              <el-input v-model="PayPassWordForm.code" placeholder="请输入验证码" style="flex:4"></el-input>
              <el-button
                type="primary"
                style="flex:1"
                @click="sendCode('setpaypassword')"
                :disabled="payCode"
              >{{payCodeValue}}</el-button>
            </div>
          </el-form-item>

          <el-button
            type="primary"
            @click="submitPayPassWordForm(PayPassWordForm,'PayPassWordForm')"
            style="width:100%"
          >提交</el-button>
        </el-form>
        </div>
    </div>
    </el-drawer>

  </div>
</template>

<script>
import SmsService from '../../api/sms'
import accountapi from '../../api/account'
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      console.log(rule, value)
      if (value === '') {
        return callback(new Error(this.$t('loginPart.PasswordPlaceHolder')))
      } else {
        var regex = new RegExp('([a-zA-Z0-9]).{7,16}')
        if (!regex.test(value)) {
          return callback(new Error(this.$t('loginPart.PassWordFormatError')))
        }
        if (this.loginPassWordForm.checkPass !== '') {
          this.$refs.loginPassWordForm.validateField('checkPass')
        }
        return callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('registerPart.PasswordAgain'))) //请再次输入密码
      } else if (value !== this.loginPassWordForm.newpass) {
        callback(new Error(this.$t('registerPart.PasswordAgainError')))
      } else {
        callback()
      }
    }

    // 验证码
    var validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入验证码'))
      } else if (!/^\d{6}$/.test(value)) {
        callback(new Error('请输入6位验证码'))
      } else {
        callback()
      }
    }

    // 支付密碼
    var validatePayPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入支付密码'))
      } else if (!/^\d{6}$/.test(value)) {
        callback(new Error('请输入6位支付密码'))
      } else {
        callback()
      }
    }

    return {
      loginCode: false,
      decoration: 'btt',
      loginCodeValue: this.$t('loginPart.sendCode'),
      payCode: false,
      payCodeValue: this.$t('loginPart.sendCode'),
      PopUpContainer: false,
      loginPassWordForm: {
        newpass: '',
        checkPass: '',
        code: ''
      },
      loginPassWordFormRules: {
        newpass: [{ validator: validatePass, trigger: 'blur' }],
        checkPass: [{ validator: validatePass2, trigger: 'blur' }],
        code: [{ validator: validateCode, trigger: 'blur' }]
      },
      PayPassWordForm: {
        pass: '',
        code: ''
      },
      PayPassWordFormRules: {
        pass: [{ validator: validatePayPass, trigger: 'blur' }],
        code: [{ validator: validateCode, trigger: 'blur' }]
      },
      loginPassWordShow: false,
      capitalPassWordShow: false
    }
  },
  methods: {
    submitForm(form, formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log('submit!' + formName)
          var params = {
            password: form.newpass,
            vcode: form.code
          }
          this.$store.dispatch('account/setloginpassword', params).then(res => {
            if (res.code == 0) {
              this.PopUpContainer = false
              this.$message({
                message: '密码设置成功',
                type: 'success',
                center: true
              })
            } else {
              this.PopUpContainer = false
              this.$message({
                message: this.$t('message.' + res.msg),
                type: 'warning',
                center: true
              })
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    submitPayPassWordForm(form, formName) {
      console.log(formName)
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log('submit!' + formName)
          this.$store.dispatch('account/setpaypassword', form).then(res => {
            console.log(res)
            if (res.code == 0) {
              this.PopUpContainer = false
              this.$message({
                message: '支付密码设置成功',
                type: 'success',
                center: true
              })
            } else {
              this.PopUpContainer = false
              this.$message({
                message: this.$t('message.' + res.msg),
                type: 'warning',
                center: true
              })
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    sendCode(type) {
      this.smsdisabled = true

      SmsService.send(type).then(res => {
        if (res.code == 0) {
          this.getSecond(60)
          this.$message({
            message: this.$t('message.send_success'),
            type: 'success',
            center: true
          })
        } else {
          this.smsdisabled = false
          this.$message({
            message: this.$t('message.send_failed'),
            type: 'warning',
            center: true
          })
        }
        console.log(res)
      })
    },
    getSecond(wait) {
      let _this = this
      let _wait = wait
      if (wait == 0) {
        this.loginCode = false
        this.loginCodeValue = this.$t('loginPart.VerificationCode')
        wait = _wait
      } else {
        this.loginCode = true
        this.loginCodeValue = wait + 's'
        wait--
        setTimeout(function() {
          _this.getSecond(wait)
        }, 1000)
      }
    },
    OpenPopUp(type) {
      this.PopUpContainer = true
      if (type == 'loginPassWordShow') {
        this.loginPassWordShow = true
        this.capitalPassWordShow = false
      }
      if (type == 'capitalPassWordShow') {
        this.loginPassWordShow = false
        this.capitalPassWordShow = true
      }
    }
  }
}
</script>

<style scoped>
.container {
  background-color: #f5f5f5;
  height: 100%;
  position: relative;
}
.PopUpContainer {
  /* background-color: #e9e9e9; */
  box-sizing: border-box;
  width: 100%;
}
.PopUpContainer .popup-wrape {
  width: 93%;
  margin: 10px auto; 
}

.safe .safeitem {
  width: 100%;
  height: 4rem;
  background-color: #fff;
  margin-bottom: 1rem;
  box-sizing: border-box;
  padding: 0 7%;
  overflow: hidden;
}
.safe .safeitem .icon {
  width: 3rem;
  height: 3rem;
  /* background-color: red; */
  /* margin-top: 1rem; */
  text-align: center;
  line-height: 4.5rem;
  float: left;
}
.safe .safeitem .icon i {
  font-size: 1.8rem;
  color: rgb(138, 135, 135);
}
.safe .safeitem .itemname {
  width: 25%;
  float: left;
  margin-left: 1rem;
  text-align: left;
  line-height: 4rem;
  font-size: 1rem;
  color: rgb(138, 135, 135);
  font-weight: 800;
  /* background-color: rgb(187, 68, 68); */
}
.safe .safeitem .setting {
  width: 25%;
  height: 3rem;
  float: right;
  margin-left: 1rem;
  text-align: center;
  line-height: 4rem;
  font-size: 1rem;
  color: rgb(145, 191, 255);
}
</style>


