<template>
  <div class="playedGameContainer" ref="playedGameContainer">
    <ul class="played-list">
      <li class="game-list-container" v-for="(item,index) in playedGames" :key="item">
        <div class="cover-container">
          <img :src="item.game.logo" alt class="game-icons" />
        </div>
        <div class="game-title" v-text="item.game.name"></div>
        <div class="game-desc" >
          {{ item.play_time | time_Filter_DDmm }} {{$t("gameScenePart.played")}}
        </div>
        <div class="game-start"  @click="enterGame(item.game_id)">{{$t("gameScenePart.enterGame")}}</div>
      </li>
    </ul>
  </div>
</template>
<script>
import gameService from '../../api/game'
import Util from '../../utils/util'
export default {
  data() {
    return {
      playedGames: [],
    }
  },
  mounted() {
     gameService.getHistoryGames().then(res => {
        this.playedGames = res.data.games
        var source = Storage.get('ones_source')
      })
      .catch(err => {})
  },

  computed: {},
  methods: {
    enterGame( game_id, url) {
      var game_ids = {
        dragonmaster:1,landlord:1,douro:1,emperor:1,fishing:1,pkthreeking:1,dragonclash:1,witchtower:1
      }
      if(!Util.isMobile()&&!game_ids[game_id]){
        this.$message({
              message: this.$t('message.error_app'),
              type: 'error',
              center: true
            })
        return 
      }
      if (this.islogin) {
        this.$router.push({
          name: 'playScene',
          params: {
            game: game_id
          }
        })
      } else {
        this.$router.push({
          name: 'loginScene',
          params: { url: '/play/' + game_id }
        })
      }
    }
  },
   filters: {
    timeFilter(value) {
      var time = parseInt(value) + '秒'
      if (parseInt(value) > 60) {
        var second = parseInt(value) % 60
        var min = parseInt(value / 60)
        time = min + '分' + second + '秒'

        if (min > 60) {
          min = parseInt(value / 60) % 60
          var hour = parseInt(parseInt(value / 60) / 60)
          time = hour + '小时' + min + '分' + second + '秒'

          if (hour > 24) {
            hour = parseInt(parseInt(value / 60) / 60) % 24
            var day = parseInt(parseInt(parseInt(value / 60) / 60) / 24)
            time = day + '天' + hour + '小时' + min + '分' + second + '秒'
          }
        }
      }

      return time
    },
    time_Filter_DDmm(value) {
      return Util.formatDate(value, 'MM-dd hh:mm')
    },
 
  }
}
</script>

<style scoped>
.playedGameContainer {
  box-sizing: border-box;
  overflow-y: auto;
  position: relative;
}
.playedGameContainer::-webkit-scrollbar {
  width: 0 !important;
}
.played-list {
  width: 100%;
}
.game-list-container {
  width: 97%;
  margin: 9px auto;
  background: #fff;
  height: 3.125rem;
  padding: 0.78125rem 0;
  position: relative;
  border-radius: 4px;
}
.cover-container {
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 12px;
  position: absolute;
  top: 0.78125rem;
  left: 0.78125rem;
  overflow: hidden;
  background: #e1e1e1;
}
.cover-container .game-icons {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  transition: all 1s ease 0s;
  opacity: 1;
}
.game-title {
  position: absolute;
  left: 4.6875rem;
  overflow: hidden;
}
.game-desc {
  height: 1.25rem;
  line-height: 1.25rem;
  font-size: 0.8125rem;
  color: #999;
  bottom: 0.9375rem;
  width: 10.9375rem;
  position: absolute;
  left: 4.6875rem;
  overflow: hidden;
}
.game-start {
  width: 4.6875rem;
  height: 1.875rem;
  line-height: 1.90625rem;
  text-align: center;
  border-radius: 0.9375rem;
  background-image: linear-gradient(90deg, #7ab4f3, #4199f7);
  color: #fff;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.78125rem;
}
</style>



