        this.shareUrl += '&channel=' + this.channel
      }
      EosApi.log({
        account: val.name,
        channel: this.channel,
        source: this.source,
        ref: this.ref
      })
    }
  },
  beforeMount() {
    window.location.href="https://xo.ones.game"
    return;
    this.timer()
    var that = this
    if (this.countdown >= 0) {
      this.countdownInterval = setInterval(function() {
        that.countdownTimer()
      }, 1000)
    }

    this.birdUsers = this.getBirdUsers()
    if (this.account) {
      this.isBirdUser = this.birdUsers[this.account.name] ? true : false
    }

    this.getRate()

    this.rateInterval = setInterval(function() {
      that.getRate()
    }, 5000)
  },
  mounted() {
    return;
    var key = Storage.get('ones_notify')
    if (key != '003') {
      Storage.set('ones_notify', '003')
