        title: '温馨提示',
        message:
          '由于《虾玩农场》挖矿增加了一个新的玩法，导致技术小哥哥需要紧急加班研发！为了提供更好的体验,虾玩农场挖矿推迟一天,10月23号20点开启',
        type: 'success',
        duration: 0
      })
    }

    if (this.account.name) {
      this.shareUrl =
        'https://' + window.location.host + '/presale?ref=' + this.account.name
      if (window.location.host == 'nft.ones.game') {
