    clearInterval(this.countdownInterval)

    clearInterval(this.rateInterval)
  },
  methods: {
    timer() {
      var curtime = parseInt(new Date() / 1000)

      var bird_begin = 1603080000
      var bird_end = 1603101600

      var unlock_begin = 1603166400
      var unlock_end = 1603202400

      var lock_begin = 1603252800
      var lock_end = 1603288800
      var lobster_begin = 1603447200 + 3600 * 2

      if (curtime > lobster_begin) {
        this.exchangeDisabled = true
        this.countdown = -1
        // this.countdown = lock_end - curtime
        this.exchangeState = 4
      } else if (curtime < lobster_begin && curtime > lock_end) {
        this.exchangeDisabled = true
        this.countdown = lobster_begin - curtime
        this.exchangeState = 3
      } else if (curtime >= lock_begin && curtime < lock_end) {
        this.exchangeDisabled = false
        this.countdown = lock_end - curtime
        this.exchangeState = 2
        this._exchangeState = 2
      } else if (curtime >= unlock_end && curtime < lock_begin) {
        this.exchangeDisabled = true
        this.countdown = lock_begin - curtime
        this.exchangeState = 2
      } else if (curtime >= unlock_begin && curtime < unlock_end) {
        this.exchangeDisabled = false
        this.countdown = unlock_end - curtime
        this.exchangeState = 1
      } else if (curtime >= bird_end && curtime < unlock_begin) {
        this.exchangeDisabled = true
        this.countdown = unlock_begin - curtime
        this.exchangeState = 1
      } else if (curtime >= bird_begin && curtime < bird_end) {
        this.exchangeDisabled = this.isBirdUser ? true : false
        this.countdown = bird_end - curtime
        this.exchangeState = 0
      } else if (curtime < bird_begin) {
        this.exchangeDisabled = true
        this.countdown = bird_begin - curtime
        this.exchangeState = 0
      }

      // todo exchangeState
      // this.exchangeState = 2
    },
    countdownTimer() {
      this.countdown--
      if (this.countdown < 0) {
        this.timer()
      }

      this.day =
        parseInt(this.countdown / 86400) > 9
          ? parseInt(this.countdown / 86400)
          : '0' + parseInt(this.countdown / 86400)
      var t = this.countdown % 86400
      this.hour =
        parseInt(t / 3600) > 9 ? parseInt(t / 3600) : '0' + parseInt(t / 3600)
      t = t % 3600
      this.minute =
        parseInt(t / 60) > 9 ? parseInt(t / 60) : '0' + parseInt(t / 60)
      this.second =
        parseInt(t % 60) > 9 ? parseInt(t % 60) : '0' + parseInt(t % 60)
    },

    showBirdUsers() {},

    getBalance(cb) {
      if (!this.eosio) {
        return
      }
      this.eosio.getBalance(
        'eosio.token',
        'EOS',
        function(balance) {
          this.balance = balance
        }.bind(this)
      )
    },
    copyShaneUrl(shareLink) {
      var _input = document.createElement('input')
      _input.value = this.$t('defiScene.eosSharePresaleTips') + shareLink

      document.body.appendChild(_input)
      _input.select()
      document.execCommand('Copy')
      document.body.removeChild(_input)
      this.$message({
        message: this.$t('message.copy_success'),
        type: 'success',
        center: true
      })
    },

    generateShareImg() {
      // this.shareDialogVisible = true
      let that = this
      setTimeout(function() {
        that.$nextTick(function() {
          //生成二维码
          var canvas1 = qrcanvas({
            data: decodeURIComponent(that.shareUrl),
            size: 50
          })

          document.getElementById('qrcode').innerHTML = ''
          document.getElementById('qrcode').appendChild(canvas1)

          //合成分享图
          html2canvas(that.$refs.box, {
            allowTaint: true,
            useCORS: true
          }).then(function(canvas) {
            let dataURL = canvas.toDataURL('image/png')
            that.imgUrl = dataURL
          })
        })
      }, 100)
    },

    getRate() {
      this.eosio.getTableRows(
        {
          code: 'onesgamenfts',
          scope: 'onesgamenfts',
          table: 'config',
          limit: 1,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0 && data.rows && data.rows.length == 1) {
            var row = data.rows[0]

            var bird_quantity = parseFloat(row.bird_quantity)
            var unlock_quantity = parseFloat(row.unlock_quantity)
            var lock_quantity = parseFloat(row.lock_quantity)

            this.exchanges[0]['rate'] = Number(
              (100 * bird_quantity) / 10000
            ).toFixed(2)
            this.exchanges[1]['rate'] = Number(
              (100 * unlock_quantity) / 62500
            ).toFixed(2)
            this.exchanges[2]['rate'] = Number(
              (100 * lock_quantity) / 110000
            ).toFixed(2)
          }
        }.bind(this)
      )
    },
    exchange() {
      this.exchangeLoading = true

      var memo = 'exchange'
      if (this.exchangeState > 0) {
        if (this.channel == 'tp' || this.source == 'tokenpocket') {
          memo += ',tp'
        } else {
          memo += ',aa'
        }

        if (this.ref) {
          memo += ',' + this.ref
        }
      }

      var actions = [
        {
          account: 'eosio.token',
          name: 'transfer',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            from: this.account.name,
            to: 'onesgamenfts',
            quantity: Number(this.amount).toFixed(4) + ' EOS',
            memo: memo
          }
        }
      ]
      console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.exchangeLoading = false

          if (code == 0) {
            this.$message({
              message: '兑换成功',
              type: 'success',
              center: true
            })
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },
