<template>
  <div class="oauthContainer" ref="oauthContainer" :style="oauthContainerObj">
    <!-- 头 -->
    <div class="nav-bar">
      <!-- <div class="nav-bar-return" @click="backHome">
        <i class="el-icon-arrow-left"></i>
      </div> -->
      <span>{{this.$route.name == "oauthLoginScene"?'':"请支付"}}</span>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      oauthContainerObj: {
        height: '',
        background: '#f5f5f5'
      }
    }
  },
  methods: {
    backHome() {
      if (
        this.$route.name == 'oauthLoginScene' ||
        this.$route.name == 'paymentScene'
      ) {
        this.$router.push({
          name: 'profileScene'
        })
      }
    }
  },
  mounted() {
    this.oauthContainerObj.height =
      this.$refs.oauthContainer.offsetParent.clientHeight + 'px'
  }
}
</script>

<style scoped>
.nav-bar {
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 1.2rem;
  /* background-color: rgb(47, 47, 47); */
  /* color: aliceblue; */
  color: #000;
}
.nav-bar .nav-bar-return {
  float: left;
  font-size: 1.65rem;
}
</style>


