<template>
  <div class="addMarketContainer manage-market">
    <div class="market-manage-nav" v-if="type!=1">
      <ol>
        <li
          v-bind:class="{ active: category=='add' }"
          data-category="add"
          @click="switchCategory('add')"
        >
          <span>增加做市数量</span>
        </li>
        <li
          v-bind:class="{ active: category=='sub' }"
          data-category="sub"
          @click="switchCategory('sub')"
        >
          <span>减少做市数量</span>
        </li>
      </ol>
      <i class="el-icon-back" @click="goBack()"></i>
    </div>

    <div class="market-add-nav" v-if="type==1">
      <i class="el-icon-back" @click="goBack()"></i>

      <div class="market-add-title">加入市场</div>
      <div class="market-add-tips">
        <span>项目尚处于早期阶段，使用需自行承担风险</span>
        <i class="el-icon-info" @click="tipsDialogVisible=true"></i>
      </div>
    </div>

    <div v-if="category=='add'||type == 1" class="add-container">
      <!-- 开始 -->
      <div class="market-add-box">
        <div class="market-add-content">
          <div class="market-add-label">
            <p>存入币种</p>
            <p @click="inputTokenAmount('in')">
              余额:
              <span>{{inToken.balance}}</span>
              <span>{{inToken.symbol}}</span>
            </p>
          </div>
          <div class="market-add-token">
            <div class="market-token-info">
              <div class="logo">
                <el-image class="logo" :src="'/static/img/tokens/'+inToken.key+'.png'">
                  <div slot="error" class="image-slot">
                    <el-image :src="defaultImg"></el-image>
                  </div>
                </el-image>
              </div>
              <div class="market-token-detail">
                <p>{{inToken.symbol}}</p>
                <div class="address">{{inToken.address}}</div>
              </div>
            </div>

            <div class="market-token-amount">
              <el-input
                v-model="inTokenAmount"
                placeholder="请输入存入数量"
                @input="inputHandle('in')"
                @change="changeHandle('in')"
              ></el-input>
            </div>
          </div>
        </div>
        <div class="market-add-divider">
          <el-divider>
            <i class="el-icon-plus"></i>
          </el-divider>
        </div>
        <div class="market-add-content">
          <div class="market-add-label">
            <p>存入币种</p>
            <p @click="inputTokenAmount('out')">
              余额:
              <span>{{outToken.balance}}</span>
              <span>{{outToken.symbol}}</span>
            </p>
          </div>
          <div class="market-add-token">
            <div class="market-token-info">
              <div class="logo">
                <el-image class="logo" :src="'/static/img/tokens/'+outToken.key+'.png'">
                  <div slot="error" class="image-slot">
                    <el-image :src="defaultImg"></el-image>
                  </div>
                </el-image>
              </div>
              <div class="market-token-detail">
                <p>{{outToken.symbol}}</p>
                <div class="address">{{outToken.address}}</div>
              </div>
            </div>
            <div class="market-token-amount">
              <el-input
                v-model="outTokenAmount"
                placeholder="请输入存入数量"
                @input="inputHandle('out')"
                @change="changeHandle('out')"
              ></el-input>
            </div>
          </div>
        </div>
      </div>

      <div class="priceTipBox">
        <div class="priceItem">
          <div class="priceTip">
            兑换价格
            <i class="el-icon-info" @click="priceDialogVisible = true"></i>
          </div>
          <div class="priceCont">
            <span>1.00 {{inToken.symbol}} ≈ {{inOutPrice}} {{outToken.symbol}}</span>
            <span></span>
            <span>1.00 {{outToken.symbol}} ≈ {{outInPrice}} {{inToken.symbol}}</span>
          </div>
        </div>
        <div class="priceItem">
          <div class="priceTip">
            <span>流动池数量</span>
            <span
              class
            >{{this.inToken.quantity|filterQuantity(inToken)}}/{{this.outToken.quantity|filterQuantity(outToken)}}</span>
          </div>
        </div>

        <div class="priceItem">
          <div class="priceTip">
            <span>预计获得凭证</span>
            <span class>{{add_liquidity_token}}</span>
            <i class="el-icon-info" @click="tokenDialogVisible = true"></i>
          </div>
        </div>
      </div>
      <el-button
        class="market-btn"
        type="primary"
        :loading="addLoading"
        @click="add"
        style="width:100%;"
      >确定</el-button>
      <div class="priceTip" style="margin:5px">* 做市存入的资产可以随时退出</div>
    </div>

    <div v-if="category=='sub'&&type != 1" class="add-container">
      <div class="market-sub-box">
        <div class="market-sub-header">
          <p>凭证</p>
          <p>
            可用凭证：
            <span>{{myLiquidities[liquidity_id] && myLiquidities[liquidity_id]["liquidity_token"]}}</span>
          </p>
        </div>
        <div class="market-add-divider">
          <el-input placeholder v-model="sub_liquidity_token" value="0"></el-input>
        </div>
        <div class="market-sub-mark">
          <el-button plain @click="updateSubToken(0.25)">25%</el-button>
          <el-button plain @click="updateSubToken(0.5)">50%</el-button>
          <el-button plain @click="updateSubToken(0.75)">75%</el-button>
          <el-button plain @click="updateSubToken(1)">全部</el-button>
        </div>
      </div>

      <div class="assets">
        <p>取回资产</p>
        <div class="assetsFrom">
          <div class="alignLeft">
            <el-image class="logo" :src="'/static/img/tokens/'+inToken.key+'.png'">
              <div slot="error" class="image-slot">
                <el-image :src="defaultImg"></el-image>
              </div>
            </el-image>
            <span>{{inToken.symbol}}</span>
          </div>
          <div class="alignRight">{{inToken.balance}} {{inToken.symbol}}</div>
        </div>
        <div class="assetsTo">
          <div class="alignLeft">
            <el-image class="logo" :src="'/static/img/tokens/'+outToken.key+'.png'">
              <div slot="error" class="image-slot">
                <el-image :src="defaultImg"></el-image>
              </div>
            </el-image>
            <span>{{outToken.symbol}}</span>
          </div>
          <div class="alignRight">{{outToken.balance}} {{outToken.symbol}}</div>
        </div>
      </div>

      <el-button
        class="market-btn"
        type="primary"
        :loading="subLoading"
        @click="sub"
        style="width:100%;margin-top:20px;"
      >确定</el-button>
    </div>

    <el-dialog title="初始兑换价格" :visible.sync="priceDialogVisible" width="100%" center>
      <span>作为市场创建者，调整存入数量可自由设置初始兑换价格</span>
    </el-dialog>

    <el-dialog title="关于凭证" :visible.sync="tokenDialogVisible" width="100%" center>
      <span>凭证是代表您在流动池的做市份额，同时也是取回流动池资产的一种证明。</span>
    </el-dialog>
    <el-dialog title="风险提示" :visible.sync="tipsDialogVisible" width="100%" center>
      <p>Eos智能合约已通过安全审计，但仍然可能存在风险，建议您根据自身风险承受能力投入适当的做市资金，请不要将无法承受损失的资金加入市场！</p>

      <p
        style="margin-top:10px;"
      >EOS代币的发币合约可随时更改，因此当流动池存在恶意代币时，该流动池的做市商可能遭受损失，对于由此产生的损失需要做市商自行承担，请务必在详细了解代币的具体情况后再参与做市！</p>
    </el-dialog>
  </div>
</template>

<script>
import i18n from '../../locales/index'
import Util from '../../utils/util'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      contract: 'onesgamedefi',

      defaultImg: '/static/img/tokens/eos.png',

      liquidity_id: 0,
      type: 1,
      category: 'add',
      liquidity_token: '0.00',

      inTokenAmount: '',
      outTokenAmount: '',
      inToken: {
        address: '',
        symbol: '',
        key: '',
        precision: 4,
        balance: '0.0000',
        quantity: 0
      },
      outToken: {
        address: '',
        symbol: '',
        key: '',
        precision: 4,
        balance: '0.0000',
        quantity: 0
      },
      inOutPrice: '0.0000',
      outInPrice: '0.0000',

      liquidity: null,
      liquidity_token: 0,
      sub_liquidity_token: 0,
      add_liquidity_token: 0,

      priceDialogVisible: false,
      tokenDialogVisible: false,
      tipsDialogVisible: false,

      addLoading: false,
      subLoading: false,

      myLiquidities: {}
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },
    account: function() {
      return this.$store.state.eos_account
    }
  },
  beforeMount() {
    this.liquidity_id = this.$route.params.liquidity_id
    this.type = this.$route.params.type
  },
  mounted: function() {
    this.getLiquidity()
  },
  watch: {
    account() {
      this.getMyLiquidities()
    },
    sub_liquidity_token(value) {
      console.log(value)
      // var liquidity_token = this.myLiquidities[this.liquidity_id][
      //   'liquidity_token'
      // ]
      var liquidity_token = this.all_liquidity_token
      this.inToken.balance = Number(
        parseFloat((value / liquidity_token) * this.inToken.quantity)
      ).toFixed(this.inToken.precision)
      this.outToken.balance = Number(
        parseFloat((value / liquidity_token) * this.outToken.quantity)
      ).toFixed(this.inToken.precision)
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    updateSubToken(alpha) {
      var liquidity_token = this.myLiquidities[this.liquidity_id][
        'liquidity_token'
      ]
      this.sub_liquidity_token = parseInt(liquidity_token * alpha)
    },

    inputHandle(type) {
      if (this.all_liquidity_token == 0) {
        if (
          parseFloat(this.outTokenAmount) > 0 &&
          parseFloat(this.inTokenAmount) > 0
        ) {
          this.inOutPrice = Number(
            this.outTokenAmount / this.inTokenAmount
          ).toFixed(4)
          this.outInPrice = Number(
            this.inTokenAmount / this.outTokenAmount
          ).toFixed(4)

          //todo
          var t =
            this.inTokenAmount *
            Math.pow(10, this.inToken.precision) *
            this.outTokenAmount *
            Math.pow(10, this.outToken.precision)
          this.add_liquidity_token = parseInt(Math.sqrt(t))
        }
      } else {
      }
    },
    inputTokenAmount(type) {
      if (type == 'in') {
        this.inTokenAmount = Number(this.inToken.balance).toFixed(
          this.inToken.precision
        )
      } else if (type == 'out') {
        this.outTokenAmount = Number(this.outToken.balance).toFixed(
          this.outToken.precision
        )
      }
      this.changeHandle(type)
    },

    changeHandle(type) {
      if (type == 'in') {
        this.inTokenAmount = Number(this.inTokenAmount).toFixed(
          this.inToken.precision
        )
      } else if (type == 'out') {
        this.outTokenAmount = Number(this.outTokenAmount).toFixed(
          this.outToken.precision
        )
      }

      if (this.all_liquidity_token == 0) {
        if (
          parseFloat(this.outTokenAmount) > 0 &&
          parseFloat(this.inTokenAmount) > 0
        ) {
          this.inOutPrice = Number(
            this.outTokenAmount / this.inTokenAmount
          ).toFixed(4)
          this.outInPrice = Number(
            this.inTokenAmount / this.outTokenAmount
          ).toFixed(4)

          //todo
          var t =
            this.inTokenAmount *
            Math.pow(10, this.inToken.precision) *
            this.outTokenAmount *
            Math.pow(10, this.outToken.precision)
          this.add_liquidity_token = parseInt(Math.sqrt(t))
        }
      } else {
        if (type == 'in') {
          this.inTokenAmount = Number(this.inTokenAmount).toFixed(
            this.inToken.precision
          )
          var outTokenAmount =
            parseFloat(this.inTokenAmount) * this.inToken.price

          this.outTokenAmount = Number(outTokenAmount).toFixed(
            this.outToken.precision
          )
        } else {
          this.outTokenAmount = Number(this.outTokenAmount).toFixed(
            this.outToken.precision
          )
          var inTokenAmount =
            parseFloat(this.outTokenAmount) * this.outToken.price

          this.inTokenAmount = Number(inTokenAmount).toFixed(
            this.inToken.precision
          )
        }

        var inTokenAmount = parseFloat(this.inTokenAmount)
        var quantity = this.inToken.quantity
        var alpha = inTokenAmount / (inTokenAmount + quantity)

        this.add_liquidity_token = parseInt(
          (alpha / (1 - alpha)) * this.all_liquidity_token
        )
      }
    },

    add() {
      this.addLoading = true

      var actions = [
        {
          account: this.inToken.address,
          name: 'transfer',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            from: this.account.name,
            to: this.contract,
            quantity: this.inTokenAmount + ' ' + this.inToken.symbol,
            memo: 'addliquidity,' + this.liquidity_id
          }
        },
        {
          account: this.outToken.address,
          name: 'transfer',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            from: this.account.name,
            to: this.contract,
            quantity: this.outTokenAmount + ' ' + this.outToken.symbol,
            memo: 'addliquidity,' + this.liquidity_id
          }
        },
        {
          account: this.contract,
          name: 'addliquidity',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            account: this.account.name,
            liquidity_id: this.liquidity_id
          }
        }
      ]
      console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.addLoading = false
          if (code == 0) {
            this.$message({ message: '加入成功', type: 'success' })
            this.outTokenAmount = ''
            this.inTokenAmount = ''

            this.getLiquidity()
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },
    // getBalance(address, symbol, cb) {
    //   this.eosio.getBalance(
    //     address,
    //     symbol,
    //     function(balance) {
    //       var key = address + '-' + symbol.toLowerCase()
    //       Vue.set(this.balances, key, balance)
    //       if (cb) {
    //         cb(balance)
    //       }
    //     }.bind(this)
    //   )
    // },
    sub() {
      var liquidity_token = this.myLiquidities[this.liquidity_id][
        'liquidity_token'
      ]
      if (liquidity_token < this.sub_liquidity_token) {
        this.$message.error('余额不足')
        return false
      }
      this.subLoading = true

      var actions = [
        {
          account: this.contract,
          name: 'subliquidity',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            account: this.account.name,
            liquidity_id: this.liquidity_id,
            liquidity_token: this.sub_liquidity_token
          }
        }
      ]
      console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.subLoading = false
          if (code == 0) {
            this.$message({ message: '减少做市成功', type: 'success' })
            this.getMyLiquidities()
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },

    getBalances() {
      this.eosio.getBalance(
        this.inToken.address,
        this.inToken.symbol,
        function(balance) {
          this.inToken.balance = balance
        }.bind(this)
      )
      this.eosio.getBalance(
        this.outToken.address,
        this.outToken.symbol,
        function(balance) {
          this.outToken.balance = balance
        }.bind(this)
      )
    },

    getLiquidity() {
      this.eosio.getTableRows(
        {
          code: this.contract,
          scope: this.contract,
          table: 'liquidity',
          limit: 1,
          lower_bound: this.liquidity_id,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          console.log(code, msg, data)
          if (
            code == 0 &&
            data.rows &&
            data.rows.length == 1 &&
            data.rows[0]['liquidity_id'] == this.liquidity_id
          ) {
            var row = data.rows[0]
            this.all_liquidity_token = parseInt(row.liquidity_token)
            this.liquidity = row

            var symbol1 = row.token1.symbol.split(',')
            var address1 = row.token1.address
            var key1 = address1 + '-' + symbol1[1].toLowerCase()
            this.inToken = {
              address: address1,
              symbol: symbol1[1],
              key: key1,
              precision: parseInt(symbol1[0]),
              balance: '0.0000',
              quantity: parseFloat(row.quantity1),
              price: row.price1
            }

            var symbol2 = row.token2.symbol.split(',')
            var address2 = row.token2.address
            var key2 = address2 + '-' + symbol2[1].toLowerCase()
            this.outToken = {
              address: address2,
              symbol: symbol2[1],
              key: key2,
              precision: parseInt(symbol2[0]),
              balance: '0.0000',
              quantity: parseFloat(row.quantity2),
              price: row.price2
            }

            this.inOutPrice = Number(row.price1).toFixed(
              parseInt(symbol1[0]) + 2
            )
            this.outInPrice = Number(row.price2).toFixed(
              parseInt(symbol2[0]) + 2
            )
          }

          this.getMyLiquidities()
        }.bind(this)
      )
    },

    getMyLiquidities() {
      if (this.account && this.account.name) {
        this._getMyLiquidities()
        this.getBalances()
      } else {
        setTimeout(
          function() {
            this.getMyLiquidities()
          }.bind(this),
          3000
        )
      }
    },
    _getMyLiquidities() {
      this.eosio.getTableRows(
        {
          code: this.contract,
          scope: this.liquidity_id,
          table: 'defipools',
          limit: 1,
          lower_bound: this.account.name,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0) {
            this.myLiquidities = {}
            for (var i in data.rows) {
              var row = data.rows[i]
              if (row.account != this.account.name) {
                break
              }

              Vue.set(this.myLiquidities, this.liquidity_id, row)
              // this.myLiquidities[row.liquidity_id] = row
            }
          }
        }.bind(this)
      )
    },

    switchCategory(category) {
      this.category = category
    }
  },
  filters: {
    filterQuantity(quantity, token) {
      if (!token) {
        return
      }
      return (
        Number(token.quantity).toFixed(token.precision) + ' ' + token.symbol
      )
    }
  }
}
</script>

