// 授权登录
<template>
  <div class="oauthLoginContainer" ref="oauthLoginContainer" :style="oauthLoginContainerObj">
    <div class="loginContainer">
      <div class="popup-container">
        <div class="popup-wrap">
          <div class="popup-header"></div>
          <!-- 已登录 -->
          <div class="islogin" style>
            <div class="popup-content">
              <div class="game-img">
                <img :src="app_logo" alt />
              </div>
              <div class="game-authorize-content">
                <div class="title">{{app_name}}申请获得以下权限：</div>
                <div class="authorize-content-list">
                  <ul>
                    <li>账号信息</li>
                    <li>使用部分币种资产</li>
                  </ul>
                </div>
              </div>
            </div>
            <div class="popup-btn-group">
              <el-button type="warning" @click="cancel">拒绝</el-button>
              <el-button type="primary" @click="login">允许</el-button>
            </div>
          </div>
          <!-- 未登录 -->
          <!-- <div class="unlogin">
              <div class="text">您还未登录,请先登录账号</div>
               <el-button type="success" plain>前去登录</el-button>
          </div>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import OauthService from '../../api/oauth'

export default {
  data() {
    return {
      oauthLoginContainerObj: {
        height: '1'
      },
      app_id: null,
      app_logo: '',
      app_name: ''
    }
  },
  beforeRouteEnter(to, from, next) {
    console.log('oauth', to, from)

    next(vm => {
      var loggedIn = vm.$store.state.account.isLogin
      // var loggedIn = true;

      if (!loggedIn) {
        next('/account/login')
      } else {
        next()
      }
    })
  },

  beforeRouteUpdate(to, from, next) {
    console.log(to, from)
  },

  created() {
    this.app_id = this.$route.query.app_id
    this.$loading({
      lock: true,
      text: '跳转中',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)'
    })
    OauthService.getApp(this.app_id).then(res => {
      console.log(res)
      if (res.code == 0) {
        this.app_name = res.data.name
        this.app_logo = res.data.logo

        if (res.data.status == 1) {
          this.authed(res.data)
        } else {
          this.login()
        }
      }
    })
  },

  methods: {
    login() {
      var params = {
        app_id: this.app_id,
        scopes: this.$route.query.scopes,
        state: this.$route.query.state,
        device: this.$route.query.device,
        redirect_url: this.$route.query.redirect_url
      }

      // const loading = this.$loading({
      //   lock: true,
      //   text: '跳转中',//正在授权',
      //   spinner: 'el-icon-loading',
      //   background: 'rgba(0, 0, 0, 0.7)'
      // })

      OauthService.login(params).then(res => {
        console.log(res)
        // loading.close()
        if (res.code == 0) {
          this.authed(res.data)
        } else {
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'error',
            center: true
          })
        }
      })
    },

    cancel() {
      var redirect_url = this.$route.query.redirect_url
    },

    authed(data) {
      var params = {
        app_id: this.app_id,
        scopes: this.$route.query.scopes,
        state: this.$route.query.state,
        device: this.$route.query.device,
        redirect_url: this.$route.query.redirect_url
      }
      // this.$loading({
      //   lock: true,
      //   text: '跳转中',//'已授权,正在跳转中',
      //   spinner: 'el-icon-loading',
      //   background: 'rgba(0, 0, 0, 0.7)'
      // })
      // return;

      var access_code = data.access_code
      var expire_time = data.expire_time

      var redirect_url = params.redirect_url
      if (redirect_url.indexOf('?') > 0) {
        redirect_url +=
          '&access_code=' + access_code + '&expire_time=' + expire_time
      } else {
        redirect_url +=
          '?access_code=' + access_code + '&expire_time=' + expire_time
      }

      redirect_url +=
        '&scopes=' +
        params.scopes +
        '&state=' +
        params.state +
        '&device=' +
        params.device +
        '&app_id=' +
        params.app_id

      window.location.href = redirect_url
    }
  },

  mounted() {
    this.oauthLoginContainerObj.height =
      this.$refs.oauthLoginContainer.offsetParent.clientHeight - 40 + 'px'
  }
}
</script>

<style scoped>
.oauthLoginContainer {
  /* background-color: #65696a; */
  overflow: hidden;
}
.popup-container {
  width: 80%;
  margin: 0 auto;
  border-radius: 1rem;
  height: 24rem;
  background-color: rgb(243, 242, 242);
  margin-top: 10rem;
  overflow: hidden;
  display: none;
}

.popup-wrap {
  width: 80%;
  height: 100%;
  margin: 0 auto;
  /* background-color: #fff; */
}
.popup-header {
  height: 4rem;
  line-height: 4rem;
  border-bottom: 1px dashed #ccc;
  margin-bottom: 1rem;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 800;
  /* background-color: #fff; */
}
.popup-content {
  height: 13rem;
  /* background-color: #fff; */
}
.popup-content .game-img {
  height: 100px;
  margin: 0 auto;
  border-radius: 5%;
  overflow: hidden;
  text-align: center;
}
.popup-content .game-img img {
  /* width: 100%; */
  height: 100%;
}
.game-authorize-content .title {
  margin-top: 0.5rem;
  font-weight: 800;
  font-size: 0.9rem;
}
.game-authorize-content .authorize-content-list {
  margin-top: 1rem;
  font-size: 0.87rem;
  color: #6a6565;
}
.game-authorize-content .authorize-content-list ul li {
  list-style: square;
  margin-left: 2rem;
}
.popup-btn-group {
  height: 4rem;
  /* background-color: blue; */
  display: flex;
  overflow: hidden;
}
.popup-btn-group .el-button {
  margin-top: 1rem;
  flex: 1;
}
.popup-btn-group .el-button:nth-child(2) {
  margin-left: 1rem;
}
.unlogin .text {
  width: 100%;
  text-align: center;
  font-size: 0.9rem;
  color: #6a6565;
  position: relative;
}
.unlogin .el-button {
  /* margin-top: 5vh; */
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, 0);
}
</style>


