import EosJs from 'eosjs';
import ScatterJS from 'scatterjs-core';
import ScatterEOS from 'scatterjs-plugin-eosjs';

import TpUtil from 'tp-eosjs'
import EosApi from '../api/eos'
import Storage from './storage'

import Util from './util'

// Don't forget to tell ScatterJS which plugins you are using.
ScatterJS.plugins(new ScatterEOS());

var network = {
    blockchain: 'eos',
    protocol: 'https',
    host: process.env.EOS_HOST,
    port: 443,
    chainId: process.env.EOS_CHAIN_ID
}

class EosService {
    constructor(contractAccount) {
        this.dappName = "OnesPlay"
        this.contractAccount = contractAccount;

        this.node = process.env.EOS_HOST
        this.hasLoging = false

        var node = Storage.get("ones_eosnode");
        if (node) {
            this.node = node
        }
        network.host = this.node;

        var eosParam = {
            keyProvider: '',
            httpEndpoint: "https://" + this.node,
            chainId: process.env.EOS_CHAIN_ID,
            timeout: 2000,
        }
        var eosParam2 = {
            keyProvider: '',
            httpEndpoint: "https://" + process.env.EOS_HOST2,
            chainId: process.env.EOS_CHAIN_ID,
            timeout: 2000,
        }
        var eosParam3 = {
            keyProvider: '',
            httpEndpoint: "https://" + process.env.EOS_HOST3,
            chainId: process.env.EOS_CHAIN_ID,
            timeout: 2000,
        }

        this.scatterReadEosJs = EosJs(eosParam);
        // this.scatterReadEosJs2 = EosJs(eosParam2);
        // this.scatterReadEosJs3 = EosJs(eosParam3);
    }

    log() {
        console.log(".......", this.hasLoging)
        if (this.hasLoging) {
            return
        }
        this.hasLoging = true
        var uuid = Storage.get('ones_uuid')

        if (!uuid) {
            uuid = ''
            var len = len || 32
            var $chars =
                'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678' /****默认去掉了容易混淆的字符oOLl,9gq,Vv,Uu,I1****/
            var maxPos = $chars.length
            for (var i = 0; i < len; i++) {
                uuid += $chars.charAt(Math.floor(Math.random() * maxPos))
            }
            Storage.set('ones_uuid', uuid)
        }

        EosApi.log({
            account: this.account.name,
            source: TpUtil.isConnected() ? "tp" : "",
            scene: 'transaction',
            uuid: uuid
        })
    }

    login(cb) {
        ScatterJS.scatter.connect('OnesPlay').then(connected => {
            console.log("connecting", connected)
            // If the user does not have Scatter or it is Locked or Closed this will return false;
            if (!connected) return cb(1, 'error_eos_connect_failed');

            this.scatter = ScatterJS.scatter;
            const eosOptions = {
                // expireInSeconds: 60
            };
            this.scatterEOSJS = this.scatter.eos(network, EosJs, eosOptions);
            window.ScatterJS = null;

            // Now we need to get an identity from the user.
            // We're also going to require an account that is connected to the network we're using.
            const requiredFields = {
                accounts: [network]
            };

            this.scatter.getIdentity(requiredFields).then(() => {

                // Always use the accounts you got back from Scatter. Never hardcode them even if you are prompting
                // the user for their account name beforehand. They could still give you a different account.
                const account = this.scatter.identity.accounts.find(x => x.blockchain === 'eos');
                console.log(account)
                this.hasLoging = false;
                this.account = account

                var nonce = Util.randomString(12) + " " + Util.randomString(12)
                var str = account.name + " " + nonce
                this.scatter.getArbitrarySignature(account.publicKey, str).then(signature => {
                    console.log(account, signature)
                    cb(0, '', {
                        publicKey: account.publicKey,
                        name: account.name,
                        nonce: nonce,
                        signature: signature
                    })
                }).catch(error => {
                    this.scatter.forgetIdentity()
                    console.log(error);
                    cb(1, 'error_eos_sign_failed')
                })
                // You can pass in any additional options you want into the eosjs reference.
            }).catch(error => {
                // The user rejected this request, or doesn't have the appropriate requirements.
                console.log(error);
                cb(1, 'error_eos_getIdentity_failed')
            });
        }).catch(error => {
            console.log(error);
            cb(1, 'error_eos_connect_failed')
            // The user rejected this request, or doesn't have the appropriate requirements.
        })

    }

    logout() {
        if (this.scatter != null) {
            this.account = null;
            this.scatter.forgetIdentity()
        } else {

        }
    }

    connect(cb) {
        console.log("connect")


        ScatterJS.scatter.connect('OnesPlay').then(connected => {

            // If the user does not have Scatter or it is Locked or Closed this will return false;
            if (!connected) return cb(1, 'error_eos_connect_failed');

            this.scatter = ScatterJS.scatter;
            const eosOptions = {
                expireInSeconds: 60
            };
            this.scatterEOSJS = this.scatter.eos(network, EosJs, eosOptions);
            window.ScatterJS = null;
            // Now we need to get an identity from the user.
            // We're also going to require an account that is connected to the network we're using.
            const requiredFields = {
                accounts: [network]
            };

            // this.scatter.forgetIdentity()
            this.scatter.getIdentity(requiredFields).then(() => {

                // Always use the accounts you got back from Scatter. Never hardcode them even if you are prompting
                // the user for their account name beforehand. They could still give you a different account.
                const account = this.scatter.identity.accounts.find(x => x.blockchain === 'eos');
                this.account = account
                console.log(account, "xxxx")

                cb(0, '')
            }).catch(error => {
                // The user rejected this request, or doesn't have the appropriate requirements.
                console.log(error);
                cb(1, 'error_eos_getIdentity_failed')
            });
        }).catch(error => {
            console.log(error);
            cb(1, 'error_eos_connect_failed')
            // The user rejected this request, or doesn't have the appropriate requirements.
        })
    }

    transfer(coin_code, amount, memo, cb) {
        this._transfer(coin_code, amount, memo, cb);
    }

    transferFreeCpu(coin_code, amount, memo, cb) {
        var contracts = {
            eos: "eosio.token",
            ones: "eosonestoken"
        }
        this.scatterEOSJS.transaction({
            actions: [{
                account: contracts[coin_code],
                name: 'transfer',
                authorization: [{
                    actor: "onesgameplay",
                    permission: "active",
                }, {
                    actor: this.account.name,
                    permission: this.account.authority,
                }],
                data: {
                    from: this.account.name,
                    to: this.contractAccount,
                    quantity: amount + ' ' + coin_code.toUpperCase(),
                    memo: memo,
                },
            }]
        }, {
            broadcast: !1,
            sign: !0
        }).then(e => {
            var n = e.transaction.transaction;
            n.signatures = e.transaction.signatures;


            console.log(e, n)
            // // That's it!
            // console.log(`Transaction ID: ${trx.transaction_id}`);

            // cb(0, "", {
            //     txid: trx.transaction_id
            // })
        }).catch(error => {
            console.log(error);
            cb(1, error.message ? error.message : error.toString())
        });
    }

    _transfer(coin_code, amount, memo, cb) {
        if (TpUtil.isConnected()) {
            this.transferByTp(coin_code, amount, memo, cb)
            return
        }
        console.log("transfer", coin_code, amount, memo)
        if (coin_code.toLowerCase() == "ones") {
            this.transferWithOnes(amount, memo, cb)
        } else if (coin_code.toLowerCase() == "eos") {
            this.transferWithEos(amount, memo, cb)
        } else if (coin_code.toLowerCase() == "usdt") {
            this.transferWithUsdt(amount, memo, cb)
        }
    }

    transferByTp(coin_code, amount, memo, cb) {

        tp.eosTokenTransfer({
            from: this.account.name,
            to: this.contractAccount,
            amount: amount,
            tokenName: coin_code.toUpperCase(),
            precision: 4,
            contract: coin_code.toLowerCase() == 'eos' ? 'eosio.token' : "eosonestoken",
            memo: memo,
            address: this.account.publicKey
        }).then(res => {
            console.log(res)
            if (res.result) {
                cb(0, "", {
                    txid: res.data.transactionId
                })
                this.log();
            } else {
                cb(1, "error_notpay")
            }
        }).catch(err => {
            console.log(err);
            var msg = ""
            if (err && err.error && err.error.name) {
                msg = err.error.name
            } else {
                msg = err
            }
            cb(1, msg)
        });
    }

    transferWithUsdt(amount, memo, cb) {
        amount = Number(amount).toFixed(4)

        this.scatterEOSJS.contract("tethertether", {
            accounts: [network]
        }).then(contract => {
            const transactionOptions = {
                authorization: [`${this.account.name}@${this.account.authority}`]
            };
            contract.transfer(this.account.name, this.contractAccount, amount + ' USDT', memo, transactionOptions).then(trx => {
                if (trx) {
                    console.log(`Transaction ID: ${trx.transaction_id}`);
                }

                cb(0, "", {
                    txid: trx ? trx.transaction_id : ""
                })
            }).catch(error => {
                console.log(error);
                cb(1, error && error.message ? error.message : error)
            });
        }).catch(err => {
            console.log(err);
            var msg = ""
            if (err && err.error && err.error.name) {
                msg = err.error.name
            } else {
                msg = err
            }
            cb(1, msg)
        });
    }

    transferWithEos(amount, memo, cb) {
        amount = Number(amount).toFixed(4)

        console.log(this.account.name, this.account.authority)
        const transactionOptions = {
            authorization: [`${this.account.name}@${this.account.authority}`]
        };

        this.scatterEOSJS.transfer(this.account.name, this.contractAccount, amount + ' EOS', memo, transactionOptions).then(trx => {
            // console.log("Transaction:", trx) // That's it!
            if (trx) {
                console.log(`Transaction ID: ${trx.transaction_id}`);
            }

            cb(0, "", {
                txid: trx ? trx.transaction_id : ""
            })
        }).catch(err => {
            console.log(err);
            var msg = ""
            if (err && err.error && err.error.name) {
                msg = err.error.name
            } else {
                msg = err
            }
            cb(1, msg)
        });
    }

    transferWithOnes(amount, memo, cb) {
        amount = Number(amount).toFixed(4)
        this.scatterEOSJS.contract("eosonestoken", {
            accounts: [network]
        }).then(contract => {
            const transactionOptions = {
                authorization: [`${this.account.name}@${this.account.authority}`]
            };
            contract.transfer(this.account.name, this.contractAccount, amount + ' ONES', memo, transactionOptions).then(trx => {
                if (trx) {
                    console.log(`Transaction ID: ${trx.transaction_id}`);
                }

                cb(0, "", {
                    txid: trx ? trx.transaction_id : ""
                })
            }).catch(error => {
                console.log(error);
                cb(1, error && error.message ? error.message : error)
            });
        }).catch(err => {
            console.log(err);
            var msg = ""
            if (err && err.error && err.error.name) {
                msg = err.error.name
            } else {
                msg = err
            }
            cb(1, msg)
        });



    }

    getScatterReadEosJs() {
        return this.scatterReadEosJs;
        var i = parseInt(Math.random() * 300);
        if (i % 3 == 0) {
            return this.scatterReadEosJs1;
        } else if (i % 3 == 1) {
            return this.scatterReadEosJs2;
        } else if (i % 3 == 2) {
            return this.scatterReadEosJs3;
        }
    }
    getBalance(address, symbol, cb) {
        if (!this.account) {
            return
        }

        this.getScatterReadEosJs().getCurrencyBalance(address, this.account.name, symbol).then(function (resp) {
            if (resp.length > 0) {
                var b = resp[0].split(" ")
                var balance = b[0]
                cb(balance)
            } else {
                cb("0.0000")
            }
        });
    }

    getEosBalance(cb) {
        if (!this.account) {
            return
        }
        this.getScatterReadEosJs().getCurrencyBalance('eosio.token', this.account.name, 'EOS').then(function (resp) {
            if (resp.length > 0) {
                var balance = Number(parseFloat(resp[0])).toFixed(4)
                cb(balance)
            } else {
                cb(0)
            }
        });
    }

    getOnesBalance(account, cb) {
        this.getScatterReadEosJs().getCurrencyBalance('eosonestoken', account, 'ONES').then(function (resp) {
            if (resp.length > 0) {
                var balance = Number(parseFloat(resp[0])).toFixed(4)
                cb(balance)
            } else {
                cb(0)
            }
        });
    }

    getCurrencyStats(address, symbol, cb) {
        // get_currency_stats:
        this.getScatterReadEosJs().getCurrencyStats(address, symbol.toUpperCase()).then(function (resp) {
            cb(resp)
        }).catch(err => {
            console.log("......", err)
            cb(null)
        });
    }

    getTableRows(options, cb) {
        this.getScatterReadEosJs().getTableRows(options).then(data => {
            cb(0, "", data)
        }).catch(e => {
            console.log(e)
            cb(1, e)
        });
    }


    sign(tx, cb) {
        EosApi.sign(tx).then(res => {
            console.log(tx)
            if (res.code == 0) {}
        })
    }

    transaction(actions, cb) {
        try {

            this.scatterEOSJS.transaction({
                actions: actions
            }).then(trx => {
                if (trx) {
                    console.log(`Transaction ID: ${trx.transaction_id}`);
                }
                this.log();

                cb(0, "", {
                    txid: trx ? trx.transaction_id : ""
                })
            }).catch(error => {
                console.log(error);
                this.log();

                var err = typeof (error) == "string" ? JSON.parse(error) : error
                var msg1 = err && err.message ? err.message : JSON.stringify(err)
                var msg = err && err.error && err.error.details ? err.error.details[0]["message"] : msg1;

                msg = msg.replace("assertion failure with message:", "");
                if (msg.indexOf("cpu") >= 0) {
                    msg = "CPU不足,请在EOS钱包抵押EOS获取CPU"
                } else if (msg.indexOf("net") >= 0) {
                    msg = "Net不足,请在EOS钱包抵押EOS获取Net"
                } else if (msg.indexOf("overdrawn") >= 0 || msg.indexOf("no balance") >= 0) {
                    msg = "余额不足"
                } else if (msg.indexOf("slippage") >= 0) {
                    msg = '超过滑点保护'
                } else if (msg.indexOf("ram") >= 0) {
                    msg = 'Ram不足,请在EOS钱包中购买RAM'
                }

                cb(1, msg)
            });

        } catch (err) {
            console.log("xxxxxxx ", err)
        }

    }
}

export default EosService;


