        <br />2、DDOS攻击。
        <br />3、自动化工具漏洞。
        <br />4、滥用第三方系统或服务造成的错误。
      </p>
      <p>
        <strong>漏洞评级与奖励标准</strong>
      </p>
      <p>
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column prop="level" label="等级(根据CVSSv3规范)"></el-table-column>
          <el-table-column prop="reward" label="奖励"></el-table-column>
        </el-table>
      </p>
      <p>
        <strong>漏洞提交</strong>
        <strong>规则</strong>
      </p>
      <p>1、发现的任何漏洞或错误必须仅通过以下 bounty@ones.game邮箱地址提交。</p>
      <p>2、在提交给bounty@ones.game电子邮件之前，不得披露给任何其他个人或组织。请在发现漏洞后的第一时间向bounty@ones.game提交。</p>
      <p>3、请提供有关该漏洞的尽可能详细的信息，包括：产生漏洞的条件，验证漏洞的具体步骤，漏洞的潜在影响。漏洞的详细报告会增加获得奖励的可能性，并且可能会增加奖励金额。</p>
      <p>4、提交报告后，请勿透露漏洞信息给任何人或机构。一旦漏洞公开，您将失去获奖资格。</p>
      <p>5、如多人报告相似漏洞，只奖励最先提交者。</p>
      <p>6、如您发现多个漏洞，我们将以其中风险等级最高的漏洞为标准为您提供奖励。</p>
