                <i class="el-icon-user-solid"></i>
              </div>
              <div class="item-label">{{this.$t("profileScene.account")}}</div>
              <div class="item-arrow">
                <i class="el-icon-arrow-right"></i>
              </div>
            </router-link>
          </li>
        </ol>

        <!-- <router-link :to="{name:'assetScene'}" style="color:rgb(101, 104, 108)">
          <li>
            <div class="item-icon">
              <i class="el-icon-s-finance"></i>
            </div>
            <div class="item-label">{{this.$t("profileScene.assets")}}</div>
            <div class="item-arrow">
              <i class="el-icon-arrow-right"></i>
            </div>
          </li>
        </router-link>-->

        <ol>
          <li>
            <router-link :to="{name:'shareScene'}" style="color:rgb(101, 104, 108)">
              <div class="item-icon">
                <i class="el-icon-share"></i>
              </div>
              <div class="item-label">{{this.$t("shareScene.title")}}</div>
              <div class="item-arrow">
                <!-- <el-badge :value="$t('gameScenePart.shareDesc')" class="item"></el-badge> -->
                <i class="el-icon-arrow-right"></i>
              </div>
            </router-link>
          </li>
          <!-- <li v-if="zone!='eos'"> -->
          <li>
            <router-link :to="{name:'vipScene'}" style="color:rgb(101, 104, 108)">
              <div class="item-icon">
                <i class="el-icon-trophy-1"></i>
              </div>
              <div class="item-label">VIP</div>
              <div class="item-arrow">
                <!-- <el-badge type="warning" :value="$t('vipScene.badgetips')" class="item"></el-badge> -->
                <i class="el-icon-arrow-right"></i>
              </div>
            </router-link>
          </li>
        </ol>

        <!-- <router-link :to="{name:'voteScene'}" style="color:rgb(101, 104, 108)">
          <li>
            <div class="item-icon">
              <i class="el-icon-magic-stick"></i>
            </div>
            <div class="item-label">{{$t("voteScene.title")}}</div>
            <div class="item-arrow">
              <el-badge :value="$t('voteScene.tips')" class="item"></el-badge>
              <i class="el-icon-arrow-right"></i>
            </div>
          </li>
        </router-link>-->
        <ol>
          <li>
            <router-link :to="{name:'onesScene'}" style="color:rgb(101, 104, 108)">
              <div class="item-icon">
                <i class="el-icon-s-marketing"></i>
              </div>
              <div class="item-label">{{this.$t("profileScene.dividend")}}</div>
              <div class="item-arrow">
                <i class="el-icon-arrow-right"></i>
              </div>
            </router-link>
          </li>
          <li v-if="zone!='eos'">
            <router-link :to="{name:'bonusScene'}" style="color:rgb(101, 104, 108)">
              <div class="item-icon">
                <i class="el-icon-present"></i>
              </div>
              <div class="item-label">{{this.$t("profileScene.bonus")}}</div>
              <div class="item-arrow">
                <i class="el-icon-arrow-right"></i>
              </div>
            </router-link>
          </li>
          <li v-if="zone!='eos'">
            <router-link :to="{name:'subscribeScene'}" style="color:rgb(101, 104, 108)">
              <div class="item-icon">
                <i class="el-icon-s-claim"></i>
              </div>
              <div class="item-label">{{$t("subscribe.subscribe")}}</div>
              <div class="item-arrow">
                <el-badge :value="$t('subscribe.subscribeDesc')" class="item"></el-badge>
                <i class="el-icon-arrow-right"></i>
              </div>
            </router-link>
          </li>
          <li v-if="false">
            <router-link :to="{name:'nodeScene'}" style="color:rgb(101, 104, 108)">
              <div class="item-icon">
                <i class="el-icon-s-opportunity"></i>
              </div>
              <div class="item-label">{{this.$t("profileScene.node")}}</div>
