<script>
import Swiper from 'swiper'
export default {
  data() {
    return {
      zone: this.$store.getters.zone,

      hasNews: this.$store.getters['notification/hasNews'],
      // 上传头像
      fileList: [{ url: 'https://cloud-image', isImage: true }],
      // 用户信息
      user: {
        img: require('../../assets/unlogin.png'),
        name: ''
      },
      islogin: this.$store.state.account.isLogin,

      cur: 0,
      windowScrollTop: 0,
      language: this.$t('message.chinese'),
      lang:
        typeof Storage && localStorage.localeLanguage
          ? localStorage.localeLanguage
          : 'zh',
      isClose: true,
      isApp: false,
      hash: this.$route.path,
      showQrcode: false
    }
  },
  watch: {},
  methods: {
    ptoLunBoFun(a, b) {
      console.log(a)
      // document.getElementsByClassName("business-imgs")[0].children[a].style =
      //   "display:block";
    },
    menupropFun(status) {
      if (window.innerWidth > 760) {
        return
      }
      if (status == 0 && !this.isApp) {
        return
      }
      this.isApp = true
      this.isClose = !this.isClose
      if (this.isClose == false) {
        document.getElementsByClassName('header-menu')[0].style =
          'display: flex;'
        document.getElementsByClassName('toggle')[0].className += ' active'
      } else {
        document.getElementsByClassName('header-menu')[0].style =
          'display: none;'
        document.getElementsByClassName('toggle')[0].className = 'toggle'
      }
    }
  },
  components: {
    Swiper
  }
}
</script>

<style scoped>
.persnalContainer {
  background-color: #f5f5f5;
}
a {
  text-decoration: none;
  color: #ffffff;
}
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.user-image {
  margin-top: 2rem;
  width: 100%;
  text-align: center;
}
.user-image .user-img {
  display: inline-block;
  width: 6rem;
  height: 6rem;
  border: solid 0.4rem #fff;
  border-radius: 100%;
  margin: 0 0 1rem;
  background-color: #fff;
}
.user-demonstration {
  font-size: 1.12rem;
}
.table-list ul ol {
  background-color: #fff;
  margin-bottom: 1rem;
}

.table-list ul ol li {
  border-bottom: 0.01rem solid #f1f1f6;
  padding: 5px 0;
}

.table-list ul ol li a {
  display: flex;
  position: relative;
}

.table-list ul li .item-icon {
  margin-left: 15px;
  color: rgb(127, 145, 179);
  font-size: 2rem;
  text-align: center;
}
.table-list ul li .item-label {
  color: rgb(36, 39, 44);
  font-size: 1.1rem;
  line-height: 2.2rem;
  margin: auto 0;
  margin-left: 15px;
}
.table-list ul li .item-arrow {
  color: rgb(101, 104, 108);
  font-size: 2rem;
  position: absolute;
  right: 4%;
  top: 50%;
  transform: translate(0, -50%);
}

.table-list ul li .item-arrow .router-link {
  color: rgb(101, 104, 108);
}

.user-demonstration {
  display: block;
  width: 100%;
  font-size: 1.1rem;
  text-align: center;
  margin-bottom: 0rem;
}
.logout {
  text-align: center;
  margin-top: 7rem;
}
</style>


