<template>
  <div class="play-container">
    <div class="loading-container" v-if="loading">
      <div class="loader">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
    <div class="play-goback" v-if="isTp" :style="goBackStyleObj" @click="goBack">
      <i class="el-icon-arrow-left" v-if="!goBackStyleObj.isLandscape"></i>
      <i class="el-icon-arrow-up" v-if="goBackStyleObj.isLandscape"></i>
    </div>
    <iframe :src="gameUrl" style="width:100%;height:100%;"></iframe>
  </div>
</template>

<script>
import Storage from '../../utils/storage'
import gameService from '../../api/game'
import TpUtil from 'tp-eosjs'

export default {
  data() {
    return {
      isTp: false,
      gameUrl: null,
      loading: true,
      isVip: false,
      gift: 0,
      islogin: this.$store.state.account.isLogin,

      positions: {
        default: { top: '10px', left: '10px' },
        landlord: { top: '10px', right: '10px', isLandscape: true },
        fishing: { bottom: '10px', right: '10px', isLandscape: true },
        douro: { top: '30px', left: '10px' },
        emperor: { top: '10px', right: '10px', isLandscape: true },
        shabake: { top: '30px', left: '10px' },
        shengshizhetian: { top: '10px', right: '30px', isLandscape: true }
      },
      goBackStyleObj: {}
    }
  },
  watch: {
    gameUrl(val) {
      console.log('url', val)
    }
  },

  mounted() {
    this.gift = this.$route.query.gift ? 1 : 0
    this.game_id = this.$route.params.game

    if (this.positions[this.game_id]) {
      this.goBackStyleObj = this.positions[this.game_id]
    } else {
      this.goBackStyleObj = this.positions['default']
    }

    this.playGame(this.game_id)
    var zone = this.$store.getters.zone
    if (zone == 'eos') {
      console.log(TpUtil.isConnected())
      if (TpUtil.isConnected()) {
        TpUtil.fullScreen({
          fullScreen: 1
        })
        this.isTp = true
      }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },

    move(e) {
      let odiv = e.target //获取目标元素

      //算出鼠标相对元素的位置
      let disX = e.clientX - odiv.offsetLeft
      let disY = e.clientY - odiv.offsetTop

      console.log(disX, disY)
      document.onmousemove = e => {
        // console.log(e)
        //鼠标按下并移动的事件
        //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
        let left = e.clientX - disX
        let top = e.clientY - disY

        //绑定元素位置到positionX和positionY上面
        this.positionX = top
        this.positionY = left

        console.log(e.clientX, disX, left)

        //移动当前元素
        odiv.style.left = left + 'px'
        odiv.style.top = top + 'px'
      }
      document.onmouseup = e => {
        document.onmousemove = null
        document.onmouseup = null
      }
    },

    isSafari() {
      var ua = navigator.userAgent.toLowerCase()
      if (
        ua.indexOf('tokenpocket') >= 0 ||
        ua.indexOf('meet') >= 0 ||
        ua.indexOf('dappbirds') >= 0
      ) {
        return false
      }
      if (
        ua.indexOf('applewebkit') > -1 &&
        ua.indexOf('mobile') > -1 &&
        ua.indexOf('safari') > -1 &&
        ua.indexOf('linux') === -1 &&
        ua.indexOf('android') === -1 &&
        ua.indexOf('chrome') === -1 &&
        ua.indexOf('ios') === -1 &&
        ua.indexOf('browser') === -1
      ) {
        return true
      } else {
        return false
      }
    },

    playGame(game_id) {
      if (this.islogin) {
        gameService
          .play(game_id)
          .then(res => {
            // console.log(res)
            var url = res.data.url
            var zone = this.$store.getters.zone
            if (zone == 'eos' && url.indexOf('play.ones.game') > 0) {
              url = url.replace('play.ones.game', 'eos.ones.game')
            }

            if (!this.gift && res.data.support_https && !this.isSafari()) {
              var lang = this.$route.query.lang
              if (lang && this.gameUrl.indexOf('play.ones.game') > 0) {
                url += '?lang=' + lang //todo
              }
              // if (this.gift) {
              //   url += '&sdw_activity=sdw_detail'
              // }
              // if (window.location.href.indexOf('http://') > 0) {
              //   url = url.rep
              // }
              this.gameUrl = url
              setTimeout(
                function() {
                  this.loading = false
                }.bind(this),
                2000
              )
            } else {
              if (this.gift) {
                url += '&sdw_activity=sdw_detail'
              }
              if (url.indexOf('third_protocol') > 0) {
                url = url.replace('third_protocol=1', '')
                url = url.replace('https://', 'http://')
              }
              window.history.replaceState(null, null, '/')
              window.location.href = url
            }
          })
          .catch(err => {
            console.log('err', err)
            this.$alert('游戏加载失败,请联系小七', '温馨提示', {
              confirmButtonText: '',
              callback: action => {
                // this.$message({
                //   type: 'info',
                //   message: `action: ${action}`
                // })
              }
            })

            this.$message({
              message: this.$t('游戏加载失败,请联系小七'),
              type: 'error',
              center: true
            })
          })
      } else {
        this.$router.push({
          name: 'loginScene',
          params: { url: '/game/' + game_id + '?action=play' }
        })
      }
    }
  }
}
</script>

<style scoped>
body {
  margin: 0px;
  padding: 0px;
  background-color: #3a3e4a;
}
.play-container {
  /* position: relative; */
}
.play-goback {
  position: fixed;
  z-index: 99999;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 50%;
  opacity: 0.7;
  cursor: pointer;
  box-sizing: border-box;
  user-select: none;

  width: 32px;
  height: 32px;
  font-size: 24px;
  color: #fff;
  background-color: #606266;
  border-color: #fff;
  white-space: nowrap;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  z-index: 100;
  border: none;
}

.loading-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.loader {
  display: inline-block;
  font-size: 0px;
  padding: 0px;
}

.loader span {
  vertical-align: middle;
  border-radius: 100%;
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 2px;
  -webkit-animation: loader 0.8s linear infinite alternate;
  animation: loader 0.8s linear infinite alternate;
}

.loader span:nth-child(1) {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
  /* background: rgba(35, 200, 182, 0.6); */
  background: rgba(65, 153, 247, 0.6);
}

.loader span:nth-child(2) {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
  /* background: rgba(35, 200, 182, 0.8); */
  background: rgba(65, 153, 247, 0.8);
}

.loader span:nth-child(3) {
  -webkit-animation-delay: -0.26666s;
  animation-delay: -0.26666s;
  /* background: rgba(35, 200, 182, 1); */
  background: rgba(65, 153, 247, 1);
}

.loader span:nth-child(4) {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
  /* background: rgba(35, 200, 182, 0.8); */
  background: rgba(65, 153, 247, 0.8);
}

.loader span:nth-child(5) {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
  /* background: rgba(35, 200, 182, 0.4); */
  background: rgba(65, 153, 247, 0.4);
}

@keyframes loader {
  from {
    transform: scale(0, 0);
  }

  to {
    transform: scale(1, 1);
  }
}

@-webkit-keyframes loader {
  from {
    -webkit-transform: scale(0, 0);
  }

  to {
    -webkit-transform: scale(1, 1);
  }
}
</style>


