<template>
  <div class="subscribeContainer" ref="subscribeContainer" >
    <div class="top-content">
      <div class="right-content">
        <h3 v-if="mySubscribe.income>0||mySubscribe.rank>100">
          {{$t("subscribe.myprofit")}}
          <span>{{mySubscribe.income}}</span> ONES
        </h3>
        <p v-if="mySubscribe.rank>100">
          {{$t("subscribe.short")}}
          <span>No.100</span>
          {{$t("subscribe.little")}}
        </p>
        <p>
          {{$t("subscribe.mysubscription")}}
          <span v-text="mySubscribe.amount"></span> ONES
        </p>
        <div class="to-sub" @click="openDialog('subscribe')">{{$t("subscribe.immediate")}}</div>
      </div>
      <div class="left-content">
        <!-- <img src="../../assets/flagsred.png" alt /> -->
        <div class="rank-num">
          <p v-if="mySubscribe.rank<=100" v-text="mySubscribe.rank"></p>
        </div>
        <div
          class="to-redeem"
          v-if="mySubscribe.rank>100 && mySubscribe.amount>0"
          style="display:none;"
        >
          <el-button
            type="info"
            round
            size="mini"
            @click="openDialog('redeem')"
          >{{$t("subscribe.redeem")}}</el-button>
        </div>
      </div>
    </div>

    <div class="background-container"></div>
    <div class="dividend-container" @click="subscribeTotalVisible = true">
      <p class="title" v-text="$t('subscribe.totalDividend')"></p>
      <p class="dividend-num" v-text="totalBonus +'ONES'"></p>
      <span class="dividend-date">
        <!-- <i class="el-icon-arrow-right"></i> -->
      </span>
    </div>

    <div class="subscribe-rank">
      <ul class="rank-list">
        <li>
          <div class="number"></div>
          <div class="uid">UID</div>
          <div class="amount">{{$t("subscribe.amount")}}(ONES)</div>
          <div class="income">{{$t("subscribe.profit")}}(ONES)</div>
        </li>
        <li v-for="(item,index) in subscribeRankList" :key="index">
          <div class="number">
            <p v-if="item.rank>3" v-text="item.rank"></p>
            <img
              :src="require('../../assets/no'+item.rank+'.png')"
              alt
              :class="'no'+ item.rank"
              v-if="item.rank<=3"
            />
          </div>
          <div class="uid" v-text="item.uid"></div>
          <div class="amount">
            <span v-text="item.amount"></span>
          </div>
          <div class="income">
            <span v-text="item.income"></span>
            <!-- <img src="../../assets/income.png" alt /> -->
          </div>
        </li>
      </ul>
    </div>

    <!-- 弹出框 -->
    <el-dialog :visible.sync="subscribeVisible">
      <!-- 赎回 -->
      <div class="reddem-dialog" v-if="dialogType=='redeem'">
        <div class="subscribe-text">
          {{$t("subscribe.confirmredeem")}}
          <span>{{mySubscribe.amount}}</span> ONES
        </div>
        <div class="subscribe-btns">
          <el-button type="info" plain @click="subscribeVisible = false">{{$t("subscribe.cancel")}}</el-button>
          <el-button
            type="primary"
            @click="confirmRedeem(mySubscribe.amount)"
          >{{$t("subscribe.confirm")}}</el-button>
        </div>
      </div>
      <!-- 认购 -->
      <div class="subscribe-dialog" v-if="dialogType=='subscribe'">
        <div class="subscribe-content">
          <el-input-number
            v-model="subscribesAmount"
            :min="100"
            :step="100"
            step-strictly
            class="input-number"
          ></el-input-number>
          <el-select v-model="type" class="input-select" placeholder="请选择">
            <el-option label="余额账户" value="balance"></el-option>
            <el-option label="活期抵押" value="stake"></el-option>
          </el-select>
        </div>
        <div class="subscribe-text">
          {{$t("subscribe.confirmsubscription")}}
          <span>{{subscribesAmount}}</span> ONES
        </div>
        <div class="subscribe-btns">
          <el-button type="info" plain @click="subscribeVisible = false">{{$t("subscribe.cancel")}}</el-button>
          <el-button
            type="primary"
            @click="confirmSubscribe(type,subscribesAmount)"
          >{{$t("subscribe.confirm")}}</el-button>
        </div>
      </div>
    </el-dialog>
    <!-- <el-dialog :visible.sync="subscribeTotalVisible" >
      <el-table :data="totalDividend.subscribeTotalList" style="width: 100%;" align="center">
        <el-table-column prop="date" :label="$t('subscribe.date')" align="center"></el-table-column>
        <el-table-column prop="volume" :label="$t('onesScene.Dividend')+'(ONES)'" align="center"></el-table-column>
      </el-table>
    </el-dialog>-->
  </div>
</template>

<script>
import gameService from '../../api/game'
export default {
  data() {
    return {
      game: 'fishing',
      subscribeVisible: false,
      subscribeTotalVisible: false,
      dialogType: null,
      mySubscribe: {},
      totalBonus: 0,
      subscribeRankList: [],
      subscribesAmount: 100,
      type: 'balance',
    }
  },
  methods: {
    openDialog(type) {
      this.dialogType = type
      this.subscribeVisible = true
    },
    confirmSubscribe(type, amount) {
      gameService.subscribe(this.game, type, amount).then(res => {
        if (res.code == 0) {
          this.subscribeVisible = false
          this.$message({
            message: '认购成功',
            type: 'success',
            center: true
          })
          this.getSubscribeDetail()
        } else {
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'error',
            center: true
          })
        }
      })
    },

    confirmRedeem(num) {
      gameService.unstake(num).then(res => {
        if (res.code == 0) {
          this.subscribeVisible = false
          this.$message({
            message: '赎回成功',
            type: 'success',
            center: true
          })
          this.getSubscribeDetail()
        } else {
          this.$message({
            message: '',
            type: 'error',
            center: true
          })
        }
      })
    },
    getSubscribeDetail() {
      // var res = {
      //   code: 0,
      //   msg: '',
      //   data: {
      //     ranks: [
      //       {
      //         uid: '10***67',
      //         amount: 20800,
      //         income: '435.4',
      //         update_time: 15899092865,
      //         rank: 1
      //       },
      //       {
      //         uid: '10***52',
      //         amount: 18700,
      //         income: '319.7',
      //         update_time: 12719395294,
      //         rank: 2
      //       },
      //       {
      //         uid: '10***25',
      //         amount: 13300,
      //         income: '99.4',
      //         update_time: 4769866418,
      //         rank: 3
      //       },
      //       {
      //         uid: '10***44',
      //         amount: 100,
      //         income: '0.0',
      //         update_time: 1589991171,
      //         rank: 30
      //       }
      //     ],
      //     userrank: {
      //       uid: 1000056,
      //       amount: 600,
      //       income: '9.7',
      //       update_time: 9538852210,
      //       rank: 19
      //     },
      //     totalBonus: '1721.79'
      //   }
      // }
      // this.subscribeRankList = res.data.ranks
      // this.mySubscribe = res.data.userrank
      // this.totalBonus = res.data.totalBonus
      gameService.prize(this.fishing).then(res => {
        if (res.code == 0) {
          this.subscribeRankList = res.data.ranks
          this.mySubscribe = res.data.userrank
          this.totalBonus = res.data.totalBonus
        }
      })
    }
  },
  mounted() {
    this.getSubscribeDetail()
  }
}
</script>

<style scoped>
.subscribeContainer {
  width: 100%;
  background-size: 100% 100%;
  overflow-y: auto;
}

.subscribeContainer::-webkit-scrollbar {
  width: 0 !important;
}
.background-container {
  margin-top: -2px;
  width: 100%;
  height: 30px;
  background-color: rgb(65, 153, 247);
}
.dividend-container {
  padding: 15px 3%;
  box-sizing: border-box;
  width: 95%;
  display: flex;
  margin: 0 auto;
  border-radius: 10px;
  margin-top: -20px;
  background-color: rgb(171, 212, 248);
  /* background-image: url(../../assets/subscribe.png); */
  background-size: 100% 100%;
}
.dividend-container > p {
  flex: 1;
}
.dividend-container p:nth-child(1) {
  color: #6b7077;
}
.dividend-container p:nth-child(2) {
  text-align: right;
  color: #e29e50;
  font-size: 1.1rem;
  font-weight: 800;
}
.dividend-container span {
  margin-left: 5px;
  font-size: 1rem;
  color: #6b7077;
}
.top-content {
  width: 100%;
  height: 155px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  background-color: rgb(65, 153, 247);
  /* padding-bottom: 15px; */
}
.top-content .right-content {
  /* flex: 5; */
  box-sizing: border-box;
  padding: 0 5%;
  color: #fff;
}
.top-content .left-content {
  flex: 4;
  /* background-color: rgba(255, 255, 255, 0.411); */
}
.top-content .left-content .rank-num {
  width: 90%;
  height: 125px;
  background-image: url(../../assets/flagsred.png);
  background-size: 100% 100%;
  margin-top: -7px;
  overflow: hidden;
}
.top-content .left-content .rank-num p {
  font-size: 2rem;
  text-align: center;
  margin-top: 70px;
  color: #fb6c19;
  font-weight: 800;
}
.top-content .left-content .to-redeem {
  width: 100%;
  box-sizing: border-box;
  padding: 0 20%;
  text-align: right;
  /* margin-top: 10px; */
  color: #fff;
  cursor: pointer;
}
.top-content .right-content h3 {
  font-size: 1.6rem;
  font-weight: 400;
  margin-bottom: 5px;
  /* white-space: nowrap; */
}
.top-content .right-content h3 span {
  font-size: 1.65rem;
  font-weight: 800;
  color: rgb(255, 255, 146);
  text-shadow: 0 0 4px #333;
}

.top-content .right-content .to-sub {
  width: 130px;
  height: 40px;
  background-image: url(../../assets/btn.png);
  background-size: 100% 100%;
  color: #fff;
  line-height: 35px;
  text-align: center;
  cursor: pointer;
  margin: 0 auto;
  margin-top: 10px;
}
.top-content .right-content p {
  font-size: 1.2rem;
  margin-top: 5px;
  white-space: nowrap;
}
.top-content .right-content p span {
  font-size: 1.35rem;
  font-weight: 800;
  color: rgb(250, 178, 61);
  text-shadow: 0 0 4px #333;
}
.subscribe-rank {
  width: 100%;
}
.subscribe-rank .list-nav {
  text-align: center;
}
.subscribe-rank .list-nav>div {
  font-size: 1.1rem !important;
  color: #475669 !important;
  font-weight: 800 !important;
 white-space: nowrap;
}
.subscribe-rank .rank-list {
  width: 100%;
  padding: 10px 2%;
  box-sizing: border-box;
}
.subscribe-rank .rank-list li {
  width: 100%;
  height: 60px;
  box-sizing: border-box;
  display: flex;
  line-height: 60px;
  border-bottom: 1px solid rgb(206, 206, 206);
}
.subscribe-rank .rank-list li .number {
  flex: 1;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 800;
  color: rgb(148, 147, 147);
}
.subscribe-rank .rank-list li .number img {
  margin-top: 15px;
  width: 30px;
}

.subscribe-rank .rank-list li .uid {
  flex: 2;
  font-size: 1.1rem;
  color: rgb(61, 61, 60);
  font-weight: 800;
}
.subscribe-rank .rank-list li .amount {
  flex: 3;
  font-size: 1.1rem;
  color: rgb(255, 143, 62);
  font-weight: 800;
  text-align: center;
}
.subscribe-rank .rank-list li .income {
  flex: 3;
  font-size: 1.1rem;
  position: relative;
  text-align: center;
}
.subscribe-rank .rank-list li .income span {
  color: rgb(252, 220, 118);
  font-weight: 800;
  text-shadow: 0 0 2px rgb(26, 25, 25);
}
.subscribe-rank .rank-list li .income img {
  width: 25px;
  height: 30px;
  position: absolute;
  top: 45%;
  transform: translate(0, -50%);
  right: 0;
}

/* 弹出框 */
.subscribe-content {
  text-align: center;
  display: flex;
  justify-content: center;
}

.subscribe-content .input-select {
  width: 120px;
}

.subscribe-text {
  text-align: center;
  line-height: 5rem;
  font-size: 1rem;
  white-space: nowrap;
  color: #475669;
}
.subscribe-btns {
  display: flex;
}
.subscribe-btns button {
  flex: 1;
}
/* 弹出框 */
</style>



