<template>
  <!--个人信息 -->
  <div class="accountSceneContainer" ref="accountSceneContainer">
    <div class="my-header" style="display:none;">
      <div class="my-avatar-container">
        <el-avatar shape="circle" fit="fill" :src="myheaderSrc"></el-avatar>
      </div>
    </div>
    <ul class="list">
      <li v-if="NickName">
        <div class="icon">
          <i class="el-icon-user-solid"></i>
        </div>
        <div class="text">
          <span class="first">{{this.$t("accountScene.nickname")}}</span>
          <span class="content">{{NickName}}</span>
        </div>
        <div class="btn">
          <!-- <el-button
            type="primary"
            icon="el-icon-edit"
            @click="OpenPopUp('NickName')"
            circle
            style="background-color:rgb(152, 167, 201);border-color:rgb(152, 167, 201);"
          ></el-button>-->
        </div>
      </li>
      <li>
        <div class="icon">
          <i class="el-icon-postcard"></i>
        </div>
        <div class="text">
          <span class="first">{{this.$t("accountScene.account")}} :</span>
          <span class="content">{{Uid}}</span>
        </div>
      </li>
      <li>
        <div class="icon">
          <i class="el-icon-mobile-phone"></i>
        </div>
        <div class="text">
          <span class="first">{{this.$t("loginPart.PhoneNumber")}}：</span>
          <span class="content">{{Mobile}}</span>
          <span class="content">
            <el-button
              v-if="!Mobile"
              type="primary"
              round
              @click="OpenPopUp('BindMobile')"
              size="mini"
              style="background-color:rgb(152, 167, 201);border-color:rgb(152, 167, 201);"
            >{{$t("accountScene.bindMobile")}}</el-button>
          </span>
        </div>
      </li>
      <li
        v-if="!(isJustalkUser||isHiCoinUser||isZTAccountUser||EosAccount||GxbAccount)||DragonExAccount"
      >
        <div class="icon">
          <img src="../../assets/dragonlogo.png" alt />
        </div>
        <div class="text">
          <span class="first">{{this.$t("accountScene.dragonex")}}：</span>
          <span class="content" v-if="DragonExAccount">{{DragonExAccount}}</span>
          <span class="content" v-if="!DragonExAccount&&!isJustalkUser&&!isHiCoinUser">
            <el-button
              type="primary"
              round
              size="mini"
              @click="bindDragonEx"
              style="background-color:rgb(152, 167, 201);border-color:rgb(152, 167, 201);"
            >{{this.$t("accountScene.bindDragonEx")}}</el-button>
          </span>
        </div>
      </li>

      <li v-if="isZTAccountUser">
        <div class="icon">
          <img src="../../assets/ztlogo.png" alt />
        </div>
        <div class="text">
          <span class="first">{{this.$t("accountScene.zt")}}：</span>
          <span class="content" v-if="ZTAccount">{{ZTAccount}}</span>
          <span class="content" v-if="!ZTAccount">
            <el-button
              type="primary"
              round
              size="mini"
              @click="bindZT"
              style="background-color:rgb(152, 167, 201);border-color:rgb(152, 167, 201);"
            >{{this.$t("accountScene.bindZT")}}</el-button>
          </span>
        </div>
      </li>

      <li v-if="EosAccount">
        <div class="icon">
          <img src="../../assets/eoslogo.svg" alt />
        </div>
        <div class="text">
          <span class="first">{{this.$t("accountScene.eos")}}：</span>
          <span class="content">{{EosAccount}}</span>
        </div>
      </li>

      <!-- <li v-if="GxbAccount">
        <div class="icon">
          <img src="../../assets/gxblogo.png" alt />
        </div>
        <div class="text">
          <span class="first">{{this.$t("accountScene.gxb")}}：</span>
          <span class="content" v-if="GxbAccount">{{GxbAccount}}</span>
        </div>
      </li>-->
    </ul>

    <!-- 退出登录 -->
    <div class="logoutBtn" v-if="!isJustalkUser">
      <el-button type="info" @click="logout">{{this.$t("accountScene.logout")}}</el-button>
    </div>

    <!-- 弹出层 -->
    <el-drawer
      class="PopUpContainer"
      :visible.sync="drawer"
      :with-header="false"
      :direction="decoration"
      :style="PopUpContainerObj"
      size="auto"
      modal-append-to-body="false"
    >
      <div class="content">
        <!-- 修改昵称-->
        <el-form
          ref="nameForm"
          v-if="PopUp_NickName"
          :model="nameForm"
          label-width="15%"
          label-position="left"
        >
          <el-form-item label="昵称">
            <el-input v-model="nameForm.name" placeholder="请输入昵称"></el-input>
          </el-form-item>
          <el-button
            type="primary"
            class="confirm-btn"
            @click="SET_NICKNAME(NewNickName)"
          >{{this.$t("accountScene.confirm")}}</el-button>
        </el-form>
        <!-- 绑定手机 -->
        <el-form
          ref="mobileForm"
          v-if="PopUp_BindMobile"
          :model="mobileForm"
          :rules="mobileFormRules"
          label-width="25%"
          label-position="left"
        >
          <el-form-item :label="this.$t('accountScene.bindMobile')" prop="mobile">
            <el-input
              v-model="mobileForm.mobile"
              :placeholder="this.$t('accountScene.mobilePlaceholder')"
              class="input-with-select"
            >
              <el-select
                v-model="mobileForm.area"
                placeholder="请选择国家/地区"
                slot="prepend"
                size="medium"
              >
                <el-option label="+86" value="+86"></el-option>
              </el-select>
            </el-input>
          </el-form-item>

          <el-form-item label="验证码" prop="vcode">
            <div style="display:flex;">
              <el-input v-model="mobileForm.vcode" placeholder="请输入验证码" style="flex:4"></el-input>
              <el-button
                type="primary"
                style="flex:1"
                @click="sendCode('bindmobile')"
                :disabled="smsdisabled"
                :loading="smsloading"
              >{{payCodeValue}}</el-button>
            </div>
          </el-form-item>
          <el-button
            type="primary"
            class="confirm-btn"
            @click="bindMobile()"
          >{{this.$t("accountScene.confirm")}}</el-button>
        </el-form>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import SmsService from '../../api/sms'
import Storage from '../../utils/storage'
import Util from '../../utils/util'

export default {
  data() {
    var validateMobile = (rule, value, callback) => {
      if (value == '')
        return callback(new Error(this.$t('loginPart.MobilePlaceHolder')))
      if (!/^1[3456789]\d{9}$/.test(value))
        return callback(new Error(this.$t('loginPart.MobileFormatError')))
      return callback()
    }

    var validateVcode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('loginPart.VerificationCodePlaceHolder')))
      } else if (!/\d/.test(value)) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError1')))
      } else if (value.length != 6) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError2')))
      } else {
        callback()
      }
    }
    return {
      myheaderSrc: require('../../assets/132.jpg'),
      drawer: false,
      decoration: 'btt',
      PopUpContainerObj: {
        width: ''
      },
      nameForm: {
        name: ''
      },
      mobileForm: {
        area: '+86',
        mobile: '',
        vcode: ''
      },
      payCode: false,
      payCodeValue: this.$t('loginPart.sendCode'),
      mobileFormRules: {
        mobile: [{ validator: validateMobile, trigger: 'blur' }],
        vcode: [{ validator: validateVcode, trigger: 'blur' }]
      },
      PopUpContainer: false,
      NickNameForm: {
        name: ''
      },
      PopUp_NickName: false,
      PopUp_BindMailbox: false,
      PopUp_BindMobile: false
    }
  },
  methods: {
    logout(e) {
      this.$store.dispatch('account/logout').then(() => {
        this.$message({
          message: this.$t('accountScene.logout'),
          type: 'info'
        })
        this.$router.push('/')
      })
      var source = this.$store.state.source
      if (
        !(
          source == 'hicoin' ||
          source == 'gxb' ||
          source == 'shandw' ||
          source == 'justalk'
        )
      ) {
        this.$store.dispatch('ASET_SOURCE', '')
      }

      if (this.$store.getters.zone == 'eos') {
        if (this.$store.getters.eosio) {
          this.$store.getters.eosio.logout()
        }
      }
    },

    bindDragonEx() {
      var dragonex_domain = Storage.get('dragonex_domain')
      if (!dragonex_domain) {
        dragonex_domain = process.env.DRAGONEX_OAUTH_DOMAIN
      }
      if (dragonex_domain == 'dragonex.co') {
        dragonex_domain = 'dragonex.in'
      }
      var url = 'https://' + dragonex_domain + '/oauth/login/'
      url += '?'

      var scopes = '1'
      var state = Util.randomString(16)
      var device = Util.getDevice()

      url +=
        'inviter=1327026&app_id=' +
        process.env.DRAGONEX_APP_ID +
        '&scopes=' +
        scopes +
        '&state=' +
        state +
        '&device=' +
        device +
        '&redirect_url=' +
        encodeURIComponent(process.env.DRAGONEX_BIND_URL)
      console.log(url)
      // return;
      this.$loading({
        lock: true,
        text: this.$t('message.redirecting'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      window.location.href = url
    },

    bindZT() {
      var host = 'www.ztb.com'
      var url =
        'https://' + process.env.ZT_OAUTH_DOMAIN + '/api/v1/oauth/authorize'
      url += '?'

      var scopes = '1'
      var state = Util.randomString(16)
      var device = Util.getDevice()

      var callbackUrl = process.env.ZT_BIND_URL

      callbackUrl = encodeURIComponent(callbackUrl)

      console.log(callbackUrl)
      url +=
        'client_id=' +
        process.env.ZT_APP_ID +
        '&response_type=code&' +
        '&scopes=all' +
        '&state=' +
        state +
        '&redirect_uri=' +
        callbackUrl

      console.log(url)
      // return;
      this.$loading({
        lock: true,
        text: this.$t('message.redirecting'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      window.location.href = url
    },

    doBindByDragonex() {
      var params = this.$route.query
      params.type = 'dragonex'

      this.$store.dispatch('account/binddragonex', params).then(res => {
        if (res.code == 0) {
          this.$message({
            message: this.$t('message.bind_success'),
            type: 'success',
            center: true
          })
          this.loading.close()
        } else {
          this.loading.close()
          this.$message.error({
            message: this.$t('message.' + res.msg),
            center: true
          })
        }
        var url = window.location.href
        if (url.indexOf('action=') > 0) {
          var new_url = url.replace('action=', 'noitca=')
          history.pushState(null, null, new_url)
        }
      })
    },

    doBindByZT() {
      var params = this.$route.query
      params.type = 'zt'

      this.$store.dispatch('account/bindzt', params).then(res => {
        if (res.code == 0) {
          this.$message({
            message: this.$t('message.bind_success'),
            type: 'success',
            center: true
          })
          this.loading.close()
        } else {
          this.loading.close()
          this.$message.error({
            message: this.$t('message.' + res.msg),
            center: true
          })
        }
        var url = window.location.href
        if (url.indexOf('action=') > 0) {
          var new_url = url.replace('action=', 'noitca=')
          history.pushState(null, null, new_url)
        }
      })
    },

    OpenPopUp(type) {
      this.drawer = true
      console.log(type)
      if (type == 'NickName') {
        this.PopUp_NickName = true
        this.PopUp_BindMailbox = false
        this.PopUp_BindMobile = false
      }
      if (type == 'BindMailbox') {
        this.PopUp_BindMailbox = true
      }
      if (type == 'BindMobile') {
        this.PopUp_BindMobile = true
        this.PopUp_NickName = false
        this.PopUp_BindMailbox = false
      }
    },
    SET_NICKNAME(val) {
      this.drawer = false
      this.PopUp_NickName = false

      this.$store.commit('SET_NICKNAME', val)
      this.$message({
        message: this.$t('accountScene.success_changeName'),
        type: 'success'
      })
    },
    bindMailbox() {
      // this.Mailbox = this.MailboxForm.mailbox;
      this.drawer = false
      this.PopUp_BindMailbox = false
      this.$message({
        message: this.$t('accountScene.success_changeMailbox'),
        type: 'success'
      })
    },
    bindMobile() {
      this.$refs.mobileForm.validate(valid => {
        if (valid) {
          this.drawer = false
          this.PopUp_BindMobile = false
          var params = {
            area: this.mobileForm.area,
            mobile: this.mobileForm.mobile,
            vcode: this.mobileForm.vcode
          }
          this.$store.dispatch('account/bindmobile', params).then(res => {
            if (res.code == 0) {
              this.$message({
                message: this.$t('accountScene.success_changeMobile'),
                type: 'success',
                center: true
              })
              this.Mobile =
                this.$store.state.account.user &&
                this.$store.state.account.user.mobile
            } else {
              this.$message({
                message: this.$t('message.' + res.msg),
                type: 'warning',
                center: true
              })
            }
          })
        } else {
          return false
        }
      })
    },

    sendCode(type) {
      this.smsloading = true

      this.$refs.mobileForm.validateField('mobile', valid => {
        if (valid == '') {
          this.smsloading = true
          this.sendSms(this.mobileForm.area, this.mobileForm.mobile)
        } else {
          console.log(valid)
          return false
        }
      })
    },
    sendSms(area, mobile) {
      SmsService.bind(area, mobile).then(res => {
        if (res.code == 0) {
          this.smsloading = false
          this.smsdisabled = true
          this.getSecond(60)
          this.$message({
            message: this.$t('message.send_success'),
            type: 'success',
            center: true
          })
        } else {
          this.smsloading = false
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'warning',
            center: true
          })
        }
        console.log(res)
      })
    },

    getSecond(wait) {
      let that = this
      let _wait = wait
      if (wait == 0) {
        this.smsdisabled = false
        this.payCodeValue = this.$t('loginPart.VerificationCode')
        wait = _wait
      } else {
        this.smsdisabled = true
        this.payCodeValue = wait + 's'
        wait--
        setTimeout(function() {
          that.getSecond(wait)
        }, 1000)
      }
    }
  },
  computed: {
    NickName() {
      return (
        this.$store.state.account.user &&
        this.$store.state.account.user.nickname
      )
    },
    Uid() {
      return (
        this.$store.state.account.user &&
        this.$store.state.account.user.ones_uid
      )
    },
    Mobile() {
      return (
        this.$store.state.account.user && this.$store.state.account.user.mobile
      )
    },
    DragonExAccount() {
      return (
        this.$store.state.account.user &&
        this.$store.state.account.user.dragonex_uid
      )
    },
    ZTAccount() {
      return (
        this.$store.state.account.user && this.$store.state.account.user.zt_uid
      )
    },
    isZTAccountUser() {
      return this.$store.state.account.user &&
        this.$store.state.account.user.source == 'zt'
        ? true
        : false
    },
    GxbAccount() {
      return (
        this.$store.state.account.user && this.$store.state.account.user.gxb_uid
      )
    },

    EosAccount() {
      return (
        this.$store.state.account.user &&
        this.$store.state.account.user.eos_address
      )
    },
    isJustalkUser() {
      return this.$store.state.account.user &&
        this.$store.state.account.user.source == 'justalk' &&
        !this.$store.state.account.user.dragonex_uid
        ? true
        : false
    },
    isHiCoinUser() {
      return this.$store.state.account.user &&
        this.$store.state.account.user.source == 'hicoin'
        ? true
        : false
    }
  },
  mounted() {
    this.PopUpContainerObj.width =
      this.$refs.accountSceneContainer.offsetParent.clientWidth + 'px'

    if (this.$route.query.action == 'dobind') {
      if (this.$route.query.source == 'dragonex') {
        this.loading = this.$loading({
          lock: true,
          text: this.$t('message.binding'),
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        this.doBindByDragonex()
      } else if (this.$route.query.source == 'zt') {
        this.loading = this.$loading({
          lock: true,
          text: this.$t('message.binding'),
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        this.doBindByZT()
      }
    }
  }
}
</script>

<style scoped>
.accountSceneContainer {
  position: relative;
  background-color: #f5f5f5;
}
.my-avatar-container {
  background: #f2f2f2;
  width: 6rem;
  height: 6rem;
  position: absolute;
  top: 2.25rem;
  left: 50%;
  border-radius: 50%;
  border: 2px solid #fff;
  transform: translateX(-50%);
  overflow: hidden;
}
.my-avatar-container /deep/ .el-avatar {
  width: 100%;
  height: 100%;
}
.my-header {
  width: 100%;
  height: 12rem;
  background: url(../../assets/headerbg.png) 0 0/100% auto;
  position: relative;
}

.PopUpContainer .content {
  width: 90%;
  margin: 2rem auto;
}
.list > li {
  height: 4rem;
  background-color: #fff;
  margin-bottom: 1rem;
  box-sizing: border-box;
  padding-left: 5%;
  padding-right: 8%;
}
.icon {
  width: 4rem;
  height: 4rem;
  line-height: 4rem;
  font-size: 1.8rem;
  width: 2rem;
  /* background-color: rgb(162, 141, 141); */
  color: rgb(116, 125, 144);
  /* color: rgb(152, 167, 201); */
  float: left;
}
.icon img {
  margin-top: 1rem;
  width: 2rem;
  height: 2rem;
  /* height: ; */
}
.text {
  float: left;
  height: 4rem;
  line-height: 4rem;
  margin-left: 1rem;
}
.text .first {
  font-size: 1.1rem;
  color: rgb(129, 132, 140);
  font-weight: 700;
}
.text .content {
  font-size: 1.13rem;
  color: rgb(235, 153, 45);
  font-weight: 700;
}
.btn {
  float: right;
  line-height: 4rem;
  height: 4rem;
}
.logoutBtn {
  text-align: center;
  margin-top: 2rem;
}

.confirm-btn {
  width: 100%;
}
.el-select /deep/ .el-input {
  width: 80px;
}
</style>


