<template>
  <div class="marketHistoryContainer">
    <div class="topNav">
      做市记录
      <!-- <i class="el-icon-s-order"></i> -->
    </div>
    <ul class="marketMaking-list">
      <li v-for="(item,index) in marketlogs" :key="index">
        <div class="topContent">
          <div class="rightCon">
            <div class="from">
              <img
                :src="'/static/img/tokens/' + item.in_token.address +'-' +item.in_token.symbol.toLowerCase() + '.png' "
                alt
              />
              <span>{{item.in_token.symbol}}</span>
            </div>
            <span class="and">+</span>
            <div class="to">
              <img
                :src="'/static/img/tokens/' + item.out_token.address +'-' +item.out_token.symbol.toLowerCase() + '.png' "
                alt
              />
              <span>{{item.out_token.symbol}}</span>
            </div>
            <p class="time">{{item.timestamp|timeFilter}}</p>
          </div>
          <div class="leftCon">{{item.type}}</div>
        </div>
        <div class="itemContent">
          <div class="rightCon">
            <div class="title">数量</div>
            <div class="contenttext">{{item.in_asset}}</div>
          </div>
          <div class="leftCon">
            <div class="title">数量</div>
            <div class="contenttext">{{item.out_asset}}</div>
          </div>
        </div>
        <div class="itemContent">
          <div class="rightCon">
            <div class="title">凭证数量</div>
            <div class="contenttext">{{item.liquidity_token}}</div>
          </div>
          <div class="leftCon">
            <div class="title">TxID</div>
            <div class="contenttext">
              <el-link
                :href="item.trx_url"
                :underline="false"
                type="primary"
              >{{item.trx_id|trxidFilter}}</el-link>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import EosApi from '../../api/eos'
import Util from '../../utils/util'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      url: 'https://bloks.io/transaction/',
      logsContract: 'onesgamelogs',
      marketlogs: [],
      logids: {}
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },
    account: function() {
      return this.$store.state.eos_account
    }
  },
  mounted() {
    this.getMarketLog()
  },
  methods: {
    getMarketLog() {
      if (!this.account) {
        return
      }
      EosApi.getMarketLog(this.account.name).then(res => {
        if (res.code == 0) {
          // var rows = []
          for (var i in res.data.rows) {
            var row = res.data.rows[i]

            row.in_token.symbol = row.in_token.symbol.split(',')[1]
            row.out_token.symbol = row.out_token.symbol.split(',')[1]
            row.in_token.key =
              row.in_token.address + row.in_token.symbol.toLowerCase()
            row.out_token.key =
              row.out_token.address + row.out_token.symbol.toLowerCase()

            row.trx_url = this.url + row.trx_id
            this.marketlogs.push(row)
            this.marketids[row.log_id] = row.log_id
          }
          //  = rows
        }
        this.getMarketLogFromChain()
      })
    },

    getMarketLogFromChain() {
      this.eosio.getTableRows(
        {
          code: this.logsContract,
          scope: this.logsContract,
          table: 'liquiditylog',
          limit: 1000,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0) {
            var rows = data.rows
            rows.sort(function(a, b) {
              return a.timestamp - b.timestamp > 0 ? -1 : 1
            })
            for (var i in rows) {
              var row = data.rows[i]

              var log_id = row.log_id

              if (
                row.account == this.eosio.account.name &&
                !this.logids[log_id]
              ) {
                row.in_token.symbol = row.in_token.symbol.split(',')[1]
                row.out_token.symbol = row.out_token.symbol.split(',')[1]
                row.in_token.key =
                  row.in_token.address + row.in_token.symbol.toLowerCase()
                row.out_token.key =
                  row.out_token.address + row.out_token.symbol.toLowerCase()

                row.trx_url = this.url + row.trx_id
                this.logids[log_id] = log_id
                this.marketlogs.push(row)
              }
            }
          }
        }.bind(this)
      )
    }
  },
  filters: {
    timeFilter(value) {
      return Util.formatDate(value, 'MM-dd hh:mm:ss')
    },
    trxidFilter(value) {
      return value.substr(0, 12) + '...'
    },
    priceFilter(value) {
      return Number(value).toFixed(6)
    }
  }
}
</script>

<style scoped>
.topNav {
  padding: 10px 0;
  text-align: center;
  font-size: 19px;
  font-weight: 800;
  background-color: #060721;
  border-bottom: 1px solid rgb(221, 223, 231);
  color: #fff;
}
.topNav .el-icon-s-order {
  float: right;
  font-size: 27px;
  color: #fff;
}
.marketMaking-list {
  background-color:  #060721;
  padding: 0 15px;
}

.marketMaking-list > li {
  padding: 10px 0;
  border-bottom: 1px solid #fff;
}
.marketMaking-list > li:last-child {
  border: none;
}
.marketMaking-list > li .topContent {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.marketMaking-list > li .topContent .rightCon {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.marketMaking-list > li .topContent .rightCon .from {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 800;
  font-size: 14px;
  color:#fff;
}
.marketMaking-list > li .topContent .rightCon .to {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 800;
  font-size: 14px;
  color:#fff;
}
.marketMaking-list > li .topContent .rightCon .and {
  font-size: 1.3rem;
  font-weight: 800;
  margin-left: 3px;
  margin-right: 3px;
  color: #fff;
}
.marketMaking-list > li .topContent .rightCon img {
  width: 25px;
  height: 25px;
  margin-right: 4px;
}
.marketMaking-list > li .topContent .leftCon {
  line-height: 25px;
}
.marketMaking-list > li .itemContent {
  display: flex;
  /* justify-content: space-between; */
  font-size: 1rem;
  line-height: 1.85rem;
}
.marketMaking-list > li .itemContent .title {
  color: rgb(255, 255, 255);
  font-size: 13px;
}
.marketMaking-list > li .itemContent .contenttext {
  color: rgb(255, 255, 255);
  font-size: 15px;
}
.marketMaking-list > li .itemContent .rightCon {
  color: rgb(177, 176, 176);
  flex: 1;
}
.marketMaking-list > li .itemContent .leftCon {
  color: rgb(221, 216, 216);
  flex: 1;
}

.marketMaking-list > li .rightCon .time {
  font-size: 12px;
  color: rgb(151, 151, 151);
  margin-left: 10px;
}
</style>


