                    style="flex:1"
                  ></el-input>
                </div>
              </el-form-item>
              <el-form-item :label="$t('loginPart.LoginPassword')" prop="password">
                <el-input
                  v-model="mobileForm.password"
                  :placeholder="$t('loginPart.PasswordPlaceHolder')"
                  show-password
                ></el-input>
              </el-form-item>
              <!-- <el-form-item> -->
              <!-- </el-form-item> -->
            </el-form>
            <el-button
              type="primary"
              class="loginbtn"
              @click="login('mobileForm')"
            >{{$t("loginPart.Login")}}</el-button>
          </div>
          <div v-if="loginType =='Sms'">
            <!-- 验证 -->
            <el-form
              ref="smsForm"
              :model="smsForm"
              :rules="smsRules"
              label-width="6.5rem"
              label-position="left"
            >
              <el-form-item :label="$t('loginPart.PhoneNumber')" prop="mobile">
                <div style="display: flex; width: 100%; height: 40px;">
                  <el-select
                    v-model="mobileForm.area"
                    placeholder="请选择国家/地区"
                    :popper-append-to-body="false"
                    class="selectOption"
                    style="flex:0.5"
                  >
                    <el-option label="+86" value="+86"></el-option>
                  </el-select>
                  <el-input
                    v-model="smsForm.mobile"
                    :placeholder="$t('loginPart.MobilePlaceHolder')"
                    style="flex:1"
                  ></el-input>
                </div>
              </el-form-item>

              <el-form-item :label="$t('loginPart.VerificationCode')" prop="vcode">
                <el-input v-model="smsForm.vcode" style="width:60%"></el-input>
                <el-button
                  style="width:35%; margin-left:1.5%; padding:12px 0;"
                  type="primary"
                  plain
                  @click="sendCode"
                  :loading="smsloading"
                  :disabled="smsdisabled"
                >{{VerificationCode}}</el-button>
              </el-form-item>
              <!-- <el-form-item> -->
              <!-- </el-form-item> -->
            </el-form>
            <el-button
              type="primary"
              class="loginbtn"
              @click="login('smsForm')"
            >{{$t("loginPart.Login")}}</el-button>
          </div>
        </div>

        <div class="forgetCode">
          <span>
            <router-link :to="{name:'registerScene'}">{{$t("loginPart.register")}}</router-link>
          </span>
          <span
            v-if="loginType =='Mobile'"
            @click="changeLoginType('Sms')"
          >{{$t("loginPart.VerificationCodeLogin")}}</span>
          <span
            v-if="loginType =='Sms'"
            @click="changeLoginType('Mobile')"
          >{{$t("loginPart.PasswordLogin")}}</span>
        </div>
      </div>

      <!-- 选择类型 -->
      <div class="login-type" v-if="zone != 'eos' && zone != 'pandora'">
        <div class="title-text">
          <ul>
            <li></li>
            <li>{{$t("loginPart.ThirdPartyLogin")}}</li>
            <li></li>
          </ul>
        </div>
        <div class="type-list">
          <div
            class="authorization-btn"
            v-for="(item,index) in authorizeList"
            :key="index"
            v-if="authorizeList.length > 1 &&(!item.zone||item.zone == zone)"
          >
            <div
              class="btn"
              :style="{backgroundColor:item.themeColor}"
              @click="toAuthorizeLogin(item.type)"
            >
              <img :src="item.img" alt />
            </div>
            <span v-text="$t('loginPart.' + item.type)"></span>
          </div>

          <div
            class="single-btn"
            v-for="(item,index) in authorizeList"
            :key="index"
            @click="toAuthorizeLogin(item.type)"
            :style="{backgroundColor:item.bgColor,color:item.textColor,borderColor:item.borderColor}"
            v-if="authorizeList.length == 1"
          >
            <img :src="item.img" alt />
            <span>&nbsp;{{$t("loginPart." + item.type)}}</span>
          </div>
        </div>
      </div>

      <!-- 选择类型 -->
      <div class="login-type" v-if="zone == 'eos'">
        <div class="title-text" v-if="!isMobile">
          <ul>
            <li></li>
            <li>{{$t("loginPart.DestopWalletLogin")}}</li>
            <li></li>
          </ul>
        </div>
        <div class="eos-type-list">
          <div
            class="single-btn"
            v-for="(item,index) in authorizeList"
            :key="index"
            @click="toAuthorizeLogin(item.type)"
            :style="{backgroundColor:item.bgColor,color:item.textColor,borderColor:item.borderColor}"
          >
            <!-- <img :src="item.img" alt /> -->
            <span v-if="!isMobile">{{$t("loginPart.Login")}}</span>
            <span v-if="isMobile">{{$t("loginPart.EOSLogin")}}</span>
          </div>

          <div class="eos-wallet-type">
            <div
              v-if="!isMobile"
              class="authorization-btn eos-wallet-btns"
              v-for="(item,index) in eosDesktopWallet"
              :key="index"
            >
              <el-popover placement="bottom-start" width="60" v-model="visible">
                <el-link
                  type="primary"
                  :underline="false"
                  :href="item.url"
                  target="_blank"
                  icon="el-icon-download"
                >{{$t("loginPart.download")}}{{$t('loginPart.' + item.name)}}</el-link>
                <div class="btn" slot="reference">
                  <img :src="item.img" alt />
                </div>
              </el-popover>
              <span v-text="$t('loginPart.' + item.name)"></span>
            </div>

            <div
              v-if="isMobile"
              class="authorization-btn eos-wallet-btns"
              v-for="(item,index) in eosMobileWallets"
              :key="index"
            >
              <el-popover placement="bottom-start" width="60" v-model="visible">
                <el-link
                  type="primary"
                  :underline="false"
                  :href="item.url"
                  target="_blank"
                  icon="el-icon-download"
                >{{$t("loginPart.download")}}{{$t('loginPart.' + item.name)}}</el-link>
                <div class="btn" slot="reference">
                  <img :src="item.img" alt />
                </div>
              </el-popover>
              <span v-text="$t('loginPart.' + item.name)"></span>
            </div>
          </div>

          <div v-if="!isMobile" class="eos-wallet-tips">{{$t("loginPart.EOSDestopWalletTips")}}</div>
          <div
            v-if="isMobile"
            class="eos-wallet-tips"
          >{{$t("loginPart.EOSMobileWalletTips",{name:$t("loginPart."+this.eosMobileWallet)})}}</div>
        </div>
      </div>

      <el-drawer
        class="PopUpContainer"
        :visible.sync="drawer"
        :with-header="false"
        direction="btt"
        :style="PopUpContainerObj"
        :wrapperClosable="false"
        :close-on-press-escape="false"
        size="auto"
        :modal-append-to-body="false"
      >
        <el-card class="login-oauth-card">
          <div slot="header" class="clearfix">
            <img src="../../assets/token/ones.png" />
            <span>OnesGame</span>
            <span>{{$t("loginPart.application")}}</span>
          </div>
          <div class="login-oauth-card-tips">{{$t("loginPart.justalkOauthTips")}}</div>

          <div class="login-oauth-card-user">
            <div class="nickname">
              <span>{{$t("loginPart.nickname")}}:</span>
              <span>{{justalkNickname}}</span>
            </div>
            <div class="info">
              <span>{{$t("loginPart.PhoneNumber")}}:</span>
              <span>{{justalkMobile}}</span>
            </div>
          </div>
          <el-row class="oauth-action" :gutter="20">
            <el-col :span="12">
              <el-button style="width:100%;" @click="cancelOauth">{{$t("loginPart.cancel")}}</el-button>
            </el-col>
            <el-col :span="12">
              <el-button
                type="success"
                style="width:100%;"
                @click="allowOauth"
              >{{$t("loginPart.allow")}}</el-button>
            </el-col>
          </el-row>
        </el-card>
      </el-drawer>
    </div>
  </div>
</template>

<script>
import SmsService from '../../api/sms'
import Util from '../../utils/util'
import Storage from '../../utils/storage'
import EosService from '../../utils/eosservice'

export default {
  data() {
    // 校验手机号
    var validateMobile = (rule, value, callback) => {
      if (value === '')
        return callback(new Error(this.$t('loginPart.MobilePlaceHolder')))

      if (!/^1[3456789]\d{9}$/.test(value))
        return callback(new Error(this.$t('loginPart.MobileFormatError')))
      return callback()
    }

    var validatePassWord = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('loginPart.PasswordPlaceHolder')))
      } else if (value.length < 8) {
        callback(new Error(this.$t('loginPart.PassWordFormatError')))
      } else {
        callback()
      }
    }

    var validateVcode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('loginPart.VerificationCodePlaceHolder')))
      } else if (!/\d/.test(value)) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError1')))
      } else if (value.length != 6) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError2')))
      } else {
        callback()
      }
    }

    return {
      isMobile:
        navigator.userAgent.match(/AppleWebKit.*Mobile.*/) ||
        !!navigator.userAgent.match(/iOS38*/) ||
        navigator.userAgent.match(/TokenPocket/) ||
        navigator.userAgent.match(/MEET/)
          ? true
          : false,
      zone: '',
      eosSources: {
        tokenpocket: 1,
        meetone: 1,
        dappreview: 1,
        dappbirds: 1,
        dappx: 1,
        dappradar: 1
      },
      eosio: null,
      authorizeList: [
        {
          type: 'dragonex',
          img: require('../../assets/dragonlogo.png'),
          bgColor: 'rgba(255, 255, 255,.32)',
          borderColor: 'rgb(212, 139, 55)',
          textColor: 'rgb(212, 139, 55)',
          themeColor: 'rgb(183, 120, 27)'
        },
        {
          type: 'zt',
          img: require('../../assets/ztlogo.png'),
          bgColor: 'rgba(255, 255, 255,.32)',
          borderColor: '#00C2CC',
          textColor: '#00C2CC',
          themeColor: '#00C2CC'
        },
        {
          type: 'justalk',
          img: require('../../assets/app/just.png'),
          bgColor: 'rgba(255, 255, 255,.32)',
          borderColor: '#00C2CC',
          textColor: '#00C2CC',
          themeColor: '#00C2CC'
        },
        {
          type: 'hicoin',
          img: require('../../assets/app/hicoin.png'),
          bgColor: '#0F377E',
          borderColor: '#fff',
          textColor: '#fff',
          themeColor: '#0F377E'
        },
        {
          type: 'bqex',
          img: require('../../assets/app/bqb.png'),
          bgColor: '#fff',
          // method: 'redirectBqex',
          borderColor: '#263141',
          textColor: '#000',
          themeColor: '#5E8DFD'
        },
        {
          type: 'gxb',
          img: require('../../assets/app/gxb.png'),
          bgColor: '#5E8DFD',
          method: 'redirectGxb',
          borderColor: '#fff',
          textColor: '#fff',
          themeColor: '#5E8DFD'
        },
        {
          type: 'eos',
          img: require('../../assets/token/eos.png'),
          bgColor: '#fff',
          borderColor: '#4199f7',
          textColor: '#4199f7',
          themeColor: '#5E8DFD',
          zone: 'eos'
        }
      ],
      eosDesktopWallet: [
        {
          name: 'TokenPocket',
          img: require('../../assets/app/tp.png'),
          url: 'https://www.tokenpocket.pro/en/download/pc'
        },
        {
          name: 'Scatter',
          img: require('../../assets/app/scatter.png'),
          url: 'https://get-scatter.com/'
        }
      ],
      eosMobileWallets: [
        {
          name: 'TokenPocket',
          img: require('../../assets/app/tp.png'),
          url: 'https://www.tokenpocket.pro'
        },
        {
          name: 'MeetOne',
          img: require('../../assets/app/meetone.png'),
          url: 'https://meet.one'
        },
        {
          name: 'Math',
          img: require('../../assets/app/math.png'),
          url: 'https://mathwallet.org/'
        }
      ],
      eosMobileWallet: 'TokenPocket',
      isShow: true,
      drawer: false,
      source: '',
      justalkNickname: '',
      justalkMobile: '',
      justalkUid: '',

      PopUpContainerObj: {
        width: ''
      },
      referer: '',
      dragonexOauthDomain: '',
      loginType: 'Mobile',
      smsloading: false,
      smsdisabled: false,

      params: null,
      loading: null,
      // 密码
      mobileForm: {
        type: 'mobile',
        area: '+86',
        mobile: '',
        password: ''
      },
      mobileRules: {
        mobile: [{ validator: validateMobile, trigger: 'blur' }],
        password: [{ validator: validatePassWord, trigger: 'blur' }]
      },
      // 验证码
      VerificationCode: this.$t('loginPart.sendCode'),
      smsForm: {
        type: 'sms',
        area: '+86',
        mobile: '',
        vcode: ''
      },
      smsRules: {
        mobile: [{ validator: validateMobile, trigger: 'blur' }],
        vcode: [{ validator: validateVcode, trigger: 'blur' }]
      }
    }
  },

  beforeMount: function() {
    var source = this.$store.getters.source //  Storage.get('ones_source')
    if (!source) {
      source = this.$route.query.source
    }
    var zone = this.$store.getters.zone

    this.zone = zone

    this.source = source
    if (this.source || zone == 'eos') {
      // 第三方登录
      var _source = this.source
      _source = this.eosSources[this.source] ? 'eos' : source
      if (zone == 'eos') {
        _source = 'eos'
      }

      for (var i in this.authorizeList) {
        if (this.authorizeList[i].type == _source) {
          var _authorizeList = []
          _authorizeList.push(this.authorizeList[i])
          this.authorizeList = _authorizeList
          break
        }
      }
    }

    if (zone == 'eos' && source) {
      var eosWallets = this.eosMobileWallets

      var eosFlag = false
      for (var i in eosWallets) {
        // console.log(source, eosWallets)
        if (eosWallets[i]['name'].toLowerCase() == source.toLowerCase()) {
          eosFlag = true
          this.eosMobileWallets = [eosWallets[i]]
          this.eosMobileWallet = eosWallets[i]['name']
          break
        }
      }
    }
  },
  mounted: function() {
    this.eosio = this.$store.getters.eosio

    this.PopUpContainerObj.width =
      this.$refs.loginContainer.offsetParent.clientWidth + 'px'

    var dragonex_domain = Storage.get('dragonex_domain')
    if (dragonex_domain) {
      this.dragonexOauthDomain = dragonex_domain
    } else {
      this.dragonexOauthDomain = process.env.DRAGONEX_OAUTH_DOMAIN
    }
    if (this.dragonexOauthDomain == 'dragonex.co') {
      this.dragonexOauthDomain = 'dragonex.in'
    }

    if (this.$route.params.url) {
      this.referer = this.$route.params.url
    } else if (this.$route.query.url) {
      this.referer = this.$route.query.url
    }

    if (this.$route.query.action == 'dologin') {
      if (this.$route.query.source == 'dragonex') {
        this.loading = this.$loading({
          lock: true,
          text: this.$t('message.logining'),
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        this.loginByDragonex()
      } else if (this.$route.query.source == 'gxb') {
        this.loading = this.$loading({
          lock: true,
          text: this.$t('message.logining'),
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        this.loginByGxb()
      } else if (this.$route.query.source == 'hicoin') {
        this.loading = this.$loading({
          lock: true,
          text: this.$t('message.logining'),
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        this.loginByHiCoin()
      } else if (this.$route.query.source == 'bqex') {
        this.loading = this.$loading({
          lock: true,
          text: this.$t('message.logining'),
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        this.loginByBqex()
      } else if (this.$route.query.source == 'zt') {
        this.loading = this.$loading({
          lock: true,
          text: this.$t('message.logining'),
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        this.loginByZT()
      }
    } else if (this.$route.query.action == 'dologingxb') {
      this.loading = this.$loading({
        lock: true,
        text: this.$t('message.logining'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.loginByGxb()
    } else {
      var agent = navigator.userAgent.toLowerCase()
      console.log(agent)
      if (this.zone == 'ones') {
        if (this.source == 'justalk') {
          this.isShow = false
          //&& agent.indexOf('justalk') > 0
          var justalk = Storage.get('ones_justalk')
          var mobile = justalk && justalk.mobile
          if (mobile) {
            mobile = mobile.slice(mobile.indexOf(' ') + 1)
            if (mobile.indexOf('+') == -1) {
              mobile = '+' + mobile
            }
            this.justalkMobile = mobile
            this.justalkNickname = justalk.nickname
            //this.drawer = true
            this.justalkUid = justalk.justalk_uid

            this.allowOauth()
          }
        } else if (this.source == 'hicoin') {
          this.isShow = false

          if (this.$route.query.source != 'hicoin') {
            this.authorizeHicoin()
          }
        } else if (this.source == 'gxb') {
          this.isShow = false
          this.redirectGxb()
        }
      } else if (this.zone == 'eos') {
        this.isShow = false
        if (this.isMobile) {
          this.redirectEOS()
        }
      } else if (this.zone == 'pandron') {
        this.isShow = false
      }
    }
  },

  beforeDestroy() {
    this.closeLoading()
  },

  methods: {
    // 登录
    login(formName) {
      console.log(formName)
      this.$refs[formName].validate(valid => {
        if (valid) {
          var params = {
            type: this[formName].type,
            area: this[formName].area,
            mobile: this[formName].mobile,
            password: this[formName].password,
            vcode: this[formName].vcode
          }

          this.$store.dispatch('account/login', params).then(res => {
            if (res.code == 0) {
              this.$message({
                message: this.$t('message.login_success'),
                type: 'success',
                center: true
              })
              var url = this.referer
              if (url) {
                window.location.href = url
              } else {
                if (this.zone == 'pandora') {
                  window.history.replaceState(null, null, '/')
                  window.location.href = '/'
                } else {
                  this.$router.push('/profile')
                }
              }
            } else {
              this.$message.error({
                message: this.$t('message.' + res.msg),
                center: true
              })
            }
          })
        } else {
          return false
        }
      })
    },

    // 统一 授权登录按钮
    toAuthorizeLogin(type) {
      switch (type) {
        case 'dragonex':
          console.log('龙网登录')
          this.redirectDragonex()
          break
        case 'zt':
          console.log('ZT登录')
          this.redirectZT()
          break
        case 'bqex':
          // if (this.authorizeList.length > 1) {
          this.$alert(
            'Ones已入住如币权交易所bqex.org, 请在BQEx App中登录或者通过手机号登录',
            '温馨提示',
            {
              confirmButtonText: '确定'
            }
          )
          // }
          break
        case 'justalk':
          console.log('just登录')
          if (this.authorizeList.length > 1) {
            this.$alert(
              'Ones已入住如Justalk.io, 请在Justalk App中登录',
              '温馨提示',
              {
                confirmButtonText: '确定'
              }
            )
          } else {
            this.authorizeJustalk()
          }
          break
        case 'hicoin':
          if (this.authorizeList.length > 1) {
            this.$alert(
              'ONES已入住HiCoin钱包, 请在HiCoin App中登录',
              '温馨提示',
              {
                confirmButtonText: '确定'
              }
            )
          } else {
            this.authorizeHicoin()
          }
          // this.authorizeHicoin()
          console.log('hicoin登录')
          break
        case 'gxb':
          console.log('gxb登录')
          if (this.authorizeList.length > 1) {
            this.$alert(
              'ONES已入住公信宝布洛克城APP, 请在布洛克城 App中登录',
              '温馨提示',
              {
                confirmButtonText: '确定'
              }
            )
          } else {
            this.redirectGxb()
          }
          break
        case 'eos':
          this.redirectEOS()
          break
      }
    },

    goBack() {
      if (this.zone == 'pandora') {
        this.$router.go(-1)
      } else {
        this.$router.push({
          name: 'gameScene'
        })
      }
      console.log('logout')
    },

    closeLoading() {
      if (this.loading) {
        this.loading.close()
      }
    },

    changeLoginType(ev) {
      console.log(ev)
      this.loginType = ev
    },

    routerChange(name) {
      console.log(name)
      //  路由跳转
      this.$router.push({
        name: 'name'
      })
    },

    // 授权登录 重定向

    // 龙网
    redirectDragonex() {
      var _url = this.referer
      console.log(_url)
      var url = 'https://' + this.dragonexOauthDomain + '/oauth/login/'
      url += '?'

      var scopes = '1'
      var state = Util.randomString(16)
      var device = Util.getDevice()

      url +=
        'inviter=1327026&app_id=' +
        process.env.DRAGONEX_APP_ID +
        '&scopes=' +
        scopes +
        '&state=' +
        state +
        '&device=' +
        device +
        '&redirect_url=' +
        encodeURIComponent(process.env.DRAGONEX_CALLBACK_URL + '&url=' + _url)
      console.log(url)
      // return;
      this.loading = this.$loading({
        lock: true,
        text: this.$t('message.redirecting'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      window.location.href = url
    },

    loginByDragonex() {
      var params = this.$route.query
      params.type = 'dragonex'

      this.$store.dispatch('account/login', params).then(res => {
        if (res.code == 0) {
          // this.closeLoading();
          this.$message({
            message: this.$t('message.login_success'),
            type: 'success',
            center: true
          })

          var url = this.referer

          if (url) {
            window.location.href = url
          } else {
            this.closeLoading()
            if (this.zone == 'pandora') {
              window.history.replaceState(null, null, '/')
              window.location.href = '/'
            } else {
              this.$router.push('/profile')
            }
          }
        } else {
          this.closeLoading()
          this.$message.error({
            message: this.$t('message.' + res.msg),
            center: true
          })
        }
        var url = window.location.href
        if (url.indexOf('action=') > 0) {
          var new_url = url.replace('action=', 'noitca=')
          history.pushState(null, null, new_url)
        }
      })
    },

    //zt
    redirectZT() {
      var _url = this.referer
      console.log(_url)
      var url =
        'https://' + process.env.ZT_OAUTH_DOMAIN + '/api/v1/oauth/authorize'
      url += '?'

      var scopes = '1'
      var state = Util.randomString(16)
      var device = Util.getDevice()

      var callbackUrl = process.env.ZT_CALLBACK_URL
      if (_url) {
        callbackUrl += '&url=' + _url
      }
      callbackUrl = encodeURIComponent(callbackUrl)

      console.log(callbackUrl)
      url +=
        'client_id=' +
        process.env.ZT_APP_ID +
        '&response_type=code&' +
        '&scopes=all' +
        '&state=' +
        state +
        '&redirect_uri=' +
        callbackUrl
      // console.log(url)
      // return;
      this.loading = this.$loading({
        lock: true,
        text: this.$t('message.redirecting'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      window.location.href = url
    },

    loginByZT() {
      var params = this.$route.query
      params.type = 'zt'

      this.$store.dispatch('account/login', params).then(res => {
        if (res.code == 0) {
          // this.closeLoading();
          this.$message({
            message: this.$t('message.login_success'),
            type: 'success',
            center: true
          })

          var url = this.referer

          if (url) {
            window.location.href = url
          } else {
            this.closeLoading()
            if (this.zone == 'pandora') {
              window.history.replaceState(null, null, '/')
              window.location.href = '/'
            } else {
              this.$router.push('/profile')
            }
          }
        } else {
          this.closeLoading()
          this.$message.error({
            message: this.$t('message.' + res.msg),
            center: true
          })
        }
        var url = window.location.href
        if (url.indexOf('action=') > 0) {
          var new_url = url.replace('action=', 'noitca=')
          history.pushState(null, null, new_url)
        }
      })
    },

    // HiCoin
    loginByHiCoin() {
      var params = this.$route.query
      params.type = 'hicoin'

      this.$store.dispatch('account/login', params).then(res => {
        if (res.code == 0) {
          // this.closeLoading();
          this.$message({
            message: this.$t('message.login_success'),
            type: 'success',
            center: true
          })

          var url = this.referer

          if (url) {
            window.location.href = url
          } else {
            this.closeLoading()
            if (this.zone == 'pandora') {
              window.history.replaceState(null, null, '/')
              window.location.href = '/'
            } else {
              this.$router.push('/profile')
            }
          }
        } else {
          this.closeLoading()
          this.$message.error({
            message: this.$t('message.' + res.msg),
            center: true
          })
        }
      })
    },

    authorizeHicoin() {
      var _url = this.referer
      console.log(_url)
      var url =
        'https://' +
        process.env.HICOIN_OAUTH_DOMAIN +
        '/api/connect/oauth/authorize'

      url += '?'
      var scopes = 'snsapi_base'
      var state = Util.randomString(16)
      var wallet_redirect = '#wallet_redirect'

      var redirect_uri = process.env.HICOIN_CALLBACK_URL
      if (_url) {
        redirect_uri += '&url=' + _url
      }
      redirect_uri = encodeURIComponent(redirect_uri).toLowerCase()
      url +=
        'app_id=' +
        process.env.HICOIN_APP_ID +
        '&redirect_uri=' +
        redirect_uri +
        '&response_type=code' +
        '&scope=' +
        scopes +
        '&state=' +
        state +
        '&' +
        wallet_redirect
      console.log(url)
      // return;
      this.$loading({
        lock: true,
        text: this.$t('message.redirecting'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      window.location.href = url
    },

    authorizeJustalk() {
      this.drawer = true
    },

    // 布洛克城
    redirectGxb() {
      var _url = this.referer //最终返回地
      var url =
        'https://' + process.env.GXB_OAUTH_DOMAIN + '/#/oauth/authorize?'
      var response_type = 'code'
      var client_id = process.env.GXB_APP_ID
      var redirect_uri = encodeURIComponent(
        process.env.GXB_CALLBACK_URL +
          encodeURIComponent('&source=gxb&url=' + _url)
      )
      // state
      var state = Util.randomString(16)

      url +=
        'response_type=code&client_id=' +
        client_id +
        '&redirect_uri=' +
        redirect_uri +
        '&state=' +
        state
      console.log(url, '-----------')

      this.loading = this.$loading({
        lock: true,
        text: this.$t('message.redirecting'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      window.location.href = url
    },

    loginByGxb() {
      var params = this.$route.query
      params.type = 'gxb'

      this.$store.dispatch('account/login', params).then(res => {
        if (res.code == 0) {
          // this.closeLoading();
          this.$message({
            message: this.$t('message.login_success'),
            type: 'success',
            center: true
          })

          var url = this.referer

          if (url) {
            window.location.href = url
          } else {
            this.closeLoading()
            this.$router.push('/')
          }
        } else {
          this.closeLoading()
          this.$message.error({
            message: this.$t('message.' + res.msg),
            center: true
          })
        }
        var url = window.location.href
        if (url.indexOf('action=') > 0) {
          var new_url = url.replace('action=', 'noitca=')
          history.pushState(null, null, new_url)
        }
      })
    },

    loginByBqex() {
      var params = this.$route.query
      params.type = 'bqex'

      this.$store.dispatch('account/login', params).then(res => {
        if (res.code == 0) {
          // this.closeLoading();
          this.$message({
            message: this.$t('message.login_success'),
            type: 'success',
            center: true
          })

          var url = this.referer

          if (url) {
            window.location.href = url
          } else {
            this.closeLoading()
            if (this.zone == 'pandora') {
              window.history.replaceState(null, null, '/')
              window.location.href = '/'
            } else {
              this.$router.push('/profile')
            }
          }
        } else {
          this.closeLoading()
          this.$message.error({
            message: this.$t('message.' + res.msg),
            center: true
          })
        }
      })
    },

    sendCode() {
      console.log(this.smsForm.mobile)
      this.$refs.smsForm.validateField('mobile', valid => {
        if (valid == '') {
          this.sendSms(this.smsForm.area, this.smsForm.mobile)
        } else {
          console.log(valid)
          return false
        }
      })
    },

    sendSms(area, mobile) {
      this.smsloading = true
      SmsService.login(area, mobile).then(res => {
        if (res.code == 0) {
          this.smsloading = false
          this.smsdisabled = true
          this.getSecond(60)
          this.$message({
            message: this.$t('message.send_success'),
            type: 'success',
            center: true
          })
        } else {
          this.smsloading = false
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'warning',
            center: true
          })
        }
        console.log(res)
      })
    },

    getSecond(wait) {
      let _this = this
      let _wait = wait
      if (wait == 0) {
        this.smsdisabled = false
        this.VerificationCode = this.$t('loginPart.VerificationCode')
        wait = _wait
      } else {
        this.smsdisabled = true
        this.VerificationCode = wait + 's'
        wait--
        setTimeout(function() {
          _this.getSecond(wait)
        }, 1000)
      }
    },

    allowOauth() {
      this.loading = this.$loading({
        lock: true,
        text: this.$t('message.authorizing'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })

      var params = {
        mobile: this.justalkMobile,
        source: this.source,
        justalk_uid: this.justalkUid,
        nickname: this.justalkNickname
      }
      this.$store.dispatch('account/authorize', params).then(res => {
        if (res.code == 0) {
          this.$message({
            message: this.$t('message.authorize_success'),
            type: 'success',
            center: true
          })
          // Storage.remove('ones_justalk')
          this.closeLoading()
          var url = this.referer
          if (url) {
            window.location.href = url
          } else {
            if (this.zone == 'pandora') {
              window.history.replaceState(null, null, '/')
              window.location.href = '/'
            } else {
              this.$router.push('/profile')
            }
          }
        } else {
          this.closeLoading()

          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'warning',
            center: true
          })
        }
      })
    },

    cancelOauth() {
      this.drawer = false
    },

    redirectEOS: function() {
      var loading = this.$loading({
        lock: true,
        text: this.$t('message.authorizing'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })

      if (!this.eosio) {
        this.eosio = new EosService(process.env.EOS_CONTRACT_NAME)
      }
      // console.log(this.eosio)
      this.eosio.login(
        function(code, msg, data) {
          console.log('eosiologin', code, msg, data)
          if (code != 0) {
            loading.close()
            this.$message.error({
              message: this.$t('message.' + msg),
              center: true
            })
            console.log('Failed to get Scatter account')
          } else {
            this.$store.dispatch('ASET_EOSIO', this.eosio)

            // loading.close()
            loading.text = this.$t('message.verifing')

            var source = Storage.get('ones_source')
            var invite_code = Storage.get('ones_invite_code')

            var params = {
              type: 'eos',
              source: source,
              publickey: data.publicKey,
              name: data.name,
              nonce: data.nonce,
              signature: data.signature,
              invite_code: invite_code,
              host: window.location.host
            }
            this.$store.dispatch('account/login', params).then(res => {
              loading.close()
              if (res.code == 0) {
                // this.closeLoading();
                this.$message({
                  message: this.$t('message.login_success'),
                  type: 'success',
                  center: true
                })
                Storage.remove('ones_invite_code')

                var url = this.referer

                if (url) {
                  window.location.href = url
                } else {
                  this.closeLoading()
                  if (this.zone == 'pandora') {
                    window.history.replaceState(null, null, '/')
                    window.location.href = '/'
                  } else {
                    this.$router.push('/profile')
                  }
                }
              } else {
                this.closeLoading()
                this.$message.error({
                  message: this.$t('message.' + res.msg),
                  center: true
                })
              }
              var url = window.location.href
              if (url.indexOf('action=') > 0) {
                var new_url = url.replace('action=', 'noitca=')
                history.pushState(null, null, new_url)
              }
            })
          }
        }.bind(this)
      )
    }
  }
}
</script>

