const ASET_SOURCE = 'ASET_SOURCE'
const ASET_REF = 'ASET_REF'
const ASET_CHANNEL = 'ASET_CHANNEL'

const ASET_ZONE = 'ASET_ZONE'
const ASET_EOSIO = 'ASET_EOSIO'
const ASET_EOS_ACCOUNT = 'ASET_EOS_ACCOUNT'

const actions = {
    [ASET_SOURCE](context, source) {
        context.commit('SET_SOURCE', source);
    },

    [ASET_REF](context, ref) {
        context.commit('SET_REF', ref);
    },

    [ASET_CHANNEL](context, channel) {
        context.commit('SET_CHANNEL', channel);
    },

    [ASET_ZONE](context, zone) {
        context.commit('SET_ZONE', zone);
    },

    [ASET_EOS_ACCOUNT](context, account) {
        context.commit('SET_EOS_ACCOUNT', account);
    },

    [ASET_EOSIO](context, payload) {
        context.commit('SET_EOSIO', payload.eosio);
    }
}
export default actions;


