<template>
  <div class="poolContainer" ref="poolContainer">
    <el-card class="box-card top-nav" v-if="liquidity">
      <span class="text item">{{liquidity.token1.symbol}} {{liquidity.token2.symbol}}</span>
    </el-card>

    <div class="fund-pool" v-if="liquidity">
      <div class="text item">资金池:{{liquidity.quantity1}} / {{liquidity.quantity2}}</div>
      <div class="text item" v-if="false">
        预估24H年化
        <!-- <div class="btn-detail">
          详情
          <i class="el-icon-arrow-right"></i>
        </div>-->
      </div>
      <!-- <div class="text item">预估每万个EOS每天收益</div> -->
    </div>

    <div slot="header" class="clearfix miner-nav">
      <span>矿工类别</span>
      <span>
        总矿工 :
        <i>{{minerData.length}}</i>
      </span>
    </div>
    <div class="miner-box" v-for="(item, index) in minerData" :key="index">
      <div class="name">
        <span v-text="item.account"></span>
        <span>{{item.quantity1}} / {{item.quantity2 | quantityFilter}}</span>
      </div>
      <!-- <div class="pool">
        <span>资金池</span>
        <span>xxxx/xxx</span>
      </div>-->
    </div>
  </div>
</template>

<script>
import Storage from '../../utils/storage'
import i18n from '../../locales/index'
import Util from '../../utils/util'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      mineContract: 'onesgamemine',
      defiContract: 'onesgamedefi',
      defaultImg: '/static/img/tokens/eos.png',

      tokens: ['ONES', 'BOX'],
      liquidity_id: 0,
      liquidity: null,
      minerData: [],
      _minerData: {}
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },

    account: function() {
      return this.$store.state.eos_account
    }
  },
  beforeMount() {
    this.liquidity_id = this.$route.params.liquidity_id
    this.getLiquidity()

    this.getPoolMiner('')
  },
  mounted() {},
  watch: {},
  beforeDestroy() {},
  methods: {
    getLiquidity(cb) {
      this.eosio.getTableRows(
        {
          code: this.defiContract,
          scope: this.defiContract,
          table: 'liquidity',
          limit: 1,
          lower_bound: this.liquidity_id,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          console.log(code, msg)
          if (code == 0) {
            if (
              data.rows &&
              data.rows.length == 1 &&
              data.rows[0]['liquidity_id'] == this.liquidity_id
            ) {
              for (var i in data.rows) {
                var row = data.rows[i]

                var symbol1 = row.token1.symbol.split(',')
                var address1 = row.token1.address
                var key1 = address1 + '-' + symbol1[1].toLowerCase()
                var token1 = {
                  address: address1,
                  symbol: symbol1[1],
                  precision: parseInt(symbol1[0]),
                  key: key1
                }

                var symbol2 = row.token2.symbol.split(',')
                var address2 = row.token2.address
                var key2 = address2 + '-' + symbol2[1].toLowerCase()
                var token2 = {
                  address: address2,
                  symbol: symbol2[1],
                  precision: parseInt(symbol2[0]),
                  key: key2
                }

                var liquidity = {
                  key1: key1,
                  key2: key2,
                  token1: token1,
                  token2: token2,
                  quantity1: row.quantity1,
                  quantity2: row.quantity2,
                  liquidity_token: row.liquidity_token,
                  price1: row.price1,
                  price2: row.price2,
                  swap_weight: row.swap_weight,
                  liquidity_weight: row.liquidity_weight
                }
                this.liquidity = liquidity
              }
              if (cb && typeof cb == 'function') {
                cb()
              }
            }
          }
        }.bind(this)
      )
    },

    getPoolMiner(account, cb) {
      if (account == '') {
        this._minerData = {}
      }
      this.eosio.getTableRows(
        {
          code: this.defiContract,
          scope: this.liquidity_id,
          table: 'defipools',
          limit: 100,
          lower_bound: account,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          console.log(code, msg)
          if (code == 0) {
            for (var i in data.rows) {
              var row = data.rows[i]
              this._minerData[row.account] = row
              account = row.account
            }

            if (data.more) {
              setTimeout(
                function() {
                  this.getPoolMiner(account, cb)
                }.bind(this),
                500
              )
            } else {
              var minerData = []
              for (var i in this._minerData) {
                minerData.push(this._minerData[i])
              }
              minerData.sort(function(a, b) {
                return a.liquidity_token - b.liquidity_token > 0 ? -1 : 1
              })
              this.minerData = minerData
              if (cb && typeof cb == 'function') {
                cb()
              }
            }
          }
        }.bind(this)
      )
    }
  },
  filters: {
    quantityFilter(value){
      return Number(value.split(" ")[0]).toFixed(4)+" "+ value.split(" ")[1];
    },
    numberFilter(value) {
      return Number(value / 10000).toFixed(4)
    }
  }
}
</script>

<style scoped>
.poolContainer {
  background-color: #0c0a1f;
  position: relative;
  overflow: hidden;
}
.poolContainer /deep/ .el-card {
  background-color: #0c0a1f;
  color: #fff;
}

.top-nav {
  font-size: 1.3rem;
}
.top-nav .text {
  border-bottom: 3px solid rgb(122, 170, 243);
  padding: 1rem 0;
}

.fund-pool {
  width: 94%;
  margin: 0 auto;
  color: #fff;
  box-sizing: border-box;
  background-color: #488ffa;
  border-radius: 10px;
  padding: 10px;
  margin-top: 10px;
}

.fund-pool .text {
  line-height: 1.7rem;
  font-size: 1rem;
}
.fund-pool .text:nth-child(1) {
  font-size: 1.1rem;
  line-height: 2rem;
}
.fund-pool .btn-detail {
  color: rgb(150, 223, 252);
  float: right;
}
.fund-pool .text:nth-child(3) {
  color: rgba(150, 211, 252, 0.952);
}
.miner-nav {
  display: flex;
  color: #fff;
  width: 92%;
  margin: 0 auto;
  margin-bottom: 10px;
  font-size: 1.1rem;
  justify-content: space-between;
}
.miner-nav span {
  padding: 1rem 0;
}
.miner-nav span i {
  font-style: normal;
  font-size: 1.4rem;
  color: #9fc4fc;
}
.miner-nav span:nth-child(2) {
  line-height: 1.7rem;
  font-size: 0.9rem;
}
.miner-nav span:nth-child(1) {
  border-bottom: 3px solid rgb(122, 170, 243);
}
.miner-box {
  width: 94%;
  margin: 0 auto;
  margin-bottom: 10px;
  color: #fff;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid #488ffa;
  padding: 10px 10px;
  line-height: 2rem;
}
.miner-box > div {
  display: flex;
  justify-content: space-between;
}
.miner-box .name span:nth-child(1) {
  color: #9fc4fc;
}
/* .miner-box .pool {

} */
</style>


