import accountapi from '../../api/account'
const state = {
    bonus: {
      ones_amount: "",
      dragon_amount: "",
      per_ones_income: "",
      user_income: "",//您的预估收益DT
      rewarded: "",
      bonus_user_reward: ""
    },
    usdt_bonus: {
      ones_amount: "",
      dragon_amount: "",
      per_ones_income: "",
      user_income: "",//你的预估总收益
      rewarded: "",//全网已分红
      bonus_user_reward: "" //你已获得分红Usdt
    },
    ones: {
      all: '', //开采难度的百分比
      released: '',
      reward: '',
      freezed: '', //冻结数量
      retire: '', //全网已销毁
      stage: '', //开采难度
      factor: '',//难度系数
      ones_balance:'',//可以抵押的余额
    },
    user: {
      all: '', //您的钻石 2
      reward: '',//奖励钻石
      released: '',//已开采
      unreleased: '',//未开采
    },
    stake:{
      all_amount: '', //全网已质押
      unstake_remain_time: '',
      user_amount: "",
      unstake_amount: '',
      stake_amount: '',
    },
    redeemNum:'',//赎回金额
    pledgeNum:'',//抵押金额
}

const getters = {
  bonus: state => {
    return state.bonus;
  },
  usdt_bonus: state => {
    return state.usdt_bonus;
  },
  ones: state => {
    return state.ones;
  },
  user: state => {
    return state.user;
  },
  stake: state => {
    return state.stake;
  }
}

const mutations = {
  ONES_BONUS(state,data){
    state.bonus = data.bonus;
    state.usdt_bonus = data.usdt_bonus;
    state.ones = data.ones;
    state.user = data.user;
    state.stake = data.stake;
  }
}

const actions = {
  // 获取ones分红
  getOnesBonus({commit}) {
    accountapi.getOnesBonus().then(res=>{
      if (res.code == '0') {
        commit('ONES_BONUS',res.data)
      }
    })
  },
  // goPledgeOnes({commit},content){
  //   console.log('-----质押------2 -----',content.num)
  // },
  // goRedeemOnes(){
  //   console.log('-----赎回------2')
  // },
}

export default {
  // namespaced: true,
  state,
  getters,
  mutations,
  actions,
}


