import Http from './http'
import account from '../store/modules/account'

export default {

  detail() {
    return Http.post({
      path: '/node/detail',
      headers: {
        'access-token': account.state.user ? account.state.user.access_token : null
      },
      data: {}
    })
  },

  // 认购
  subscribe(amount) {
    return Http.post({
      path: '/node/subscribe',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: {
        amount: amount
      }
    })
  },

  // 用户订购
  getOrders() {
    return Http.post({
      path: '/node/orders',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: {
      }
    })
  },

  // 用户收益
  getIncomes() {
    return Http.post({
      path: '/node/incomes',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: {
      }
    })
  },

   // 分红
   getBonus() {
    return Http.post({
      path: '/node/bonus',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: {
      }
    })
  },
}


