<template>
  <div class="container">
    <div class="registerContainer">
      <div class="nav">
        <div class="back">
          <i class="el-icon-arrow-left" @click="goBack"></i>
        </div>
        {{$t("registerPart.register")}}
        <!-- mailboxPassword -->
      </div>
      <div class="logo">
        <img src="../../assets/loginlogo.png" class="logo" alt />
      </div>
      <!-- 表单部分 -->
      <div class="register-form">
        <el-form
          ref="registerForm"
          :model="registerForm"
          :rules="registerRules"
          label-width="6.4rem"
          label-position="left"
        >
          <el-form-item :label="$t('loginPart.PhoneNumber')" prop="mobile" class>
            <div style="display: flex; width: 100%; height: 40px;">
              <el-select
                v-model="registerForm.area"
                placeholder="请选择国家/地区"
                :popper-append-to-body="false"
                class="selectOption"
                style="flex:0.5"
              >
                <el-option label="+86" value="+86"></el-option>
              </el-select>
              <el-input
                v-model="registerForm.mobile"
                :placeholder="$t('loginPart.MobilePlaceHolder')"
                style="flex:1"
              ></el-input>
            </div>
          </el-form-item>
          <el-form-item
            :label="$t('loginPart.LoginPassword')"
            prop="password"
            style="display:none;"
          >
            <el-input
              v-model="registerForm.password"
              show-password
              :placeholder="$t('loginPart.PasswordPlaceHolder')"
            ></el-input>
          </el-form-item>
          <el-form-item
            :label="$t('loginPart.LoginPassword1')"
            prop="password2"
            style="display:none;"
          >
            <el-input
              v-model="registerForm.password2"
              show-password
              :placeholder="$t('registerPart.PasswordAgain')"
            ></el-input>
          </el-form-item>
          <el-form-item :label="$t('loginPart.VerificationCode')" prop="vcode">
            <el-input
              v-model="registerForm.vcode"
              :placeholder="$t('loginPart.VerificationCodePlaceHolder')"
            >
              <el-button
                slot="append"
                type="primary"
                plain
                @click="sendCode"
                :disabled="smsdisabled"
                :loading="smsloading"
              >{{VerificationCode}}</el-button>
            </el-input>
          </el-form-item>
          <el-form-item :label="$t('registerPart.InvitationCode')">
            <el-input
              v-model="registerForm.invite_code"
              :disabled="inviteDisabled"
              :placeholder="$t('registerPart.InvitationCodePlaceHolder')"
            ></el-input>
            <div class="register-tips">{{$t('registerPart.InvitationCodeTips')}}</div>
          </el-form-item>
        </el-form>
        <el-button type="primary" class="loginbtn" @click="register">{{$t("registerPart.register")}}</el-button>
      </div>

      <div class="register-banner" @click="enterDragonMaster">
        <img src="../../assets/banner/dragonmaster.png" alt />
      </div>
    </div>
  </div>
</template>

<script>
import SmsService from '../../api/sms'
import Storage from '../../utils/storage'

export default {
  data() {
    var validateMobile = (rule, value, callback) => {
      if (value == '')
        return callback(new Error(this.$t('loginPart.MobilePlaceHolder')))
      if (!/^1[3456789]\d{9}$/.test(value))
        return callback(new Error(this.$t('loginPart.MobileFormatError')))
      return callback()
    }

    var validatePass = (rule, value, callback) => {
      console.log(rule, value)
      if (value === '') {
        return callback(new Error(this.$t('loginPart.PasswordPlaceHolder')))
      } else {
        var regex = new RegExp('([a-zA-Z0-9]).{7,16}')
        if (!regex.test(value)) {
          return callback(new Error(this.$t('loginPart.PassWordFormatError')))
        }
        if (this.registerForm.password2 !== '') {
          this.$refs.registerForm.validateField('password2')
        }
        return callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('registerPart.PasswordAgain'))) //请再次输入密码
      } else if (value !== this.registerForm.password) {
        callback(new Error(this.$t('registerPart.PasswordAgainError')))
      } else {
        callback()
      }
    }
    var validateVcode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(this.$t('loginPart.VerificationCodePlaceHolder')))
      } else if (!/\d/.test(value)) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError1')))
      } else if (value.length != 6) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError2')))
      } else {
        callback()
      }
    }

    return {
      inviteDisabled: false,
      VerificationCode: this.$t('loginPart.sendCode'),
      smsdisabled: false,
      smsloading: false,
      // 手机号注册
      registerForm: {
        area: '+86',
        mobile: '',
        vcode: '',
        password: '',
        password2: '',
        invite_code: ''
      },
      registerRules: {
        mobile: [{ validator: validateMobile, trigger: 'blur' }],
        // password: [{ validator: validatePass, trigger: "blur" }],
        // password2: [{ validator: validatePass2, trigger: "blur" }],
        vcode: [{ validator: validateVcode, trigger: 'blur' }]
      }
    }
  },
  mounted: function() {
    if (this.$route.query.ref) {
      var invite_code = this.$route.query.ref
      this.registerForm.invite_code = invite_code
      this.inviteDisabled = true
    } else {
      var invite_code = Storage.get('ones_invite_code')
      if (invite_code) {
        this.registerForm.invite_code = invite_code
        this.inviteDisabled = true
      }
    }

    var source = Storage.get('ones_source')
    if (source == 'justalk') {
      var justalk = Storage.get('ones_justalk')
      if (justalk && justalk.mobile) {
        var mobile = justalk.mobile.slice(2 + justalk.mobile.indexOf('86'))
        this.registerForm.mobile = mobile
      }
    }

    var source = Storage.get('ones_source')
    if (source == 'justalk') {
      var justalk = Storage.get('ones_justalk')
      if (justalk.mobile) {
        var mobile = justalk.mobile.slice(2 + justalk.mobile.indexOf('86'))
        this.registerForm.mobile = mobile
      }
    }
  },
  methods: {
    enterDragonMaster() {
      this.$router.push({
        name: 'gameDetailsScene',
        params: {
          game: 'dragonmaster'
        }
      })
    },
    onConfirm(value) {
      this.value = value
      this.showPicker = false
    },
    changeregisterType(ev) {
      var lilist = ev.target.parentNode.children
      // console.log(lilist)
      for (var i in lilist) {
        // console.log(lilist[i].className)
        if (lilist[i].className == 'active') {
          lilist[i].classList.remove('active')
        }
      }
      ev.target.setAttribute('class', 'active')
    },
    routerChange(name) {
      //  路由跳转
      this.$router.push({
        name: 'name'
      })
    },

    register() {
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          var params = {
            area: this.registerForm.area,
            mobile: this.registerForm.mobile,
            vcode: this.registerForm.vcode,
            invite_code: this.registerForm.invite_code
          }
          this.$store.dispatch('account/register', params).then(res => {
            if (res.code == 0) {
              this.$message({
                message: this.$t('message.register_success'),
                type: 'success',
                center: true
              })
              Storage.remove('ones_invite_code')
              if ((this.zone == 'pandora')) {
                window.location.href = '/'
              } else {
                this.$router.push('/profile')
              }
            } else {
              this.$message({
                message: this.$t('message.' + res.msg),
                type: 'warning',
                center: true
              })
            }
          })
        } else {
          return false
        }
      })
    },

    sendCode() {
      console.log(this.registerForm.mobile)
      this.$refs.registerForm.validateField('mobile', valid => {
        if (valid == '') {
          this.smsloading = true
          this.sendSms(this.registerForm.area, this.registerForm.mobile)
        } else {
          console.log(valid)
          return false
        }
      })
    },

    getSecond(wait) {
      let _this = this
      let _wait = wait
      if (wait == 0) {
        this.smsdisabled = false
        this.VerificationCode = this.$t('loginPart.VerificationCode')
        wait = _wait
      } else {
        this.smsdisabled = true
        this.VerificationCode = wait + 's'
        wait--
        setTimeout(function() {
          _this.getSecond(wait)
        }, 1000)
      }
    },

    sendSms(area, mobile) {
      SmsService.register(area, mobile).then(res => {
        if (res.code == 0) {
          this.smsloading = false
          this.smsdisabled = true
          this.getSecond(60)
          this.$message({
            message: this.$t('message.send_success'),
            type: 'success',
            center: true
          })
        } else {
          this.smsdisabled = false
          this.smsloading = false
          // this.smsdisabled = true
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'warning',
            center: true
          })
        }
        console.log(res)
      })
    },

    goBack() {
      if ((this.zone == 'pandora')) {
        this.$router.go(-1)
      } else {
        this.$router.push({
          name: 'gameScene'
        })
      }
      console.log('logout')
    }
  }
}
</script>

<style scoped>
a {
  text-decoration: none;
  color: rgb(159, 159, 159);
}
.registerContainer {
  width: 100%;
  /* background-image: url(../../assets/registerBg.jpg); */
  background-size: 100% 100%;
}
.registerContainer .nav {
  height: 50px;
  line-height: 50px;
  font-size: 1.5rem;
  position: relative;
  text-align: center;
  color: rgb(245, 244, 244);
  background-color: rgb(65, 153, 247);
}
.registerContainer .nav .back {
  float: left;
}
.registerContainer .nav .back i {
  font-size: 1.7rem;
  margin-left: 0.7rem;

  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  color: #fff;
}
.registerContainer .nav .registered {
  color: rgb(124, 140, 172);
  float: right;
  margin-right: 3;
  font-size: 1.2rem;
  height: 3rem;
  line-height: 3rem;
  /* font-weight: 800; */
}
.logo {
  width: 30%;
  margin: 1rem auto;
  overflow: hidden;
  /* background-color: rgba(157, 103, 103, 0.544); */
}
.logo img {
  width: 100%;
  height: 100%;
}
.register-form {
  width: 100%;
  margin-top: 2rem;
  box-sizing: border-box;
  padding-left: 1rem;
  padding-right: 1rem;
  position: relative;
}

.forgetCode {
  height: 1.5rem;
  margin-top: 4.5rem;
  line-height: 1.5rem;
  font-size: 0.8rem;
  color: rgb(142, 141, 141);
  overflow: hidden;
  position: relative;
}
.forgetCode i {
  position: absolute;
  top: 53%;
  transform: translate(0, -50%);
  font-size: 1rem;
}
.forgetCode span {
  margin-left: 5rem;
}

.register-type {
  height: 10rem;
  margin-top: 1rem;
  box-sizing: border-box;
  padding-left: 2rem;
  padding-right: 2rem;
}
.register-type .title-text {
  height: 3rem;
}
.register-type .title-text ul {
  width: 100%;
}
.register-type .title-text ul li:nth-child(1) {
  width: 28%;
  height: 1px;
  background-color: #666;
  margin-top: 0.9rem;
}
.register-type .title-text ul li {
  float: left;
  color: rgb(160, 159, 159);
}
.register-type .title-text ul li:nth-child(3) {
  width: 28%;
  height: 1px;
  background-color: #666;
  margin-top: 0.9rem;
}
.type-list ul {
  display: flex;
  margin-top: 0.7rem;
}
.type-list ul li {
  flex: 1;
  text-align: center;
  color: rgb(101, 101, 101);
}
.type-list ul .active {
  /* font-size: 0.9rem; */
  font-weight: 800;
  color: rgb(167, 167, 233);
}

.loginbtn .el-button {
  width: 100%;
}
.loginbtn {
  width: 100%;
  height: 3.5rem;
}
.tipsText {
  margin-top: 4rem;
  color: rgb(86, 85, 85);
}
.registerMailbox {
  width: 100%;
  height: 6rem;
  margin: 1.3rem auto;
}
.registerMailbox img {
  width: 100%;
  height: 100%;
}
.changeRegisterType {
  width: 100%;
  text-align: center;
  color: rgb(235, 173, 107);
}
.mobileLabel {
  position: relative;
}
.mobileLabel .el-input {
  position: absolute;
  width: 64%;
}

.register-banner {
  width: 100%;
  height: 12rem;
  box-sizing: border-box;
  padding: 0 2%;
  margin-top: 1.5rem;
}
.register-banner img {
  width: 100%;
  height: 100%;
}

.register-tips {
  font-size: 12px;
  color: #606266;
}
</style>


