<template>
  <div class="onesContainer" ref="onesContainer">
    <!-- 分红部分 -->
    <div id="content">
      <div class="billBox">
        <!-- 账单 -->
        <el-table
          :data="orderTableData"
          style="width: 100%;"
          max-height="250"
          size="small"
          @row-click="viewOrderDetail"
        >
          <el-table-column prop="create_time" label="时间" align="center" :formatter="createTime"></el-table-column>
          <el-table-column prop="amount" label="金额" align="center" :formatter="amount"></el-table-column>
          <el-table-column prop="game_type" label="游戏" align="center" :formatter="gametype"></el-table-column>
          <el-table-column prop="reward_ones" label="奖励" align="center" :formatter="reward_ones"></el-table-column>
        </el-table>
      </div>

      <!-- 弹出 -->
      <el-dialog :visible.sync="orderDialogVisible" :before-close="handleClose">
        <ul class="handleList">
          <li>
            <div
              class="leftText"
              style="flex:1;color:#888;font-size:1.1rem"
            >{{this.$t("onesScene.Scenes")}}</div>
            <div
              class="info"
              style="flex:1;text-align:right;color:#888;font-weight:800;font-size:1.1rem"
            >{{enterDataIn.game_type | game_type_Filter}}</div>
          </li>
          <li>
            <div
              class="leftText"
              style="flex:1;color:#888;font-size:1.1rem"
            >{{this.$t("onesScene.time")}}</div>
            <div
              class="info"
              style="flex:1;text-align:right;color:#888;font-weight:800;font-size:1.1rem"
            >{{enterDataIn.create_time | time_Filter_DDmm}}</div>
          </li>
          <li>
            <div
              class="leftText"
              style="flex:1;color:#888;font-size:1.1rem"
            >{{this.$t("onesScene.Amount")}}</div>
            <div
              class="info"
              style="flex:1;text-align:right;color:#888;font-weight:800;font-size:1.1rem"
            >{{enterDataIn.amount/10000 | numFilter_2}}{{enterDataIn.coin_code | toUp_filter}}</div>
          </li>
          <li>
            <div
              class="leftText"
              style="flex:1;color:#888;font-size:1.1rem"
            >{{this.$t("onesScene.mined")}}ONES</div>
            <div
              class="info"
              style="flex:1;text-align:right;color:#888;font-weight:800;font-size:1.1rem"
            >{{enterDataIn.releaded_ones/10000 | numFilter_2}}</div>
          </li>
          <li>
            <div
              class="leftText"
              style="flex:1;color:#888;font-size:1.1rem"
            >{{this.$t("onesScene.unmined")}}ONES</div>
            <div
              class="info"
              style="flex:1;text-align:right;color:#888;font-weight:800;font-size:1.1rem"
            >{{(enterDataIn.ones - enterDataIn.releaded_ones)/10000 | numFilter_2}}</div>
          </li>
          <li style="border:none">
            <div
              class="leftText"
              style="flex:2;color:#888;font-size:1.1rem"
            >{{this.$t("onesScene.daysA")}}</div>
            <div
              class="info"
              style="flex:1;text-align:right;color:#888;font-weight:800;font-size:1.1rem"
            >{{enterDataIn.remain_day}}Day</div>
          </li>
        </ul>
      </el-dialog>
      <!-- 弹出 -->
    </div>
  </div>
</template>

<script>
import AccountService from '../../api/account'
import Util from '../../utils/util'
export default {
  data() {
    return {
      zone: this.$store.getters.zone,

      orderDialogVisible: false,
      orderTableData: [],
      enterDataIn: {
        create_time: 1585715557,
        amount: 0,
        ones: 0,
        coin_code: 'usdt',
        type: 'decoration',
        img: '1',
        releaded_ones: 6947,
        remain_day: 100
      }
    }
  },
  mounted: function() {
    this.getShopOrders()
  },
  methods: {
    handleClose(done) {
      this.orderDialogVisible = false
    },

    viewOrderDetail(row, column, event) {
      this.orderDialogVisible = true
      this.enterDataIn = row
    },

    type(row, column, cellValue, index) {
      if (cellValue == 'decoration') return '装饰'
      if (cellValue == 'coin') return '金币'
    },

    gametype(row, column, cellValue, index) {
      if (cellValue == 'dragon')
        return this.$t('gameScenePart.dragonmaster.name')
      if (cellValue == 'witchtowergame')
        return this.$t('gameScenePart.witchtower.name')
      if (cellValue == 'castle')
        return this.$t('gameScenePart.dragonclash.name')
      if (cellValue == 'pkthreekinggame')
        this.$t('gameScenePart.pkthreeking.name')
    },
    createTime(row, column, cellValue, index) {
      return Util.formatDate(cellValue, 'MM-dd hh:mm')
    },
    amount(row, column, cellValue, index) {
      var _value =
        Number(parseFloat(cellValue / 10000)).toFixed(2) +
        this.orderTableData[index].coin_code.toUpperCase()
      return _value
    },
    reward_ones(row, column, cellValue, index) {
      return Number(parseFloat(cellValue / 10000)).toFixed(2)
    },
    showBill() {
      this.showBillBox = true
    },

    getShopOrders() {
      AccountService.getShopOrders().then(res => {
        if (res.code == 0) {
          // console.log(res.data.orders.length)
          this.orderTableData = res.data.orders
        } else {
          // console.log(this.tableData)
        }
      })
    }
  },

  filters: {
    toUp_filter(value) {
      return value.toUpperCase()
    },
    game_type_Filter(value) {
      if (value == 'dragon') return '驯龙高手'
      if (value == 'witchtowergame') return '勇闯女巫塔'
      if (value == 'castle') return '龙堡争霸'
      if (value == 'pkthreekinggame') return '怼怼三国'
      if (value == 'fishinggame') return '大神捕鱼'
    }
  }
}
</script>

<style scoped>
.onesContainer {
  position: relative;
}

#content {
  width: 100%;
}

.billBox {
  /* width: 95%; */
  height: 3rem;
  margin: 0 auto;
  border-top: 2px solid #8189a29f;
  background-color: #f1f1f1;
  box-shadow: 0.5px 0.5px 0.5px #b4b4b59f;
}

.handleList {
  width: 100%;
  box-sizing: border-box;
  padding-right: 5%;
  padding-right: 5%;
  font-size: 16px;
  line-height: 32px;
}
.handleList li {
  display: flex;
  /* border-bottom: 1px solid rgb(204, 204, 204); */
}
</style>


