import Http from './http'
import account from '../store/modules/account'
import onesbonus from '../store/modules/onesbonus'

export default {
  login(params) {
    return Http.post({
      path: "/account/login",
      headers: {},
      data: params
    })
  },

  register(account, cb) {
    return Http.post({
      path: "/account/register",
      headers: {},
      data: account
    })
  },

  authorize(account, cb) {
    return Http.post({
      path: "/account/authorize",
      headers: {},
      data: account
    })
  },

  logout(params) {
    return Http.post({
      path: "/account/logout",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: params
    })
  },

  check() {
    return Http.post({
      path: "/account/check",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {}
    })
  },

  setPayPassword(password, vcode) {
    console.log("setPayPassword..........", password, vcode)
    return Http.post({
      path: "/account/setpaypwd",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {
        password: password,
        vcode: vcode
      }
    })
  },

  setLoginPassword(login_password, vcode) {
    return Http.post({
      path: "/account/setloginpwd",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {
        login_password: login_password,
        vcode: vcode
      }
    })
  },

  bindMobile(area, mobile, vcode) {
    return Http.post({
      path: "/account/bindmobile",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {
        area: area,
        mobile: mobile,
        vcode: vcode
      }
    })
  },

  bindDragonex(params) {
    return Http.post({
      path: "/account/binddragonex",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: params
    })
  },

  bindZT(params) {
    return Http.post({
      path: "/account/bindzt",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: params
    })
  },

  // 钻石分红
  getOnesBonus(params) {
    return Http.post({
      path: "/prize/detail",
      headers: {
        "access-token": account.state.user.access_token
      },
      // data: params
    }).then(function (response) {
      return response;
    }).catch(function (error) {
      console.log(error);
    });
  },

  // 质押
  stake(amount) {
    return Http.post({
      path: "/diamond/stake",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {
        amount
      }
    }).then(function (response) {
      // console.log(response);
      return response
    }).catch(function (error) {
      // console.log(error);
    });
  },

  //赎回
  unstake(amount) {
    return Http.post({
      path: "/diamond/unstake",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {
        amount
      }
    }).then(function (response) {
      return response
    }).catch(function (error) {

    })
  },

  //赎回
  restake() {
    return Http.post({
      path: "/diamond/restake",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {}
    }).then(function (response) {
      return response
    }).catch(function (error) {

    })
  },

  //领取
  claim() {
    return Http.post({
      path: "/diamond/claim",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {}
    }).then(function (response) {
      return response
    }).catch(function (error) {

    })
  },

  //消费账单
  getShopOrders() {
    return Http.post({
      path: "/shop/orders",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {}
    }).then(function (response) {
      return response
    }).catch(function (error) {})
  }
}


