import Http from './http'
import account from '../store/modules/account'

export default {
  // asset/transfer 
  transfer(params) {
    return Http.post({
      path: '/asset/transfer',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: params
    })
  },
  balance() {
    return Http.post({
      path: '/asset/balance',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: {}
    })
  },

  // 新
  balances() {
    return Http.post({
      path: '/asset/balances',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: {}
    })
  },

  predeposit(params) {
    return Http.post({
      path: '/asset/predeposit',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: params
    })
  },

  deposit(params) {
    return Http.post({
      path: '/asset/deposit',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: params
    })
  },

  withdraw(params) {
    return Http.post({
      path: '/asset/withdraw',
      headers: {
        'access-token': account.state.user.access_token
      },
      data: params
    })
  },

  history(type, coin_code) {
    return Http.post({
      path: "/asset/history",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {
        coin_code: coin_code,
        type: type
      }
    })
  },

  transactions(coin_code, type) {
    return Http.post({
      path: "/asset/transactions",
      headers: {
        "access-token": account.state.user.access_token
      },
      data: {
        coin_code: coin_code,
        type: "deposit"
      }
    })
  },
}


