<template>
  <div class="nodeContainer" ref="nodeContainer">
    <div class="top-content">
      <div class="right-content">
        <h3 v-if="myNode.reward_quantity||myNode.rank<100">
          {{$t("node.myprofit")}}
          <span
            @click="claimDialogVisible=true"
          >{{myNode.reward_quantity|quantityFilter}}</span> EOS
        </h3>
        <p v-if="myNode.rank>100">
          {{$t("node.short")}}
          <span>No.100</span>
          {{$t("node.little")}}
        </p>
        <p>
          {{$t("node.mysubscription")}}
          <span
            @click="viewMyOrder()"
          >{{myNode.stake_quantity|quantityFilter}}</span> ONES
        </p>
        <div class="to-sub" @click="nodeSubscribeVisible=true">{{$t("node.immediate")}}</div>
      </div>
      <div class="left-content">
        <!-- <img src="../../assets/flagsred.png" alt /> -->
        <div class="rank-num">
          <p v-if="myNode.rank<=100" v-text="myNode.rank"></p>
        </div>

        <!-- <div class="to-redeem" v-if="myNode.rank>100 && myNode.amount>0" style="display:none;">
          <el-button
            type="info"
            round
            size="mini"
            @click="openSubscribeDialog('redeem')"
          >{{$t("node.redeem")}}</el-button>
        </div>-->
      </div>

      <div class="node-rule">
        <i class="el-icon-question" @click="nodeRuleVisible=true"></i>
      </div>
    </div>

    <div class="background-container"></div>
    <div class="dividend-container" @click="viewBonus()">
      <div class="dividend-item">
        <p class="title" v-text="$t('node.bonusPool')"></p>
        <p class="dividend-num" v-text="totalBonus"></p>
      </div>
      <div class="dividend-item">
        <p class="title" v-text="$t('node.totalDividend')"></p>
        <p class="dividend-num">{{totalIncome}}</p>
        <!-- <span class="dividend-date">
          <i class="el-icon-arrow-right"></i>
        </span>-->
      </div>
      <div class="dividend-item">
        <p class="title">总抵押</p>
        <p class="dividend-num">{{totalStaked}}</p>
      </div>
      <div class="dividend-item">
        <p class="title">超级节点已销毁</p>
        <p class="dividend-num">{{totalRetire}}</p>
      </div>
      <!-- <div class="dividend-item">
        <p class="title">预计销毁</p>
        <p class="dividend-num">{{expectedRetire}}</p>
      </div>-->
      <div class="dividend-item">
        <p class="title">年化收益</p>
        <p class="dividend-num">{{annualized}}</p>
      </div>
    </div>

    <div class="node-rank">
      <ul class="rank-list">
        <li>
          <div class="number"></div>
          <div class="uid">UID</div>
          <div class="amount">{{$t("node.amount")}}(ONES)</div>
          <div class="income">{{$t("node.profit")}}(EOS)</div>
        </li>
        <li v-for="(item,index) in nodeRankList" :key="index">
          <div class="number">
            <p v-if="item.rank>3" v-text="item.rank"></p>
            <img
              :src="require('../../assets/no'+item.rank+'.png')"
              alt
              :class="'no'+ item.rank"
              v-if="item.rank<=3"
            />
          </div>
          <div class="uid">{{item.account|accountFilter}}</div>
          <div class="amount">
            <span>{{item.stake_quantity|quantityFilter}}</span>
          </div>
          <div class="income">
            <span>{{item.reward_quantity|quantityFilter}}</span>
            <!-- <img src="../../assets/income.png" alt /> -->
          </div>
        </li>
      </ul>
    </div>

    <!-- 弹出框 -->
    <el-dialog :visible.sync="nodeSubscribeVisible" title="我要认购" center>
      <!-- 认购 -->
      <div class="node-dialog">
        <div style="text-align:center;margin:15px;">
          <p>分红已开启</p>
          <p>(请先认真阅读规则,仅前100名有奖励,前100名赎回在一定时间内会收取一定的手续费)</p>
        </div>

        <div class="node-content">
          <el-input-number
            v-model="nodesAmount"
            :min="10"
            :step="10"
            step-strictly
            class="input-number"
          ></el-input-number>
        </div>
        <div class="node-text">
          余额：
          <span>{{onesBalance}} ONES</span>
        </div>
        <div class="node-btns">
          <el-button type="info" plain @click="nodeSubscribeVisible=false;">{{$t("node.cancel")}}</el-button>
          <el-button
            type="primary"
            :loading="confirmNodeLoading"
            @click="confirmNode(nodesAmount)"
          >{{$t("node.confirm")}}</el-button>
        </div>
      </div>
    </el-dialog>

    <!-- 赎回 -->
    <el-dialog :visible.sync="nodeRedeemVisible">
      <div class="redeem-dialog">
        <div class="node-text">
          {{$t("node.confirmredeem")}}
          <span>{{redeemNode.quantity}}</span>
          <span
            v-if="redeemNode.retireAmount>0"
            style="display:block;line-height:20px;margin-bottom:10px;"
          >赎回会损失{{redeemNode.retireAmount}} ONES({{redeemNode.retire}}%),将直接销毁</span>
        </div>
        <div class="node-btns">
          <el-button type="info" plain @click="cancelRedeem()">{{$t("node.cancel")}}</el-button>
          <el-button
            type="primary"
            :loading="unstakeLoading"
            @click="confirmRedeem(redeemNode)"
          >{{$t("node.confirm")}}</el-button>
        </div>
      </div>
    </el-dialog>

    <el-dialog title="超级节点计划,瓜分100%奖池" :visible.sync="nodeRuleVisible" class="nodeDialog" center>
      <div id="programme">
        <!-- <h4 class="top-nav">超级节点计划,瓜分50%奖池</h4> -->
        <div
          class="sub-nav"
        >超级节点计划是为了更好的奖励ONES持有者的一种方式。任何玩家都可以参与节点竞选，按照ONES认购的数量排名，前100名都会获得节点奖励，排名实时更新，100名之外的节点将退回认购的ONES,无法获得节点奖励也可继续认购进入前100名。具体内容如下：</div>
        <ul>
          <li>
            <strong>一、如何参与</strong>
            <p>使用ONES认购即可参与,10.00 ONES起认购</p>
          </li>
          <li>
            <strong>二、如何奖励</strong>
            <p>1、前100名的玩家将会获得超级节点奖池奖励</p>
            <p>2、每天20:00(UTC-8)对奖池和排名进行快照并分发奖励</p>
            <p>3、每天将发放奖池的1%</p>
            <p>4、100名之后将无法获得奖励（当抵押金额一样时合约按照eos账户名称进行排序,请确保自己在100名以内）</p>
          </li>
          <li>
            <strong>三、奖池来源</strong>
            <p>1、游戏50%的收入(EOS),实时结算,游戏的ONES50%将销毁不在进入奖池。</p>
            <p>2、Swap 0.1%的手续费自动计入到合约（onesgamedivd）里</p>
          </li>
          <li>
            <strong>四、如何退出</strong>
            <p>超级节点有两种退出方式：一种是被动退出，一种是主动退出</p>
            <p>1、被动退出：当排名跌出前100时会退出超级节点，被动退出无手续费。</p>
            <p>2、主动退出: 玩家可以点击退出超级节点，主动退出需要支付手续费，手续费将被销毁。</p>
            <p>2.1 认购30天以内若赎回将销毁20%的ONES；</p>
            <p>2.2 认购30-90天以内若赎回将销毁10%的ONES；</p>
            <p>2.3 认购90天-180天以内若赎回将销毁5%的ONES;</p>
            <p>2.4 认购180天以上赎回不销毁ONES</p>
          </li>
          <li>
            <strong>五、年化计算公式</strong>
            <p>当前奖池EOS*0.01/当前抵押ONES*365*EOSONES价格*100%</p>
          </li>
        </ul>
      </div>
    </el-dialog>

    <el-dialog :visible.sync="nodeOrderVisible" title="我的认购" center>
      <el-table :data="nodeOrders" style="width: 100%;" align="center">
        <el-table-column
          prop="timestamp"
          :label="$t('node.date')"
          align="center"
          :formatter="formatterTime"
          width="100px"
        ></el-table-column>
        <el-table-column prop="quantity" :label="$t('node.amount')" align="center"></el-table-column>
        <el-table-column :label="$t('node.action')" align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="openRedeemDialog(scope.row)"
            >{{$t("node.redeem")}}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>

    <el-dialog :visible.sync="claimDialogVisible" title="我的收益" center>
      <el-table :data="bonusTableData" border style="width: 100%">
        <el-table-column prop="type" label="类型"></el-table-column>
        <el-table-column prop="amount" label="数量"></el-table-column>
      </el-table>

      <p style="margin-top:10px;">每日20:00分红,请手动点击领取</p>
      <el-button class="claim-btn" :loading="claimLoading" type="primary" @click="claim">领取</el-button>
    </el-dialog>

    <el-dialog :visible.sync="nodeBonusVisible" title="每日分红" center>
      <el-table :data="nodeBonus" style="width: 100%;" align="center">
        <el-table-column
          prop="timestamp"
          :formatter="formatterDay"
          :label="$t('node.date')"
          align="center"
        ></el-table-column>
        <el-table-column prop="bonus_quantity" :label="$t('node.income')+'(EOS)'" align="center"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import nodeService from '../../api/node'
import Util from '../../utils/util'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      nodeRuleVisible: false,
      nodeSubscribeVisible: false,

      divdContract: 'onesgamedivd',
      defiContract: 'onesgamedefi',

      myNode: {},
      redeemNode: {},
      totalBonus: '0.0000 EOS',
      totalIncome: '0.0000 EOS',
      totalStaked: '0.0000 ONES',
      totalRetire: '0.0000 ONES',
      expectedRetire: '0.0000 ONES',

      annualized: '0.00%',

      nodeRankList: [],
      _nodeRankList: [],
      _accounts: {},
      nodesAmount: 10,
      type: 'balance',
      confirmNodeLoading: false,
      unstakeLoading: false,

      //订单
      nodeOrderVisible: false,
      nodeOrders: [],

      //收益
      nodeUserIncomeVisible: false,
      nodeIncomes: [],

      //领取
      claimDialogVisible: false,
      bonusTableData: [],
      claimLoading: false,

      //收益
      nodeBonusVisible: false,
      nodeBonus: [],

      //赎回
      nodeRedeemVisible: false,

      onesBalance: '0.0000'
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },
    account: function() {
      return this.$store.state.eos_account
    }
  },
  mounted() {
    this.getNodeDetail()
    this.getOnesBalance()
  },
  watch: {
    account(newVal, oldVal) {
      var myNode = {
        stake_quantity: 0.0
      }
      for (var i in this.nodeRankList) {
        if (this.nodeRankList[i]['account'] == newVal) {
          myNode = this.nodeRankList[i]
        }
      }
      if (!myNode.rank) {
        myNode.rank = this.nodeRankList.length + 1
      }
      this.myNode = myNode

      this.getOnesBalance()
    },

    myNode(newVal, oldVal) {
      var bonusTableData = []
      if (newVal) {
        bonusTableData.push({
          type: '待领取',
          amount: newVal.unclaim_quantity
        })
        bonusTableData.push({
          type: '总收益',
          amount: newVal.reward_quantity
        })
      }
      this.bonusTableData = bonusTableData
    }
  },
  methods: {
    claim() {
      this.claimLoading = true
      var actions = [
        {
          account: this.divdContract,
          name: 'claim',
          authorization: [
            {
              actor: this.eosio.account.name,
              permission: this.eosio.account.authority
            }
          ],
          data: {
            account: this.eosio.account.name
          }
        }
      ]
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.claimLoading = false
          this.getNodeDetail()

          if (code == 0) {
            this.$message({
              message: '领取成功',
              type: 'success',
              center: true
            })
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },

    viewMyOrder() {
      this.nodeOrderVisible = true

      this.eosio.getTableRows(
        {
          code: this.divdContract,
          scope: this.divdContract,
          table: 'stakelog',
          limit: 100,
          lower_bound: this.account.name,
          index_position: 2, //todo
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          var isSuccess = false
          if (code == 0) {
            var nodeOrders = []
            for (var i in data.rows) {
              var row = data.rows[i]
              if (row.account != this.account.name) {
                break
              }
              nodeOrders.push(row)
            }

            this.nodeOrders = nodeOrders
          }
        }.bind(this)
      )

      return

      nodeService.getOrders().then(res => {
        if (res.code == 0) {
          this.nodeOrders = res.data.orders
        }
      })
    },

    viewMyIncome() {
      this.nodeUserIncomeVisible = true

      nodeService.getIncomes().then(res => {
        if (res.code == 0) {
          this.nodeIncomes = res.data.incomes
        }
      })
    },

    viewBonus() {
      this.nodeBonusVisible = true

      this.eosio.getTableRows(
        {
          code: this.divdContract,
          scope: this.divdContract,
          table: 'bonuslog',
          limit: 100,
          reverse: true,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          var isSuccess = false
          if (code == 0) {
            var nodeBonus = []
            for (var i in data.rows) {
              var row = data.rows[i]
              nodeBonus.push(row)
            }
            nodeBonus.sort(function(a, b) {
              return a.timestamp - b.timestamp > 0 ? -1 : 1
            })
            this.nodeBonus = nodeBonus
          }
        }.bind(this)
      )

      return

      nodeService.getBonus().then(res => {
        if (res.code == 0) {
          this.nodeBonus = res.data.bonus
        }
      })
    },

    getOnesBalance(cb) {
      if (!this.eosio) {
        return
      }
      this.eosio.getBalance(
        'eosonestoken',
        'ONES',
        function(balance) {
          console.log('.......', balance)
          this.onesBalance = balance
        }.bind(this)
      )
    },

    confirmNode(amount) {
      amount = Number(amount).toFixed(4) + ' ONES'
      this.confirmNodeLoading = true
      var actions = [
        {
          account: 'eosonestoken',
          name: 'transfer',
          authorization: [
            {
              actor: this.eosio.account.name,
              permission: this.eosio.account.authority
            }
          ],
          data: {
            from: this.eosio.account.name,
            to: this.divdContract,
            quantity: amount,
            memo: 'stake'
          }
        }
      ]
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          this.confirmNodeLoading = false
          if (code == 0) {
            this.nodeSubscribeVisible = false
            this.$message({
              message: '认购成功',
              type: 'success',
              center: true
            })
            this.getNodeDetail()
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )

      return
      nodeService.subscribe(amount).then(res => {
        if (res.code == 0) {
          this.nodeSubscribeVisible = false
          this.$message({
            message: '认购成功',
            type: 'success',
            center: true
          })
          this.getNodeDetail()
        } else {
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'error',
            center: true
          })
        }
      })
    },

    confirmRedeem(row) {
      var stake_id = row.stake_id

      this.unstakeLoading = true
      var actions = [
        {
          account: this.divdContract,
          name: 'unstake',
          authorization: [
            {
              actor: this.eosio.account.name,
              permission: this.eosio.account.authority
            }
          ],
          data: {
            account: this.eosio.account.name,
            stake_id: stake_id
          }
        }
      ]
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          this.unstakeLoading = false
          this.nodeRedeemVisible = false
          if (code == 0) {
            this.nodeSubscribeVisible = false
            this.$message({
              message: '赎回成功',
              type: 'success',
              center: true
            })
            this.getNodeDetail()
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )

      return
      var amount = row.amount
      var order_id = row.order_id
      nodeService.reddem(num).then(res => {
        this.nodeRedeemVisible = false
        if (res.code == 0) {
          this.$message({
            message: '赎回成功',
            type: 'success',
            center: true
          })
          this.getNodeDetail()
        } else {
          this.$message({
            message: msg,
            type: 'error',
            center: true
          })
        }
      })
    },

    cancelRedeem() {
      this.nodeRedeemVisible = false
    },

    getNodeDetail() {
      this._accounts = {}
      this._getNodeList()
      this._getNodeInfo()
    },

    _getNodeInfo() {
      var that = this
      this.eosio.getTableRows(
        {
          code: this.divdContract,
          scope: this.divdContract,
          table: 'config',
          limit: 1,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (data && data.rows && data.rows.length == 1) {
            var row = data.rows[0]
            that.totalBonus = row.bonus_quantity
            that.totalIncome = row.reward_quantity
            that.totalStaked = row.stake_quantity
            that.totalRetire = row.retire_quantity
            that.expectedRetire =
              Number(parseFloat(row.stake_quantity) * 0.2).toFixed(4) + ' ONES'

            that._getOnesPrice()
          }
        }
      )
    },

    _getOnesPrice() {
      var that = this
      this.eosio.getTableRows(
        {
          code: this.defiContract,
          scope: this.defiContract,
          table: 'liquidity',
          limit: 1,
          key_type: 'i64',
          lower_bound: 3,
          json: true
        },
        function(code, msg, data) {
          if (
            data.rows &&
            data.rows.length == 1 &&
            data.rows[0]['liquidity_id'] == 3
          ) {
            var price = data.rows[0]['price1']
            that.annualized =
              Number(
                ((parseFloat(that.totalBonus) * 0.01) /
                  parseFloat(that.totalStaked)) *
                  365 *
                  100 *
                  price
              ).toFixed(2) + '%'
          }
        }
      )
    },

    _getNodeList(lower_bound) {
      if (!lower_bound) {
        this._nodeRankList = []
      }
      var that = this
      this.eosio.getTableRows(
        {
          code: this.divdContract,
          scope: this.divdContract,
          table: 'accounts',
          limit: 100,
          lower_bound: lower_bound,
          index_position: 1,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          for (var i in data.rows) {
            var row = data.rows[i]
            row.stake_amount = parseFloat(row.stake_quantity)
            if (!that._accounts[row.account]) {
              that._nodeRankList.push(row)
              that._accounts[row.account] = row
            }

            lower_bound = row.account
          }
          if (data.more) {
            setTimeout(function() {
              that._getNodeList(lower_bound)
            })
          } else {
            var rows = []
            for (var i = that._nodeRankList.length - 1; i >= 0; i--) {
              rows.push(that._nodeRankList[i])
            }
            rows.sort(function(a, b) {
              var s1 = parseInt(10000 * parseFloat(a.stake_quantity))
              var s2 = parseInt(10000 * parseFloat(b.stake_quantity))
              if (s1 == s2) {
                return a.account > b.account ? -1 : 1
              } else {
                return s1 - s2 > 0 ? -1 : 1
              }
            })

            var myNode = {
              stake_quantity: 0.0
            }
            var _rows = []
            for (var i in rows) {
              rows[i]['rank'] = parseInt(i) + 1

              if (rows[i]['account'] == that.account.name) {
                myNode = rows[i]
              }
              // console.log(rows[i])
              if (rows[i]['stake_amount'] > 0) {
                _rows.push(rows[i])
              }
            }
            if (!myNode.rank) {
              myNode.rank = _rows.length + 1
            }
            that.myNode = myNode
            that.nodeRankList = _rows
          }
        }
      )
    },

    openRedeemDialog(row) {
      this.nodeRedeemVisible = true
      this.redeemNode = row
      // console.log(row)
      var curtime = parseInt(new Date() / 1000)
      var difftime = curtime - row.timestamp
      if (difftime < 30 * 86400) {
        this.redeemNode.retire = 20
      } else if (difftime >= 30 * 86400 && difftime < 90 * 86400) {
        this.redeemNode.retire = 10
      } else if (difftime >= 90 * 86400 && difftime < 180 * 86400) {
        this.redeemNode.retire = 5
      } else if (difftime > 180 * 86400) {
        this.redeemNode.retire = 0
      }

      this.redeemNode.retireAmount = Number(
        ((1.0 * this.redeemNode.retire) / 100) * parseFloat(row.quantity)
      ).toFixed(4)
    },

    formatterTime(row, column, cellValue, index) {
      return Util.formatDate(cellValue, 'MM-dd hh:mm')
    },
    formatterDay(row, column, cellValue, index) {
      return Util.formatDate(cellValue, 'yyyy-MM-dd')
    }
  },
  filters: {
    accountFilter(value) {
      var len = value.length
      var str = ''
      for (var i in value) {
        if (i >= len / 3 && i < (len * 2) / 3) {
          str += '*'
        } else {
          str += value[i]
        }
      }
      return str
    },
    quantityFilter(value) {
      return Number(parseFloat(value)).toFixed(4)
    },
    timeFilter(value) {
      return Util.formatDate(value, 'MM-dd hh:mm:ss')
    },
    slippageFilter(value) {
      return parseFloat(value * 100.0).toFixed(2) + '%'
    }
  }
}
</script>

