      width="95%"
      class="share-rule-dialog"
    >
      <h6>如何获得推荐奖励？</h6>
      <p>分享链接邀好友。若新用户从您的链接登录应用，则推荐成功。</p>
      <h6>推荐奖励比例是多少？</h6>
      <p>推荐奖励总共为10%，用户获得比例最低是1%,最高10%，VIP等级越高比例越高，按照邀请人和被邀请人等级最高来计算</p>
      <h6>推荐基金是多少？</h6>
      <p>当玩家获得比例不足10%时，会把剩余部分存入推荐基金中，推荐基金用于奖励每周推荐值最高的玩家(详细规则待定)</p>
      <h6>推荐值如何计算？</h6>
      <p>推荐值分为两部分，推荐人数和推荐消费金额，两者加权求和。推荐1个记1分，最高100分，推荐玩家消费1EOS记1分，不设上限</p>

