<script>
import InviteService from '../../api/invite'

export default {
  data() {
    return {
      activeEosTab: 'invite',
      zone: this.$store.getters.zone,
      shareDialogVisible: false,

      imgUrl: 'https://play.ones.game/qrcode/qrcode.png?v=0.1',
      qrcodeUrl: ['https://play.ones.game/qrcode/qrcode.png?v=0.1'],
      islogin: this.$store.state.account.isLogin,
      user: {
        last_user_number: 0,
        user_number: 0,
        incomes: { dt: 0, usdt: 0, coin: 0 },
        last_incomes: { dt: 0, usdt: 0, coin: 0 }
      },
      recommend_fund: 0,
      tabPosition: 1,
      invitationCode: '',
      invitationLink: '',
      invites: [],
      rebates: [],
      invitationRanking: [
        {
          id: '7772347390',
          level: '8',
          consumption: '8888ONES',
          reward: '874390'
        }
      ]
    }
  },
  methods: {
    copyShaneUrl(shareLink) {
      var _input = document.createElement('input')
      if (this.zone == 'eos') {
        _input.value = this.$t('shareScene.eosShareTips') + shareLink
      } else {
        _input.value = this.$t('shareScene.shareTips') + shareLink
      }
      document.body.appendChild(_input)
      _input.select()
      document.execCommand('Copy')
      document.body.removeChild(_input)
      this.$message({
        message: this.$t('message.copy_success'),
        type: 'success',
        center: true
      })
    },
    handleTabClick(tab, event) {
      console.log(tab, event)

      if (tab.name == 'rebate') {
        this.getRebates()
      }
    },
    shareBtnClick() {
      console.log(this.$refs.shareBtn1.style)
      // shareBtn1
    },

    getRebates() {
      console.log('1111')
      InviteService.rebates().then(res => {
        if (res.code == 0) {
          this.rebates = res.data.rebates
        }
      })
    },

    claimRebate(row) {
      var order_id = row.order_id
      InviteService.claim(order_id).then(res => {
        if (res.code == 0) {
          this.getRebates()
          this.$message({
            message: '领取成功',
            type: 'success',
            center: true
          })
        } else {
          this.$message({
            message: '领取失败',
            type: 'warning',
            center: true
          })
        }
      })
    }
  },

  mounted() {
    var ones_uid = this.$store.state.account.user
      ? this.$store.state.account.user.ones_uid
      : ''
    this.invitationCode = ones_uid
    if (this.zone == 'eos') {
      this.invitationLink = 'https://eos.ones.game' + '?ref=' + ones_uid
    } else {
      this.invitationLink =
        'https://play.ones.game' + '/account/register?ref=' + ones_uid
    }
    if (this.islogin) {
      InviteService.detail().then(res => {
        if (res.code == 0) {
          this.user = res.data.user
          this.invites = res.data.invites

          this.recommend_fund = res.data.recommend_fund
        }
      })

      var path = parseInt(parseInt(ones_uid) / 1000)

      var url =
        'https://play.ones.game/qrcode/img/' + path + '/' + ones_uid + '.png'
      if (path > 1002) {
        url += '?uid=' + path
      }
      this.qrcodeUrl = [url]
      console.log(this.qrcodeUrl)
    }
  }
}
</script>

