  methods: {
    handleClick(tab, event) {
      console.log(tab, event)
    }
  },
  mounted() {
    if (this.zone == 'eos') {
      this.coin_code = 'EOS'
    }
    VipService.detail().then(res => {
      if (res.code == 0) {
        this.user.vip_amount = parseFloat(res.data.user.vip_amount)
        this.user.vip_level = res.data.user.vip_level
      }
    })
  },
  filters: {
    percentFilter(value, level) {
      // 0 1 599 999 2999 7500 10000 30000 50000 70000 100000
      var amountNumList = [
        0,
        9.9,
        199,
        499,
        999,
        1999,
        4999,
        9999,
        19999,
        29999,
        49999
      ]
      var _amount = amountNumList[level]
      var _lastAmount = amountNumList[level + 1]
      var _value = (value - _amount) / (_lastAmount - _amount)
      return Number(parseFloat(_value * 100).toFixed(2))
    },
    imbalanceFilter(value, level) {
      var amountNumList = [
        0,
        9.9,
        199,
        499,
        999,
        1999,
        4999,
        9999,
        19999,
        29999,
        49999
      ]
      var _amount = amountNumList[level + 1]
      return parseFloat(_amount - value).toFixed(2)
    },
    imgFilter(value) {
      var _value = value
      var _src = require('../../assets/grade/' + _value + '.png')
      return _src
    }
  }
}
