<template>
  <div class="container">
    <div class="erc20deposit">
      <!-- Erc20充值 -->
      <div class="charge-url">
        <div class="title">{{coin_code}}充值地址(ERC20)</div>
        <div class="url">{{coin_address}}</div>

        <input class="erc20_address" type="text" :value="coin_address" id="erc20-address" />
        <div class="copy-btn">
          <el-button type="primary" @click="copy">{{this.$t("assetScene.copy")}}</el-button>
        </div>
      </div>

      <div class="QRcode">
        <qrcode-vue :value="coin_address" :size="qrcode_size" level="H"></qrcode-vue>
      </div>

      <div class="current">
        当前账户余额
        <span>{{coin_amount}} {{coin_code}}</span>
      </div>

      <div class="erc20deposit-tip">
        <p>{{this.$t("assetScene.depositTips1")}}</p>
        <p>{{this.$t("assetScene.depositTips2",{coin_code:coin_code})}}</p>
        <p>{{this.$t("assetScene.depositTips3",{coin_code:coin_code})}}</p>
        <p
          class="erc20-tips"
        >{{this.$t("assetScene.depositTips4",{coin_code:coin_code,reward_amount:reward_amount,deposit_amount:deposit_amount})}}</p>
        <p class="erc20-tips">{{this.$t("assetScene.depositTips5")}}</p>
        <p class="erc20-tips">{{this.$t("assetScene.depositTips6")}}</p>
        <p class="erc20-tips">{{this.$t("assetScene.depositTips66")}}</p>
      </div>
    </div>

    <div class="deposit-items">
      <div class="title">{{this.$t("assetScene.depositRecord")}}</div>

      <div
        class="title"
        v-if="depositRecords && depositRecords.length==0"
      >{{this.$t("assetScene.noDepositRecord")}}</div>

      <ul class="infinite-list" style="overflow:auto">
        <li v-for="(item,index) in depositRecords">
          <div class="item-text">
            <span>{{$t("assetScene.amount")}}</span>
            <span>{{Number(item.amount/10000).toFixed(2)}}{{coin_code}}</span>
          </div>
          <div class="item-text">
            <span>{{$t("assetScene.time")}}</span>
            <span>{{item.create_time}}</span>
          </div>
          <div class="item-text">
            <span>{{$t("assetScene.status")}}</span>
            <span>{{item.status==1?$t("assetScene.status_success"):$t("assetScene.status_waiting")}}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import AssetService from '../../../api/asset'
import QrcodeVue from 'qrcode.vue'

export default {
  components: {
    QrcodeVue
  },
  data() {
    return {
      deposit_amount: 100,
      reward_amount: '3',
      coin_code: 'USDT',
      coin_amount: 0,
      coin_address: '',
      qrcode_size: 150,
      form: {},

      depositRecords: []
    }
  },
  mounted: function() {
    //action=deposit
    var coin_code = this.$route.params.coin_code
    var coin_amount = this.$route.params.coin_amount
    if (coin_code) {
      this.coin_code = coin_code.toUpperCase()
      this.coin_amount = Number(coin_amount).toFixed(4)

      if (this.coin_code == 'DT') {
        this.reward_amount = '1.0'
        this.deposit_amount = '100.00'
      } else if (this.coin_code == 'USDT') {
        this.reward_amount = '3.0'
        this.deposit_amount = '100.00'
      } else if (this.coin_code == 'ZTB') {
        this.reward_amount = '5.0'
        this.deposit_amount = '100.00'
      } else {
        this.reward_amount = '1.0'
        this.deposit_amount = '100.00'
      }
    }
    this.getBalances()
    this.getTransactions()
  },
  methods: {
    getBalances() {
      AssetService.balance().then(res => {
        if (res.code == 0) {
          this.balances = res.data.erc20_balances

          this.coin_address = res.data.eth_address

          console.log(this.coin_code)
          var key = this.coin_code.toLowerCase()
          this.coin_amount = Number(this.balances[key]).toFixed(4)
        }
      })
    },
    getTransactions() {
      AssetService.transactions(this.coin_code, 'deposit').then(res => {
        if (res.code == 0) {
          this.depositRecords = res.data.txs
        }
      })
    },

    copy() {
      var NumClip = document.getElementById('erc20-address')
      var NValue = NumClip.value
      var valueLength = NValue.length
      this.selectText(NumClip, 0, valueLength)
      if (document.execCommand('copy', false, null)) {
        document.execCommand('copy', false, null)
      }
      this.$message({
        message: this.$t('message.copy_success'),
        type: 'success',
        center: true
      })
    },

    selectText(textbox, startIndex, stopIndex) {
      if (textbox.createTextRange) {
        //ie
        var range = textbox.createTextRange()
        range.collapse(true)
        range.moveStart('character', startIndex) //起始光标
        range.moveEnd('character', stopIndex - startIndex) //结束光标
        range.select() //不兼容苹果
      } else {
        //firefox/chrome
        textbox.setSelectionRange(startIndex, stopIndex)
        textbox.focus()
      }
    }
  }
}
</script>

<style scoped>
ul,
li {
  list-style: none;
}
.container {
  width: 100%;
  /* height: 100vh; */
  margin-top: 45px;
  box-sizing: border-box;
  overflow: hidden;
}

.tabBox {
  position: relative;
}
.content {
  word-break: break-all;
  width: 100%;
  float: left;
  color: #d3d3d3;
  font-size: 1.3rem;
  font-family: 'Arial Black';
}
.content.active {
  display: block !important;
}
.charge-url {
  width: 100%;
  border-bottom: 1px solid #ccc;
  margin-top: 20px;
}
.charge-url .title {
  /* line-height: 4vh; */
  text-align: center;
  font-family: 'Arial Black';
}
.charge-url .url {
  line-height: 4vh;
  text-align: center;
  color: #9db0c1;
  font-weight: 800;
}

.charge-url input {
  opacity: 0;
}

.charge-url .copy-btn {
  text-align: center;
  margin-bottom: 10px;
  margin-top: -10px;
}
.erc20deposit-tip {
  padding: 10px 15px;
  line-height: 22px;
  font-size: 12px;
}

.erc20deposit-tip .erc20-tips {
  color: #e64c65;
}

.QRcode {
  width: 150px;
  height: 150px;
  margin: 0 auto;
  margin-top: 1vh;
  background-color: #fff;
  overflow: hidden;
}

.QRcode img {
  width: 100%;
  height: 100%;
}
.erc20deposit .current {
  line-height: 3vh;
  margin-top: 0vh;
  font-size: 0.9rem;
  color: #888;
  text-align: right;
  padding: 5px 20px;
}
.erc20deposit .current span {
  color: #ffc02d;
  font-size: 0.9rem;
  font-weight: 800;
}

.deposit-items {
  border-top: 1px solid #ccc;
  margin-top: 10px;
  background: #f5f5f5;
}
.deposit-items .title {
  text-align: center;
  margin-bottom: 10px;
  margin-top: 10px;
}
.deposit-items li {
  margin-bottom: 0.7rem;
  box-sizing: border-box;
  color: #606266;
  padding: 2% 3%;
  background-color: #fff;
}
.deposit-items li .top {
  font-size: 1.2rem;
}
.deposit-items li .top span {
  float: right;
  font-size: 1rem;
}
.deposit-items .item-text {
  display: flex;
  justify-content: space-between;
  margin-top: 0.1rem;
  font-size: 1rem;
}

.deposit-items .item-text span:nth-child(1) {
  color: #303133;
}
.deposit-items .item-text span:nth-child(2) {
  font-size: 1.1rem;
  font-weight: 500;
}
</style>


