<template>
  <div class="defiContainer" ref="defiContainer">
    <div class="defiContent">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>Ones是专业的游戏DeFi应用平台。目前Ones已推出两个功能，分别为OnesPlay游戏平台、OnesSwap闪兑，未来还将推出更多协议，如：众筹发行、稳定币、去中心化借贷、ERC721交易等。</span>
        </div>
        <el-steps direction="vertical" :active="3">
          <el-step
            title="OnesPlay"
            icon="el-icon-news"
            description="OnesPlay游戏平台为玩家提供好玩有趣的游戏，目前平台拥有上百款游戏，包括养成类、塔防、竞技类、棋牌类、对战类、卡牌类等众多游戏类型。"
          ></el-step>
          <el-step
            title="OnesSwap"
            icon="el-icon-news"
            description="Swap闪兑是去中心化代币兑换协议与自动做市协议（AMM），协议通过恒定乘积算法实时提供兑换价格，用户可以即时完成代币兑换。除了代币兑换，用户还可以自由创建兑换市场，也可以选择加入已创建的市场成为做市商，为市场提供流动性（按比例存入两种代币），做市商根据流动性占比，自动赚取交易用户所支付的手续费。"
          ></el-step>
          <el-step
            title="众筹发行、去中心化借贷、ERC721交易"
            icon="el-icon-news"
            description="未来计划开发众筹发行、去中心化借贷、ERC721闪兑，以此形成游戏DeFi协议矩阵，构建一站式游戏DeFi应用平台。"
          ></el-step>
        </el-steps>

        <el-divider></el-divider>

        <div style="text-align:center;">
          <el-link
            :underline="false"
            target="_blank"
            href="https://cdn.jsdelivr.net/gh/onesgame/onesgame.github.io/ONES-GAME-DeFi-Whitepaper-V1.0.pdf"
          >
            详细请查看白皮书
            <i class="el-icon-view el-icon--right"></i>
          </el-link>
        </div>
      </el-card>
      <el-divider>超级节点计划奖励</el-divider>
      <el-card class="box-card token-card">
        <div class="title">已全面开启</div>
        <p>1)奖池来源：a)游戏收入的50%用于分红 b)闪对手续费0.1%用于分红</p>
        <p>2)奖励规则: 使用ONES认购参与，前100名的玩家将会获得超级节点奖池奖励。详见超级节点计划页面（开启前将合约化）</p>
      </el-card>
      <el-divider>OnesToken</el-divider>

      <el-card class="box-card token-card">
        <div>ONES在EOS主链发行ONES代币，发行总量为1千万，无预留无预挖，团队0占比。</div>
        <div>
          <div class="title">1.OnesPlay</div>
          <p>分配160万游戏兑换，40万作为市场推广、运营、糖果、空投等。</p>
        </div>
        <div>
          <div class="title">2.Swap闪兑</div>
          <p>分配300万ONES用于Swap闪兑做市挖矿。</p>
          <p class="subtitle">释放规则：</p>
          <p>每秒释放0.02 ONES约3年释放结束，用于交易挖矿</p>
          <p>每秒释放0.01 ONES约3年释放结束，用于做市挖矿</p>
          <p>具体释放规则详见交易做市挖矿页面</p>
        </div>
        <div>
          <div class="title">3.其它挖矿</div>
          <p>分配500万ONES用于其他形式的挖矿，挖矿开始前这500万ONES处于锁定状态。包含稳定币、去中心化借贷等。ONES基金会有权根据实际需要调整挖矿分配规则。</p>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import Storage from '../../utils/storage'
import i18n from '../../locales/index'
import Util from '../../utils/util'

export default {
  data() {
    return {
      zone: this.$store.getters.zone
    }
  },
  mounted() {},
  watch: {},
  methods: {}
}
</script>

<style scoped>
.defiContainer {
  background-color: #060721;
  position: relative;
  color: #fff;
}
.token-card .title {
  font-size: 20px;
  font-weight: 500;
  line-height: 40px;
}
.token-card .subtitle {
  color: #f90;
}
.token-card p {
  line-height: 30px;
}
.defiContent /deep/ .el-card {
  color: #fff;
  background-color: #060721;
}
.defiContent /deep/ .el-divider__text {
  /* background-color: rgb(235, 233, 233); */
  background-color: #060721;
  color: #fff;
}

.el-step /deep/ .el-step__description.is-finish {
  color: rgb(237, 240, 245);
  margin-bottom: 10px;
}

.defiContent /deep/ .el-step__icon {
  background-color: #16195B;
}
</style>


