      liquidity_token: 0,
      all_liquidity_token: 0,

      tokens: {},
      tokensrank: {
        'eosio.token-eos': 100,
        'tethertether-usdt': 99,
        'crayfishgold-xw': 99,
        'eosonestoken-ones': 98,
        'lobstertoken-xo': 97,
        'btc.ptokens-pbtc': 97,
        'minedfstoken-dfs': 96,
        'token.defi-box': 95,
        'eosiotptoken-tpt': 94
      },
      blacks: {
        crayfishball: 1,
        onesgametest: 1
      },
      _tokens: [],
      pairs: {},
      liquidites: {},
      searchtoken: '',
      balances: {},

      tokenForm: {
        symbol: '',
        address: ''
      },
      tokenFormRules: {
        symbol: [
          { required: true, message: '请输入币种名称', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '请输入合约名称', trigger: 'blur' },
          { max: 12, message: '输入有误,请重新输入', trigger: 'blur' }
        ]
      },
      swapLoading: false,

      swapLogs: [],

      lines: {},
      path: [],
      hasMinPath: false,
      hasMultiPath: false,
      multiPaths: [],

      //mine
      myPrevMineOnes: 0,
      myCurMineOnes: 0,
      minePool: null,

      inputType: 'in',

      bonusQuantity: '0.0000 EOS',
      bonusStaked: '0.0000',
      bonusRate: '0.00',

      inputing: false
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },
    account: function() {
      return this.$store.state.eos_account
    }
  },
  watch: {
    searchtoken(value) {
      var _value = value.toUpperCase()
      for (var i in this.tokens) {
        // console.log(this.tokens[i]['ishidden'],this.tokens[i]['symbol'],'-------1')
        var ishidden =
          this.tokens[i]['symbol'].indexOf(_value) === -1 ? true : false
        // console.log(ishidden,'-----2')
        this.tokens[i]['ishidden'] = ishidden
        var _new = this.tokens[i]
        Vue.set(this.tokens, i, _new)
        // console.log(this.tokens[i]['ishidden'],'-------3')
      }
    },

    tokens(value) {
      console.log('xxxxxxxxxx', value)
      var _tokens = value
      for (var i in _tokens) {
        _tokens[i]['rank'] = 0
      }
      for (var i in this.tokensrank) {
        if (_tokens[i]) {
          _tokens[i]['rank'] = this.tokensrank[i]
        }
      }
      var _t = []
      for (var i in _tokens) {
        _t.push(_tokens[i])
      }
      _t.sort(function(a, b) {
        return a.rank - b.rank > 0 ? -1 : 1
      })
      this._tokens = _t
    },

    inToken(val1, val2) {
      this.getBalance(this.inToken.address, this.inToken.symbol)
    },
    outToken(val1, val2) {
      this.getBalance(this.outToken.address, this.outToken.symbol)
    },

    eosio(val1, val2) {
      console.log('11111......')
    },
    account(val1, val2) {
      this.getMyMine()
    }
  },
  beforeMount() {
    var inToken = Storage.get('defi_swap_intoken')
    var outToken = Storage.get('defi_swap_outtoken')
    if (inToken && inToken.address != 'onesgametest') {
      // if (inToken) {
      this.inToken = inToken
    }
    if (outToken) {
      this.outToken = outToken
    }
  },
  mounted() {
    this.getLiquidities(0)
    this.getSwapPool()
    this.getDivdTable()

    // var key = Storage.get('ones_notify')
    // if (key != '003') {
    // Storage.set('ones_notify', '003')
    // this.noticeDialogVisible = true
    // this.$notify({
    //   title: '挖矿合约升级公告',
    //   type: 'success',
    //   message: '挖矿合约(onesgamemine)升级将于今日19:00-20:00之间升级,支持做市挖矿BOX和DFS、升级过程中会导致挖矿短暂暂停',
    //   duration: 0
    // })
    // }

    clearInterval(this.liquidityInterval)
    this.liquidityInterval = setInterval(
      function() {
        this.getLiquidities(0)
      }.bind(this),
      5000
    )
  },

  beforeDestroy() {
    this.swap_switch = 0
    this.mine_switch = 0
    clearInterval(this.liquidityInterval)
  },

  methods: {
    checkReport() {
      this.$router.push({
        name: 'defiContract'
      })
    },
    goPool() {
      this.$router.push({
        name: 'defiPool',
        params: {
          liquidity_id: this.cur_liquidity_id
        }
      })
    },
    changeSlippage(val) {
      this.defaultSlippage = parseInt(val)
      console.log(val, 'xxxxxxxx')
      Storage.set('ones_slippage', val)
    },
    refresh() {
      this.getLiquidities(0)
      this.getBalance(this.inToken.address, this.inToken.symbol)
      this.getBalance(this.outToken.address, this.outToken.symbol)

      this.getSwapPool()
      this.getDivdTable()
    },
    swap() {
      if (
        !(
          parseFloat(this.inTokenAmount) > 0 &&
          parseFloat(this.outTokenAmount) > 0
        )
      ) {
        this.$message({
          message: '请输入兑换数量',
          type: 'error',
          center: true
        })
        return
      }

      // if (this.slippage > this.defaultSlippage / 100.0) {
      //   this.$message({
      //     message: '当前滑点超过滑点保护值',
      //     type: 'error'
      //   })
      //   return
      // }
      var third_id =
        parseInt(new Date() / 1) + '' + parseInt(10000 * Math.random())
      third_id = parseInt(third_id.slice(4))
      var memo =
        'swap,' +
        third_id +
        ',' +
        this.defaultSlippage +
        ',' +
        this.cur_liquidity_id
      if (this.hasMultiPath) {
        memo =
          'swap,' +
          third_id +
          ',' +
          this.defaultSlippage +
          ',' +
          this.liquidity_ids.join('-')
      } else {
        if (this.mine_switch) {
          memo += '-' + this.cur_liquidity_id
        }
      }
      var actions = [
        {
          account: this.inToken.address,
          name: 'transfer',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            from: this.account.name,
            to: this.defiContract,
            quantity: this.inTokenAmount + ' ' + this.inToken.symbol,
            memo: memo
          }
        }
      ]
      this.swapLoading = true
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          if (code == 0) {
            this.getSwapResult(third_id)
          } else {
            this.swapLoading = false
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },

    getSwapResult(third_id) {
      this._getSwapResult(
        third_id,
        0,
        function(code, msg, data) {
          this.swapLoading = false
          if (code == 0) {
            this._getMyMine(
              function(code) {
                var amount = this.myCurMineOnes - this.myPrevMineOnes

                if (amount > 0) {
                  this.$message({
                    message:
                      '兑换成功',//,获得' + Number(amount).toFixed(4) + ' ONES',
                    center: true,
                    type: 'success'
                  })
                  this.changeHandle()
                } else {
                  this.$message({
                    message: '兑换成功',
                    center: true,
                    type: 'success'
                  })
                }

                if (this.swap_switch) {
                  setTimeout(
                    function() {
                      this.swap()
                    }.bind(this),
                    2000
                  )
                }
              }.bind(this)
            )
            this.refresh()
          } else if (code == 100) {
            this.$message({
              message: '兑换成功',
              center: true,
              type: 'success'
            })
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },

    _getSwapResult(third_id, counter, cb) {
      this.eosio.getTableRows(
        {
          code: this.logsContract,
          scope: this.logsContract,
          table: 'swaplog',
          limit: 1,
          lower_bound: third_id,
          index_position: 2,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          var isSuccess = false
          if (code == 0 && data.rows && data.rows.length == 1) {
            if (data.rows[0].third_id == third_id) {
              isSuccess = true
            }
          }
          if (isSuccess) {
            cb(0, '', data.rows[0])
          } else {
            if (counter > 10) {
              cb(100, 'timeout')
            } else {
              setTimeout(
                function() {
                  this._getSwapResult(third_id, ++counter, cb)
                }.bind(this),
                1000
              )
            }
          }
        }.bind(this)
      )
    },

    inputBalance(type) {
      if (type == 'in') {
        this.inTokenAmount = this.balances[this.inToken.key]
          ? this.balances[this.inToken.key]
          : 0
      } else {
        this.outTokenAmount = this.balances[this.outToken.key]
          ? this.balances[this.outToken.key]
          : 0
      }
      this.changeHandle(type)
    },
    inputHandle(type) {
      this.isinputing = true
      this.updateTokenAmount(type)
    },

    changeHandle(type) {
      if (!type && this.isinputing) {
        return
      }
      this.isinputing = false

      type = type ? type : this.inputType
      this.inputType = type
      if (this.all_liquidity_token > 0) {
        this.updateTokenAmount(type)

        this.inTokenPrice = Number(
          parseFloat(this.outTokenAmount) / parseFloat(this.inTokenAmount)
        ).toFixed(6)
        this.outTokenPrice = Number(
          parseFloat(this.inTokenAmount) / parseFloat(this.outTokenAmount)
        ).toFixed(6)

        this.slippage = 1 - this.inTokenPrice / this.inToken.price

        var inTokenAmount = parseFloat(this.inTokenAmount)
        var quantity = this.inToken.quantity
        var alpha = inTokenAmount / (inTokenAmount + quantity)

        this.add_liquidity_token = parseInt(
          (alpha / (1 - alpha)) * this.all_liquidity_token
        )
        this.inTokenAmount = Number(this.inTokenAmount).toFixed(
          this.inToken.precision
        )
        this.outTokenAmount = Number(this.outTokenAmount).toFixed(
          this.outToken.precision
        )

        this.miningProfit = 0
        if (!this.hasMultiPath) {
          var weight =
            this.curliquidity['weight'] > 0 ? this.curliquidity['weight'] : 0
          if (weight > 0 && this.minePool) {
            var curtime = parseInt(new Date() / 1000)
            var swap_quantity =
              0.02 * (curtime - this.minePool.swap_time) +
              parseFloat(this.minePool.swap_quantity / 10000)
            var amount = 0
            if (this.inToken.address == this.eosTokenAddress) {
              amount = this.inTokenAmount
            } else {
              amount = this.outTokenAmount
            }
            amount = parseInt(amount)
            var miningProfit = 0

            for (var i = 0; i < amount; i++) {
              miningProfit += swap_quantity * 0.0001 * weight
              swap_quantity -= swap_quantity * 0.0001 * weight
            }
            this.miningProfit = Number(miningProfit).toFixed(4)
            console.log(this.miningProfit)
          }
        }
      }
    },

    updateTokenAmount(type) {
      if (this.all_liquidity_token > 0) {
        if (!this.hasMultiPath) {
          if (type == 'in') {
            var inTokenAmount = (1 - this.funddivdFee) * this.inTokenAmount
            var alpha =
              (1.0 * inTokenAmount) / parseFloat(this.inToken.quantity)
            var r = 1 - this.marketFee
            var outTokenAmount =
              ((1.0 * (alpha * r)) / (1 + alpha * r)) *
              parseFloat(this.outToken.quantity)

            this.outTokenAmount = Number(outTokenAmount).toFixed(
              this.outToken.precision
            )
          } else {
            var r = 1 - this.marketFee
            var alpha =
              (this.outTokenAmount * 1.0) /
              (r * (parseFloat(this.outToken.quantity) - this.outTokenAmount))
            var inTokenAmount = alpha * parseFloat(this.inToken.quantity)
            inTokenAmount = inTokenAmount / (1 - this.funddivdFee)

            this.inTokenAmount = Number(inTokenAmount).toFixed(
              this.inToken.precision
            )
          }
        } else {
          //multiPaths
          if (type == 'in') {
            var inTokenAmount = this.inTokenAmount
            var outTokenAmount = 0
            for (var i = 1; i < this.multiPaths.length; i++) {
              var key1 = this.multiPaths[i - 1]['key']
              var key2 = this.multiPaths[i]['key']

              var liquidity_id = this.pairs[key1 + '|' + key2]
              var liquidity = this.liquidites[liquidity_id]

              var inTokenQuantity =
                liquidity.key1 == key1
                  ? parseFloat(liquidity.quantity1)
                  : parseFloat(liquidity.quantity2)
              var outTokenQuantity =
                liquidity.key2 == key2
                  ? parseFloat(liquidity.quantity2)
                  : parseFloat(liquidity.quantity1)

              var alpha = (1.0 * inTokenAmount) / parseFloat(inTokenQuantity)
              var r = 1 - this.marketFee
              outTokenAmount =
                ((1.0 * (alpha * r)) / (1 + alpha * r)) *
                parseFloat(outTokenQuantity)
              inTokenAmount = outTokenAmount
            }
            this.outTokenAmount = Number(outTokenAmount).toFixed(
              this.outToken.precision
            )
          } else {
            var outTokenAmount = this.outTokenAmount
            var inTokenAmount = 0
            for (var i = 1; i < this.multiPaths.length; i++) {
              var key1 = this.multiPaths[i - 1]['key']
              var key2 = this.multiPaths[i]['key']

              var liquidity_id = this.pairs[key1 + '|' + key2]
              var liquidity = this.liquidites[liquidity_id]

              var outTokenQuantity =
                liquidity.key2 == key2
                  ? parseFloat(liquidity.quantity2)
                  : parseFloat(liquidity.quantity1)
              var inTokenQuantity =
                liquidity.key1 == key1
                  ? parseFloat(liquidity.quantity1)
                  : parseFloat(liquidity.quantity2)

              var alpha = (1.0 * outTokenAmount) / parseFloat(outTokenQuantity)
              var r = 1 - this.marketFee
              inTokenAmount =
                ((1.0 * (alpha * r)) / (1 + alpha * r)) *
                parseFloat(inTokenQuantity)
              outTokenAmount = inTokenAmount
            }
            this.inTokenAmount = Number(inTokenAmount).toFixed(
              this.inToken.precision
            )
          }
        }
      }
    },

    switchToken() {
      var token = this.inToken
      this.inToken = this.outToken
      this.outToken = token
      this.inTokenPrice = this.inToken.price
      this.outTokenPrice = this.outToken.price

      if (parseFloat(this.inTokenAmount) > 0) {
        this.inTokenAmount = this.outTokenAmount
        this.changeHandle('in')

        this.inoutOrder = true
      }
      Storage.set('defi_swap_intoken', this.inToken)
      Storage.set('defi_swap_outtoken', this.outToken)
    },

    getMyMine() {
      if (this.account) {
        this._getMyMine()
        this.getBalances(0)
      }
    },

    getSwapPool() {
      this.eosio.getTableRows(
        {
          code: this.mineContract,
          scope: this.mineContract,
          table: 'config',
          limit: 1,
          json: true
        },
        function(code, msg, data) {
          if (code == 0 && data.rows && data.rows.length >= 1) {
            this.minePool = data.rows[0]
          }
        }.bind(this)
      )
    },
    _getMyMine(cb) {
      this.eosio.getTableRows(
        {
          code: this.mineContract,
          scope: this.mineContract,
          table: 'account',
          limit: 1,
          lower_bound: this.account.name,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0 && data.rows && data.rows.length >= 1) {
            var row = data.rows[0]
            if (row.account == this.account.name) {
              if (this.myPrevMineOnes == 0) {
                this.myPrevMineOnes = parseFloat(row.swap_quantity)
              } else {
                this.myPrevMineOnes = this.myCurMineOnes
              }
              this.myCurMineOnes = parseFloat(row.swap_quantity)
            }
          }
          if (cb) {
            cb(code)
          }
        }.bind(this)
      )
    },

    getDivdTable() {
      this.eosio.getTableRows(
        {
          code: this.divdContract,
          scope: this.divdContract,
          table: 'config',
          limit: 1,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0 && data.rows && data.rows.length == 1) {
            this.bonusQuantity =
              Number(parseFloat(data.rows[0]['bonus_quantity'])).toFixed(2) +
              ' EOS'
            this.bonusStaked = data.rows[0]['stake_quantity']

            this._getOnesPrice()
          }
        }.bind(this)
      )
    },

    _getOnesPrice() {
      var that = this
      this.eosio.getTableRows(
        {
          code: this.defiContract,
          scope: this.defiContract,
          table: 'liquidity',
          limit: 1,
          key_type: 'i64',
          lower_bound: 3,
          json: true
        },
        function(code, msg, data) {
          if (
            data.rows &&
            data.rows.length == 1 &&
            data.rows[0]['liquidity_id'] == 3
          ) {
            var price = data.rows[0]['price1']
            that.bonusRate =
              Number(
                ((parseFloat(that.bonusQuantity) * 0.01) /
                  parseFloat(that.bonusStaked)) *
                  365 *
                  100 *
                  price
              ).toFixed(2) + '%'
          }
        }
      )
    },

    getLiquidities(liquidity_id, cb) {
      this.eosio.getTableRows(
        {
          code: this.defiContract,
          scope: this.defiContract,
          table: 'liquidity',
          limit: 100,
          lower_bound: liquidity_id,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0) {
            for (var i in data.rows) {
              var row = data.rows[i]
              if (
                this.blacks[row.token1.address] ||
                this.blacks[row.token2.address]
              ) {
                continue
              }
              var liquidity_id = row.liquidity_id

              this.max_liquidity_id =
                this.max_liquidity_id > liquidity_id
                  ? this.max_liquidity_id
                  : liquidity_id

              var symbol1 = row.token1.symbol.split(',')
              var address1 = row.token1.address
              var key1 = address1 + '-' + symbol1[1].toLowerCase()

              Vue.set(this.tokens, key1, {
                address: address1,
                symbol: symbol1[1],
                precision: parseInt(symbol1[0]),
                key: key1
              })

              var symbol2 = row.token2.symbol.split(',')
              var address2 = row.token2.address
              var key2 = address2 + '-' + symbol2[1].toLowerCase()

              Vue.set(this.tokens, key2, {
                address: address2,
                symbol: symbol2[1],
                precision: parseInt(symbol2[0]),
                key: key2
              })

              this.pairs[key1 + '|' + key2] = liquidity_id
              this.pairs[key2 + '|' + key1] = liquidity_id

              this.liquidites[liquidity_id] = {
                key1: key1,
                key2: key2,
                token1: this.tokens[key1],
                token2: this.tokens[key2],
                quantity1: row.quantity1,
                quantity2: row.quantity2,
                liquidity_token: row.liquidity_token,
                price1: row.price1,
                price2: row.price2,
                weight: Number(row.swap_weight).toFixed(1)
              }
            }
            this.updateCurLiquidity()
            if (data.more) {
              setTimeout(
                function() {
                  this.getLiquidities(this.max_liquidity_id, cb)
                }.bind(this),
                500
              )
            } else {
              if (cb && typeof cb == 'function') {
                cb()
              }
              this.getMyMine()
            }
          }
        }.bind(this)
      )
    },

    viewMyOrders() {
      this.$router.push({
        name: 'swapLog'
      })
    },
    viewHistoryOrders() {
      this.$router.push({
        name: 'swapLog'
      })
    },
    createMarket() {
      this.$router.push({
