      window.location.href = 'https://xo.ones.game'
      // this.$router.push({
      //   name: 'defiPresale'
      // })
    },
    selectTokens(type) {
      this.tokenDrawerVisible = true
      this.tokenType = type
    },
    selectToken(token) {
      if (this.tokenType == 'in') {
        if (
          token.symbol == this.outToken.symbol &&
          token.address == this.outToken.address
        ) {
          this.outToken = this.inToken
        }
        this.inToken = token
      } else {
        if (
          token.symbol == this.inToken.symbol &&
          token.address == this.inToken.address
        ) {
          this.inToken = this.outToken
        }
        this.outToken = token
      }
      Storage.set('defi_swap_intoken', this.inToken)
      Storage.set('defi_swap_outtoken', this.outToken)
      this.inTokenAmount = ''
      this.outTokenAmount = ''
      this.tokenDrawerVisible = false
      this.updateCurLiquidity()
      this.searchtoken = ''

      return false
    },

    updateCurLiquidity() {
      var key1 = this.inToken.key
      var key2 = this.outToken.key

      this.hasMultiPath = false

      var cur_liquidity_id = this.pairs[key1 + '|' + key2]
      // console.log(key1, key2, this.pairs)
      if (
        cur_liquidity_id &&
        this.liquidites[cur_liquidity_id]['liquidity_token'] > 0
      ) {
        this.cur_liquidity_id = this.pairs[key1 + '|' + key2]
        this.curliquidity = this.liquidites[this.cur_liquidity_id]
        this.curliquidity.order = this.curliquidity.key1 == key1 ? true : false
        this.all_liquidity_token = this.curliquidity['liquidity_token']

        this.inToken.quantity =
          this.curliquidity.key1 == key1
            ? this.curliquidity.quantity1
            : this.curliquidity.quantity2
        this.outToken.quantity =
          this.curliquidity.key2 == key2
            ? this.curliquidity.quantity2
            : this.curliquidity.quantity1

        this.inToken.price =
          this.curliquidity.key1 == key1
            ? Number(this.curliquidity.price1).toFixed(
                this.curliquidity.token1.precision + 2
              )
            : Number(this.curliquidity.price2).toFixed(
                this.curliquidity.token1.precision + 2
              )
        this.outToken.price =
          this.curliquidity.key2 == key2
            ? Number(this.curliquidity.price2).toFixed(
                this.curliquidity.token1.precision + 2
              )
            : Number(this.curliquidity.price1).toFixed(
                this.curliquidity.token1.precision + 2
              )

        this.inTokenPrice = this.inToken.price
        this.outTokenPrice = this.outToken.price
        this.liquidity_token = this.curliquidity.liquidity_token

        if (
          parseFloat(this.inTokenAmount) > 0 &&
          parseFloat(this.outTokenAmount) > 0
        ) {
          this.changeHandle()
        }
      } else {
        this.initLine()
        for (var i in this.liquidites) {
          var _key1 = this.liquidites[i]['key1']
          var _key2 = this.liquidites[i]['key2']
          if (
            !(
              (_key1 == key1 && _key2 == key2) ||
              (_key1 == key2 && _key2 == key1)
            )
          ) {
            this.addLine(_key1, _key2)
          }
        }
        var paths = this.getMinPath(key1, key2)

        console.log('xxxxxxx', paths)
        if (paths.length == 0) {
          this.all_liquidity_token = 0
          this.cur_liquidity_id = 0
          this.curliquidity = {}
        } else {
          var all_liquidity_token = 1
          var multiPaths = []
          multiPaths.push({
            key: key1,
            symbol: key1.split('-')[1].toUpperCase()
          })
          // this.inTokenPrice = this.inToken.price
          // this.outTokenPrice = this.outToken.price
          var inTokenPrice = 1
          var outTokenPrice = 1
          this.liquidity_ids = []
          for (var i = 1; i < paths.length; i++) {
            var _key1 = paths[i - 1]
            var _key2 = paths[i]
            multiPaths.push({
              key: _key2,
              symbol: _key2.split('-')[1].toUpperCase()
            })
            var liquidity_id = this.pairs[_key1 + '|' + _key2]
            this.liquidity_ids.push(liquidity_id)

            var liquidity = this.liquidites[liquidity_id]
            all_liquidity_token *= liquidity['liquidity_token']

            inTokenPrice *=
              liquidity.key1 == _key1 ? liquidity.price1 : liquidity.price2

            outTokenPrice *=
              liquidity.key2 == _key2 ? liquidity.price1 : liquidity.price2
          }

          this.inTokenPrice = Number(inTokenPrice).toFixed(6)
          this.outTokenPrice = Number(outTokenPrice).toFixed(6)
          if (all_liquidity_token > 0) {
            this.hasMultiPath = true
            this.multiPaths = multiPaths
            this.all_liquidity_token = all_liquidity_token
            // multiPaths =
          }
        }
      }
    },

    addTokenPop() {
      this.tokenDrawerVisible = false
      this.tokenDialogVisible = true

      for (var i in this.liquidites) {
        this.liquidites[i]['isshow'] = false
      }
      return false
    },
    getBalances(index) {
      this.getBalance(this.inToken.address, this.inToken.symbol)
      this.getBalance(this.outToken.address, this.outToken.symbol)
    },

    getBalance(address, symbol, cb) {
      if (!this.eosio) {
        return
      }
      this.eosio.getBalance(
        address,
        symbol,
        function(balance) {
          var key = address + '-' + symbol.toLowerCase()
          Vue.set(this.balances, key, balance)
          if (cb) {
            cb(balance)
          }
        }.bind(this)
      )
    },

    addToken() {
      //this.$refs.ruleForm.validateField('checkPass');
      this.$refs['tokenForm'].validate(valid => {
        if (valid) {
          this.addLoading = true
          this.eosio.getCurrencyStats(
            this.tokenForm.address,
            this.tokenForm.symbol,
            function(ret) {
              this.addLoading = false
              if (ret) {
                var symbol = this.tokenForm.symbol.toUpperCase()
                var supply = ret[symbol]['supply']
                var precision = supply.split(' ')[0].split('.')[1]
                if (ret[symbol]) {
                  var token = {
                    address: this.tokenForm.address,
                    symbol: this.tokenForm.symbol,
                    precision: parseInt(precision.length)
                  }
                  this.selectToken(token)
                  this.tokenDialogVisible = false
                } else {
                  this.$message({
                    message: '信息输入有误',
                    type: 'warning',
                    center: true
                  })
                  return false
                }
              } else {
                this.$message({
                  message: '信息输入有误',
                  type: 'warning',
                  center: true
                })
                return false
              }
            }.bind(this)
          )
        } else {
          return false
        }
      })
    },

    initLine() {
      this.lines = {}
      this.hasMinPath = false
    },
    addLine(v, w) {
      this._addLine(v, w)
      this._addLine(w, v)
    },
    _addLine(v, w) {
      !this.lines[v] && (this.lines[v] = [])
      this.lines[v].push(w)
    },
    getMinPath(v, w) {
      this.step(this.lines[v], [v], w)
      return this.path
    },
    step(adjacentNodes, tempRes, w) {
      //当前节点没有相邻节点
      if (!adjacentNodes) {
        return
      }
      //存在可以到达的路径，并且比正在探测的路径短则直接退出探测
      if (this.hasMinPath && this.path.length < tempRes.length) {
        return
      }
      adjacentNodes.forEach(item => {
        //当前探测的点已经走过了，不再重复走
        if (tempRes.indexOf(item) !== -1) {
          return
        }
        let newTempRes = tempRes.concat(item)
        //到达终点
        if (item === w) {
          if (this.hasMinPath) {
            if (newTempRes.length < this.path.length) {
              //已有最短路径，且比当前路径更短，替换
              this.path = newTempRes
            }
          } else {
            //目前没有最短路径，替换
            this.path = newTempRes
            this.hasMinPath = true
          }
        } else {
          this.step(this.lines[item], newTempRes, w)
        }
      })
    },
    openRank() {
      this.getMineList()
      this.rankDialogVisible = true
    },
    getMineList() {
      this.eosio.getTableRows(
        {
          code: this.mineContract,
          scope: this.mineContract,
          table: 'account',
          limit: 100,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          var rows = []
          if (data.rows) {
            for (var i in data.rows) {
              var row = data.rows[i]
              rows.push({
                account: row.account,
                amount: parseFloat(row.mine_quantity)
              })
            }
            rows.sort(function(a, b) {
              return a.amount - b.amount > 0 ? -1 : 1
            })
            for (var i in rows) {
              rows[i]['rank'] = parseInt(i) + 1
              rows[i]['amount'] = Number(rows[i]['amount']).toFixed(2) + ' EOS'
            }
            this.swapRankData = rows
          }
        }.bind(this)
      )
    }
  },
  filters: {
    accountFilter(value) {
      var len = value.length
      var str = ''
      for (var i in value) {
        if (i >= len / 3 && i < (len * 2) / 3) {
          str += '*'
        } else {
          str += value[i]
        }
      }
      return str
    },
    timeFilter(value) {
      return Util.formatDate(value, 'MM-dd hh:mm:ss')
    },
    slippageFilter(value) {
      return parseFloat(value * 100.0).toFixed(2) + '%'
    }
  }
}
</script>

