<template>
  <div class="addMarketContainer">
    <div class="market-add-nav">
      <i class="el-icon-back" @click="goBack()"></i>
      <div class="market-add-title">创建市场</div>
    </div>
    <div class="add-container">
      <!-- 开始 -->

      <div class="market-add-box">
        <div class="market-add-content">
          <div class="market-add-label">
            <div>币种名称A</div>
            <div>
              <el-button type="text" @click="selectTokens('in')">选择已有币种</el-button>
            </div>
          </div>
          <div class="market-add-token">
            <div class="market-token-amount">
              <el-input v-model="inTokenSymbol" placeholder="请输入币种A名称"></el-input>
            </div>
          </div>
          <div class="market-add-label">
            <p>合约地址</p>
          </div>
          <div class="market-add-token">
            <div class="market-token-amount">
              <el-input v-model="inTokenAddress" placeholder="请输入合约地址"></el-input>
            </div>
          </div>
        </div>
      </div>
      <div class="market-add-box">
        <div class="market-add-content">
          <div class="market-add-label">
            <div>币种名称B</div>
            <div>
              <el-button type="text" @click="selectTokens('out')">选择已有币种</el-button>
            </div>
          </div>
          <div class="market-add-token">
            <div class="market-token-amount">
              <el-input v-model="outTokenSymbol" placeholder="请输入币种B名称"></el-input>
            </div>
          </div>
        </div>
        <div class="market-add-content">
          <div class="market-add-label">
            <p>合约地址</p>
          </div>
          <div class="market-add-token">
            <div class="market-token-amount">
              <el-input v-model="outTokenAddress" placeholder="请输入币种名称"></el-input>
            </div>
          </div>
        </div>
      </div>

      <el-button
        class="market-btn"
        type="primary"
        :loading="createLoading"
        @click="createMarket"
        style="width:100%;"
      >确定</el-button>
      <div class="priceTip" style="margin:5px">* 创建市场无需任何费用</div>
    </div>

    <el-drawer
      class="PopUpContainer"
      :visible.sync="tokenDrawerVisible"
      direction="btt"
      :style="PopUpContainerObj"
      size="auto"
      :with-header="false"
      title="选择币种"
      modal-append-to-body="false"
    >
      <!-- 充值框 -->
      <div class="tokens-content">
        <div class="tokens-header">
          <span>选择币种</span>
          <div class="flexb">
            <div>
              <i class="el-icon-close" @click="tokenDrawerVisible = false"></i>
            </div>
          </div>
        </div>
        <el-input
          size="large"
          placeholder
          prefix-icon="el-icon-search"
          v-model="searchtoken"
          class="search-token"
        ></el-input>

        <ul class="currency-list">
          <li
            v-for="(item, index) in tokens"
            :key="index"
            class="currency-item"
            @click="selectToken(item)"
            v-if="!item.ishidden"
          >
            <el-image
              class="logo"
              :src="
                '/static/img/tokens/' +
                  item.address +
                  '-' +
                  item.symbol.toLowerCase() +
                  '.png'
              "
            >
              <div slot="error" class="image-slot">
                <el-image :src="defaultImg"></el-image>
              </div>
            </el-image>
            <!-- <img :src="item.img" class="logo" /> -->
            <div class="right-part">
              <p>{{ item.symbol }}</p>
              <p>{{ item.address }}</p>
            </div>
          </li>
        </ul>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import i18n from '../../locales/index'
import Util from '../../utils/util'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      contract: 'onesgamedefi',

      defaultImg: '/static/img/tokens/eos.png',

      searchtoken: '',
      tokenType: '',
      max_liquidity_id: 0,
      tokens: {},

      inTokenSymbol: 'EOS',
      inTokenAddress: 'eosio.token',
      outTokenSymbol: '',
      outTokenAddress: '',

      tokenDrawerVisible: false,
      createLoading: false
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },
    account: function() {
      return this.$store.state.eos_account
    }
  },
  mounted: function() {
    this.getLiquidities(0)
  },
  watch: {
    searchtoken(value) {
         console.log("creatMarkrt-----------")
      value = value.toUpperCase()
      for (var i in this.tokens) {
        var ishidden =
          this.tokens[i]['symbol'].indexOf(value) >= 0 ? false : true;
        this.tokens[i]['ishidden'] = ishidden
      }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    getLiquidities(liquidity_id, cb) {
      this.eosio.getTableRows(
        {
          code: this.contract,
          scope: this.contract,
          table: 'liquidity',
          limit: 100,
          lower_bound: liquidity_id,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0) {
            for (var i in data.rows) {
              var row = data.rows[i]
              var liquidity_id = row.liquidity_id

              this.max_liquidity_id =
                this.max_liquidity_id > liquidity_id
                  ? this.max_liquidity_id
                  : liquidity_id

              var symbol1 = row.token1.symbol.split(',')
              var address1 = row.token1.address
              var key1 = address1 + '-' + symbol1[1].toLowerCase()

              Vue.set(this.tokens, key1, {
                address: address1,
                symbol: symbol1[1],
                precision: parseInt(symbol1[0])
              })

              var symbol2 = row.token2.symbol.split(',')
              var address2 = row.token2.address
              var key2 = address2 + '-' + symbol2[1].toLowerCase()
              Vue.set(this.tokens, key2, {
                address: address2,
                symbol: symbol2[1],
                precision: parseInt(symbol2[0])
              })
            }
            if (data.more) {
              setTimeout(function() {
                that.getLiquidities(this.max_liquidity_id, cb)
              }, 200)
            } else {
              if (cb && typeof cb == 'function') {
                cb(data.rows)
              }
            }
          }
        }.bind(this)
      )
    },

    selectTokens(type) {
      this.tokenDrawerVisible = true
      this.tokenType = type
      this.searchtoken = ''
    },

    selectToken(token) {
      console.log(token, 'symbol')
      if (this.tokenType == 'in') {
        this.inTokenSymbol = token.symbol
        this.inTokenAddress = token.address
      } else {
        this.outTokenSymbol = token.symbol
        this.outTokenAddress = token.address
      }
      this.tokenDrawerVisible = false
      return false
    },

    getToken(address, symbol, cb) {
      symbol = symbol.toUpperCase()
      this.eosio.getCurrencyStats(
        address,
        symbol,
        function(ret) {
          if (ret && ret[symbol]) {
            var supply = ret[symbol]['supply']
            var precision = supply.split(' ')[0].split('.')[1]

            var token = {
              address: address,
              symbol: symbol,
              precision: parseInt(precision.length)
            }
            cb(0, token)
          } else {
            cb(1, null)
          }
        }.bind(this)
      )
    },
    
    createMarket() {
      this.doCreateMarket()
    },
    doCreateMarket() {
      console.log(this.inTokenSymbol, this.inTokenAddress)
      console.log(this.outTokenSymbol, this.outTokenAddress)

      this.createLoading = true
      var self = this
      this.getToken(this.inTokenAddress, this.inTokenSymbol, function(
        code,
        inToken
      ) {
        console.log(code, inToken)
        if (code != 0) {
          self.createLoading = false

          self.$message({
            message: '币种名称A或合约地址信息输入有误',
            type: 'warning',
            center: true
          })
        } else {
          self.getToken(self.outTokenAddress, self.outTokenSymbol, function(
            code,
            outToken
          ) {
            console.log(code, outToken)

            if (code != 0) {
              self.createLoading = false
              self.$message({
                message: '币种名称B或合约地址信息输入有误',
                type: 'warning',
                center: true
              })
            } else {
              // self.createLoading = false

              try {
                self._createMarket(inToken, outToken)
              } catch (err) {
                console.log(err)
              }
            }
          })
        }
      })
    },
    _createMarket(inToken, outToken) {
      this.createLoading = true
      var actions = [
        {
          account: this.contract,
          name: 'newliquidity',
          authorization: [
            {
              actor: this.eosio.account.name,
              permission: this.eosio.account.authority
            }
          ],
          data: {
            account: this.eosio.account.name,
            token1: {
              address: inToken.address,
              symbol: inToken.precision + ',' + inToken.symbol
            },
            token2: {
              address: outToken.address,
              symbol: outToken.precision + ',' + outToken.symbol
            }
          }
        }
      ]
      console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.createLoading = false
          if (code == 0) {
            this.$message({
              message: '创建成功',
              type: 'success',
              center: true
            })
            //todo
            this.getLiquidities(
              this.max_liquidity_id,
              function(rows) {
                var liquidity_id = 0
                for (var i in rows) {
                  var row = rows[i]
                  var inTokenSymbol = inToken.precision + ',' + inToken.symbol
                  var outTokenSymbol =
                    outToken.precision + ',' + outToken.symbol

                  if (
                    (row.token1.address == inToken.address &&
                      row.token1.symbol == inTokenSymbol &&
                      row.token2.address == outToken.address &&
                      row.token2.symbol == outTokenSymbol) ||
                    (row.token2.address == inToken.address &&
                      row.token2.symbol == inTokenSymbol &&
                      row.token1.address == outToken.address &&
                      row.token1.symbol == outTokenSymbol)
                  ) {
                    liquidity_id = row.liquidity_id
                    this.$router.push({
                      name: 'addDefiMarket',
                      params: {
                        liquidity_id: liquidity_id,
                        type: 1
                      }
                    })
                  }
                }
              }.bind(this)
            )
          } else {
            this.$message({
              message: msg,
              type: 'error',
              center: true
            })
          }
        }.bind(this)
      )
    }
  }
}
</script>

