<template>
  <div class="assetContainer" ref="assetContainer" v-wechat-title="this.title">
    <!-- 头 -->
    <div class="asset-nav-bar">
      <div class="title">
        <!-- todo优化 -->
        <span>{{title}}</span>
      </div>

      <div class="asset-link-left">
        <!-- 返回资产页面 -->
        <span>
          <!-- 返回各自之前的页面 -->
          <i class="el-icon-arrow-left" @click="goBack"></i>
        </span>
      </div>

      <div class="asset-link-right">
        <!-- 记录 -->
        <span
          v-if="this.$route.name == 'justalkDepositScene' 
            || this.$route.name == 'bqexDepositScene'
            || this.$route.name == 'gxbDepositScene'
            || this.$route.name == 'hicoinDepositScene'
            || this.$route.name == 'hicoinWithdrawScene'
            || this.$route.name == 'dragonexDepositScene'
            || this.$route.name == 'dragonexWithdrawScene'
            || this.$route.name == 'eosWithdrawScene'
            || this.$route.name == 'eosDepositScene'
            || this.$route.name == 'gxbWithdrawScene'"
        >
          <i class="el-icon-s-order" style="font-size:2rem;" @click="enterRecored"></i>
        </span>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: ''
    }
  },
  mounted() {
    var channels = {
      eos: 'EOS',
      dragonex: 'DragonEx',
      erc20: 'Erc20',
      justalk: 'Justalk',
      bqex: 'BQEx',
      hicoin: 'HiCoin',
      gxb: this.$t('assetScene.gxb')
    }
    var route_name = this.$route.name
    console.log(route_name)
    if (route_name.indexOf('Deposit') > 0) {
      var key = route_name.slice(0, route_name.indexOf('Deposit'))
      this.title =
        (channels[key] ? channels[key] : key) + this.$t('assetScene.deposit')
    } else if (route_name.indexOf('Withdraw') > 0) {
      var key = route_name.slice(0, route_name.indexOf('Withdraw'))
      this.title =
        (channels[key] ? channels[key] : key) + this.$t('assetScene.withdraw')
    } else {
      if (route_name == 'assetRecordScene') {
        this.title = this.$t('assetRecord.title')
      }
    }
  },
  methods: {
    backHome() {
      if (
        this.$route.name == 'oauthLoginScene' ||
        this.$route.name == 'paymentScene'
      ) {
        this.$router.push({
          name: 'profileScene'
        })
      }
    },

    enterRecored() {
      var wallet = ''
      if (
        this.$route.name == 'dragonexDepositScene' ||
        this.$route.name == 'dragonexWithdrawScene'
      ) {
        wallet = 'DragonEx'
      } else if (this.$route.name == 'justalkDepositScene') {
        wallet = 'Justalk'
      } else if (this.$route.name == 'bqexDepositScene') {
        wallet = 'Bqex'
      } else if (this.$route.name == 'hicoinDepositScene') {
        wallet = 'HiCoin'
      } else if (
        this.$route.name == 'eosDepositScene' ||
        this.$route.name == 'eosWithdrawScene'
      ) {
        wallet = 'Eos'
      }
      this.$router.push({
        name: 'assetRecordScene',
        params: { wallet: wallet }
      })
    },
    goBack() {
      this.$router.push({
        name: 'walletScene',
      })
      // this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
.assetContainer {
  background-color: #f5f5f5;
}
.asset-nav-bar {
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 1.2rem;
  color: #606266;
  display: flex;
  border-top: 2px solid #f5f5f5;
  position: relative;
}

.asset-nav-bar .title {
  white-space: nowrap;
  height: 50px;
  line-height: 50px;
  font-size: 1.5rem;
  /* color: #000; */
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
}
.asset-link-right {
  position: absolute;
  right: 0px;
  height: 50px;
  line-height: 50px;
  font-size: 1.5rem;
  /* color: rgb(236, 236, 236); */
  margin-right: 18px;
}
.asset-link-left {
  position: absolute;
  left: 0px;
  height: 50px;
  line-height: 50px;
  font-size: 1.5rem;
  /* color: rgb(236, 236, 236); */
  margin-left: 18px;
}
</style>


