<template>
  <div class="gameDetailContainer" ref="gameDetailContainer" v-wechat-title="this.title">
    <div class="game-header">
      <img style="transition: all 1s ease 0s; opacity: 1;" :src="gameInfo.bg" class="game-bg-img" />
      <div class="linear-mask"></div>
      <div class="game-info-container">
        <img
          data-loaded="1"
          class="game-icon"
          style="transition: all 1s ease 0s; opacity: 1;"
          :src="gameInfo.logo"
        />
        <div class="game-title">
          <div class="game-name">{{gameInfo.name}}</div>
          <span class="game-tag">{{gameInfo.type}}</span>
        </div>
        <div class="hot-play-info">
          <span class="star-list">
            <i class="star2"></i>
            <i class="star2"></i>
            <i class="star2"></i>
            <i class="star2"></i>
            <i class="star2"></i>
          </span>
          <!-- <span class="light-num">128.8万</span>人在玩 -->
        </div>
        <div class="game-info">{{gameInfo.sub}}</div>
        <div>
          <!-- <el-button
            type="success"
            plain
            @click="giftDialogVisible = true"
          >{{$t("gameScenePart.claimGift")}}</el-button>-->
          <span class="play-game-btn" @click="playGame(game_id)">{{$t("gameScenePart.playGame")}}</span>
        </div>
      </div>

      <div class="action" style="display:none;">
        <div class="play-btn">
          <el-button
            type="success"
            plain
            @click="giftDialogVisible = true"
          >{{$t("gameScenePart.claimGift")}}</el-button>
          <el-button type="primary" @click="playGame(game_id)">{{$t("gameScenePart.playGame")}}</el-button>
        </div>
        <!-- <div class="video-btn">
          <el-button
            type="text"
            size="small"
            @click="dialogVisible=true"
          >{{$t("gameScenePart.viewvideo")}}</el-button>
        </div>-->
      </div>
      <div class="game-rating" style="display:none;">
        <el-rate
          v-model="rateNum"
          disabled
          show-score
          text-color="rgb(91, 115, 180)"
          :colors="ratecolors"
        >4.8</el-rate>
      </div>

      <!-- <p class="count-stats">7777人已关注</p> -->
    </div>
    <div class="game-banner">
      <div class="game-banner-items">
        <el-image
          class="game-banner-img"
          v-for="item in gameInfo.imgs"
          :src="item"
          :preview-src-list="gameInfo.imgs"
          :key="item"
        >
          <div slot="placeholder" class="image-slot">
            loading
            <span class="dot">...</span>
          </div>
        </el-image>
      </div>
      <!-- <el-carousel :interval="4000" type="card" height="150px">
        <el-carousel-item v-for="item in banners" :key="item">
          <img :src="item.img" alt style="width:100%;height:100%;" :preview-src-list="item.imgs" />
        </el-carousel-item>
      </el-carousel>-->
    </div>
    <div class="game-main">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- part1 -->
        <el-tab-pane label="详情" name="first">
          <div class="game-template details">
            <div class="section-title" v-if="gameExt.strategy && gameExt.strategy.length>0">简介</div>
            <div class="description-paragraph" v-html="gameInfo.desc"></div>
            <div class="description-paragraph" v-html="gameExt.details"></div>
          </div>
        </el-tab-pane>
        <!-- part2 -->
        <el-tab-pane label="攻略" name="second" v-if="gameExt.strategy && gameExt.strategy.length>0">
          <div class="game-template details" v-for="(item,index) in gameExt.strategy">
            <div class="section-title">{{item.title}}</div>
            <div class="description-paragraph" v-html="item.content"></div>
          </div>
          <!-- <div class="game-gift">
            <iframe :src="giftUrl" id="gift-iframe"></iframe>
          </div>-->
        </el-tab-pane>
        <!-- part3 -->
        <!-- <el-tab-pane label="社区" name="third"></el-tab-pane> -->
      </el-tabs>

      <el-button
        v-if="gameInfo.hasgift"
        type="success"
        plain
        style="width:150px;"
        @click="playGame(game_id, 1)"
      >{{$t("gameScenePart.claimGift")}}</el-button>
    </div>

    <el-dialog title="怼怼三国" :visible.sync="dialogVisible" :width="dialogWidth">
      <iframe
        height="300"
        width="100%"
        max-width="500px"
        src="https://player.youku.com/embed/XNDEzNTI4Mjc4NA=="
        frameborder="0"
        allowfullscreen
      ></iframe>
    </el-dialog>

    <el-dialog title="找小七领礼包" center :visible.sync="giftDialogVisible" :width="dialogWidth">
      <div style="text-align:center">
        <img src="../../assets/wechat1.png" alt style="width:150px;height:150px;" />
      </div>
      <div style="text-align:center">
        <el-button type="text" @click="copy()">小七微信</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import gameService from '../../api/game'
import TpUtil from 'tp-eosjs'
import Util from '../../utils/util'

export default {
  data() {
    return {
      title: '',
      game: '',
      giftDialogVisible: false,
      dialogVisible: false,
      islogin: this.$store.state.account.isLogin,
      istesting: true,

      rateNum: 4.8, //游戏评分
      ratecolors: ['#99A9BF', 'rgb(135, 183, 223)', 'rgb(68, 173, 235)'],
      activeName: 'first',
      gameInfo: {},
      gameExt: { details: '', strategy: [] }
    }
  },
  mounted() {
    var zone = this.$store.getters.zone
    if (zone == 'eos') {
      if (TpUtil.isConnected()) {
        TpUtil.fullScreen({
          fullScreen: 0
        })
      }
    }
    // 获取游戏详情
    this.game_id = this.$route.params.game
    gameService
      .getGameDetail(this.game_id)
      .then(res => {
        this.gameInfo = res.data.game
        this.title = this.gameInfo.name
      })
      .catch(err => {})

    this.gameExt = gameService.getGameDetails(this.game_id)
  },

  computed: {
    gameDetailsName() {
      if (this.$route.path == '/game/pkthreeking')
        return this.$t('gameScenePart.pkthreeking.name')
      if (this.$route.path == '/game/witchtower')
        return this.$t('gameScenePart.witchtower.name')
      if (this.$route.path == '/game/dragonmaster')
        return this.$t('gameScenePart.dragonmaster.name')
      if (this.$route.path == '/game/dragonclash')
        return this.$t('gameScenePart.dragonclash.name')
    }
  },
  methods: {
    copy() {
      var _input = document.createElement('input')
      _input.value = 'eminem_0615'
      document.body.appendChild(_input)
      _input.select()
      document.execCommand('Copy')
      document.body.removeChild(_input)
      this.$message({
        message: this.$t('message.copy_success'),
        type: 'success',
        center: true
      })
    },
    handleClick(tab, event) {
      // if (tab.name == "third") {
      //   this.$router.push({
      //     name:"communityScene"
      //   })
      // }
      // document.getElementById('gift-iframe').onload=function(){
      //       document.getElementById("gift-iframe").contentWindow.document.getElementById("app").style.display = "none";
      //     }
      // this.giftUrl =
      //   'http://www.shandw.com/auth/?channel=13668&openid=e9644695e9a9b032be1ecc91ed5379c6&time=1591775298&nick=xxxx&avatar=https%3A%2F%2Fcdn.jsdelivr.net/gh/onesgame/onesgame.github.io%2Fstatic%2Fimg%2Favatar.png&sex=1&phone=18999999999&sdw_ld=1&sdw_kf=1&sdw_dl=1&sdw_bt=1&sdw_sy=1&sdw_tl=1&gid=2021815759&sign=f37e1135d0e51714a4a00ea8bd6ed31c&sdw_activity=sdw_detail'
    },
    playGame(game_id, show_gift) {
      var game_ids = {
        dragonmaster: 1,
        landlord: 1,
        douro: 1,
        emperor: 1,
        fishing: 1,
        pkthreeking: 1,
        dragonclash: 1,
        witchtower: 1,
        longxingshewu: 1
      }
      if (!Util.isMobile() && !game_ids[game_id]) {
        this.$message({
          message: this.$t('message.error_app'),
          type: 'error',
          center: true
        })
        return
      }
      if (this.islogin) {
        this.$router.push({
          name: 'playScene',
          params: {
            game: game_id
          },
          query: {
            gift: show_gift
          }
        })
      } else {
        var url = '/play/' + game_id
        if (show_gift) {
          url += '?gift=1'
        }
        this.$router.push({
          name: 'loginScene',
          params: { url: url }
        })
      }
      return
    }
  }
}
</script>

<style scoped>
.gameDetailContainer {
  box-sizing: border-box;
  overflow-y: auto;
  position: relative;
}
.gameDetailContainer::-webkit-scrollbar {
  width: 0 !important;
}

/* .game-logo {
  width: 100%;
  box-sizing: border-box;
  padding: 2rem 1rem 1.5rem;
  background-color: #fff;
  text-align: center;
}
.game-logo .game-banner-img {
  width: 100%;
}
.game-logo .logo-img {
  width: 8rem;
  height: 8rem;
  display: block;
  margin: 0 auto;
}
.game-logo .base-info {
  font-size: 1.8rem;
  text-align: center;
  line-height: 4rem;
  color: rgb(78, 87, 102);
}

.game-logo .action {
  margin-top: 10px;
}
.game-logo .count-stats {
  line-height: 2rem;
  font-size: 0.8rem;
  color: #888;
} */
.game-header {
  height: 305px;
  width: 100%;
  position: relative;
  overflow: hidden;
  background: #2b323d;
}

.game-header .game-bg-img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  opacity: 0.8;
}

.game-header .linear-mask {
  width: 100%;
  height: 11.25rem;
  position: absolute;
  bottom: 0;
  left: 0;
  background: linear-gradient(
    180deg,
    transparent,
    rgba(33, 39, 47, 0) 5%,
    rgba(33, 39, 47, 0.33) 20%,
    #21272f 40%,
    #21272f
  );
}

.game-info-container {
  width: 13.4375rem;
  height: 8.125rem;
  position: absolute;
  bottom: 1.25rem;
  left: 50%;
  transform: translate3d(-50%, 0, 0);
}

.game-icon {
  width: 4.375rem;
  height: 4.375rem;
  border-radius: 12px;
  position: absolute;
  top: 0;
  left: 0;
}

.game-title {
  height: 1.5625rem;
  line-height: 1.5625rem;
  font-size: 1.0625rem;
  color: #fff;
  position: absolute;
  top: 0;
  left: 5.15625rem;
  width: 12rem;
}
.game-title .game-name {
  height: 1.5625rem;
  display: inline-block;
  float: left;
  line-height: 1.5625rem;
  max-width: 7rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.game-title .game-tag {
  height: 0.9375rem;
  display: inline-block;
  float: left;
  line-height: 0.9375rem;
  border-radius: 3px;
  margin-left: 0.25rem;
  border: 1px solid #ffb400;
  font-size: 0.5625rem;
  color: #ffb400;
  background: #000000;
  padding: 0 0.46875rem;
  margin-top: 0.25rem;
}
.hot-play-info {
  position: absolute;
  left: 5.15625rem;
  top: 1.875rem;
  font-size: 0.625rem;
  color: #929dac;
  width: 100%;
  height: 1.25rem;
  overflow: hidden;
}
.star-list {
  width: 4.0625rem;
  height: 0.6875rem;
  display: inline-block;
}
.star2 {
  background: url(data:image/png;base64,)
    0 0/100% 100% no-repeat;
}
.star0,
.star1,
.star2 {
  display: block;
  width: 0.71875rem;
  height: 0.6875rem;
  float: left;
  margin-right: 0.0625rem;
}
.light-num {
  color: #ff9000;
}

.game-info {
  color: #929dac;
  height: 1.5625rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: 1.5625rem;
  font-size: 0.8125rem;
  position: absolute;
  left: 5.15625rem;
  top: 2.96875rem;
  width: 8.28125rem;
}
.play-game-btn {
  width: 100%;
  height: 2.5rem;
  line-height: 2.53125rem;
  color: #fff;
  font-size: 1.0625rem;
  border-radius: 1.25rem;
  text-align: center;
  background-image: linear-gradient(90deg, #7ab4f3, #4199f7);
  position: absolute;
  bottom: 0;
  left: 0;
}

.game-banner-items {
  /* margin-top: 10px; */
  /* background-color: #fff; */
  /* height: 9.375rem; */
  padding: 0.9375rem 0;
  overflow-y: hidden;
  overflow-x: scroll;
  /* -webkit-overflow-scrolling: touch; */
  white-space: nowrap;
  position: relative;
}
.game-banner-items .game-banner-img {
  background: #e1e1e1;
  display: inline-block;
  height: 150px;
  width: 85px;
  margin-left: 0.9375rem;
}

.game-banner-items .game-banner-img:last-child {
  margin-right: 0.9375rem;
}

/deep/ .el-image-viewer__close {
  color: #fff;
  background-color: #606266;
  border-color: #fff;
}
.game-main {
  margin-top: 0.2rem;
  width: 100%;
  box-sizing: border-box;
  padding: 0rem 1rem 1.5rem;
  text-align: center;
}
.game-main /deep/ .el-tabs__nav {
  width: 100%;
  display: flex;
}
.game-main /deep/ .el-tabs__nav .el-tabs__item {
  flex: 1;
}

.game-template {
  box-sizing: border-box;
  padding: 10px 15px;
  margin-bottom: 15px;
  background-color: #fff;
}
.game-template .section-title {
  margin-bottom: 10px;
  text-align: left;
  padding-left: 6px;
  border-left: 4px solid rgb(123, 160, 230);
  font-size: 1rem;
  color: rgb(102, 102, 102);
}
.paragraph-img {
  width: 100%;
}
/* 详情 */
.game-template .description-paragraph {
  text-align: left;
  font-size: 0.9rem;
  color: rgb(75, 75, 75);
  line-height: 1.5rem;
}

.game-gift {
  position: relative;
  /* height:100%; */
  width: 100%;
  height: 900px;
}

.game-gift iframe {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  /* bottom: 0; */
  margin: 0;
  /* padding: 0; */
  height: 100%;
  /* height:200px; */
  width: 100%;
  z-index: 100;
  border: none;
}

.image-slot {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  color: #c0c4cc;
  vertical-align: middle;
}
/* 详情 */
</style>



