<template>
  <div class="assetRecordContainer" ref="assetRecordContainer">
    <div id="content">
      <el-table :data="orders" stripe>
        <el-table-column
          prop="create_time"
          :label="$t('assetRecord.create_time')"
          :formatter="formatterTime"
        ></el-table-column>
        <el-table-column
          prop="amount"
          :label="$t('assetRecord.amount')"
          :formatter="formatterAmount"
        ></el-table-column>
        <el-table-column prop="type" :label="$t('assetRecord.type')" :formatter="formatterType"></el-table-column>
        <el-table-column
          prop="status"
          :label="$t('assetRecord.status')"
          :formatter="formatterStatus"
        ></el-table-column>
      </el-table>
      <!-- <el-pagination layout="prev, pager, next" :total="50"></el-pagination> -->
    </div>
  </div>
</template>

<script>
import Util from '../../utils/util'

import AssetService from '../../api/asset'
export default {
  data() {
    return {
      sourceForm: '',
      orders: []
    }
  },
  methods: {
    formatterTime(row, column, cellValue, index) {
      return Util.formatDate(cellValue, 'MM-dd hh:mm')
    },
    formatterAmount(row, column, cellValue) {
      return cellValue + row.coin_code.toUpperCase()
    },
    formatterType(row, column, cellValue) {
      return this.$t('assetRecord.' + cellValue)
    },
    formatterStatus(row, column) {
      var status = ''
      if (row['status'] == 0) {
        //unpaied
        status = 'status_unpaied'
      } else if (row['status'] == 1) {
        //paied
        status = 'status_paied'
      } else if (row['status'] == 2) {
        //paied failed
        status = 'status_paied_failed'
      } else if (row['status'] == -1) {
        // cancel
        status = 'status_paied_cancel'
      } else {
        //waiting
        status = 'status_paied_waiting'
      }

      return this.$t('assetRecord.' + status)
    }
  },
  mounted() {
    this.sourceForm = this.$route.params._type

    var wallet = this.$route.params.wallet
    var coin_code = this.$route.params.coin_code

    AssetService.history(wallet, coin_code).then(res => {
      // console.log(res.data.orders)
      this.orders = res.data.orders
    })
  }
}
</script>

<style scoped>
.assetRecordContainer {
  width: 100%;
  overflow-y: auto;
  position: relative;
}

.assetRecordContainer::-webkit-scrollbar {
  width: 0 !important;
}

#content {
  width: 100%;
  width: 100%;
  position: relative;
}

.tab_css {
  width: 100%;
  display: none;
  float: left;
}
.el-tabs li {
  width: 100%;
  margin-bottom: 0.7rem;
  box-sizing: border-box;
  color: #606266;
  padding: 2% 3%;
  background-color: #fff;
}
.el-tabs li .top {
  font-size: 1.2rem;
}
.el-tabs li .top span {
  float: right;
  font-size: 1rem;
}
.el-tabs .item_text {
  margin-top: 0.6rem;
  font-size: 1rem;
}
.el-tabs .item_text span:nth-child(1) {
  color: #303133;
  margin-left: 0.5rem;
  font-size: 1.3rem;
  font-weight: 800;
}
.el-tabs .item_text span:nth-child(2) {
  float: right;
  font-size: 1.1rem;
  font-weight: 800;
}
.el-tabs .address {
  margin-top: 1.6rem;
  line-height: 1.9rem;
}
.el-tabs .address p:nth-child(2) {
  color: #303133;
  font-size: 1rem;
  font-weight: 800;
}
</style>



