<template>
  <div class="daoContainer" ref="daoContainer">
    <div class="daoContent">
      <div class="dao-header">
        <el-card class="box-card">
          <div class="banner">
            <p>ONES DAO</p>
            <p>做市、抵押参与投票治理</p>
          </div>
        </el-card>

        <el-card class="box-card">
          <div slot="header" class="mine-card-header">
            <span>投票</span>
            <span @click="ruleDialogVisible=true">
              规则
              <i class="el-icon-info"></i>
            </span>
          </div>

          <div class="card-item">
            <span>你的票数</span>
            <span @click="ticketDialogVisible=true">
              {{my_all_ticket}}
              <i class="el-icon-s-marketing"></i>
            </span>
          </div>
        </el-card>
      </div>

      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>提案列表</span>
        </div>
        <div class="dao-items">
          <div class="dao-item" @click="toDetail(2)">
            <div>No.2</div>
            <div class>
              <div>交易、做市挖矿机制变更</div>
              <div>方案通过</div>
            </div>
            <div class>已执行完成</div>
          </div>
          <div class="dao-item" @click="toDetail(1)">
            <div>No.1</div>
            <div class>
              <div>超级节点退出机制</div>
              <div>方案2胜出</div>
            </div>
            <div class>已执行完成</div>
          </div>
        </div>
      </el-card>
    </div>

    <el-dialog title="规则" :visible.sync="ruleDialogVisible" width="100%" center>
      <p>1. 做市ONES和质押OENS即可获得投票票数</p>
      <p>2. 更改做市、退出质押会自动重新投票</p>
      <p>3. 投票结果实时反馈最新排行</p>
      <p>4. 随时可以修改重新投票</p>
      <p>● ONES做市: 1 ONES = 1票</p>
      <p>● 节点质押: 1 ONES = 1票</p>
      <p>● ONES 虾玩农场质押：</p>
      <p>&nbsp;&nbsp;&nbsp;&nbsp;随存随取：1 ONES = 0.5票</p>
      <p>&nbsp;&nbsp;&nbsp;&nbsp;存7天：1 ONES = 1票</p>
      <p>&nbsp;&nbsp;&nbsp;&nbsp;存1月：1 ONES = 1.25票</p>
      <p>&nbsp;&nbsp;&nbsp;&nbsp;存3月：1 ONES = 1.5票</p>
      <p>&nbsp;&nbsp;&nbsp;&nbsp;存6月：1 ONES = 2票</p>
      <!-- </p> -->
    </el-dialog>

    <el-dialog
      title="我的票"
      class="ticket-dialog"
      :visible.sync="ticketDialogVisible"
      width="100%"
      center
    >
      <p>
        <span>总票数</span>
        <span>{{this.my_all_ticket}}</span>
      </p>
      <p>
        <span>超级节点票数</span>
        <span>{{my_node_ticket}}</span>
      </p>
      <p>
        <span>做市票数</span>
        <span>{{my_liquidity_ticket}}</span>
      </p>
      <p>
        <span>虾丸锁仓票数</span>
        <span>{{my_lock_ticket}}</span>
      </p>
    </el-dialog>
    <el-dialog title="提案详情" :visible.sync="detailDialogVisible" width="100%" center>
      <div class="dao-box">
        <div class="dao-title">提案内容</div>
        <p>当前超级节点的退出机制无法满足ONES社区的需求并且对ONES持币用户利益造成一定的损失，设计超级节点的初衷是为了增加ONES用户的共识，获得更多的收益。但从目前来看现在的超级节点并没有发挥太好的作用。目前进入超级节点的ONES总共25万枚，前10名的玩家就占据近80%的ONES，这对于大部分的ONES持有者，并不是一个很好的机制。所以我们将对超级节点的退出机制做更新和优化。</p>
        <div class="dao-title">退出方案</div>
        <div
          class="dao-desc"
        >1、超级节点退出，可以销毁获得总收益的50%对应的ONES即可退出（按照ONES当前价格计算），举例：如果质押了100个ONES，获得的收益为2个EOS，退出时，需要销毁1个EOS等值的ONES，即可退出超级节点。</div>
        <div
          class="dao-desc"
        >2、超级节点退出，质押30天退出销毁20%ONES， 质押90天退出销毁10%ONES， 质押180天退出销毁5%ONES ，180天以上退出不销毁ONES。</div>
      </div>

      <div class="dao-box">
        <div class="dao-title">执行条件</div>
        <div class="dao-desc">1）该提案的参与ONES占ONES流通量比例需≥30%</div>
        <div class="dao-desc">2）投票成功后基金会将开始执行</div>
        <div class="dao-desc">3）投票结束后未达到执行条件的提案将被撤销</div>
      </div>

      <el-card class="dao-card">
        <div slot="header">
          <span>投票</span>
        </div>
        <div>
          <div class="optionsCont">
            <div class="list">
              <div class="backProgress" style="width: 94.2%;"></div>
              <div class="listWord">
                <div style="font-weight: bold;">方案1</div>
                <div>已得票数: 242695.191902（94.20%）</div>
              </div>
              <div class="icon"></div>
            </div>
            <div class="list">
              <div class="backProgress" style="width: 5.79%;"></div>
              <div class="listWord checkedCls">
                <div style="font-weight: bold;">方案2</div>
                <div>已得票数: 14942.937544（5.79%）</div>
              </div>
              <div class="icon"></div>
            </div>
          </div>
        </div>
        <div class="dao-action">
          <div class>
            <span>方案1：</span>
            <el-input-number
              v-model="num"
              size="small"
              @change="handleChange"
              :min="1"
              :max="10"
              label="描述文字"
            ></el-input-number>
          </div>
          <div>
            <span>方案2：</span>
            <el-input-number
              v-model="num"
              size="small"
              @change="handleChange"
              :min="1"
              :max="10"
              label="描述文字"
            ></el-input-number>
          </div>

          <el-button type="primary">成功按钮</el-button>
        </div>
      </el-card>
    </el-dialog>
  </div>
</template>

<script>
import Storage from '../../utils/storage'
import i18n from '../../locales/index'
import Util from '../../utils/util'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      category: 'market',
      voteContract: 'onesgamevote',
      defiContract: 'onesgamedefi',

      ruleDialogVisible: false,
      ticketDialogVisible: false,

      my_all_ticket: 0,
      my_liquidity_ticket: 0,
      my_node_ticket: 0,
      my_lock_ticket: 0
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },

    account: function() {
      return this.$store.state.eos_account
    }
  },
  beforeMount() {},
  mounted() {
    this.getAccount()
  },
  watch: {},
  beforeDestroy() {},
  methods: {
    getAccount() {
      this.eosio.getTableRows(
        {
          code: this.voteContract,
          scope: this.voteContract,
          table: 'account',
          limit: 1,
          lower_bound: this.account.name,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (data && data.rows && data.rows.length == 1) {
            var row = data.rows[0]
            this.my_all_ticket = row.ticket
            this.my_liquidity_ticket = row.liquidity_ticket
            this.my_node_ticket = row.node_ticket
            this.my_lock_ticket = row.lock_ticket
          }
        }.bind(this)
      )
    },
    toDetail(id) {
      this.$router.push({
        name: 'defiDaoDetail',
        params: {
          id: id
        }
      })
    }
  },
  filters: {
    numberFilter(value) {
      return Number(value / 10000).toFixed(4)
    },

    numbersFilter(value) {
      var arr = []
      var tokens = ['ONES']

      for (var i in value) {
        if (tokens[i]) {
          if (tokens[i] == 'BOX') {
            arr.push(Number(value[i] / 1000000).toFixed(6) + ' ' + tokens[i])
          } else {
            arr.push(Number(value[i] / 10000).toFixed(4) + ' ' + tokens[i])
          }
        }
      }
      return arr.join(' | ')
    },
    weightFilter(value) {
      return Number(value).toFixed(1)
    }
  }
}
</script>

<style scoped>
.daoContainer {
  background-color: #060721;
  position: relative;
}
.daoContent {
  padding: 15px;
}
.daoContent /deep/ .el-card {
  background-color: #060721;
  border: 1px solid rgb(140, 174, 197) !important;
}
.box-card {
  background-color: #060721;
  border: 1px solid rgb(88, 111, 153);
  margin-bottom: 15px;
}
.box-card /deep/ .el-card {
  border: 1px solid rgb(140, 174, 197) !important;
  border-radius: 10px;
}
.banner {
  box-sizing: border-box;
  padding-top: 0.8rem;
  margin-bottom: 0.8rem;
  height: 4.4rem;
  background: #040718;
  border-radius: 10px;
}
.banner p {
  text-align: center;
}
.banner p:first-child {
  font-size: 18px;
  color: #f69b4a;
  font-weight: 500;
}

.daoContent /deep/ .el-card__body {
  padding: 10px 20px;
}

.daoContent /deep/ .el-card {
  color: rgb(241, 241, 241);
}

.daoContent .box-card {
  /* border: 1px solid #5e77af;
  border-radius: 10px; */
  background: #040718;
  border: 1px solid rgb(88, 111, 153);
  border-radius: 10px;
  color: #fafbfd;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  padding: 10px 10px 5px;
  position: relative;
}

.daoContent .box-card .card-body {
  padding-left: 10px;
}

.mine-card-header {
  display: flex;
  justify-content: space-between;
}

.card-item {
  display: flex;
  justify-content: space-between;
}

.dao-item {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: #ccc solid 1px;
}
.dao-item:last-child {
  border-bottom: #ccc solid 0px;
}

.dao-item div:first-child {
  flex: 1;
}
.dao-item div:nth-child(2) {
  flex: 2;
}

.optionsCont .list {
  position: relative;
  padding: 0.4rem 0.62667rem;
  background: #fff;
  margin-bottom: 0.29333rem;
  border-radius: 0.10667rem;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
}

.optionsCont .list .backProgress {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 0;
  height: 100%;
  background: rgba(254, 183, 94, 0.2);
  border-radius: 0.10667rem 0 0 0.10667rem;
  z-index: 1;
}

.optionsCont .list .listWord {
  position: relative;
  -webkit-align-self: start;
  align-self: start;
  font-size: 0.41333rem;
  margin-top: -0.06667rem;
  z-index: 2;
}

.optionsCont .list .listWord div:nth-child(2) {
  font-size: 0.34667rem;
  margin-top: 0.08rem;
}

.optionsCont .list .icon {
  -webkit-align-self: start;
  align-self: start;
  margin-top: -0.02667rem;
}

.dao-action button {
  width: 100%;
  margin-top: 10px;
}

.dao-title {
  font-size: 18px;
  font-weight: 500;
  padding-bottom: 5px;
  margin-bottom: 5px;
  margin-top: 10px;
  border-bottom: #ccc solid 1px;
}

.ticket-dialog p {
  display: flex;
  justify-content: space-between;
}
</style>


