import Util from '../../utils/util'
import EosService from '../../utils/eosservice'
import Storage from '../../utils/storage'

export default {
  data() {
    return {
      zone: this.$store.getters.zone,
      contract: 'onesgamedefi',
      divdContract: 'onesgamedivd',

      tokenDrawerVisible: false,
      tokenDialogVisible: false,
      marketTipsDialogVisible: false,
      profitTipsDialogVisible: false,

      cur_liquidity_id: 0,
      max_liquidity_id: 0,
      curliquidity: {},
      estimatedIncome: 0,
      annualizedIncome: 100.0,
      tokenForm: {
        symbol: '',
        address: ''
      },
      blacks: {
        crayfishball: 1,
        onesgametest: 1
      },
      tokenFormRules: {
        symbol: [
          { required: true, message: '请输入币种名称', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '请输入合约名称', trigger: 'blur' },
          { max: 12, message: '输入有误,请重新输入', trigger: 'blur' }
        ]
      },

      searchtoken: '',
      tokenType: '',
      defaultImg: '/static/img/tokens/eos.png',
      inoutOrder: true,
      inToken: {
        symbol: 'EOS',
        precision: 4,
        address: 'eosio.token',
        key: 'eosio.token-eos'
      },
      outToken: {
        symbol: 'USDT',
        precision: 4,
        address: 'tethertether',
        key: 'tethertether-usdt'
      },
      tokens: {},
      tokensrank: {
        'eosio.token-eos': 100,
        'tethertether-usdt': 99,
        'eosonestoken-ones': 98,
        'crayfishgold-xw': 99,
        'btc.ptokens-pbtc': 97,
        'lobstertoken-xo': 97,
        'minedfstoken-dfs': 96,
        'token.defi-box': 95,
        'eosiotptoken-tpt': 94
      },
      _tokens: [],
      pairs: {},
      liquidites: {},
      myLiquidities: {},

      //create market
      createLoading: false,

      bonusQuantity: '0.0000 EOS',
      bonusRate: '0.00',
      bonusStaked: 0,

      addMarketDialogVisible: false,
      add_market_liquidity_id: 0,

      subMarketDialogVisible: false,
      sub_market_liquidity_id: 0,

      inMarketTokenAmount: '',
      outMarketTokenAmount: '',
      inMarketToken: {
        address: '',
        symbol: '',
        key: '',
        precision: 4,
        balance: '0.0000',
        quantity: 0
      },
      outMarketToken: {
        address: '',
        symbol: '',
        key: '',
        precision: 4,
        balance: '0.0000',
        quantity: 0
      },
      inMarketOutPrice: '0.0000',
      outMarketInPrice: '0.0000',

      market_liquidity: null,
      market_liquidity_token: 0,
      sub_liquidity_token: 0,
      add_liquidity_token: 0,
      all_market_liquidity_token: 0,

      priceMarketDialogVisible: false,
      tokenMarketDialogVisible: false,
      tipsMarketDialogVisible: false,

      addLoading: false,
      subLoading: false
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },
    account: function() {
      return this.$store.state.eos_account
    }
  },
  watch: {
    searchtoken(value) {
      console.log('market-----------')
      value = value.toUpperCase()
      for (var i in this.tokens) {
        var ishidden =
          this.tokens[i]['symbol'].indexOf(value) === -1 ? true : false
        console.log(this.tokens[i]['symbol'], ishidden)
        this.tokens[i]['ishidden'] = ishidden
      }
    },
    tokens(value) {
      var _tokens = value
      for (var i in _tokens) {
        _tokens[i]['rank'] = 0
      }
      for (var i in this.tokensrank) {
        if (_tokens[i]) {
          _tokens[i]['rank'] = this.tokensrank[i]
        }
      }
      var _t = []
      for (var i in _tokens) {
        _t.push(_tokens[i])
      }
      _t.sort(function(a, b) {
        return a.rank - b.rank > 0 ? -1 : 1
      })
      this._tokens = _t
    },
    sub_liquidity_token(value) {
      console.log(value)
      var liquidity_token = this.all_market_liquidity_token
      this.inMarketToken.balance = Number(
        parseFloat((value / liquidity_token) * this.inMarketToken.quantity)
      ).toFixed(this.inMarketToken.precision)
      this.outMarketToken.balance = Number(
        parseFloat((value / liquidity_token) * this.outMarketToken.quantity)
      ).toFixed(this.outMarketToken.precision)
    }
  },
  beforeMount() {
    var inToken = Storage.get('defi_swap_intoken')
    var outToken = Storage.get('defi_swap_outtoken')
    if (inToken) {
      this.inToken = inToken
    }
    if (outToken) {
      this.outToken = outToken
    }
    if (this.$route.params.in) {
      this.inToken = this.$route.params.in
      Storage.set('defi_swap_intoken', this.inToken)
    }
    if (this.$route.params.out) {
      this.outToken = this.$route.params.out
      Storage.set('defi_swap_outtoken', this.outToken)
    }
  },
  mounted() {
    this.getLiquidities(0)
    this.getDivdTable()
  },

  methods: {
    refresh() {},

    getMyLiquidities() {
      console.log('xxxxxxxxxxxx', this.liquidites)
      this.getMyLiquiditiy(1)
      this.getMyLiquiditiy(3)
      this.getMyLiquiditiy(4)

      this.getMyLiquiditiy(37)
      this.getMyLiquiditiy(51)

      this.liquidity_ids = []
      for (var i in this.liquidites) {
        if (this.liquidites[i]['liquidity_token'] > 0) {
          if (!(i == 1 || i == 3 || (i == 4) | (i == 37))) {
            this.liquidity_ids.push(i)
          }
        }
      }
      // for (var i in this.liquidites) {
      //   var liquidity_id = i
      //   setTimeout(
      //     function() {
      //       this.getMyLiquiditiy(liquidity_id)
      //     }.bind(this),
      //     parseInt(i) * 500
      //   )
      // }
      // console.log("x............",this.liquidity_ids)
      this._getMyLiquidities(0)
    },
    _getMyLiquidities(k) {
      console.log(this.liquidity_ids.length, k)
      if (this.liquidity_ids.length > k) {
        setTimeout(
          function() {
            var liquidity_id = this.liquidity_ids[k]
            this.getMyLiquiditiy(liquidity_id)
            k += 1
            this._getMyLiquidities(k)
          }.bind(this),
          500
        )
      }
    },
    getMyLiquiditiy(liquidity_id) {
      if (!this.account) {
        return
      }
      // console.log('xxx,_getMyLiquidities')
      this.eosio.getTableRows(
        {
          code: this.contract,
          scope: liquidity_id,
          table: 'defipools',
          limit: 1,
          lower_bound: this.account.name,
          // index_position: 2,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0) {
            if (
              data.rows &&
              data.rows.length == 1 &&
              data.rows[0].account == this.account.name
            ) {
              Vue.set(this.myLiquidities, liquidity_id, data.rows[0])
            } else {
              Vue.delete(this.myLiquidities, liquidity_id)
            }
          }
        }.bind(this)
      )
    },
    getLiquidities(liquidity_id, cb) {
      this.eosio.getTableRows(
        {
          code: this.contract,
          scope: this.contract,
          table: 'liquidity',
          limit: 100,
          lower_bound: liquidity_id,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0) {
            for (var i in data.rows) {
              var row = data.rows[i]
              if (
                this.blacks[row.token1.address] ||
                this.blacks[row.token2.address]
              ) {
                continue
              }
              var liquidity_id = row.liquidity_id

              this.max_liquidity_id =
                this.max_liquidity_id > liquidity_id
                  ? this.max_liquidity_id
                  : liquidity_id

              var symbol1 = row.token1.symbol.split(',')
              var address1 = row.token1.address
              var key1 = address1 + '-' + symbol1[1].toLowerCase()

              Vue.set(this.tokens, key1, {
                address: address1,
                symbol: symbol1[1],
                precision: parseInt(symbol1[0]),
                key: key1
              })
              // this.tokens[key1] = {
              //   address: address1,
              //   symbol: symbol1[1],
              //   precision: parseInt(symbol1[0])
              // }

              var symbol2 = row.token2.symbol.split(',')
              var address2 = row.token2.address
              var key2 = address2 + '-' + symbol2[1].toLowerCase()
              Vue.set(this.tokens, key2, {
                address: address2,
                symbol: symbol2[1],
                precision: parseInt(symbol2[0]),
                key: key2
              })

              // this.tokens[key2] = {
              //   address: address2,
              //   symbol: symbol2[1],
              //   precision: parseInt(symbol2[0])
              // }

              this.pairs[key1 + '|' + key2] = liquidity_id
              this.pairs[key2 + '|' + key1] = liquidity_id

              this.liquidites[liquidity_id] = {
                key1: key1,
                key2: key2,
                token1: this.tokens[key1],
                token2: this.tokens[key2],
                quantity1: row.quantity1,
                quantity2: row.quantity2,
                liquidity_token: row.liquidity_token,
                price1: row.price1,
                price2: row.price2,
                weight: Number(row.liquidity_weight).toFixed(1)
              }
            }
            if (data.more) {
              setTimeout(function() {
                that.getLiquidities(this.max_liquidity_id, cb)
              }, 500)
            } else {
              if (cb && typeof cb == 'function') {
                cb()
              }
              this.updateCurLiquidity()

              setTimeout(
                function() {
                  this.getMyLiquidities()
                }.bind(this),
                500
              )
            }
          }
        }.bind(this)
      )
    },
    toSwap() {
      this.$router.push({
        name: 'defiSwap'
      })
    },
    toMine() {
      this.$router.push({
        name: 'defiMine'
      })
    },
    toNode(type) {
      this.$router.push({
        name: 'nodeScene'
      })
    },
    toAbout(type) {
      this.$router.push({
        name: 'defiAbout'
      })
    },
    toPresale(type) {
      window.location.href = 'https://xo.ones.game'
      // this.$router.push({
      //   name: 'defiPresale'
      // })
    },

    addTokenPop() {
      this.tokenDrawerVisible = false
      this.tokenDialogVisible = true

      for (var i in this.liquidites) {
        this.liquidites[i]['isshow'] = false
      }
      return false
    },
    addToken() {
      //this.$refs.ruleForm.validateField('checkPass');
      this.$refs['tokenForm'].validate(valid => {
        if (valid) {
          this.eosio.getCurrencyStats(
            this.tokenForm.address,
            this.tokenForm.symbol,
            function(ret) {
              if (ret) {
                var symbol = this.tokenForm.symbol.toUpperCase()
                var supply = ret[symbol]['supply']
                var precision = supply.split(' ')[0].split('.')[1]
                if (ret[symbol]) {
                  var token = {
                    address: this.tokenForm.address,
                    symbol: this.tokenForm.symbol,
                    precision: parseInt(precision.length)
                  }
                  console.log(token)
                  this.selectToken(token)
                  this.tokenDialogVisible = false
                } else {
                  this.$message({
                    message: '信息输入有误',
                    type: 'warning',
                    center: true
                  })
                  return false
                }
              } else {
                this.$message({
                  message: '信息输入有误',
                  type: 'warning',
                  center: true
                })
                return false
              }
            }.bind(this)
          )
        } else {
          return false
        }
      })
    },

    getDivdTable() {
      this.eosio.getTableRows(
        {
          code: this.divdContract,
          scope: this.divdContract,
          table: 'config',
          limit: 1,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0 && data.rows && data.rows.length == 1) {
            this.bonusQuantity =
              Number(parseFloat(data.rows[0]['bonus_quantity'])).toFixed(2) +
              ' EOS'

            this.bonusStaked = data.rows[0]['stake_quantity']

            this._getOnesPrice()
          }
        }.bind(this)
      )
    },

    _getOnesPrice() {
      var that = this
      this.eosio.getTableRows(
        {
          code: this.contract,
          scope: this.contract,
          table: 'liquidity',
          limit: 1,
          key_type: 'i64',
          lower_bound: 3,
          json: true
        },
        function(code, msg, data) {
          if (
            data.rows &&
            data.rows.length == 1 &&
            data.rows[0]['liquidity_id'] == 3
          ) {
            var price = data.rows[0]['price1']

            that.bonusRate =
              Number(
                ((parseFloat(that.bonusQuantity) * 0.01) /
                  parseFloat(that.bonusStaked)) *
                  365 *
                  100 *
                  price
              ).toFixed(2) + '%'
          }
        }
      )
    },

    selectTokens(type) {
      this.tokenDrawerVisible = true
      this.tokenType = type
      this.searchtoken = ''
    },
    selectToken(token) {
      console.log(token, 'symbol')
      if (this.tokenType == 'in') {
        this.inToken = token
        Storage.set('defi_swap_intoken', this.inToken)
      } else {
        this.outToken = token
        Storage.set('defi_swap_outtoken', this.outToken)
      }

      this.tokenDrawerVisible = false
      this.updateCurLiquidity()
      return false
    },
    updateCurLiquidity() {
      var key1 = this.inToken.key
      var key2 = this.outToken.key

      console.log(key1, key2, this.pairs)
      if (this.pairs[key1 + '|' + key2]) {
        this.cur_liquidity_id = this.pairs[key1 + '|' + key2]
        this.curliquidity = this.liquidites[this.cur_liquidity_id]
        this.curliquidity.order = this.curliquidity.key1 == key1 ? true : false

        var total = 0
        var cur_weight = this.curliquidity.weight
        for (var i in this.liquidites) {
          // console.log(this.liquidites[i], 'xxxxxxxx')
          if (this.liquidites[i]['weight'] > 0) {
            var amount =
              parseFloat(this.liquidites[i]['quantity1']) *
              this.liquidites[i]['weight']
            if (this.liquidites[i]['key1'] != 'eosio.token-eos') {
              var _id = this.pairs[
                this.liquidites[i]['key1'] + '|' + this.liquidites[i]['key2']
              ]
              amount *= this.liquidites[_id]['price2']
            }
            total += amount
          }
        }

        this.estimatedIncome = Number(
          86400 * 0.01 * ((100 * cur_weight) / total)
        ).toFixed(4)

        var _id = this.pairs['eosio.token-eos|eosonestoken-ones']
        var price = this.liquidites[_id]['price2']
        this.annualizedIncome = Number(
          86400 * 0.01 * ((1 * cur_weight) / total) * 365 * price * 100
        ).toFixed(2)

        console.log(this.annualizedIncome)
      } else {
        this.cur_liquidity_id = 0
        this.curliquidity = {}
      }
    },

    // addMarket() {
    //   if (!this.cur_liquidity_id) {
    //     return
    //   }
    //   this.$router.push({
    //     name: 'addDefiMarket',
    //     params: {
    //       liquidity_id: this.cur_liquidity_id,
    //       type: 1
    //     }
    //   })
    // },
    toXO() {
      window.location.href = 'https://xo.ones.game'
    },
    addMarket(liquidity_id) {
      this.add_market_liquidity_id = liquidity_id
      this.addMarketDialogVisible = true

      var row = this.liquidites[liquidity_id]

      this.market_liquidity = row

      this.all_market_liquidity_token = row.liquidity_token
      console.log(row, 'xxxxxx')
      var address1 = row.token1.address
      var key1 = address1 + '-' + row.token1.symbol.toLowerCase()
      this.inMarketToken = {
        address: address1,
        symbol: row.token1.symbol,
        key: key1,
        precision: row.token1.precision,
        balance: '0.0000',
        quantity: parseFloat(row.quantity1),
        price: row.price1
      }

      var address2 = row.token2.address
      var key2 = address2 + '-' + row.token2.symbol.toLowerCase()
      this.outMarketToken = {
        address: address2,
        symbol: row.token2.symbol,
        key: key2,
        precision: row.token2.precision,
        balance: '0.0000',
        quantity: parseFloat(row.quantity2),
        price: row.price2
      }

      this.inMarketOutPrice = Number(row.price1).toFixed(
        parseInt(this.inMarketToken.precision) + 2
      )
      this.outMarketInPrice = Number(row.price2).toFixed(
        parseInt(this.outMarketToken.precision) + 2
      )

      this.getBalances()
    },
    subMarket(liquidity_id) {
      this.sub_market_liquidity_id = liquidity_id
      this.subMarketDialogVisible = true

      var row = this.liquidites[liquidity_id]

      this.market_liquidity = row

      this.all_market_liquidity_token = row.liquidity_token
      console.log(row, 'xxxxxx')
      var address1 = row.token1.address
      var key1 = address1 + '-' + row.token1.symbol.toLowerCase()
      this.inMarketToken = {
        address: address1,
        symbol: row.token1.symbol,
        key: key1,
        precision: row.token1.precision,
        balance: '0.0000',
        quantity: parseFloat(row.quantity1),
        price: row.price1
      }

      var address2 = row.token2.address
      var key2 = address2 + '-' + row.token2.symbol.toLowerCase()
      this.outMarketToken = {
        address: address2,
        symbol: row.token2.symbol,
        key: key2,
        precision: row.token2.precision,
        balance: '0.0000',
        quantity: parseFloat(row.quantity2),
        price: row.price2
      }

      this.inMarketOutPrice = Number(row.price1).toFixed(
        parseInt(this.inMarketToken.precision) + 2
      )
      this.outMarketInPrice = Number(row.price2).toFixed(
        parseInt(this.outMarketToken.precision) + 2
      )

      this.updateSubToken(0)
      // this.getBalances()
    },
    manageMarket(liquidity_id) {
      this.$router.push({
        name: 'addDefiMarket',
        params: {
          liquidity_id: liquidity_id,
          type: 2
        }
      })
    },
    updateSubToken(alpha) {
      this.subAlpha = alpha
      console.log(
        this.myLiquidities,
        this.myLiquidities[this.sub_market_liquidity_id]
      )
      var liquidity_token = this.myLiquidities[this.sub_market_liquidity_id][
        'liquidity_token'
      ]

      this.sub_liquidity_token = parseInt(liquidity_token * alpha)
    },
    sub() {
      var liquidity_token = this.myLiquidities[this.sub_market_liquidity_id]
        ? this.myLiquidities[this.sub_market_liquidity_id]['liquidity_token']
        : 0
      if (liquidity_token == 0 || liquidity_token < this.sub_liquidity_token) {
        this.$message.error('余额不足')
        return false
      }
      this.subLoading = true

      var actions = [
        {
          account: this.contract,
          name: 'subliquidity',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            account: this.account.name,
            liquidity_id: this.sub_market_liquidity_id,
            liquidity_token: this.sub_liquidity_token
          }
        }
      ]
      console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.subLoading = false
          if (code == 0) {
            this.$message({ message: '减少做市成功', type: 'success' })
            setTimeout(
              function() {
                this.getMyLiquidities()
              }.bind(this),
              500
            )
            if (this.subAlpha == 1) {
              this.subMarketDialogVisible = false
              this.sub_market_liquidity_id = 0
            }
            this.subAlpha = 0
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },
    createMarket() {
      this.$router.push({
        name: 'createDefiMarket'
      })
    },
    toMarketLog() {
      this.$router.push({
        name: 'marketLog'
      })
    },

    //market
    inputMarketHandle(type) {
      if (this.all_market_liquidity_token == 0) {
        if (
          parseFloat(this.outMarketTokenAmount) > 0 &&
          parseFloat(this.inMarketTokenAmount) > 0
        ) {
          this.inMarketOutPrice = Number(
            this.outMarketTokenAmount / this.inMarketTokenAmount
          ).toFixed(4)
          this.outMarketInPrice = Number(
            this.inMarketTokenAmount / this.outMarketTokenAmount
          ).toFixed(4)

          //todo
          var t =
            this.inMarketTokenAmount *
            Math.pow(10, this.inMarketToken.precision) *
            this.outMarketTokenAmount *
            Math.pow(10, this.outMarketToken.precision)
          this.add_liquidity_token = parseInt(Math.sqrt(t))
        }
      } else {
      }
    },
    inputMarketTokenAmount(type) {
      if (type == 'in') {
        this.inMarketTokenAmount = Number(this.inMarketToken.balance).toFixed(
          this.inMarketToken.precision
        )
      } else if (type == 'out') {
        this.outMarketTokenAmount = Number(this.outMarketToken.balance).toFixed(
          this.outMarketToken.precision
        )
      }
      this.changeMarketHandle(type)
    },

    changeMarketHandle(type) {
      if (type == 'in') {
        this.inMarketTokenAmount = Number(this.inMarketTokenAmount).toFixed(
          this.inMarketToken.precision
        )
      } else if (type == 'out') {
        this.outMarketTokenAmount = Number(this.outMarketTokenAmount).toFixed(
          this.outMarketToken.precision
        )
      }

      if (this.all_market_liquidity_token == 0) {
        if (
          parseFloat(this.outMarketTokenAmount) > 0 &&
          parseFloat(this.inMarketTokenAmount) > 0
        ) {
          this.inMarketOutPrice = Number(
            this.outMarketTokenAmount / this.inMarketTokenAmount
          ).toFixed(4)
          this.outMarketInPrice = Number(
            this.inMarketTokenAmount / this.outMarketTokenAmount
          ).toFixed(4)

          //todo
          var t =
            this.inMarketTokenAmount *
            Math.pow(10, this.inMarketToken.precision) *
            this.outMarketTokenAmount *
            Math.pow(10, this.outMarketToken.precision)
          this.add_liquidity_token = parseInt(Math.sqrt(t))
        }
      } else {
        if (type == 'in') {
          this.inMarketTokenAmount = Number(this.inMarketTokenAmount).toFixed(
            this.inMarketToken.precision
          )
          var outMarketTokenAmount =
            parseFloat(this.inMarketTokenAmount) * this.inMarketToken.price

          this.outMarketTokenAmount = Number(outMarketTokenAmount).toFixed(
            this.outMarketToken.precision
          )
        } else {
          this.outMarketTokenAmount = Number(this.outMarketTokenAmount).toFixed(
            this.outMarketToken.precision
          )
          var inMarketTokenAmount =
            parseFloat(this.outMarketTokenAmount) * this.outMarketToken.price

          this.inMarketTokenAmount = Number(inMarketTokenAmount).toFixed(
            this.inMarketToken.precision
          )
        }

        var inMarketTokenAmount = parseFloat(this.inMarketTokenAmount)
        var quantity = this.inMarketToken.quantity
        var alpha = inMarketTokenAmount / (inMarketTokenAmount + quantity)

        this.add_liquidity_token = parseInt(
          (alpha / (1 - alpha)) * this.all_market_liquidity_token
        )
      }
    },

    add() {
      if (
        !(
          parseFloat(this.outMarketTokenAmount) > 0 &&
          parseFloat(this.inMarketTokenAmount)
        )
      ) {
        this.$message.error('请输入金额')
        return false
      }
      this.addLoading = true

      var actions = [
        {
          account: this.inMarketToken.address,
          name: 'transfer',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            from: this.account.name,
            to: this.contract,
            quantity:
              this.inMarketTokenAmount + ' ' + this.inMarketToken.symbol,
            memo: 'addliquidity,' + this.add_market_liquidity_id
          }
        },
        {
          account: this.outMarketToken.address,
          name: 'transfer',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            from: this.account.name,
            to: this.contract,
            quantity:
              this.outMarketTokenAmount + ' ' + this.outMarketToken.symbol,
            memo: 'addliquidity,' + this.add_market_liquidity_id
          }
        },
        {
          account: this.contract,
          name: 'addliquidity',
          authorization: [
            {
              actor: this.account.name,
              permission: this.account.authority
            }
          ],
          data: {
            account: this.account.name,
            liquidity_id: this.add_market_liquidity_id
          }
        }
      ]
      console.log(actions)
      this.eosio.transaction(
        actions,
        function(code, msg, data) {
          console.log(code, msg, data)
          this.addLoading = false
          if (code == 0) {
            this.$message({ message: '加入成功', type: 'success' })
            this.outMarketTokenAmount = ''
            this.inMarketTokenAmount = ''
            this.addMarketDialogVisible = false
            this.add_market_liquidity_id = 0
            // this.getMyLiquidities()
            setTimeout(
              function() {
                this.getMyLiquidities()
              }.bind(this),
              500
            )
            setTimeout(
              function() {
                this.getMyLiquidities()
              }.bind(this),
              1500
            )
          } else {
            this.$message({
              message: msg,
              type: 'error'
            })
          }
        }.bind(this)
      )
    },

    getBalances() {
      this.eosio.getBalance(
        this.inMarketToken.address,
        this.inMarketToken.symbol,
        function(balance) {
          this.inMarketToken.balance = balance
        }.bind(this)
      )
      this.eosio.getBalance(
        this.outMarketToken.address,
        this.outMarketToken.symbol,
        function(balance) {
          this.outMarketToken.balance = balance
        }.bind(this)
      )
    }
  },
  filters: {
    percentFilter(value) {
      if (value * 10000 < 1) {
        return '<0.01%'
      }
      return Number(value * 100).toFixed(2) + '%'
    },
    quantityFilter(quantity, liquidity_token, all_liquidity_token) {
      var p = quantity.split(' ')[0].split('.')
      var precision = p.length > 1 ? p[1].length : 4
      var symbol = quantity.split(' ')[1]
      var amount = parseFloat(quantity.split(' ')[0])

      return (
        Number((liquidity_token / all_liquidity_token) * amount).toFixed(
          precision
        ) +
        ' ' +
        symbol
      )
    },
    profitFilter(
      quantity1,
      quantity2,
      liquidity_token,
      all_liquidity_token,
      all_quantity1,
      all_quantity2,
      price
    ) {
      var amount1 = parseFloat(quantity1.split(' ')[0])
      var amount2 = parseFloat(quantity2.split(' ')[0])
      var precision = quantity1.split(' ')[0].split('.')[1].length

      // var all_liquidity_token = liquidity['liquidity_token']

      var newamount1 =
        (liquidity_token / all_liquidity_token) * parseFloat(all_quantity1)
      var newamount2 =
        (liquidity_token / all_liquidity_token) * parseFloat(all_quantity2)

      var profit = newamount1 + newamount2 * price - amount1 - amount2 * price
      return Number(profit).toFixed(precision)
    }
  }
}
</script>

