import AccountService from '../../api/account'
import Storage from '../../utils/storage'

import cryptoJs from "crypto-js"

const ONES_ACCOUNT_KEY = "ones_account"

var isLoggedIn = function () {
  // return true;
  var account = Storage.get(ONES_ACCOUNT_KEY);
  if (account) {
    if (account.access_expire_time > Date.now() / 1000) {
      return account
    }
  } else {
    return false;
  }
};



const _account = isLoggedIn() || null
const state = {
  isLogin: _account ? true : false,
  user: _account,
  dragonex_uid: '',
};

// actions
const actions = {
  check({
    commit
  }) {
    return new Promise((resolve, reject) => {
      if (state.user && state.user.access_token) {
        AccountService.check().then(res => {
          console.log(res)
          if (res.code == 0) {
            // commit("login", res.data)
          } else {
            // commit("failed")
            // 验证登录
          }
          resolve(res)
        })
      } else {
        resolve({
          code: 1
        })
      }
    });
  },

  login({
    commit
  }, params) {
    if (params.type == "mobile") {
      params.password = cryptoJs.SHA256(params.password).toString()
    }

    return new Promise((resolve, reject) => {
      AccountService.login(
        params
      ).then(res => {
        console.log(res)
        if (res.code == 0) {
          commit("login", res.data)
        } else {
          commit("failed")
        }
        resolve(res)
      })
    });
  },

  register({
    commit
  }, account) {
    return new Promise((resolve, reject) => {
      account.password = cryptoJs.SHA256(account.password).toString()
      console.log(account.password)

      var source = Storage.get('ones_source')
      account.source = source

      if (source == "justalk") {
        var justalk = Storage.get("ones_justalk")
        account.justalk_uid = justalk.justalk_uid;
        account.nickname = justalk.nickname
      }

      console.log(account)

      AccountService.register(
        account,
      ).then(res => {
        console.log(res)
        if (res.code == 0) {
          commit("register", res.data)
        }
        resolve(res)
      })
    });

  },

  authorize({
    commit
  }, account) {
    return new Promise((resolve, reject) => {
      console.log(account)

      AccountService.authorize(
        account,
      ).then(res => {
        console.log(res)
        if (res.code == 0) {
          commit("authorize", res.data)
        }
        resolve(res)
      })
    });
  },

  logout({
    commit
  }) {
    return new Promise((resolve, reject) => {
      commit("logout")
      resolve()
    })
  },

  setpaypassword({
    commit
  }, payform) {
    console.log("setpaypassword", payform)
    return new Promise((resolve, reject) => {
      // 加密
      const vcode = payform.code
      var password = payform.pass
      password = cryptoJs.SHA256(password).toString()
      AccountService.setPayPassword(password, vcode).then(res => {
        console.log(res)
        if (res.code == 0) {
          commit("SET_PAY_PASSWORD")
        }
        resolve(res)
      })
    });
  },

  setloginpassword({
    commit
  }, params) {
    return new Promise((resolve, reject) => {
      // 加密
      var password = cryptoJs.SHA256(params.password).toString()

      AccountService.setLoginPassword(
        password, params.vcode
      ).then(res => {
        console.log(res)
        resolve(res)
      })
    });
  },

  bindmobile({
    commit
  }, params) {
    return new Promise((resolve, reject) => {
      AccountService.bindMobile(
        params.area, params.mobile, params.vcode
      ).then(res => {
        console.log(res)
        if (res.code == 0) {
          commit("bindMobile", res.data)
        }
        resolve(res)
      })
    });
  },

  binddragonex({
    commit
  }, params) {
    return new Promise((resolve, reject) => {
      AccountService.bindDragonex(
        params
      ).then(res => {
        console.log(res)
        if (res.code == 0) {
          commit("bindDragonex", res.data)
        }
        resolve(res)
      })
    });
  },

  bindzt({
    commit
  }, params) {
    return new Promise((resolve, reject) => {
      AccountService.bindZT(
        params
      ).then(res => {
        console.log(res)
        if (res.code == 0) {
          commit("bindZT", res.data)
        }
        resolve(res)
      })
    });
  },
}

const mutations = {

  SET_PAY_PASSWORD(state) {
    state.user.has_pay_password = true
  },

  login(state, account) {
    Storage.set(ONES_ACCOUNT_KEY, JSON.stringify(account));
    state.isLogin = true
    state.user = account
  },

  logout(state) {
    Storage.remove(ONES_ACCOUNT_KEY);
    state.user = null;
    state.isLogin = false
  },

  failed(state) {
    Storage.remove(ONES_ACCOUNT_KEY);
    state.user = null;
    state.isLogin = false
  },

  register(state, account) {
    Storage.set(ONES_ACCOUNT_KEY, JSON.stringify(account));
    state.user = account
    state.isLogin = true
  },

  authorize(state, account) {
    Storage.set(ONES_ACCOUNT_KEY, JSON.stringify(account));
    state.user = account
    state.isLogin = true
  },

  bindMobile(state, data) {
    // state.user.area = res.data.area;
    state.user.mobile = data.mobile;
    var account = state.user
    Storage.set(ONES_ACCOUNT_KEY, JSON.stringify(account));
  },

  bindDragonex(state, data) {
    // state.user.area = res.data.area;
    state.user.dragonex_uid = data.dragonex_uid;
    var account = state.user
    Storage.set(ONES_ACCOUNT_KEY, JSON.stringify(account));
  },

  bindZT(state, data) {
    // state.user.area = res.data.area;
    state.user.zt_uid = data.zt_uid;
    var account = state.user
    Storage.set(ONES_ACCOUNT_KEY, JSON.stringify(account));
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
}


