    <el-dialog
      title="详细玩法规则"
      :visible.sync="eosRuleDialog"
      :width="dialogWidth"
      class="eosRuleDialog"
    >
      <h6>0、如何赚钱？</h6>
      <p>玩游戏送豪礼,EOS送个不停.现在玩《驯龙高手》最高送1000EOS,邀请好友送EOS,还有邀请基金等你拿</p>
      <h6 v-if="zone == 'eos'">1、什么是ONES？</h6>
      <p v-if="zone == 'eos'">ONES是OnesGame的通证积分,可以享受游戏特权,参与超级节点获得节点收益等</p>
      <h6 v-if="zone != 'eos'">1、在哪里可以玩？</h6>
      <p v-if="zone != 'eos'">
        下载
        <a href="https://www.tokenpocket.pro/zh/download/app" target="_blank">TokenPocket</a>钱包搜索OnesPlay即可体验
      </p>

      <h6>2、如何获取ONES？</h6>
      <p>玩家可以在《驯龙高手》游戏中购买道具将赠送获得ONES，赠送的ONES分为60天线性释放,在释放期间,玩家如果不使用释放的ONES，60天到期当天可选择全额退款。如果玩家在释放期间使用了释放的ONES则失去全额退款权利。</p>

      <h6>3、ONES有多少？如何赠送？</h6>
      <p>ONES在EOS上总共发行1000万枚,永不增发且将通过游戏收益持续销毁。ONES释放难度将采取10个大阶段（每个大阶段分10个小阶段），每个小阶段赠送的ONES将逐步减少(如果在释放期间不使用ONES则不会计算到阶段中，反之释放的ONES将计算到阶段难度内)。</p>
      <h6>4、ONES游戏收益如何分配？</h6>
      <p>50%为节点奖励,25%二级市场回购销毁,5%为驯龙和龙堡升级奖励,10%推荐奖励,10%团队研发</p>
      <h6>5、什么是超级节点计划？如何成为超级节点并获得节点奖励？</h6>
      <p>超级节点计划是为了更好的奖励ONES持有者的一种方式。任何玩家都可以参与节点竞选，按照ONES认购的数量排名，前100名都会获得节点奖励，排名实时更新，100名之外的节点将退回认购的ONES,无法获得节点奖励也可继续认购进入前100名。</p>
      <h6>6、如何退出超级节点？</h6>
      <p>超级节点有两种退出一种是被动退出，一种是主动退出，当排名跌出前100时会被动退出无手续费。主动需要支付手续费，手续费将被销毁。认购30天以内若赎回将销毁20%的ONES；认购30-60天以内若赎回将销毁30%的ONES；认购60-90天以内若赎回将销毁40%的ONES，认购90天以上若赎回将销毁50%的ONES</p>
    </el-dialog>

