  beforeMount() {
    var key = Storage.get('ones_notify')
    if (key != '136') {
      Storage.set('ones_notify', '136')
      if (window.location.host != 'play.ones.game') {
        this.noticeDialogVisible = true
      }
    }
  },
  mounted() {
    // window.addEventListener('scroll', this.handleScroll, true)

    var source = Storage.get('ones_source')
    this.source = source
    for (var i in this.bannerItem) {
      if (this.bannerItem[i].show && this.bannerItem[i].show == source) {
        this.bannerItem[i]['isshow'] = true
      }
      if (this.bannerItem[i].show && this.bannerItem[i].show == this.zone) {
        this.bannerItem[i]['isshow'] = true
      }

      if (
        this.bannerItem[i].hidden &&
        (this.bannerItem[i].hidden == source ||
          this.bannerItem[i].hidden == this.zone)
      ) {
        this.bannerItem[i]['isshow'] = false
      }
    }
    // console.log(this.bannerItem)

    // var source = this.$route.query.source
    if (source == 'gxb') {
      for (var i in this.games) {
        if (
          this.games[i]['game_id'] == 'dragonmaster' ||
          this.games[i]['game_id'] == 'landlord'
        ) {
          this.games[i]['isshow'] = false
        }

        if (this.games[i]['game_id'] == 'fishing') {
          this.games[i]['isbeta'] = true
        }
      }
    }
    var game = Storage.get('event_game')
    if (game != 'longxingshewu' && this.zone != 'eos') {
      this.eventDialogVisible = true
      Storage.set('event_game', 'longxingshewu')
    }

    // var ones_uid =
    //   this.$store.state.account.user && this.$store.state.account.user.ones_uid
    //     ? this.$store.state.account.user.ones_uid
    //     : 0
    // if (
    //   this.$route.query.vip == 'onesgame' ||
    //   ones_uid == 1000056 ||
    //   ones_uid == 1000008
    // ) {
    //   this.isVip = true
    // }

    var category = Storage.get('gameSceneCategory')
    if (category) {
      this.category = category
      console.log(this.category)
    }

    this.getGames()

    // gameService
    //   .getHistoryGames()
    //   .then(res => {
    //     this.playedGames = res.data.games
    //     var source = Storage.get('ones_source')
    //   })
    //   .catch(err => {})

    var zone = this.$store.getters.zone
    if (zone == 'eos') {
      if (TpUtil.isConnected()) {
        TpUtil.fullScreen({
          fullScreen: 0
        })
      }
    }
  },
  beforeDestroy() {
    if (this.loading) {
      this.loading.close()
    }
  },
  beforeRouteLeave(to, form, next) {
    next()
  },

  methods: {
    mapping() {
      this.$message({
        message: '暂未开启,敬请期待',
        type: 'info',
        center: true
      })
    },
    toDefi() {
      this.$router.push({
        name: 'defiSwap',
        params: {}
      })
    },
    toXwMine(type) {
      // this.$router.push({
      //   name: 'defiPresale'
      // })
      window.location.href = 'https://xo.ones.game'
    },
    luckydraw() {
      this.$router.push({
        name: 'luckywheelScene',
        params: {}
      })
    },
    openRank() {
      return
      gameService
        .getGameRank()
        .then(res => {
          if (res.code == 0) {
            this.gameRankData = res.data.ranks
          }
        })
        .catch(err => {})
      this.rankDialogVisible = true
    },
    closeEvent() {
      this.eventDialogVisible = false
    },
    openDialog(type) {
      if (type == 'HappyBeans') {
        this.happyBeansDialog = true
      } else if (type == 'EosTips') {
        this.eosTipsDialog = true
      }
    },
    goDownload() {
      this.$router.push({
        name: 'downloadScene'
      })
    },
    getGames() {
      return
      gameService
        .getGames(this.zone)
        .then(res => {
          var games = res.data.games
          var source = Storage.get('ones_source')
          this.remainNumber = res.data.remain_number

          var hideNonEnglishGame = false
          if (i18n.locale == 'en') {
            hideNonEnglishGame =
              Storage.get('ones_nonengame') == 1 ? true : false
          }

          for (var i in games) {
            var game = games[i]
            if (game['game_id'] == 'dragonmaster') {
              games[i]['name'] = this.$t('gameScenePart.dragonmaster.name')
              games[i]['sub'] = this.$t('gameScenePart.dragonmaster.desc')
              games[i]['type'] = this.$t('gameScenePart.dragonmaster.type')
            } else if (game['game_id'] == 'dragonclash') {
              games[i]['name'] = this.$t('gameScenePart.dragonclash.name')
              games[i]['sub'] = this.$t('gameScenePart.dragonclash.desc')
              games[i]['type'] = this.$t('gameScenePart.dragonclash.type')
            } else {
              if (hideNonEnglishGame) {
                games[i]['isshow'] = false
              }
            }

            if (source == 'gxb') {
              if (
                game['game_id'] == 'dragonmaster' ||
                game['game_id'] == 'landlord'
              ) {
                games[i]['isshow'] = false
              }
              if (game['game_id'] == 'fishing') {
                games[i]['isbeta'] = true
              }
            }
          }
          this.games = games
        })
        .catch(err => {})
    },
    enterPlayedGame() {
      this.$router.push({
        name: 'playedGameScene'
      })
    },
    viewGameDetail(item) {
      if (item.istesting || item.channel == 'shandw') {
        return
      }
      this.$router.push({
        name: 'gameDetailsScene',
        params: {
          game: item.game_id
        }
      })
    },

    swtichGames(category) {
      this.category = category
      Storage.set('gameSceneCategory', this.category)
    },

    handleCommand(category) {
      this.swtichGames(category)
    },

    enterPoster(name, game_id) {
      if (game_id) {
        if (game_id == 'dragonmaster' && this.source == 'gxb') {
          return
        }
        this.$router.push({
          name: 'gameDetailsScene',
          params: {
            game: game_id
          }
        })
        return
      }
      if (name == 'poster-share') {
        if (this.islogin) {
          this.$router.push({
            name: 'shareScene'
          })
        } else {
          this.$router.push({
            name: 'loginScene',
            params: { url: '/share' }
          })
        }
      } else if (name == 'poster-eos') {
        this.eosTipsDialog = true
      } else if (name == 'poster-erc20') {
        if (this.islogin) {
          this.$router.push({
            name: 'assetScene'
          })
        } else {
          this.$router.push({
            name: 'loginScene',
            params: {
              url:
                window.location.protocol +
                '//' +
                window.location.host +
                '/asset'
            }
          })
        }
      } else if (name == 'poster-fishing1') {
        if (this.islogin) {
          this.$router.push({
            name: 'subscribeScene'
          })
        } else {
          this.$router.push({
            name: 'loginScene',
            params: {
              url:
                window.location.protocol +
                '//' +
                window.location.host +
                '/fishing'
            }
          })
        }
      } else if (name == 'luckdraw') {
        this.$router.push({
          name: 'luckdrawScene'
        })
      } else if (name == 'option') {
        this.playGame('', 'https://play.ones.game/dragon?item=option')
      }
    },

    viewVideo() {
      this.dialogVisible = true
    },

    playGame(game_id, url) {
      var game_ids = {
        dragonmaster: 1,
        landlord: 1,
        douro: 1,
        emperor: 1,
        fishing: 1,
        pkthreeking: 1,
        dragonclash: 1,
        witchtower: 1,
        longxingshewu: 1
      }
      if (!Util.isMobile() && !game_ids[game_id]) {
        this.$message({
          message: this.$t('message.error_app'),
          type: 'error',
          center: true
        })
        return
      }
      if (this.islogin) {
        this.$router.push({
          name: 'playScene',
          params: {
            game: game_id
          }
        })
      } else {
        this.$router.push({
          name: 'loginScene',
          params: { url: '/play/' + game_id }
        })
      }
      return
      var lang = this.$route.query.lang
      var _url = url
      if (lang) {
        _url += '?lang=' + lang
      }

      this.loading = this.$loading({
        lock: false,
        text: this.$t('message.redirecting'),
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      if (this.islogin) {
        // if (url) {
        //   window.location.href = _url
        // } else {
        gameService
          .play(game_id)
          .then(res => {
            console.log(res)
            // this.loading.close()
            window.location.href = res.data.url
          })
          .catch(err => {
            this.$message({
              message: this.$t('跳转游戏失败,请联系小七'),
              type: 'error',
              center: true
            })
            this.loading.close()
          })
        // }
      } else {
        this.$router.push({
          name: 'loginScene',
          params: { url: url ? _url : '/game/' + game_id + '?action=play' }
        })
      }
    }
  }
}
</script>

