<template>
  <div class="marketHistoryContainer">
    <el-card class="box-card">
      <div slot="header" class="clearfix" v-if="state=='list'">
        <span>交易记录</span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="state='my'">我的</el-button>
      </div>

      <div slot="header" class="clearfix" v-if="state=='my'">
        <span>我的</span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="state='list'">交易记录</el-button>
      </div>

      <ul class="marketMaking-list mymarketMaking-list" v-if="state=='my'">
        <li v-for="(item,index) in mySwapLogs" :key="index">
          <div class="topContent">
            <div class="rightCon">
              <div class="from">
                <img
                  :src="'/static/img/tokens/' + item.in_token.address +'-' +item.in_token.symbol.toLowerCase() + '.png' "
                  alt
                />
                <span>{{item.in_token.symbol}}</span>
              </div>
              <span class="and">
                <i class="el-icon-right"></i>
              </span>
              <div class="to">
                <img
                  :src="'/static/img/tokens/' + item.out_token.address +'-' +item.out_token.symbol.toLowerCase() + '.png' "
                  alt
                />
                <span>{{item.out_token.symbol}}</span>
              </div>
            </div>
            <div class="leftCon">
              <p class="time">{{item.timestamp|timeFilter}}</p>
            </div>
          </div>
          <div class="itemContent">
            <div class="rightCon">
              <div class="title">支付数量</div>
              <div class="contenttext">{{item.in_asset}}</div>
            </div>
            <div class="leftCon">
              <div class="title">获得数量</div>
              <div class="contenttext">{{item.out_asset}}</div>
            </div>
          </div>
          <div class="itemContent">
            <div class="rightCon">
              <div class="title">TxID</div>
              <div class="contenttext">
                <el-link
                  :href="item.trx_url"
                  :underline="false"
                  type="primary"
                >{{item.trx_id|trxidFilter}}</el-link>
              </div>
            </div>
            <div class="leftCon">
              <div class="title">兑换价格</div>
              <div class="contenttext">{{item.price|priceFilter}}</div>
            </div>
          </div>
        </li>
      </ul>

      <ul class="swap-list" v-if="state=='list'">
        <li v-for="(item,index) in swapLogs" :key="index">
          <div class="swap-log-header">
            <span
              v-if="item.account=='onesgamefund'"
              style="color:#0da88b;"
            >{{item.account|accountFilter}}</span>
            <span>{{item.account|accountFilter}}</span>

            <span v-if="item.account!='onesgamefund'">{{item.timestamp|timeFilter}}</span>
          </div>
          <div class="swap-log-content">
            <span class="swap-log-type">{{$t("defiScene.swap")}}</span>
            <div class="swap-log-detail">
              <p class="in">
                <span v-text="item.in_asset"></span>
              </p>
              <i class="el-icon-right"></i>
              <p class="out">
                <span v-text="item.out_asset"></span>
              </p>
            </div>
          </div>
        </li>
      </ul>
    </el-card>
  </div>
</template>

<script>
import EosApi from '../../api/eos'
import Util from '../../utils/util'
import EosService from '../../utils/eosservice'

export default {
  data() {
    return {
      url: 'https://bloks.io/transaction/',
      defiContract: 'onesgamedefi',
      logsContract: 'onesgamelogs',

      mySwapLogs: [],
      swapLogs: [],
      swapids: {},
      max_swap_id: 0,

      state: 'list'
    }
  },
  computed: {
    eosio: function() {
      return this.$store.state.eosio
    },
    account: function() {
      return this.$store.state.eos_account
    }
  },

  mounted() {
    this.getMySwapLog()

    this.getSwapLogs(this.max_swap_id, 10000)
    this.swapInterval = setInterval(
      function() {
        this.getSwapLogs(this.max_swap_id)
      }.bind(this),
      10000
    )
  },

  beforeDestroy() {
    clearInterval(this.swapInterval)
  },

  methods: {
    getSwapLogs(swap_id, next) {
      if (this.swapLoading) {
        return
      }
      console.log(parseInt(new Date() / 1000))
      this.eosio.getTableRows(
        {
          code: this.logsContract,
          scope: this.logsContract,
          table: 'swaplog',
          limit: 100,
          lower_bound: swap_id,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0) {
            var rows = data.rows
            for (var i in rows) {
              var row = data.rows[i]
              if (
                row.account.indexOf('mababa') >= 0 ||
                row.account.indexOf('segi') >= 0
              ) {
                continue
              }
              var swap_id = row.swap_id

              if (this.max_swap_id < swap_id) {
                this.swapLogs = [row].concat(this.swapLogs)
                if (this.swapLogs.length > 10) {
                  this.swapLogs.pop()
                }
                this.max_swap_id = swap_id
              }
            }

            if (data.more) {
              setTimeout(
                function() {
                  this.getSwapLogs(this.max_swap_id, 500)
                }.bind(this),
                next
              )
            }
          }
        }.bind(this)
      )
    },

    getMySwapLog() {
      if (!this.account) {
        return
      }
      EosApi.getSwapLog(this.account.name).then(res => {
        console.log(res)

        if (res.code == 0) {
          // var rows = []
          for (var i in res.data.rows) {
            var row = res.data.rows[i]

            row.in_token.symbol = row.in_token.symbol.split(',')[1]
            row.out_token.symbol = row.out_token.symbol.split(',')[1]

            row.trx_url = this.url + row.trx_id
            this.mySwapLogs.push(row)
            this.swapids[row.swap_id] = row.swap_id
          }
          //  = rows
        }
        this.getSwapLogFromChain()
      })
    },

    getSwapLogFromChain() {
      this.eosio.getTableRows(
        {
          code: this.logsContract,
          scope: this.logsContract,
          table: 'swaplog',
          limit: 1000,
          key_type: 'i64',
          json: true
        },
        function(code, msg, data) {
          if (code == 0) {
            var rows = data.rows
            rows.sort(function(a, b) {
              return a.timestamp - b.timestamp > 0 ? -1 : 1
            })
            for (var i in rows) {
              var row = data.rows[i]

              var swap_id = row.swap_id

              if (
                row.account == this.eosio.account.name &&
                !this.swapids[swap_id]
              ) {
                row.in_token.symbol = row.in_token.symbol.split(',')[1]
                row.out_token.symbol = row.out_token.symbol.split(',')[1]

                row.trx_url = this.url + row.trx_id
                this.swapids[swap_id] = swap_id
                this.mySwapLogs.push(row)
              }
            }
          }
        }.bind(this)
      )
    }
  },
  filters: {
    timeFilter(value) {
      return Util.formatDate(value, 'MM-dd hh:mm:ss')
    },
    trxidFilter(value) {
      return value.substr(0, 12) + '...'
    },
    priceFilter(value) {
      return Number(value).toFixed(6)
    }
  }
}
</script>

<style scoped>
.topNav {
  padding: 10px 0;
  text-align: center;
  font-size: 19px;
  font-weight: 800;
  background-color: #040718;
  border-bottom: 1px solid #ccc;
  color: rgb(97, 96, 96);
}
.topNav .el-icon-s-order {
  float: right;
  font-size: 27px;
  color: #ccc;
}
.marketMaking-list {
  background-color: #040718;
  padding: 0 15px;
}
.marketMaking-list > li {
  padding: 10px 0;
  border-bottom: 1px solid #ccc;
}
.marketHistoryContainer /deep/ .el-card {
  background-color: #040718;
  color: azure;
}
.marketMaking-list > li:last-child {
  border: none;
}
.marketMaking-list > li .topContent {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.marketMaking-list > li .topContent .rightCon {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.marketMaking-list > li .topContent .rightCon .from {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 800;
  font-size: 14px;
  color: rgb(253, 247, 247);
}
.marketMaking-list > li .topContent .rightCon .to {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 800;
  font-size: 14px;
  color: rgb(252, 250, 250);
}
.marketMaking-list > li .topContent .rightCon .and {
  font-size: 1.3rem;
  font-weight: 800;
  margin-left: 3px;
  margin-right: 3px;
  color: rgb(248, 247, 247);
}
.marketMaking-list > li .topContent .rightCon img {
  width: 25px;
  height: 25px;
  margin-right: 4px;
}
.marketMaking-list > li .topContent .leftCon {
  line-height: 25px;
}
.marketMaking-list > li .itemContent {
  display: flex;
  /* justify-content: space-between; */
  font-size: 1rem;
  line-height: 1.85rem;
}
.marketMaking-list > li .itemContent .title {
  color: rgb(247, 244, 244);
  font-size: 13px;
}
.marketMaking-list > li .itemContent .contenttext {
  color: rgb(248, 247, 247);
  font-size: 15px;
}
.marketMaking-list > li .itemContent .rightCon {
  color: rgb(253, 250, 250);
  width: 50%;
}
.marketMaking-list > li .itemContent .leftCon {
  color: rgb(255, 253, 253);
}
.marketMaking-list > li .time {
  font-size: 12px;
  color: rgb(252, 252, 252);
  margin-left: 10px;
}

/deep/ .el-card__body {
  padding: 0px;
}
.swap-list {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  background-color: #040718;
  margin-bottom: 120px;
}
.swap-list > li {
  padding: 1rem 0;
  border-bottom: 1px solid #ccc;
}
.swap-list > li:last-child {
  border: none;
}
.swap-list > li .swap-log-header {
  display: flex;
  justify-content: space-between;
  color: rgb(243, 243, 243);
  margin-bottom: 10px;
}
.swap-list > li .swap-log-header span:first-child {
  font-size: 16px;
}
.swap-list > li .swap-log-content {
  display: flex;
  justify-content: space-between;
  color: rgb(138, 185, 247);
}
.swap-list > li .swap-log-content .swap-log-detail {
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  color: #d2d6ec;
  font-size: 14px;
}
.swap-list > li .swap-log-content .el-icon-right {
  font-size: 16px;
  font-weight: 800;
  color: #9ebdfc;
  line-height: 20px;
}
</style>


