import axios from 'axios'
export default {

    post(properties) {
        var _url = process.env.API_HOST
        if (window.location.href.indexOf("play.ones.games/") > 0) {
            _url = process.env.TEST_HOST
        }
        
        var url = properties.url ? properties.url : _url;
        return axios.post(url + properties.path, {}, {
            headers: properties.headers,
            data: properties.data
        }).then((response) => {
            var res = {
                code: -1,
                msg: "network error"
            };
            if (response.status == 200) {
                res = response.data;
            }
            return Promise.resolve(res);
        }).catch(function (err) {
            console.error("client err", err);
            var msg = err.toString();
            msg = msg.indexOf("Network") > 0 ? "error_network" : msg;
            return Promise.resolve({
                code: 1,
                msg: msg
            })
        });
    },

    get(properties) {
        return axios.get(properties.url).then((response) => {
            var res = {
                code: -1,
                msg: "network error"
            };
            if (response.status == 200) {
                res = response.data;
            }
            return Promise.resolve(res);
        }).catch(function (err) {
            console.error("client err", err);
            var msg = err.toString();
            msg = msg.indexOf("Network") > 0 ? "error_network" : msg;
            return Promise.resolve({
                code: 1,
                msg: msg
            })
        });
    }
}


